.emptyMsgContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 421px;
}
.contentBox{
    text-align: center;
    padding-top: 1em;
    padding-bottom: 1em;
}
.contentBox .title{
    font-family: var(--primaryFontBold);
    font-size: 1em;
    color: var(--purple);
    margin-bottom: 11px;
}
.contentBox .message{
    font-family: var(--primaryFont);
    font-size: 1em;
    color: #686868;
    margin-bottom: 11px;
    padding-top: 30px;
}


/* Media Queries */
/* Mobile devices small screen */

@media (max-width: 380px) {
    .contentBox .title{

        font-size: 0.75em;

    }
    .contentBox .message{
        font-size: 0.75em;
    }

    .contentBox .actionLink{
        font-size: 0.75em;
    }
}

/* Mobile devices large screen */

@media (max-width: 575.98px) {
.DiscoveryOverviewWrapper {
    min-height: 250px;
}
.discoveryBtn {
    font-size: 0.875em;
}
.imgBox{
    width: 60px;
    height: 60px;
}
.contentBox .title, .contentBox .message{
    font-size: 0.85em;
}
}

/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
    .emptyMsgContainer{
        min-height: 250px;
    }
}


@media( min-width: 922px) and (max-width: 1199.99px) {
    /* .emptyMsgContainer{
        min-height: 260px;
    } */
    .contentBox .title{

        font-size: 0.85em;

    }
    .contentBox .message{
        font-size: 0.85em;
    }

    .contentBox .actionLink{
        font-size: 0.85em;
    }
    }


