.mainTitleBox .title{
    font-family: var(--primaryFontMedium);
    /* font-size: 1.5em; */
    margin-bottom: 32px;
}

.lineBarDiv{
    position: relative;
}
.lineBar{
    margin: 24px 0;
}
.lineBarDiv p{
    position: absolute;
    top: -9px;
    background: white;
    width: 35px;
    text-align: center;
    margin-bottom: 0;
    left: 47%;
    color: #5E606A;
    font-size: 14px;
    font-family: var(--primaryFontMedium);
    font-weight: 700;
}
.loginGoogle{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    border-radius: 60px;
    margin-bottom: 15px;
    padding: 1px;
}
.submitBtnGoogle{
    font-family: var(--primaryFontBold);
    background: #fff;
    font-size: 16px;
    max-width: 100%;
    width: 100%;
    height: 44px;
    color: #5E606A;
    padding-top: 7px;
    border: 0;
    transition: 0s;
}
.submitBtnGoogle svg{
    margin-right: 8px;
    position: relative;
    top: -2px;
}
.submitBtnGoogle:hover{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    color: #fff;
    transition: 0s;
}
.submitBtnGoogle:hover svg path{
    fill: #fff;
}