.btn.focus,
.btn:focus {
  outline: none;
  box-shadow: none;
}
.btn-badge-pill {
  background-color: rgb(236, 236, 236);
  color: rgb(0, 0, 0);
  border-radius: 20px;
  padding: 2px 20px;
}
.selectable-btn-wrapper .btn-badge-pill {
  background-color: rgb(255, 255, 255);
  border-color: rgb(236, 236, 236);
}

.btn-badge-pill.active {
  background-color: rgb(140, 56, 175);
  color: rgb(255, 255, 255);
  border-radius: 20px;
  padding: 2px 20px;
}

.outline-pill {
  background-color: transparent !important;
  outline: #442f6e;
  color: #442f6e !important;
  border-radius: 20px;
  padding: 2px 10px !important;
  min-width: 150px !important;
}

.outline-pill:not(:disabled):not(.disabled):active:focus {
  box-shadow: 0 0 0 0.2rem rgb(68 47 110 / 50%) !important;
}

.outline-pill:active,
.outline-pill:focus,
.outline-pill:not(:disabled):not(.disabled):active:focus {
  background-color: transparent !important;
  outline: #442f6e;
  color: #442f6e !important;
  border-radius: 20px !important;
  padding: 2px 20px !important;
  border-color: #442f6e !important;
}

.btn-primary {
  font-family: var(--primaryFont);
  background-color: var(--primaryColor);
  border-color: var(--primaryColor);
  color: #fff;
  /* min-width: 215px; */
}

.btn-primary:hover {
  background-color: #442f6e;
  border-color: #442f6e;
}

.btn-primary:not(:disabled):not(.disabled):active{
  color: #fff;
  background-color: #442f6e;
  border-color: #442f6e;
}
.btn-primary:focus {
  color: #fff;
  background-color: #442f6e;
  border-color: #442f6e;
  box-shadow: none;
}

.btn-primary.disabled, .btn-primary:disabled {
  color: #fff;
  background-color: #442f6e;
  border-color: #442f6e;
}

.show>.btn-primary.dropdown-toggle {
  color: #fff;
  background-color: #442f6e;
  border-color: #442f6e;
}
.show>.btn-primary.dropdown-toggle:focus {
  box-shadow: none;
}

.btn-outline-primary {
  color: #442f6e;
  border-color: #442f6e;
}

.btn-outline-primary:hover {
  color: #fff;
  background-color: #442f6e;
  border-color: #442f6e;
}

.btn-check:active + .btn-outline-primary, .btn-check:checked + .btn-outline-primary, .btn-outline-primary.active, .btn-outline-primary.dropdown-toggle.show, .btn-outline-primary:active {
  color: #fff;
  background-color: #442f6e;
  border-color: #442f6e;
  box-shadow: none;
}

.btn-check:active + .btn-outline-primary:focus, .btn-check:checked + .btn-outline-primary:focus, .btn-outline-primary.active:focus, .btn-outline-primary.dropdown-toggle.show:focus, .btn-outline-primary:active:focus {
  box-shadow: none;
}

/* social button */
.btn-facebook {
  background-color: #4267b2;
  color: #ffffff;
}

.btn-facebook i {
  margin-right: calc(0.34435vw + 3.38843px);
}

.btn-min-width {
  min-width: 215px;
}

.btn-google > div {
  margin-right: calc(0.34435vw + 3.38843px) !important;
  padding: 0 !important;
}

.btn-google > span {
  padding: 8px 10px 8px 0px !important;
  font-family: var(--primaryFont);
}


@media (max-width: 575.98px) {
  .btn-badge-pill{
    font-size: 14px;
  }
}