.DiscoveryOverviewWrapper{
    /* background-image: url("../../../../../assets/images/dashboard/bg-img2.svg") ;
    background-size: cover;
    background-repeat: no-repeat; */
    min-height: 530px;
    width: auto;
    border: 0px;
    position: relative;
}

.innerHeading {
    font-family: var(--primaryFontBold);
    font-size: 24px;
    color: #52545A;
    margin-bottom: 0;
    margin-top: 5px;
}

.innerDiv {
    background: #E6E7EB;
    height: -webkit-fill-available;
    width: 96%;
    position: absolute;
    top: 70px;
    left: 2%;
    bottom: 14px;
    border-radius: 12px;
    padding: 16px 0 0;
    overflow: hidden;
    text-decoration: none;
}

.innerDiv:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 51.89%, rgba(0, 0, 0, 0.34) 113.01%);
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
}

.DiscoveryOverBody {
    display: flex;
    flex-direction: column;
}

.topLinkstars {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    /* animation: rotate 3s linear infinite; */
}

.innerIcon {
    padding: 0 10px 0 10px;
    margin-bottom: 10px;
}

.innerIcon img {
    width: 85px;
    height: 85px;
    border-radius: 100px;
    margin: auto;
}

.innerIcon p{
    margin-bottom: 0;
    background: #fff;
    text-align: center;
    color: #5E606A;
    padding: 4px 8px 3px;
    margin-top: 9px;
    font-size: 10px;
    width: max-content;
    font-family: var(--primaryFont);
    font-weight: 600;
    border-radius: 40px;
    margin: 10px auto;
}

.discoverHeading {
    visibility: hidden;
}
.discoveryBtn {
    background: #fff;
    position: relative;
    bottom: 44px;
    color: #5B3F91;
    z-index: 10;
    border: 0;
    text-decoration: none;
    height: 36px;
    font-family: var(--primaryFont);
    font-weight: 600;
    width: 170px;
    line-height: 36px;
    transition: .3s;
    display: block;
    margin: auto;
    font-size: 16px;
    box-shadow: rgb(0 0 0 / 25%) 0px 54px 55px, rgb(0 0 0 / 12%) 0px -12px 30px, rgb(0 0 0 / 12%) 0px 4px 6px, rgb(0 0 0 / 17%) 0px 12px 13px, rgb(0 0 0 / 9%) 0px -3px 5px;
}
.discoveryBtn:hover {
    color:#fff;
    transition: .3s;
    background: #5B3F91;
}

