.logo {
    transform: scale(1.5);
}

.navLink {
    border-radius: 40px;
    padding-left: 20px !important;
    padding-right: 20px !important;
    font-size: 17px;
    font-weight: 600;
    font-family: var(--primaryFontBold);
    padding-top: 12px;
    position: relative;
    top: 4px;
}

.navLink:hover {
    background: orange;
}


.navBtn {
    min-height: 42px;
    padding-inline: 25px;
    font-family: var(--primaryFontBold) !important;
}

.navBtn:hover {
    background: #5b3f91 !important;
    color: var(--bs-white) !important;
}

.navSignUp:hover {
    background: #8b39b2 !important;
    color: var(--bs-white);
}