.deleteConfirmModal .modalTitle{
    font-family: var(--primaryFontMedium);
    font-size: 24px;
    line-height: 26px;
    color: var(--primaryColor);
    margin-bottom: 22px;
}

.deleteConfirmModal .modalText{
    font-family: var(--primaryFont);
    font-size: 1em;
    line-height: 20px;
    color: #575757;
    margin-bottom: 43px;
}

.deleteConfirmModal .modalContent{
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
    box-shadow: 0px 2px 6px rgba(31, 61, 84, 0.2);
    border: none;
}

.deleteConfirmModal .modalHeader{
    border-bottom: none;
}

.deleteConfirmModal .modalHeader .close{
    color: var(--primaryColor);
}

.deleteConfirmModal .modalBody{
    text-align: center;
    padding: 16px 32px 44px 32px;
}

.deleteConfirmModal .modalBody .btnBox{
    display: flex;
    justify-content: space-evenly;
}

.deleteConfirmModal .modalBody .btnBox .btnAction{
    padding: 11px 16px;
    font-family: var(--primaryFontMedium);
}

@media (max-width: 575.98px) {
    .deleteConfirmModal .modalBody{
        padding: 0px 32px 44px 32px;
    }

    .deleteConfirmModal .modalBody .icon{
        margin-bottom: 32px;
    }
    
    .deleteConfirmModal .modalBody .btnBox{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .deleteConfirmModal .modalBody .btnBox .btnAction{
        min-width: 177px;
        margin-bottom: 22px;
        width: fit-content;
    }
    .deleteConfirmModal .modalBody .btnBox .btnAction:last-child{
        margin-bottom: 0px;
    }
}