/* The checkBoxCont */
.tsbsDiv:first-child {
    margin-left: 0;
}
.checkBoxCont {
        position: relative;
    cursor: pointer;
    font-size: 16px;
    -webkit-user-select: none;
    user-select: none;
    z-index: 1;
    color: #858585;
    text-align: center;
    display: flex;
    line-height: 34px;
    border-radius: 50px;
    left: 0;
  }

  /* Hide the browser's default checkbox */
  .checkBoxCont .checkInput {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  /* Create a custom checkbox */
  .checkBoxStyle {
    background-color: #fff;
    z-index: -1;
    border: 1px solid #858585;
    height: 35px;
    border-radius: 50px;
    width: max-content;
    padding: 0 20px;
  }

  /* On mouse-over, add a grey background color */
  .checkBoxCont:hover .checkInput ~ .checkBoxStyle {
    background-color: #ccc;
  }

   /* When the checkbox is checked, add a blue background */
   .checkInput:checked .checkBoxCont {
    color: #fff;
  }

  /* When the checkbox is checked, add a blue background */
  .checkBoxCont .checkInput:checked ~ .checkBoxStyle {
    background-color: #7250B2;
    color: #fff;
  }

  /* Create the checkmark/indicator (hidden when not checked) */
  .checkBoxStyle:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the checkmark when checked */
  .checkBoxCont .checkInput:checked ~ .checkBoxStyle:after {
    display: block;
  }

  /* Style the checkmark/indicator */
  .checkBoxCont .checkBoxStyle:after {
    top: -8px;
    border-width: 0 3px 3px 0;
    position: absolute;
    right: -8px;
    background: #5127a1;
    content: "\271B";
    color: #fff;
    font-size: 17px;
    height: 24px;
    width: 24px;
    line-height: 26px;
    border-radius: 20px;
    transform: rotate(45deg);
    box-shadow: 0px 5px 8px #cbcbcb4a;
  }

  .industryStep .DropDownBox{
    margin-bottom: 14px;
  }

  @media (max-width: 575.98px){
    .industryStep .DropDownBox{
      margin-bottom: 24px;
    }

    .industryStep h2{
      font-size: 24px;
    }
  }