@keyframes ani_bell {
    90% {
      transform: scale(1.1);
    }
}

:global(.countNotify) {
    animation: ani_bell 1s infinite;
}

.topNotify{
    background: #FBFCFF;
    padding: 12px;
}
.topNotify h3{
    font-size: 18px;
    line-height: 24px;
    color: #343434;
    font-family: var(--primaryFontBold);
}
.topNotify p{
    font-size: 14px;
    line-height: 18px;
    color: #5E606A;
    margin-bottom: 0;
    font-family: var(--primaryFontBold);
}
.topNotify p span{
    background: #F07C26;
    border-radius: 16px;
    padding: 4px 12px;
    color: #fff;
    font-size: 11px;
    position: relative;
    top: -1px;
    left: 7px;
}
.notifyList{
    
}
.notifyListUl li .textNotifyDiv{
    margin-bottom: 20px;
}
.notifyListUl li .textNotifyDiv:last-child{
    margin-bottom: 0px;
}
.notifyListing{
    max-height: 340px;
    overflow-y: scroll;
}
.notifyList ul{
    margin: 0;
    padding: 0;
}
.notifyList li{
    list-style: none;
    margin-bottom: 0;
    border-bottom: 3px solid #fbfcff;
}
.AccordionsingleNotifyBar{
    display: block;
    position: relative;
    padding: 12px;
}
.AccordionsingleNotifyBar .imgIcon{
    width: 32px;
    height: 32px;
    position: relative;
}
.AccordionsingleNotifyBar img{
    width: 32px;
    height: 32px;
    border-radius: 50px;
}
.seenMsgEye{
    position: absolute;
    width: 19px;
    height: 19px;
    left: 18px;
    background: #E7E7E7;
    border-radius: 30px;
    top: 17px;
}
.seenMsgEye svg{
    width: 13px;
    height: 13px;
    position: absolute;
    left: 3px;
    top: 3px;
}
.textNotifyDiv{
    padding-left: 10px;
    padding-right: 21px;
    position: relative;
}
.textNotifyDiv .notifyText{
    font-size: 14px;
    line-height: 18px;
    color: #343434;
    font-weight: 500;
    margin-bottom: 5px;
}
.textNotifyDiv .notifyText span{
    font-family: var(--primaryFontBold);
}
.textNotifyDiv .notifyTime{
    margin-bottom: 0;
    font-weight: 500;
    font-size: 10px;
    line-height: 12px;
    color: #858585;
}
.seenMsg{
    position: absolute;
    right: 12px;
    top: 0;
    height: 100%;
    display: flex;
    align-items: center;
}
.seenMsg svg{
    height: 12px;
    width: 12px;
}
.seenLike{
    position: absolute;
    width: 19px;
    height: 19px;
    left: 18px;
    background: #DC3646;
    border-radius: 30px;
    top: 17px;
}
.seenLike svg{
    width: 13px;
    height: 13px;
    position: absolute;
    left: 3px;
    top: 3px;
}
.seenMsgIcon{
    position: absolute;
    width: 19px;
    height: 19px;
    left: 18px;
    background: #FFB96C;
    border-radius: 30px;
    top: 17px;
}
.seenMsgIcon svg{
    width: 13px;
    height: 13px;
    position: absolute;
    left: 3px;
    top: 3px;
}
.topNotifyOlder h3{
    margin-bottom: 0;
}
.closeBtn{
    display: none;
}

@media (max-width: 575.99px) {
    .notifyBellIcon{
        position: absolute;
        right: 35px;
        top: 18px;
        height: 100%;
    }
    .closeBtn{
        display: block;
    position: absolute;
    top: 65px;
    right: -32px;
    }
}