.calendlyBtn{
    background: #FFFFFF;
    position: fixed;
    bottom: 30px;
    right: 30px;
    z-index: 5;
    display: block;
    width: 48px;
    height: 48px;
    padding: 0px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    border: 0px;
    box-shadow: 0px 4px 40px rgb(36 36 36 / 20%);
}
.bookCall{
    position: fixed;
    bottom: 28px;
    right: 28px;
    z-index: 1000;
    display: block;
    width: 150px;
    height: 52px;
    padding: 0px;
    border: 1px solid #E7E7E7;
    box-shadow: 0px 4px 40px rgb(36 36 36 / 20%);
    background: #fff;
    opacity: 0;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    border-radius: 50em;
    transition: .4s;
}
.bookCall span{
    background: linear-gradient(289.79deg, #9772EE 23.59%, #9461FA 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
}

.calendlyBtn:hover .bookCall{
    opacity: 1;
    transition: .4s;
}

.text {
    position: absolute;
    right: 40px;
    display: inline-block;
    background: #9461fa;
    top: 10px;
    font-size: 14px;
    padding: 4px 10px;
    white-space: nowrap;
    color: #fff;
    opacity: 0;
    transition: all 0.5s;
}

.text:after {
    content: " ";
    position: absolute;
    width: 10px;
    height: 10px;
    right: -10px;
    top: 7px;
    border-left: 8px solid #9461fa;
    border-top: 7px solid transparent;
    border-bottom: 7px solid transparent;
}

