/* Media Queries */
/* Mobile devices small screen */

.tooltip {
    background-color: #FFF8F1;
}

.tooltip :global(.introjs-button) {
    padding: 2px 0px;
    border: 0;
    border-bottom: 1px solid;
    border-radius: 0;
    margin: 0 6px 10px;
    background: transparent;
}

.highlight {
    box-shadow: 0px 0px 0px 1px #5b3f91 !important;
}
:global(.introjs-helperLayer).highlight {
    box-shadow: 0 0 0 1px #5b3f91 !important;
}

@media (max-width: 380px) {
    .componentHeading {
        font-size: 1em !important;
    }

    .textCaption {
        font-size: 0.875em !important;
    }
    .analyticDetailCard {
        min-height: 110px !important;
    }
}

/* Mobile devices large screen */

@media (max-width: 575.98px) {
    .componentHeading {
        font-size: 1.125em;
    }

    .textCaption {
        font-size: 1em;
    }
}


/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
    .componentHeading {
        font-size: 1.2125em;

    }
    .analyticDetailCard {
        min-height: 166px !important;
    }

    .detImgBox {
        right: -27%;
        opacity: 0.3;
        bottom: -77%;
        width: 91%;

    }


}

