}

.summaryCard {
    border-radius: 18px;
    min-height: 210px;
    overflow: hidden;

}

.summaryCardBody {
    justify-content: space-between;
    display: flex;
    flex-direction: column;

}

.heading {
    color: inherit;
}
.subHeading, .bodyText{
    color: inherit;
}
.linkText {
    font-family: var(--primaryColor);
}
/* Mobile devices small screen */

@media (max-width: 380px) {}

/* Mobile devices large screen */

@media (max-width: 575.98px) {
    .summaryCard {
        margin-top: 0.75em;
        margin-bottom: 0.75em;
    }

    .summaryCard {
        min-height: 165px;
    }
}

/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {}

/* Extra large devices (large desktops, 1200px and up) */

@media (min-width: 1200px) and (max-width: 1399.98px) {}

@media (min-width: 1400px) and (max-width: 1599.98px) {}