.theme-books-section.style_1 .content-container {
  box-shadow: 0px 5px 20px #00000029;
  border-radius: 30px;
}

.theme-books-section.style_2 .content-container {
  border: none;
  box-shadow: none;
}


/* Freelance service section code  */

.freelancer-service-box {
  /* background-image: url("../../../assets/images/layers/overlay-dots.png");
    background-repeat: no-repeat;
    background-position: top right;
    background-size: 27%; */
  position: relative;
  border-radius: 50px;
}

.freelancer-service-box .section-tag {
  font-family: var(--themePrimaryFont);
  font-size: 2em;
  margin-bottom: 1.25rem;
  color: var(--themePrimaryColor);
  font-weight: 600;
}

.freelancer-service-box .message-text {
  font-size: 1.3em;
  font-family: var(--themePrimaryFont);
  line-height: 40px;
  letter-spacing: 1px;
  margin-bottom: 1.4em;
}

.btn-box.btn-more .btn {
  min-width: 174px;
}

.circle-overlay-container {
  position: relative;
  z-index: 0;
}

.circle-overlay-solid {
  width: 310px;
  height: 310px;
  background-color: var(--themePrimaryColor);
  border-radius: 50%;
  position: absolute;
  top: 0px;
  right: 0px;
  /* background-blend-mode: overlay;
    mix-blend-mode: soft-light; */
  opacity: 0.5;
  z-index: 0;
}

/* Freelance service section code ends */

/* theme generic section code */
.theme-service-box .section-tag {
  color: var(--themeTextColor) !important;
}

.theme-service-box .message-text {
  color: var(--themeTextColor) !important;
}

