.theme-book-section .card .card-body {
  min-height: 11em;
}

.borderless-card .card-body .card-text {
  overflow: auto;
  max-height: 7em;
}

#books.style_3 .img-short-card .card-footer .btnSection button {
  text-decoration: none;
  min-width: auto;
  font-size: 16px !important;
  color: var(--themeTextColor) !important;
  background: transparent;
}

#books.style_3 .img-short-card .card-title {
  color: var(--themeTextColor) !important;
}

#books.style_3 .img-short-card .card-text {
  color: var(--themeTextColor) !important;
}

#books.style_3 .img-short-card .card-body {
  padding-bottom: 15px;
}

#books.style_3 .card-footer {
  padding-top: 20px;
}
#books.style_3 .card-edit-block .edit-box{
  margin-right: 0;
}

/* theme generic section code */

@media (max-width: 575.98px) {
  .circle-overlay-solid {
    width: 100px;
    height: 100px;
    background-color: var(--themePrimaryColor);
    border-radius: 50%;
    position: absolute;
    top: 0px;
    right: 0px;
    /* background-blend-mode: overlay;
        mix-blend-mode: soft-light; */
    opacity: 0.5;
  }

  .freelancer-service-box .section-tag {
    font-size: 1.6em;
    margin-bottom: 18px;
    font-weight: 800;
  }

  .freelancer-service-box .message-text {
    font-size: 1em;
    font-weight: 800;
    margin-bottom: 24px;
    line-height: 24px;
  }

  #books.style_3 .card-wrapper {
    margin-bottom: 15px;
}

  #books.style_3 .card-footer {
    padding-top: 0px;
  }
}