.upgradeConfirmationModal .modalContent {
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
}

.upgradeConfirmationModal .modalHeader {
    border-bottom: none;
}

.upgradeConfirmationModal .modalHeader .closeIcon {
    border: none;
    margin-left: auto;
}

.upgradeConfirmationModal .modalHeader .closeIcon .icon{
    color: var(--primaryColor);
}

.upgradeConfirmationModal .modalHeader .title {
    font-family: var(--primaryFontBold);
    font-size: 2.625em;
    text-align: center;
}

.upgradeConfirmationModal .contentBox {
    width: 81%;
    margin: 0 auto;
    text-align: center;
}

.upgradeConfirmationModal .contentBox .heading {
    font-size: 1.375em;
    font-family: var(--primaryFontMedium);
    margin-bottom: 16px;
    color: #5E606A;
}

.contentBox .desc {
    font-family: var(--primaryFont);
    font-size: 1em;
    color: #7A7A7A;
}

.upgradeConfirmationModal .modalFooter {
    justify-content: center;
    border-top: none;
}

.btnOutlinePrimary {
    background-color: #ffffff;
    color: var(--primaryColor);
    border: 1px solid var(--primaryColor);
    text-decoration: none;
    padding: 12px 33px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    min-width: 181px;
    margin-right: 22px;
    text-align: center;
}

.btnOutlinePrimary:hover {
    color: var(--primaryColor);
    cursor: pointer;
}

.btnPro {
    background-color: var(--orange);
    color: #ffffff;
    text-decoration: none;
    padding: 12px 33px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    min-width: 181px;
    text-align: center;
}

.btnPro:hover {
    color: #ffffff;
    cursor: pointer;
}

.upgradeConfirmationModal .modalFooter {
    margin-bottom: 65px;
}

@media (max-width: 575.98px) {
    .upgradeConfirmationModal .modalHeader .title {
        font-size: 1.375em;
    }

    .upgradeConfirmationModal .contentBox {
        width: 100%;
    }

    .upgradeConfirmationModal .contentBox .heading {
        font-size: 1.125em;
    }

    .upgradeConfirmationModal .modalFooter .btnBox {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .upgradeConfirmationModal .modalFooter .btnOutlinePrimary {
        margin-right: 0px;
        margin-bottom: 16px;
    }
}