.contentHeadingBox{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 24px;
}
.dFlexDiv {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.btnNew {
    background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85% );
     color: #ffffff;
     font-family: var(--primaryFontBold);
     font-size: 14px;
     margin-left: 8px;
     border: none;
     z-index: 1;
     border-radius: 40px;
     box-shadow: rgb(100 100 111 / 20%) 0px 7px 29px 0px;
     transition: .3s;
     height: 45px;
     padding: 0 20px;
     text-transform: capitalize;
 }
 .btnNew:hover {
     background: linear-gradient(289.79deg, #FFA10D 23.59%, #FF7439 63.85% );
     transition: .3s;
     transform: scale(1.1);
     border-radius: 5px;
 }

.title{
    font-family: var(--primaryFontBold);
    font-size: 24px;
    color: #52545A;
}
.title span {
    background: linear-gradient(289.79deg, #9772EE 23.59%, #9461FA 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    font-style: italic;
    font-weight: 800;
    font-size: 24px;
    line-height: 32px;
}
.para{
    font-family: var(--primaryFont);
    font-size: 14px;
    color: #858585;
    margin-bottom: 0px;
}
.tutorialWrapper{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.imgBox{
    margin-right: 5px;
    cursor: pointer;
}
.imgBox .icon{
    width: 33px;
    height: 33px;
}

.infoBox{
    color: #52545a;
}

.tutorialWrapper .text{
    font-size: 0.75rem;
    color: inherit;
    line-height: 18px;
    margin-bottom: 1px;
}

.stepTutorialModal iframe{
    display: block;
}
.modalTitle {
    font-size: 20px;
    font-weight: 700;
    color: #000;
    font-family: var(--primaryFontBold);
}

/* Media Queries */
/* Mobile devices small screen */

@media (max-width: 380px) {

}

/* Mobile devices large screen */

@media (max-width: 575.98px) {
    .contentHeadingBox{
        margin-bottom: 16px;
    }
    .title{
        font-size: 1.3em;
    }
    .para{
        font-size: 1em;
    }

    .dashboardContentBox .dashboardContentBody{
        padding: 17px 11px;
    }
    .modalTitle {
        font-size: 16px;
        line-height: 19px;
    }
    .dFlexDiv {
        display: flex;
        width: 100%;
        flex-direction: column;
        margin-top: 65px;
        align-items: flex-start;
    }
    .btnNew {
        margin-top: 15px;
    }
}

/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {

    .title{

        font-size: 1.3em;
    }
    .para{
       font-size: 1em;

    }
}


@media( min-width: 922px) and (max-width: 1199.99px) {

    .title{

        font-size: 1.3em;
    }
    .para{
       font-size: 1em;

    }

    }


