
.publishModal .modalContent {
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
}

.publishModal .closeBtn{
    position: absolute;
    top: 20px;
    right: 20px;
}

.publishModal .modalHeader {
    border-bottom: none;
    padding: 0px;
}

.publishModal .modalHeader .img {
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
}

.publishModal .modalBody {
    padding: 24px;
}

.publishModal .modalBody .title {
    font-size: 22px;
    font-family: var(--primaryFontBold);
    color: var(--primaryColor);
    margin-bottom: 15px;
}

.publishModal .publishGuideList .guideList {
    padding-left: 20px;
}

.publishGuideList .guideList .listItem {
    list-style: none;
    padding-left: 10px;
    color: #575757;
    position: relative;
}


.publishGuideList .guideList .listItem:before {
    content: " ";
    position: absolute;
    left: -20px;
    width: 20px;
    height: 20px;
    background-image: url(data:image/png;base64,);;
    background-position: center center;
    background-repeat: no-repeat;
}

.publishModal .userLinkWrapper {
    margin-top: 22px;
}

.publishModal .modalBody .linkTitle {
    color: var(--primaryColor);
}

.publishModal .userLinkWrapper .userLink {
    color: var(--orange);
}



@media (max-width: 800px) {
    
    :global(.modal[class*="PublishModal"] .modal-dialog-centered) {
        min-height: calc(100% - 1rem);
    }
    
    .publishModal .modalHeader {
        height: 218px;
        overflow: hidden;
        border-radius: 15px;
    }
}


@media (max-width: 575.99px) {
    .publishModal .modalBody .title {
        font-size: 18px;
    }

    .publishGuideList .guideList .listItem {
       font-size: 14px;
    }
}