
.navbar-drawer.offcanvas-end {
  width: 65%;
  border: none;
  top: 60px;
  box-shadow: -3px 3px 6px #00000029;
}

.navbar-drawer .offcanvas-body {
  padding: 0 7px;
}

.offcanvas-backdrop {
  display: none;
}

.navbar-drawer .drawer-header {
  position: relative;
  margin-top: 30px;
  text-align: center;
}

.navbar-drawer .drawer-header .user-img{
  width: 60px;
  height: 60px;
  border-radius: 50em;
  -webkit-border-radius: 50em;
  -moz-border-radius: 50em;
  -ms-border-radius: 50em;
  -o-border-radius: 50em;
}

.drawer-header .drawer-heading{
  font-size: 0.9375em;
  font-family: var(--primaryFontMedium);
  border-bottom: 1px solid #D5D5D5;
  padding: 12px 25px;
  width: fit-content;
  margin: 0 auto;
}

.drawer-main-menu {
  margin-top: 3em !important;
  padding: 0em;
  list-style: none;
  font-weight: bold;
  color: #fff;
}

.drawer-menu-item {
  /* background-color: black; */
  padding: 12px 26px;
  margin-bottom: 0em;
}

.drawer-menu-item a {
  margin: 0;
  font-size: 1em;
  text-decoration: none;
  color: black;
  font-family: var(--primaryFontMedium);
}

.drawer-menu-item.collapsable {
  margin-left: -1.5em;
  margin-right: -1.3em;
}

.drawer-menu-item.selected {
  background: #000000db;
  color: white;
}
/* .drawer-menu-item:hover{
  background: #000000db;

} */

.drawer-menu-item.selected a {
  color: white;
}

.drawer-sub-menu {
  display: none;
  list-style: none;
  /* margin: 1.3em 0em; */
  padding: 0;
}

.drawer-sub-menu.show {
display: block;
list-style: none;
/* margin: 1.3em 0em; */
padding: 0;
}

.drawer-sub-menu li {
    padding: 1em;
    margin-bottom: 0em;
}

.drawer-sub-menu li a {
  padding-left: 2.5em;
  text-decoration: none;
  color: black
}



