.discover_page {
    overflow-x: hidden;
}

.discoverTabs_area .discover-register-dropdown {
    text-align: left;
    display: block;
    width: 100%;
    padding: 16px;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    appearance: none;
}
.discoverTabs_area .dropdown-menu.show{
    position: relative !important;
    transform: translate(0, 0) !important;
}


.discoverTabs_area .discover-register-dropdown::after {
    top: 26px !important;
    right: 16px !important;
}

.discoverTabs_area .form-control{
    padding: 16px;
}

.discoverTabs_area .nav-tabs {
    border: none;
}
.discoverTabs_area .btn-badge-pill.active{
    background: #F58634;
}
.animatestep3{
    position: absolute;
    left: 80px;
    top: 10%;
    opacity: 1;
    animation: animatestep3 1000ms;
}

.animatestep32 {
    position: absolute;
    right: 0;
    top: 30%;
    opacity: 1;
    animation: animatestep32 1500ms;
}

.animatestep33 {
    position: absolute;
    left: 80px;
    top: 80%;
    opacity: 1;
    animation: animatestep33 2000ms;
}

.animate4Text h2 {
    font-family: var(--primaryFontBold);
    color: #fff;
    margin-bottom: 60px;
    font-size: 32px;
    opacity: 1;
    position: absolute;
    width: 100%;
    top: -90px;
    animation: textStep2 1000ms;
}

.animate5Brands {
    opacity: 1;
    animation: brandsStep2 1500ms;
}

.step2Tabs {
    position: absolute;
    width: 100%;
    left: 0;
    top: 50%;
    transform: translate(0%, -50%);
    text-align: center;
}

.tabsRightImg .stars1 {
    position: absolute;
    top: 18%;
    left: 8%;
    opacity: 1;
    animation: starsAnimate 2500ms;
}

.tabsRightImg .stars2 {
    position: absolute;
    top: 50%;
    left: 53%;
    opacity: 1;
    animation: starsAnimate 2500ms;
}

.tabsRightImg .stars3 {
    position: absolute;
    top: 83%;
    left: 20%;
    opacity: 1;
    animation: starsAnimate 2500ms;
}

.tabsRightImg .animate1 {
    position: absolute;
    top: -110px;
    left: 150px;
    transform: translate(-50%, 50%);
    animation: animate1 1000ms;
}

.tabsRightImg .animate2 {
    position: absolute;
    top: 70px;
    right: -100px;
    transform: translate(-50%, 50%);
    animation: animate2 1500ms;
}

.tabsRightImg .animate3 {
    position: absolute;
    left: 20px;
    top: 338px;
    transform: translate(0%, 50%);
    animation: animate3 2000ms;
}

.tabsRightImg .animate4 {
    position: absolute;
    right: 50%;
    bottom: 25px;
    transform: translate(50%, 50%);
    animation: animate3 2000ms;
    -webkit-transform: translate(50%, 0%);
    -moz-transform: translate(50%, 0%);
    -ms-transform: translate(50%, 0%);
    -o-transform: translate(50%, 0%);
}

.step41 {
    position: absolute;
    left: 65px;
    top: 10%;
    opacity: 1;
    animation: step41 1000ms;
}

.step42 {
    position: absolute;
    right: 65px;
    top: 30%;
    opacity: 1;
    animation: step42 1000ms;
}

.step43 {
    position: absolute;
    left: 0;
    top: 55%;
    opacity: 1;
    animation: step43 1000ms;
}

.step44 {
    position: absolute;
    right: 0;
    bottom: 0;
    opacity: 1;
    animation: step44 1000ms;
}

.step5Tabs .step5 {
    position: absolute;
    left: 19%;
    top: 27%;
    opacity: 1;
    animation: step5 1000ms;
}

@keyframes animate1 {
    from {
        right: -40%;
        transform: translate(0%, 50%);
    }
}

@keyframes animate2 {
    from {
        top: -20%;
        transform: translate(-50%, 0%);
    }
}

@keyframes animate3 {
    from {
        right: -40%;
        transform: translate(0%, 50%);
    }
}

@keyframes starsAnimate {
    from {
        margin-top: 60px;
        opacity: 0;
    }
}

@keyframes textStep2 {
    from {
        top: -200px;
        opacity: 0;
    }
}

@keyframes brandsStep2 {
    from {
        margin-top: 120px;
        opacity: 0;
    }
}

@keyframes animatestep3 {
    from {
        top: -20%;
        opacity: 0;
    }
}

@keyframes animatestep32 {
    from {
        right: -20%;
        opacity: 0;
    }
}

@keyframes animatestep33 {
    from {
        margin-top: -120px;
        opacity: 0;
    }
}

@keyframes step41 {
    from {
        top: -30%;
        opacity: 0;
    }
}

@keyframes step42 {
    from {
        right: -20%;
        opacity: 0;
    }
}

@keyframes step43 {
    from {
        left: 20%;
        opacity: 0;
    }
}

@keyframes step44 {
    from {
        right: -20%;
        opacity: 0;
    }
}

@keyframes step5 {
    from {
        top: -20%;
        opacity: 0;
    }
  }


  @media (max-width: 1366px) {
    .tabsRightImg .animate1 {
        top: -70px;
        left: 170px;
  }
    .tabsRightImg .animate2 {
        top: 70px;
        right: -140px;
    }
    .tabsRightImg .animate3 {
        left: 40px;
        top: 290px;
    }
    .tabsRightImg .animate4 {
        right: 45%;
        bottom: 10px;
    }

    .tabsRightImg .animate1 > img {
        width: 80%;
    }
    
    .tabsRightImg .animate2 > img {
        width: 80%;
    }
    
    .tabsRightImg .animate3 > img {
        width: 80%;
    }
    
    .tabsRightImg .animate4 > img {
        width: 80%;
    }
  }


  @media (max-width: 1100px) {
    .tabsRightImg .animate1 {
        left: 9%;
        top: 30px;
        transform: translate(0px, 0px);
    }
    .tabsRightImg .animate3 {
        bottom: 35%;
    }
    .tabsRightImg .animate2 {
        position: absolute;
        right: 53%;
        transform: translate(-50%, 50%);
        top: 49%;
    }
    .tabsRightImg .stars1 {
        left: 3%;
    }
    .step2Tabs {
        top: 62%;
    }
    .animatestep33 {
        left: 31%;
    }
    .step41 {
        left: 193px;
    }
    .step42 {
        right: 250px;
        top: -19%;
    }
    .step5Tabs .step5 {
        top: 22%;
        width: 100%;
        text-align: center;
        left: 0;
    }
    .step5Tabs .step5 img{
        width: 300px;
    }
  }



  @media (max-width: 767px) {
   .animatestep3 {
    position: absolute;
    left: 0;
    top: -9px;
   }
   .animatestep33 {
    left: 19%;
    margin-top: 45px;
}
.step41 {
    left: 25%;
    top: 73px;
}
.step42 {
    right: 112px;
    top: -19%;
}
  }





  @media (max-width: 800px) {
    .tabsRightImg .animate2 {
        position: absolute;
        right: auto;
        left: 26%;
        transform: translate(-50%, 50%);
        top: 40%;
    }
    .tabsRightImg .animate1 {
        top: 50px;
    }
  }

  @media (max-width: 575px) {
    .animate1 img,
    .animate2 img,
    .animate3 img {
        width: 150px;
    }
    .tabsRightImg .animate3 {
        bottom: 65%;
    }
    .tabsRightImg .animate2 {
        left: 54%;
        top: 21%;
    }
    .tabsRightImg .animate1 {
        top: 49px;
        left: -6px;
    }
    .tabsRightImg .stars2 {
        left: 45%;
    }
    .animate5Brands img {
        width: 300px;
    }
    .animatestep3 img,
    .animatestep32 img,
    .animatestep33 img {
        width: 100px;
    }
    .animatestep3 {
        top: 55px;
    }
    .animatestep32 {
        top: -60%;
        z-index: 1;
    }
    .animatestep33 img {
        width: 260px;
    }
    .animatestep33 {
        left: 35%;
        margin-top: -13px;
    }
    .step41 img,
    .step42 img,
    .step43 img,
    .step44 img {
        width: 90px;
    }
    .step44 {
        right: -20px;
        bottom: -13px;
    }
    .step41 {
        top: 7px;
        left: 34%;
    }
    .step42 {
        right: 45px;
        top: -25%;
    }
    .step5Tabs .step5 {
        top: 10%;
    }
    .step5Tabs .step5 img {
        width: 165px;
    }
    .tabsRightImg .stars1 {
        left: 24%;
        top: 39px;
        z-index: 1;
    }
    .tabsRightImg .stars2 {
        left: 47%;
        z-index: 1;
        top: 0;
    }
    .tabsRightImg .stars3 {
        left: 91%;
    }
    .animate4Text h2 {
        top: -31px;
        font-size: 18px;
        text-align: right;
        padding-right: 20px;
        position: absolute;
    }
    .animate5Brands {
        text-align: right;
        top: 8px;
        position: relative;
        padding-right: 14px;
    }
   }