.dashboard-btn-link {
    color: var(--purple);
    text-decoration: none;
    font-family: var(--secondaryFontMedium);
    font-size: 1em;
}


/* Dashboard Card Styling */
.dashboard-card-section {
    padding: 15px 30px;
    color: #fff;
}
.rating-box-wrapper .opacity-half{
    opacity: 50%;
}
.dashboard-card-section.view-count {
    background-color: var(--orange);

}
.dashboard-card-section.click-count{
    background-color: var(--purple);

}

.dashboard-card-section.click-count {
    background-color: var(--purple);

}

.dashboard-card-section .dashboard-card-label {
    font-size: 12px;
    position: relative;
    bottom: -18px;
    font-family: var(--secondrayFontMedium);

}

.dashboard-card-section .dashboard-card-unit {
    font-size: 1.7em;
    font-family: var(--primaryFontMedium);
}

/* END Dashboard Card Styling */

/* Msg Card single card styling */
.dashboard-msg-overview{
    min-height: 530px;
}
.msg-card-box {
    border-right-width: 0px;
    border-top-width:0px ;
    border-bottom-width: 0px;
    border-left-width: 5px;
    /* border-left-color: var(--orange); */
    margin: 20px 0px;
    border-radius: 0px;
    padding-left: 30px;
    padding-right: 20px;
}

.msg-card-box .msg-header-sec {
    margin-bottom: 2px;
}

.msg-card-box .msg-header-sec .sender-title {
    font-family: var(--secondaryFontMedium);
    font-size: 1.125em;
    color: var(--grey);
}

.msg-card-box .msg-header-sec .msg-date {
    font-family: var(--secondaryFont);
    font-size: 1em;
    color: var(--grey);
}

.msg-card-box .msg-topic {
    font-family: var(--secondaryFont);
    font-size: 1.125em;
    color: var(--grey);
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    min-height: 25px;
    -webkit-box-orient: vertical;
    overflow: hidden;
    width: 50%;
    word-wrap: break-word;
    white-space: pre-wrap;
    word-break: break-word;
}

.msg-card-box:nth-child(1n){
    border-left-color: var(--purple) ;
}

.msg-card-box:nth-child(2n) {
    border-left-color: var(--orange);
}

/* Rating section */
.rating-box-wrapper{
    display: flex;
    flex-direction: row;
    padding-top: 20px;
    padding-bottom: 20px;
}

.rating-box-wrapper .rating-box{
    width: 20%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-right: 20px;
}

.rating-bar{
    background-color: var(--purple);
    height: 10px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    width: 100%;
    text-align: right;
}

.rating-box .icon{
    position: relative;
    top: -7px;
    right: -5px;
    font-size: 20px;
    color: var(--gold);
}
/* END */




/* END */

/* Media Queries */
/* Mobile devices small screen */

@media (max-width: 380px) {
    .dashboard-btn-link {
        font-size: 0.75em !important;
    }

    .rating-box .icon {
        font-size: 12px !important;
        top: -12px !important;
    }
    
    .rating-bar {
        height: 5px !important;
    }
    .msg-card-box .msg-header-sec .sender-title {
        font-size: 0.875em !important;
    }
    
    .msg-card-box .msg-header-sec .msg-date {
        font-size: 0.75em !important;
    }
    
    .msg-card-box .msg-topic {
        font-size: 0.875em !important;
    }
}

/* Mobile devices large screen */

@media (max-width: 575.98px) {
.dashboard-btn-link {
    font-size: 0.875em;
}
.overview-info-card{
    margin-top: 0.75em;
}

.rating-box .icon {
    font-size: 14px;
    top: -10px;
}
.rating-bar{
    height: 7px;
}
.msg-card-box .msg-header-sec .sender-title {
    font-size: 1em;
}

.msg-card-box .msg-header-sec .msg-date {
    font-size: 0.875em;
}

.msg-card-box .msg-topic {
    font-size: 1em;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    min-height: 25px;
    -webkit-box-orient: vertical;
    overflow: hidden;
    width: 45%;
    word-wrap: break-word;
    white-space: pre-wrap;
    word-break: break-word;

}
.message-overview-sec{
margin-bottom: 3em;
}
}

/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
    .discovery-overview-sec{
        margin-top: 2em;
        margin-bottom: 2em;
    }
    .message-overview-sec{
        margin-bottom: 3em;
    }
}


