.welcomeModal .imgBox .img{
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    width: 100%;
    height: 295px;
    object-fit: cover;
}

.welcomeModal .imgBox .heading{
    position: absolute;
    font-family: var(--secondaryFontBold);
    font-size: 2.625em;
    line-height: 53px;
    left: 50%;
    top: 110px;
    transform: translate(-50%);
    -webkit-transform: translate(-50%);
    -moz-transform: translate(-50%);
    -ms-transform: translate(-50%);
    -o-transform: translate(-50%);
    color: var(--purple);
}

.welcomeModal .contentBox .text{
    font-family: var(--secondaryFont);
    font-size: 1.025em;
    line-height: 22px;
    margin-bottom: 5px;
}

.welcomeModal .btnWrapper{
    display: flex;
    justify-content: space-evenly;
}

.btnWrapper .btnBox .button{
    min-width: 180px;
    color: var(--bs-white);
}

@media (max-width: 575.98px) {

    .welcomeModal .btnWrapper{
        flex-direction: column;
    }

    .welcomeModal .imgBox .heading{
        font-size: 1.9em;
        line-height: 30px;
    }

    .welcomeModal .imgBox .img{
        height: 240px;
    }
}