.steps-slider{
    padding-bottom: 60px;
}
.steps-slider .slick-slide > div{
    text-align: center;
}

.steps-slider .slick-dots{
    bottom: 50px;
}

.steps-slider .slick-dots li{
    color: #c4c4c4;
}

.steps-slider .slick-dots li.slick-active button::before{
    color: var(--orange);
}