.integrationUpgradeCard {
    position: relative;
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
    background-color: #FBFBFB;
    width: 100%;
    max-width: fit-content;
    margin-bottom: 66px;
}

.integrationUpgradeCard .cardBody {
    padding: 16px 20px 47px 20px;
    width: fit-content;
    height: 290px;
}

.integrationUpgradeCard .cardBody .iconBox {
    text-align: right;
}

.integrationUpgradeCard .cardBody .iconBox .icon {
    color: var(--primaryColor) !important;
    font-size: 1em;
}

.integrationUpgradeCard .cardBody .label {
    font-family: var(--primaryFontMedium);
    font-size: 1.125em;
}

.integrationUpgradeCard .btnBox .processBtn {
    font-family: var(--primaryFont);
    font-size: 1em;
    padding: 9px 34px;
}

.integrationUpgradeCard .otpConfirmBox {
    margin-top: 36px;
}

.integrationUpgradeCard .otpConfirmBox .label{
    margin-bottom: 12px;
}

@media (max-width: 575.95px) {
    .integrationUpgradeCard .cardBody,
    .integrationUpgradeCard{
        width: 100%;
        max-width: 100%;
    }
    
}