/* Freelance service section code  */

.style_1.theme-blogs-section .content-container {
  border: none;
  box-shadow: none;
}

.freelancer-service-box {
  /* background-image: url("../../../assets/images/layers/overlay-dots.png");
      background-repeat: no-repeat;
      background-position: top right;
      background-size: 27%; */
  position: relative;
  border-radius: 50px;
}

.freelancer-service-box .section-tag {
  font-family: var(--themePrimaryFont);
  font-size: 2em;
  margin-bottom: 1.25rem;
  color: var(--themePrimaryColor);
  font-weight: 600;
}

.freelancer-service-box .message-text {
  font-size: 1.3em;
  font-family: var(--themePrimaryFont);
  line-height: 40px;
  letter-spacing: 1px;
  margin-bottom: 1.4em;
}

.btn-box.btn-more .btn {
  min-width: 174px;
}

.circle-overlay-container {
  position: relative;
  z-index: 0;
}

.circle-overlay-solid {
  width: 310px;
  height: 310px;
  background-color: var(--themePrimaryColor);
  border-radius: 50%;
  position: absolute;
  top: 0px;
  right: 0px;
  /* background-blend-mode: overlay;
      mix-blend-mode: soft-light; */
  opacity: 0.5;
  z-index: 0;
}

/* Freelance service section code ends */

/* theme generic section code */
.theme-service-box .section-tag {
  color: var(--themeTextColor) !important;
}

.theme-service-box .message-text {
  color: var(--themeTextColor) !important;
}

.theme-service-box {
  /* background-image: url("../../../assets/images/layers/overlay-dots.png");
      background-repeat: no-repeat;
      background-position: top right;
      background-size: 27%; */
  position: relative;
  border-radius: 50px;
}

.theme-service-box .section-tag {
  font-family: var(--themePrimaryFont);
  font-size: 2em;
  margin-bottom: 1.25rem;
  color: var(--themePrimaryColor);
  font-weight: 600;
}

.theme-service-box .message-text {
  font-size: 1.3em;
  font-family: var(--themePrimaryFont);
  line-height: 40px;
  letter-spacing: 1px;
  margin-bottom: 1.4em;
}

#blogs.style_3 .img_full_card_accoridion {
  height: 140px;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

#blogs.style_3 .accordianOpenedActive {
  height: 300px;
}

#blogs.style_3 .Accordionbutton {
  background: var(--themePrimaryColor);
}

#blogs.style_3 .Accordionbutton button {
  background: var(--themePrimaryColor) !important;
  box-shadow: none !important;
  height: 62px !important;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
}

#blogs.style_3 .Accordionbutton button:focus {
  box-shadow: none;
}

#blogs.style_3 .Accordionbutton button .card-title {
  margin: 0;
  color: var(--themeTextColor);
  font-size: 26px;
}

#blogs.style_3 .Accordionbutton .card-text {
  color: var(--themeTextColor);
  font-size: 21px;
}

#blogs.style_3 .Accordionbutton .accordion-body {
  padding-top: 0;
}

#blogs.style_3 .Accordionbutton .accordion-body .card-footer {
  padding: 0;
  border: none;
  background: transparent;
  padding-top: 27px;
}

#blogs.style_3 .Accordionbutton .accordion-body .card-footer .link {
  text-align: left !important;
}

#blogs.style_3 .Accordionbutton .accordion-body .card-footer .btn-box button {
  padding: 0 !important;
  border: none !important;
  font-size: 21px !important;
  height: auto !important;
  text-align: left;
  color: var(--themeTextColor);
}

#blogs.style_3 .wrapper-box {
  display: block;
}

#blogs.style_3 .wrapper-box .card-wrapper {
  margin-bottom: 40px;
}

#blogs.style_3 .wrapper-box .card-wrapper:last-child {
  margin-bottom: 0;
}


/* theme generic section code */

@media (max-width: 575.98px) {
  #blogs.style_3 .wrapper-box .card-wrapper {
    width: 100%;
    margin-bottom: 24px;
    padding: 0;
  }

  #blogs.style_3 .wrapper-box {
    margin: auto;
  }

  #blogs.style_3 .img_full_card_accoridion {
    height: 88px;
  }

  #blogs.style_3 .Accordionbutton button {
    height: 40px !important;
  }

  #blogs.style_3 .Accordionbutton button .card-title {
    font-size: 16px;
  }

  #blogs.style_3 .Accordionbutton .accordion-button::after {
    width: 15px !important;
    height: 15px !important;
    background-size: contain;
  }

  #blogs.style_3 .Accordionbutton .card-text {
    font-size: 14px;
    margin-bottom: 0;
  }

  #blogs.style_3 .Accordionbutton .accordion-body .card-footer {
    padding-top: 20px;
  }

  #blogs.style_3 .Accordionbutton .accordion-body .card-footer .btn-box button {
    font-size: 14px !important;
  }

  #blogs.style_3 .section-body {
    overflow-x: unset;
  }
  #blogs.style_3 .card-edit-block {
    top: 8px;
}

  .circle-overlay-solid {
    width: 100px;
    height: 100px;
    background-color: var(--themePrimaryColor);
    border-radius: 50%;
    position: absolute;
    top: 0px;
    right: 0px;
    /* background-blend-mode: overlay;
          mix-blend-mode: soft-light; */
    opacity: 0.5;
  }

  .freelancer-service-box .section-tag {
    font-size: 1.6em;
    margin-bottom: 18px;
    font-weight: 800;
  }

  .freelancer-service-box .message-text {
    font-size: 1em;
    font-weight: 800;
    margin-bottom: 24px;
    line-height: 24px;
  }
}