.profileDataWrapper{
    padding: 24px;
    padding-bottom: 0px;
    border-radius: inherit;
    -webkit-border-radius: inherit;
    -moz-border-radius: inherit;
    -ms-border-radius: inherit;
    -o-border-radius: inherit;
}

.profileDataWrapper .title{
    font-family: var(--primaryFontBold);
    font-size: 16px;
    margin-bottom: 24px;
}

.userImageWrapper{
    display: flex;
    width: 100%;
    margin-bottom: 32px;
}

.userImageWrapper .userImageBox{
    width: 100%;
    max-width: 180px;
    margin-right: 24px;
}

.userImageWrapper .userImageDropBox{
    flex: 1;
}

.profileDataWrapper .inputField{
    height: 48px;
    padding: 16px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}

.profileDataWrapper .profileDropdown{
    height: 48px;
}

.profileDropdown::after{
    top: 22px !important;
}

.lineBar{
    margin-top: 24px;
    margin-bottom: 24px;
}

.btnBox{
    padding: 24px;
    padding-top: 0px;
    text-align: right;
}

.btnBox .btnPrimary{
    background-color: var(--primaryColor);
    color: #ffffff;
    padding: 8px 24px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}

.btnBox .btnPrimary:hover{
    background-color: var(--primaryColorHover);
}

@media (max-width: 575.98px){
    .profileDataWrapper{
        padding: 16px;
        padding-bottom: 0px;
    }
    .userImageWrapper{
        flex-direction: column;
        align-items: center;
    }

    .userImageWrapper .userImageBox{
        margin-right: 0px;
    }

    .userImageWrapper .userImageDropBox{
        width: 100%;
    }

    .btnBox{
        padding: 16px;
        padding-top: 0px;
    }
}