.notifyBell .accordion-button::after{
    display: none;
}
.notifyBell .accordion{
    position: absolute;
    right: 0;
    top: -22px;
}
.notifyBell .accordion-body{
    padding: 0;
    height: auto;
}
.notifyBell .accordion-item{
    border: none;
    background: transparent;
}
.notifyBell .accordion-item h2 button{
    background: transparent !important;
    box-shadow: none;
}
.notifyBell .accordion-item h2 button:focus{
    box-shadow: none;
}
.notifyBell .accordion-collapse{
    position: absolute;
    right: 0px;
    background: #fdfdfd;
    border: 1px solid #e3e3e3;
    top: 72px;
    border-radius: 3px;
    box-shadow: 0px 11px 14px #02020214;
    width: 400px;
}
.notifyBell .countNotify{
    background: #f58634;
    color: #fff;
    position: absolute;
    width: 22px;
    text-align: center;
    height: 22px;
    font-weight: bold;
    line-height: 24px;
    border-radius: 30px;
    font-size: 12px;
    top: 12px;
    right: 35px;
}
.closeBtnNotify button{
    background: #fff;
}
/* .notifyBell{
    display: none;
} */
@media (max-width: 575.99px) {
    .notifyBell .accordion-collapse {
        position: fixed;
        top: 59px;
        width: 100%;
    }
    .notifyBell .accordion-body{
        padding-left: 10px;
    }
}