.title {
    font-family: var(--primaryFontBold);
    font-size: 32px;
    line-height: 40px;
}

.paymentMethodCard {
    background: #FBFCFF;
    padding: 24px;
    border: none;
    border-radius: 8px;
    /* height: 100%; */
    min-height: 540px
}

.paymentMethodCard :global(.card-body)  {
    padding: 0px;
}

.paymentMethodCard .labelText{
    font-family: var(--primaryFontBold);
    font-size: 16px;
    margin-bottom: 8px;
}

.paymentMethodCard .formControl{
    padding: 16px;
    height: 48px;
}

.paymentMethodCard .cardDetailBox{
    position: relative;
}

.paymentMethodCard .cardDetailBox .formControl{
    padding-left: 40px;
}

.paymentMethodCard .cardDetailBox .iconBox{
    position: absolute;
    bottom: 30px;
    left: 22px;
}

.billingDataBox .title{
    font-family: var(--primaryFontBold);
    font-size: 24px;
    margin-bottom: 16px;
    color: #343434;
}

.billingDataBox .subTotalBox{
    margin-bottom: 16px;
}

.billingDataBox .subTotalBox .text,
.billingDataBox .subTotalBox .subAmount{
    font-family: var(--primaryFontBold);
    font-size: 18px;
    color: #5E606A
}

.totalAmountBox{
    display: flex;
    justify-content: space-between;
}
.totalAmountBox .text,
.totalAmountBox .totalAmount{
    font-family: var(--primaryFontBold);
    font-size: 18px;
}

.paymentMethodCard :global(.card-footer){
    border: none;
    background-color: transparent;
    padding: 0px;
}

.btnBox{
    margin-top: 24px;
}

.btnBox .gradientBtn{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    color: #ffffff;
    font-family: var(--primaryFontBold);
    font-size: 15px;
    padding: 12px 24px;
    width: 100%;

    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}
