  /* theme generic section code */
  .style_1.theme-coupon-section .content-container {
    box-shadow: none;
    border-radius: 30px;
  }

  .theme-service-box .section-tag {
    color: var(--themeTextColor) !important;
  }

  .theme-service-box .message-text {
    color: var(--themeTextColor) !important;
  }

  .theme-service-box {
    /* background-image: url("../../../assets/images/layers/overlay-dots.png");
      background-repeat: no-repeat;
      background-position: top right;
      background-size: 27%; */
    position: relative;
    border-radius: 50px;
  }

  .theme-service-box .section-tag {
    font-family: var(--themePrimaryFont);
    font-size: 2em;
    margin-bottom: 1.25rem;
    color: var(--themePrimaryColor);
    font-weight: 600;
  }

  .theme-service-box .message-text {
    font-size: 1.3em;
    font-family: var(--themePrimaryFont);
    line-height: 40px;
    letter-spacing: 1px;
    margin-bottom: 1.4em;
  }

  .style_3 .promo_code .card {
    background: var(--themePrimaryColor);
    border: 1px solid #000;
    padding-bottom: 8px;
  }

  .style_3 .promo_code .card .img-box img {
    height: 140px;
    width: 140px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
  }

  .style_3 .promo_code .card .card-footer {
    background: transparent;
    border: none;
  }

  .style_3 .promo_code .card .card-footer .link a {
    padding: 8px 10px;
    display: block;
    border-radius: 15px;
    color: var(--themeTextColor);
    text-decoration: none;
    font-size: 16px;
  }

  .style_2 .promo_code .card .card-footer .link a {
    color: var(--themeTextColor);
  }

  .style_3 .coupon.horizontal-card .card-body .content-box {
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-start;
    padding-left: 15px;
  }

  .style_3 .coupon.horizontal-card .card-title {
    font-family: var(--themePrimaryFontMedium);
    color: var(--themeTextColor);
    font-size: 1.5em;
    margin-top: 10px;
    margin-bottom: 12px;
    text-decoration: underline;
    line-height: 26px;
  }

  .style_3 .coupon.horizontal-card .card-text {
    color: var(--themeTextColor);
    font-weight: 500 !important;
    margin-top: 10px;
    font-size: 1.875em;
    line-height: 29px;
  }

  .style_3 .coupon.horizontal-card .btnSection button {
    border: none;
    width: 100%;
    display: flex;
    justify-content: center;
    font-size: 18px !important;
    background: var(--themeTextColor);
    color: var(--themePrimaryColor) !important;
  }

  .style_3 .coupon.horizontal-card .btnSection button svg {
    display: none;
  }

  #coupon.style_2 .circle-image-card {
    min-height: 400px;
  }

  #coupon.style_2 .card-footer button {
    background: var(--themeTextColor);
    color: var(--themePrimaryColor);
    border-color: var(--themeTextColor);
  }

  #coupon.style_3 .card-footer button {
    padding: 8px 0 !important;
    background: var(--themeTextColor);
    color: var(--themePrimaryColor);
  }

  #coupon.style_3 .img_loader_card .imageLoaderCard {
    transform: translate(-50%, -50%) !important;
  }

  #coupon.style_3 .horizontal-card .card-edit-block .edit-box {
    position: relative;
    right: 8px;
  }

  #coupon.style_4 .card-body {
    padding: 0;
  }

  #coupon.style_4 .theme-general-card {
    background: transparent;
    border: 1px solid black;
  }

  #coupon.style_4 .theme-general-card .title {
    font-size: 35px;
    font-family: var(--themeSecondaryFont);
    color: var(--themeTextColorSecondary);
    margin-left: 10px;
    margin-right: 10px;
  }

  #coupon.style_4 .theme-general-card .card-text {
    font-size: 24px;
    margin-top: 10px;
    color: var(--themeTextColorSecondary);
    margin-left: 10px;
    margin-right: 10px;
  }

  #coupon.style_4 .upload-generic-card-image {
    display: none;
    top: 15px;
    right: 60px;
  }

  #coupon.style_4 .card-edit-block .edit-option {
    top: 4px;
    right: 4px;
    position: relative;
  }

  #coupon.style_4 .theme-general-card:hover .upload-generic-card-image {
    display: block;
  }

  #coupon.style_4 .img-Card {
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    height: 244px;
  }

  #coupon.style_2 .circle-image-card .card-title {
    font-size: 35px;
    font-family: var(--themePrimaryFont);
    line-height: 42px;
  }

  #coupon.style_2 .circle-image-card .card-text {
    font-size: 22px;
    font-family: var(--themePrimaryFont);
  }


  /* theme generic section code */

  @media (max-width: 575.98px) {
    #coupon.style_2 .circle-image-card {
      min-height: 346px;
    }

    #coupon.style_2 .circle-image-card .card-title {
      font-size: 1.3125em;
    }

    #coupon.style_2 .circle-image-card .card-text {
      font-size: 1em;
      font-family: var(--themePrimaryFontRegular);
    }

    #coupon.style_4 .theme-general-card .img-Card {
      height: 193px !important;
    }

    #coupon.style_4 .theme-general-card .title {
      font-size: 21px;
    }

    #coupon.style_4 .theme-general-card .card-text {
      font-size: 18px;
    }

    #coupon.style_4 .theme-general-card .card-edit-block {
      top: 13px;
    }

    .circle-overlay-solid {
      width: 100px;
      height: 100px;
      background-color: var(--themePrimaryColor);
      border-radius: 50%;
      position: absolute;
      top: 0px;
      right: 0px;
      /* background-blend-mode: overlay;
          mix-blend-mode: soft-light; */
      opacity: 0.5;
    }

    .freelancer-service-box .section-tag {
      font-size: 1.6em;
      margin-bottom: 18px;
      font-weight: 800;
    }

    .freelancer-service-box .message-text {
      font-size: 1em;
      font-weight: 800;
      margin-bottom: 24px;
      line-height: 24px;
    }

    .style_3 .promo_code .card .img-box img {
      width: 75px;
      height: 75px;
    }

    .style_3 .coupon.horizontal-card .card-body .content-box {
      padding-left: 3px;
    }

    .style_3 .promo_code .card .card-title {
      font-size: 1.125em;
      margin-bottom: 0px;
    }

    .style_3 .promo_code .card .card-text {
      font-size: 1.3125em;
      margin-bottom: 0px;
    }
  }