
.contact-us-page .contact-header .contact-header-section{
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact-us-page .contact-header .contact-header-section .contact-main-heading{
    font-family: var(--primaryFontExtraBold);
    color: var(--primaryColor);
    font-size: 2.5em;
}

.contact-us-page .contact-header .contact-header-section .contact-main-subheading{
    font-family: var(--primaryFontMedium);
    color:  #a4a2bc;
}


.contact-us-page .contact-section{
    border-radius: 10px;
    min-height: -webkit-fill-available;
}

.contact-section .card-contact-info {
  padding-top: 10px;
  border-radius: 10px;
  height: 100%;
  background: -webkit-linear-gradient(
        -90deg, #5b3f91, #482392) 30% 0% ;
  position: relative;
  display: block;
}

.card-contact-info .data-box {
    justify-content: center;
    align-items: center;
}

.card-contact-info .data-box .main-heading{
    font-family: var(--primaryFontMedium);
    color: #fff;
    font-size: 1.75em;
}

.card-contact-info .data-box .main-subheading{
    font-family: var(--primaryFont);
    color: #a4a2bc;
    letter-spacing: 1;
}

.contact-info-text{
    color:#fff ;
    font-family: var(--primaryFontMedium);
    letter-spacing: 1.2px;
}



/* Contact Message Us Styling */
/* .contact-form-label{
    font-size: 1em ;
}
.contact-text-input{
    border-width: 0px 0px 1px 0px;
    border-radius: 0px;
} */
.contact-text-input:active{
    border-color: var(--primaryColor);
}

.contact-input-group {
  position: relative;
  padding: 15px 0 0;
  margin-top: 10px;
}

.contact-text-input {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-radius: 0px;
  border-bottom: 1px solid #d2d2d2;
  outline: 0;
  font-size: 16px;
  color: #212121;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}

.contact-text-input::placeholder {
  color: transparent;
}

.contact-text-input:placeholder-shown ~ .contact-form-label {
  font-size: 16px !important;
  cursor: text;
  top: 16px;
  /* left: 16px; */
}

.material-input> label,
.contact-text-input:focus ~ .contact-form-label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 12px !important;
  color: #9b9b9b;
}

.contact-text-input:focus ~ .contact-form-label {
  color: var(--primaryColor);
}

.contact-text-input:focus {
  padding-bottom: 6px;
  border-bottom: 2px solid var(--primaryColor);
}


.form-submit-section{
  justify-content: center;
  align-items: center;
}
.form-submit-section .contact-submit-btn{
    float: right;
    /* margin-right: 20px; */
}
/* END MESSAGE  */


/* Circle Styling */

.card-contact-info .overlay-circle{
  height: 100px;
  width: 100px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  right: -55%;
  z-index: 2;
  opacity: 0.4;

}

.card-contact-info .dot {
    width: 150px;
  height: 147px;
  border-top-left-radius: 459px;
  border-bottom-right-radius: 27px;
  /* border-top-right-radius: 510px; */
  background: #f44c57;
  float: right;
  position: relative;
  opacity: 0.6;
  z-index: 1;
  right: -16px;
  bottom: 0px;
}




/* END Circle Styling */



/* Media Queries for Mobile screens */
@media (max-width: 380px) {
.contact-us-page .contact-header .contact-header-section .contact-main-subheading{
  text-align: center;
  margin-top: 10px;
}
}

/* large mobile view */
@media (max-width: 575.98px) {
  .contact-field-box .contact-field-section{
    display: block !important;
  }
  .contact-input-group{
    flex-direction: column !important;
  }
.contact-us-page .contact-header .contact-header-section .contact-main-subheading{
  text-align: center;
  margin-top: 10px;
}
.data-box{
  text-align: center;
}
.dot {
bottom: 0px !important;
}
.contact-field-box .material-input-group{
  margin-top: 10px !important;
}
.form-submit-section .contact-submit-btn{
  float: none;
}
.form-submit-section{
  text-align: center;
}
}


/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
  .form-submit-section .contact-submit-btn{
    width: 10em;
  }
  .contact-field-box .contact-input-group{
    margin-top: 6px;
  }
}