.modalBody {
    position: relative;
}

.modalDialog {
    max-width: 560px;
}

.subscriptionModal .modalContent {
    background-color: #ffffff;
    color: #343434;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
}

.subscriptionModal .modalBody {
    border-bottom: none;
    text-align: center;
    padding: 40px 80px;
}

.subscriptionModal .iconBox {
    margin-bottom: 32px;
}

.subscriptionModal .modalBody .contentBox {
    margin-bottom: 24px;
}

.warning .title {
    color: var(--orange);
}

:global(.error) .title {
    color: #DC3646;
}

:global(.error) .subTitle>span {
    color: #DC3646;
    background: none;
    -webkit-text-fill-color: initial;
}

.subscriptionModal .title,
.subscriptionModal .subTitle {
    font-family: var(--primaryFontBold);
    font-size: 24px;
}

.subTitle span {
    display: inline-block;
    background: -webkit-linear-gradient(289.79deg, #9461FA 23.59%, #FFA847 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.subscriptionModal .description {
    font-family: var(--primaryFontMedium);
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    color: #858585;
    margin-bottom: 0px;
}

.subscriptionModal .description a{
    color: var(--primaryColor);
    text-decoration: none;
}

.subscriptionModal .description a:hover{
    color: var(--primaryColor);
    text-decoration: none;
}

.subscriptionModal .modalFooter {
    justify-content: center;
    border-top: none;
    padding-bottom: 32px;
}

.subscriptionModal .btnOutline {
    font-family: var(--primaryFontMedium);
    background-color: #ffffff;
    color: #5E606A;
    font-size: 15px;
    border: 1px solid #5E606A;
    padding: 12px 32px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    cursor: pointer;
    width: 100%;
    transition: .3s;
}
.subscriptionModal .btnOutline:hover {
    background: #5E606A;
    transition: .3s;
    color: #fff;
}
.subscriptionModal :global(.btnPro) {
    font-family: var(--primaryFontMedium);
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    color: #ffffff;
    font-size: 15px;
    padding: 12px 32px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    cursor: pointer;
    border: 0;
    width: 100%;
}
.subscriptionModal :global(.btnPro):hover { 
    background: linear-gradient(289.79deg, #9461FA 23.59%, #FFA847 63.85%);
    color: #ffffff;
}

.subscriptionModal :global(.btnDanger) {
    font-family: var(--primaryFontMedium);
    background: #DC3646;
    color: #ffffff;
    font-size: 15px;
    padding: 12px 32px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    cursor: pointer;
    border: 0;
    width: 100%;
    transition: .3s;
}
.subscriptionModal :global(.btnDanger):hover { 
    background: #f05160;
    color: #ffffff;
    transition: .3s;
}

@media (max-width: 575.98px) {

    .subscriptionModal .modalBody {
        padding: 32px 24px 24px 24px;
    }

    .subscriptionModal .modalBody .contentBox {
        margin-bottom: 16px;
    }

    .subscriptionModal .title,
    .subscriptionModal .subTitle {
        font-size: 18px;
    }

    .subscriptionModal .description {
        font-size: 16px;
    }

    .subscriptionModal .modalFooter {
        padding-bottom: 24px;
    }

    .subscriptionModal .modalFooter .btnOutline {
        margin-bottom: 16px;
    }

}