.GamificationWrapper {
    position: fixed;
    bottom: 30px;
    right: 30px;
    background-color: #8624f6;
}

.btnGamification {
    display: flex;
    background: linear-gradient(158.01deg, #2D0183 19.4%, #15003E 99.98%);
    padding: 14px 30px;
    border-radius: 40px;
    color: var(--bs-white);
    font-weight: 600;
    min-width: 60px;
    height: 60px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    position: fixed;
    bottom: 30px;
    right: 30px;
    z-index: 150;
    box-shadow: 0 0 4px 0px var(--midGray);
    transition: all 0.4s;
}

.isMenuOpen {
    padding: 10px;
    /* width: 60px; */
    background: linear-gradient(158.01deg, #d1cdda 19.4%, #ffffff 99.98%);
    box-shadow: 0 0 4px 0px var(--midGray);
}

.counter {
    position: absolute;
    right: 0;
    top: -10px;
    background: var(--bs-white);
    color: #8624f6;
    width: 24px;
    height: 24px;
    box-shadow: 0 0 2px 2px var(--bs-gray-400);
    border-radius: 30px;
    line-height: 25px;
    text-align: center;
}

.menuCompleted {
    padding: 14px 10px;
}

.btnTitle {
    max-width: 134px;
    line-height: 16px;
}

.backDrop {
    background: #000;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    opacity: 0.7;
}

.btnBack {
    position: fixed;
    bottom: 28px;
    font-size: 1.5rem;
    z-index: 1000;
    height: 64px;
    width: 64px;
    border-radius: 160px;
    color: var(--orange);
    background: #fff8f1;
    left: 90px;
    justify-content: center;
    align-items: center;
    display: flex;
    box-shadow: 0 0 4px 0px var(--midGray);
    cursor: pointer;
}

.noSelect {
    -webkit-touch-callout: none;
      -webkit-user-select: none;
       -khtml-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
}

