    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    width: 100%;
  }
  .img-preview-box {
    overflow-y: overlay;
    height: 550px !important;
    margin-bottom: 20px;
  }
  .view-section-box {
    overflow-y: scroll;
    overflow-x: hidden;
  }

}

/* Mobile devices */
@media (max-width: 380px) {
  .dashboard-floating-btns {
    width: 72.35%;

    z-index: 10;
  }
  .linear-gradient {
    z-index: 4;
  }
  .dashboard-screen .view-section-box {
    z-index: 6;
  }

  .db-edit-profile-card .btn-box-wrapper .btn {
    font-size: 0.75em;
  }
  .img-preview-box {
    overflow-y: scroll;
    height: 500px !important;
  }
  .dashboard-screen .db-edit-profile-card .user-img {
    max-width: 50px !important;
    max-height: 50px !important;
    min-width: 50px !important;
    min-height: 50px !important;
    margin-right: 5px !important;
  }
  .edit-profile-box .user-name-text {
    margin-top: 0.4em !important;
    font-size: 12px;
  }
  .db-edit-profile-card .btn-box-wrapper .btn {
    font-size: 0.575em !important;
    padding: 0.3em;
  }
  .btn-mobile {
    padding-left: 0px !important;
  }
  .dashboard-screen-container {
    padding: 0px !important;
  }
  .view-section-box {
    overflow-y: scroll;
    overflow-x: hidden;
  }

  .alert-dismissible .btn-close {
    padding: 1em;
  }
  .help-section {
    overflow-y: auto;
  }
  .help-section .points {
    font-size: 14px !important;
    margin-top: 3px !important;
  }
  .dashboard-container {
    width: 100%;
    margin-left: 0;
}
}
