.integrationInfoCard {
    position: relative;
    border-radius: 18px;
    -webkit-border-radius: 18px;
    -moz-border-radius: 18px;
    -ms-border-radius: 18px;
    -o-border-radius: 18px;
}

.integrationInfoCard .integrationInfoCardBody {
    padding: 66px 64px;
}

.parent {
	width: 100%;
	height: 100%;
	position: relative;
	overflow: hidden;
}
.parent .blur {
    display: block;
    -moz-filter: blur(5px);
    -o-filter: blur(5px);
    -ms-filter: blur(5px);
    filter: blur(5px);
    position: absolute;
    top: -10px;
    right: -10px;
    bottom: -10px;
    left: -10px;
    background-color: #ccc;
    pointer-events:none
}
.modalIcon{
    text-align: left;
    color: var(--purple);
    font-size: 1.35em;
}
.modalHeader{
    left: 48%;
    position: relative;
}
.modalBox{
    font-family: var(--secondaryFont);
    color: var(--purple);
    text-align: center;
}
.overlayModal{
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) !important;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    flex-direction: column;
    background-color: #fff;
    box-shadow: -3px 3px 6px #00000029;
    border-radius:17px ;
    -webkit-border-radius:17px ;
    -moz-border-radius:17px ;
    -ms-border-radius:17px ;
    -o-border-radius:17px ;
    padding: 30px;
  }
.overlayModal .btnBox{
    text-align: center;
    margin-top: 20px;
    font-family: var(--secondaryFontBold);
}
.steps {
    margin-bottom: 40px;
}

.steps .stepMsgHeading {
    font-family: var(--primaryFontMedium);
    font-size: 1.25em;
    line-height: 25px;
    color: #5E606A;
    margin-bottom: 30px;
}

.steps .stepHeading {
    font-family: var(--primaryFontBold);
    font-size: 1.625em;
    line-height: 26px;
    color: var(--primaryColor);
    margin-bottom: 22px;
}

.steps .stepSubHeading {
    font-family: var(--primaryFontMedium);
    color: #5E606A;
    font-size: 1.125em;
    line-height: 21px;
}

.stepCardWrapper {
    margin-bottom: 66px;
    display: flex;
    flex-wrap: wrap;
}

.stepCard {
    width: 100%;
    max-width: 346px;
}

.stepCard .stepText {
    font-family: var(--primaryFont);
    font-size: 1.125em;
    line-height: 21px;
    color: #535353;
    margin-top: 12px;
}

.appDownloadBox .stepSubHeading {
    margin-bottom: 22px;
}
.appDownloadBox{
    margin-bottom: 66px;
}

.appBtnWrapper {
    display: flex;
    flex-wrap: wrap;
}

.appBtnWrapper .appBtnBox {
    margin-right: 13px;
    margin-bottom: 0;
}

.btnBox .processBtn {
    min-width: 168px;
    padding: 9px 34px;
}

.stepTwoBox .contentBox {
    margin-bottom: 30px;
}

.stepTwoBox .stepCard{
    margin-left: 44px;
    max-width: 333px;
}

.stepTwoBox .stepCard:first-child{
    margin-left: 0;
}

.stepThreeBox .contentBox {
    margin-bottom: 44px;
}

.stepThreeBox .stepCard{
    max-width: 289px;
    margin-right: 44px;
}

.steps .contentBox .emphasize {
    color: var(--primaryColor);
    text-transform: uppercase;
    font-size: 1.5em;
}

.contentBox .note {
    font-family: var(--primaryFont);
    color: var(--primaryColor);
    font-size: 1em;
}

.stepThreeBox .contentBox .stepSubHeading {
    margin-bottom: 22px;
}

.stepThreeBox .btnBox .stepSubHeading {
    margin-bottom: 22px;
}
.stepOneBox .stepCard{
    max-width: 248px;
     margin-left: 44px;
}
.stepOneBox .stepCard:first-child{
     margin-left: 0;
}

@media (max-width: 575.98px) {
    .appBtnWrapper .appBtnBox{
        margin-bottom: 1em;
    }
    .integrationInfoCard .integrationInfoCardBody {
        padding: 16px;
    }
    .overlayModal{
        top: 45%;
        left: 50%;
        width: 85%;
        position: fixed;
    }

    .steps .stepMsgHeading {
        font-size: 1.12em;
        line-height: 25px;
    }

    .steps .stepHeading {
        font-size: 1.3125em;
    }

    .steps .stepSubHeading {
        font-size: 1em;
    }

    .stepCard {
        width: 90% !important;
        max-width: 100%;
        text-align: left;
    }

    .stepCard .imgBox{
        width: 100%;
    }

    .stepCard .imgBox .img{
        width: 100%;
    }

    .stepCard .stepText {
        font-size: 1em;
    }

    .stepThreeBox .stepCard{
        margin-right: 0px;
    }
}