.checkout-section{
    background-image: url("../../assets/images/layers/checkout-layer.png");
    width: 100%;
    height: calc(100vh - 58px);
    background-size: contain;
    background-repeat: repeat;
}

.checkout-section .content-sec{
    text-align: center;
}

.checkout-section .main-title{
    font-family: var(--primaryFontBold);
    color: var(--primaryColor);
    font-size: 2.5em;
}

.checkout-section .content-sec .desc{
    font-family: var(--primaryFontSemiBold);
    font-size: 1.25em;
    color: var(--primaryColor);
}

.checkout-section .amount-box{
    margin-top: 50px;
    margin-bottom: 18px;
}

.checkout-section .amount-box strong{
    display: flex;
    justify-content: space-between;
}

@media (max-width: 575.98px){
    .checkout-section .content-sec{
        text-align: left;
    }
    .checkout-section .main-title{
        font-size: 1.1875em;
    }
    
    .checkout-section .content-sec .desc{
        font-size: 1.0625em;
        margin-bottom: 0;

    }
}