/* Color Theme */

:root {
  --brightVoilet: #83479b;
  --violet: #7850a0;
  --primaryColor: #5b3f91;
  --primaryColorHover: #8B39B2;
  --primaryColorFocus: #7250B2;
  --black: #1e1e1e;
  --gray: #575757;
  --midGray: #bbbbbb;
  --lightGray: #eeedee;
  --midnight: #1a0741;
  --darkPurple: #312638;
  --primaryFont: "MarkPro";
  --primaryFontMedium: "MarkPro-Medium";
  --primaryFontBold: "MarkPro-Bold";
  --primaryFontLight: "MarkPro-Light";
  --primaryFontSemiBold: "MarkPro-SemiBold";
  --primaryFontExtraBold: "MarkPro-Heavy";
  --secondaryFont: "MarkPro";
  --secondaryFontMedium: "MarkPro-Medium";
  --secondaryFontBold: "MarkPro-Bold";
  --secondaryFontLight: "MarkPro-Light";
  --secondaryFontExtraBold: "MarkPro-Heavy";
  --borderColor: #dee2e6;
  --babyBlueColor: #D4F1F4;
  --lightblue: #CEE7E9;
  --whiteTint: #FBFCFF;

  /* logo */
  --sun: #F2C94C;
  --bitterSweet: #EE3197;
  --leaf: #91CA74;
  --sky: #B0E3F9;
  --orange: #F58634;
  --rocket: #8B39B2;
  /* color */
  --black: #000000;
  --grey: #5E606A;
  --grey2: #858585;
  --midnight: #1a0741;
  --darkPurple: #312638;
  --concrete: #f9f9f9;
  --purple: #5B3F91;
  --purpleHover: #7250B2;
  --purpleDisabled: #CBABE3;
  --orangeHover: #F07C26;
  --gold: #FFB96C;
  --pink: #FFEFFE;
  --bluepastel: #EDF6FF
}

html,
body {
  width: auto !important;
  scroll-behavior: smooth;
}

iframe {
  display: none;
}

.theme-wrapper iframe, .main-wrapper iframe, .App iframe {
  display: block;
}

.dropdown .dropdown-divider {
  width: 95%;
  margin: 0 auto;
}

.dropdown-item.active,
.dropdown-item:active {
  color: #fff;
  text-decoration: none;
  background-color: var(--primaryColor);
}

/* utilities */
.border-radius-inherit {
  border-radius: inherit;
  -webkit-border-radius: inherit;
  -moz-border-radius: inherit;
  -ms-border-radius: inherit;
  -o-border-radius: inherit;
}

/* TYPOGRAPHY */

body:hover::-webkit-scrollbar-thumb {
  visibility: visible;
}

::-webkit-scrollbar {
  width: 8px;
  border-radius: 12px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 12px;
  margin-top: 0rem;
  margin-bottom: 0rem;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--primaryColor);
  border-radius: 12px;
  border-right: none;
  border-left: none;
  min-height: 40px;
  visibility: none;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--primaryColor);
}

body {
  font-family: var(--primaryFont);
  font-size: 16px;
  color: var(--black);
  overflow-x: hidden !important;
}

/* Text Colors */

.primary-font {
  font-family: var(--primaryFont);
}

.primary-color {
  color: var(--primaryColor);
}

/* Background Colors */

.bg-primary {
  background-color: var(--primaryColor) !important;
}

.bg-gold {
  background-color: var(--gold);
}

.bg-gray {
  background-color: var(--gray);
}

.bg-bluepastel {
  background-color: var(--bluepastel);
}

.bg-orange {
  background-color: var(--orange);
}

.bg-midgray {
  background-color: var(--midGray);
}

.bg-babyblue {
  background-color: var(--babyBlueColor);
}

.bg-lightblue {
  background-color: var(--lightblue);
}

.bg-leaf {
  background-color: var(--leaf);
}

.bg-sun {
  background-color: var(--sun);
}

.bg-concreate {
  background-color: var(--concrete);
}

.bg-rocket {
  background-color: var(--rocket);
}

.bg-pink {
  background-color: var(--pink);
}

.blur-3 {
  filter: blur(3px);
  -webkit-filter: blur(3px);
}

.blur-8 {
  filter: blur(8px);
  -webkit-filter: blur(8px);
}

.badge-primary {
  background-color: var(--primaryColor);
  color: #ffffff;
}

.text-underline {
  text-decoration: underline;
}

/* ======== TYPOGRAPHY ======= */

/* 1 - Font Weight */

.w-20 {
  width: 20%;
}

.w-80 {
  width: 80%;
}

.font-12 {
  font-size: 0.75em;
}

.font-14 {
  font-size: 0.875em;
}

.font-17 {
  font-size: 1.0625em;
}

.font-18 {
  font-size: 1.125em;
}

.font-light {
  font-family: var(--primaryFontLight);
}

.font-weight-light {
  font-weight: 300;
}


.font-normal {
  font-family: var(--primaryFont);
}

.font-weight-normal {
  font-weight: 400;
}

.font-medium {
  font-family: var(--primaryFontMedium);
}

.font-bold {
  font-family: var(--primaryFontBold);
}

.font-weight-bold {
  font-weight: 600;
}

.font-extrabold {
  font-family: var(--primaryFontExtraBold);
}

.font-weight-extrabold {
  font-weight: 900;
}

.z-index-10 {
  z-index: 10;
}

.grayscale-1 {
  filter: grayscale(1);
  -webkit-filter: grayscale(1);
}

