.guide-msg-slider-section {
  display: block;
  background-color: var(--themeTextColor);
  color: var(--themePrimaryColor);
  position: absolute;
  top: 0px;
  right: 0px;
  padding: 2px 5px;
  border: 2px;
  font-size: 12px;
  z-index: 1;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
}
#gallery .btn-box{
  display: inline-block;
}

.theme-gallery-box-container .featured-img-box {
  position: relative;
  display: flex;
  justify-content: center;
}
.theme-gallery-box .theme-img-box:hover .card-edit-block{
  display: block;
  visibility: visible;
}

.theme-gallery-box-container .featured-img-box .featured-image {
  position: relative;
  width: 100%;
  height: 379px;
  border-radius: 17px;
  max-width: 375px;
}
.theme-gallery-box-container .featured-image .main-img {
  width: 100%;
  height: 100%;
  border-radius: inherit;
  object-fit: cover;
  /* border: 1px solid var(--themeTextColor); */
}

.featured-image:active .guide-msg,
.theme-img-box.active .guide-msg {
  display: inline;
}

.featured-image:hover .guide-msg,
.theme-img-box:hover .guide-msg {
  display: inline;
  border-top-right-radius: inherit;
  border-bottom-left-radius: inherit;
}

.featured-image .guide-msg,
.theme-img-box .guide-msg {
  display: none;
  text-align: left;
  position: absolute;
  top: 0px;
  right: 0px;
  border-radius: 2px;
  background-color: var(--themeTextColor);
  color: var(--themePrimaryColor);
  padding: 2px 10px;
  font-size: 10px;
  font-family: var(--primaryFont);
}
.featured-image .guide-msg {
  font-size: 12px;
}

#gallery.style_2 .theme-img-box:hover .card-edit-block{
  display: block;
}

.theme-thumbnail-container {
  overflow: scroll;
  height: 100%;
  max-height: 376px;
  overflow-x: hidden;
  padding-right: 10px;
}
.theme-thumbnail-container::-webkit-scrollbar-thumb {
  background-color: var(--themePrimaryColor);
}
.theme-thumbnail-container .theme-img-box {
  position: relative;
  display: block;
  width: 100%;
  height: 175px;
  border-radius: 17px;
  margin-bottom: 15px;
}

.theme-thumbnail-container .theme-img-box .gallery-img {
  width: 100%;
  height: 100%;
  border-radius: inherit;
  border: 1px solid var(--themeTextColor);
  object-fit: cover;
  cursor: pointer;
}

/* mobile slider */

.slick-slider-thumbnail {
  border: 1px solid #6987ff;
  border-radius: 17px;
  margin: 5px -30px 5px 0px;
  width: 95px;
  overflow: hidden;
  height: 75px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slick-slider-thumbnail-parent {
  padding: 4px;
}

.selected-gallery-image {
  border-radius: 17px;
  overflow: hidden;
}

.slick-slider-thumbnail-parent .slick-slider-thumbnail:active,
.slick-slider-thumbnail-parent .slick-slider-thumbnail:focus {
  border: 2px solid #6987ff !important;
}

/* .gallery-items-slider .slick-cloned { display: none !important; } */






/* mobile slider home */
@media (max-width: 380px) {
  .slick-slider-thumbnail{
    height: 63px !important;
  }
}

@media (max-width: 575.98px) {
  .freelancer-gallery-box .section-tag {
    font-size: 1.6em;
    margin-bottom: 18px;
    font-weight: 800;
  }

  .freelancer-gallery-box .message-text {
    font-size: 1em;
    font-weight: 800;
    margin-bottom: 24px;
    line-height: 24px;
  }
  .freelancer-gallery-box .slick-slider {
    margin-left: -24px;
    margin-right: -24px;
  }
}


/* Media Queries for tablet device */
@media (min-width: 768px) and (max-width: 921.88px) {
  /* .theme-gallery-box-container .featured-img-box .featured-image {
    height: 280px;
  }
  .theme-thumbnail-container .theme-img-box {
    height: 90px;
  } */
  .selected-gallery-image > img{
    height: 365px !important;
  }
}


/* Media Queries for large src */
@media (min-width: 1200px) and (max-width: 1599.98px) {
  .theme-thumbnail-container .theme-img-box {
    height: 140px;
  }
  .theme-gallery-box-container .featured-img-box .featured-image {
    height: 320px;
  }
  .theme-thumbnail-container {
    overflow: scroll;
    height: 100%;
    max-height: 318px;
    overflow-x: hidden;
    padding-right: 10px;
  }
}
