/* Gallery Variation 2 Start */
.gallery-items-slider-3 .slick-list{
  padding-top:20px;
    width: 95%;
    margin: 0 auto;
}
.gallery-items-slider-3 .gallery-slider-arrow-left{
  position: absolute;
  top: 45%;
  left: 1px;
  z-index: 100;
  cursor:pointer;
}
.gallery-items-slider-3 .gallery-slider-arrow-right{
  position: absolute;
  top: 45%;
  right: 1px;
  z-index: 100;
  cursor:pointer;
}
.gallery-items-slider-3 .slick-slide img{
  width: 100%;
  height: 255px;
  margin: 0 auto;
  border-radius:30px;
  padding: 12px;
  cursor:pointer;
  transition:0.2s ease;
}
.gallery-items-slider-3 .slick-slide .gallery-image-3:hover{
  transform: scale(1.1);
}
.gallery-items-slider-3 .slick-slide .guide-msg{
  display: none;
}
.gallery-items-slider-3 .slick-slide > div:hover .guide-msg{
  display: inline !important;
}
.gallery-items-slider-3 > .slick-list > .slick-track{
  width:auto !important;
}
/* delete icon */
.gallery-items-slider-3 .slick-slide .guide-msg{
  position: absolute;
  right: 25px;
  top: -12px;
  z-index: 10;
}
.gallery-items-slider-3 .slick-slide .guide-msg img{
  background-color:  #FFFFFF;
  border-radius: 50px;
  width: 25px;
  height: 30px;
  padding:6px;
  cursor: pointer;
}
/* delete icon */

/* for mobile */
@media (max-width: 480px) {
  .gallery-items-slider-3 .slick-slide .gallery-image-3{
    height:200px !important;
  }
