/* master theme generic */
.theme-header-banner-section {
  width: 100%;
  height: 100%;
  padding-top: 179px;
  padding-bottom: 179px;
}

.theme-header-banner-section .user-img-box {
  position: relative;
}

.theme-header-banner-box .user-img-box .user-image {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  max-width: 222px;
  min-width: 222px;
  max-height: 222px;
  min-height: 222px;
  object-fit: cover;
    min-width: 200px;
    min-height: 200px;
}

.theme-header-banner-box .user-detail-box {
  color: var(--themeTextColor);
}

.theme-header-banner-box .user-detail-box .user-detail {
  padding-bottom: 34px;
}

.theme-header-banner-box .user-detail .name {
  font-family: var(--primaryFontBold);
  font-size: 50px;
  word-break: break-all;
}

.theme-header-banner-box .user-detail .description {
  font-family: var(--primaryFontMedium);
  font-size: 24px;
}

.theme-header-banner-box .user-detail-box .social-links .link {
  color: var(--themeTextColor);
}

.user-img-box .action-btn-box {
  position: absolute;
  top: -50px;
  right: -21px;
}

.user-img-box .icon-box {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--themeTertiaryColor);
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.user-img-box .icon-box .icon-img {
  width: 16px;
  height: 16px;
}

/* master theme generic end */

/* freelance master theme styles */
.theme-header-freelancer-section {
  position: relative;
  padding-top: 120px;
  padding-bottom: 120px;
}

.theme-header-freelancer-box .user-detail-box {
  color: var(--themeTextColor);
}

.theme-header-freelancer-box .user-detail-box .intro-text {
  /* text-transform: capitalize; */
  font-size: 1.125rem;
  margin-bottom: 2em;
  z-index: 1;
  position: relative;
}

.theme-header-freelancer-box .user-detail-box .occupation-text {
  font-family: var(--themePrimaryFont);
  font-size: 3.2em;
  margin-bottom: 0.3em;
  font-weight: 600;
  line-height: 66px;
  z-index: 1;
  position: relative;
}

.theme-header-freelancer-box .user-detail-box .social-link-box {
  margin-bottom: 2.4em;
}

.theme-header-freelancer-box .user-detail-box .social-link-box .social-links {
  text-transform: capitalize;
  margin-left: -8px;
  margin-right: -8px;
}

.theme-header-banner-section .user-detail-box .social-icon .icon{
  font-size: 21px;
}

.theme-header-freelancer-box
  .user-detail-box
  .social-link-box
  .social-links
  .social-icon {
  display: inline-block;
  width: 20px;
  min-width: 20px;
  text-align: center;
}

.theme-header-freelancer-box
  .user-detail-box
  .social-link-box
  .social-links
  .social-text
  a {
  text-decoration: none !important;
}

.theme-header-freelancer-box .overlay-dots {
  background-image: url("../../../assets/images/layers/circle-1.png");
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
}

.theme-header-freelancer-box .flex-row-reverse .overlay-dots {
  background-position: left;
}

.theme-header-freelancer-box .banner-img-sec {
  position: relative;
  /* padding: 20px; */

  padding: 20px;
  border: 1px solid transparent;
}

.theme-header-freelancer-box .banner-img-sec .overlay-banner {
  /* background-color: var(--themePrimaryColor); */
  background-color: #ffffff;
  /* opacity: 0.5; */
  background-blend-mode: overlay;
  mix-blend-mode: soft-light;
  border-radius: 50%;
}

.theme-header-freelancer-box .dot-overlay-layer {
  background-image: url("../../../assets/images/layers/onlydots.png");
  background-size: 98%;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-header-freelancer-box .banner-img-sec.active {
  border: 1px solid var(--primaryColor);
}

.theme-header-freelancer-box .banner-img-sec.et-mode:hover {
  position: relative;
  border: 1px solid var(--primaryColor);
}

.theme-header-freelancer-box .banner-img-sec.et-mode:hover .action-btn-box,
.theme-header-freelancer-box
  .banner-img-sec.et-mode:hover
  .action-btn-box
  .guide-msg {
  display: inline-block !important;
}

.theme-header-freelancer-box
  .banner-img-sec.et-mode:hover
  .action-btn-box
  .guide-msg.icon-box {
  border-radius: 50%;
  padding: 15px;
}

.theme-header-freelancer-box .banner-img-sec .action-btn-box {
  position: absolute;
  right: 15%;
  bottom: 20%;
}

.theme-header-freelancer-box .banner-img-sec.active .guide-msg {
  display: inline;
}

.theme-header-freelancer-box .banner-img-sec.active .guide-msg.icon-box {
  padding: 15px;
  border-radius: 50%;
  width: 60px;
  height: 60px;
}

.theme-header-freelancer-box .banner-img-sec:hover .guide-msg.icon-box {
  padding: 15px;
  border-radius: 50%;
  width: 60px;
  height: 60px;
}

.theme-header-freelancer-box
  .banner-img-sec:hover
  .guide-msg.icon-box
  .icon-img {
  width: 30px;
  height: 30px;
}

.theme-header-freelancer-box .banner-img-sec .action-btn-box .floating-btn-box {
  position: absolute;
  width: 60px;
  height: 60px;
  left: -34px;
  top: -15px;
}

.theme-header-freelancer-box
  .banner-img-sec.active
  .guide-msg.icon-box
  .icon-img {
  position: relative;
  width: 30px;
  height: 30px;
  object-fit: contain;
}

.theme-header-freelancer-box .banner-img-sec .guide-msg:hover {
  display: inline;
}

.theme-header-freelancer-box .banner-img-sec .guide-msg {
  display: none;
  text-align: left;
  position: absolute;
  bottom: 20%;
  left: 0px;
  border-radius: 2px;
  background-color: var(--primaryColor);
  color: #ffffff;
  padding: 2px 5px;
  font-size: 20px;
  font-family: var(--primaryFont);
}

.theme-header-freelancer-box
  .banner-img-sec
  .action-btn-box
  .profile-img-float-btn {
  position: absolute;
  left: 20px;
  top: -74px;
}

.theme-header-freelancer-box .circle-overlay {
  min-width: 430px;
  min-height: 430px;
  width: 100%;
  height: 100%;
  aspect-ratio: 1 / 1;
  /* background-color: #ffffff; */
  border-radius: 50%;
  margin: 0 auto;
  /* background-blend-mode: overlay;
    mix-blend-mode: soft-light; */
}

/* .theme-header-freelancer-box .circle-overlay.active {
    position: relative;
    border: 1px solid var(--primaryColor);
    padding: 20px;
} */

.theme-header-freelancer-box .flex-row-reverse .circle-overlay {
  margin-left: 0;
  margin-right: auto;
}

.mini-circle-overlay {
  width: 120px;
  height: 120px;
  background-color: #ffffff;
  border-radius: 50%;
  position: absolute;
  top: -14px;
  left: -40px;
  background-blend-mode: overlay;
  mix-blend-mode: soft-light;
}

.theme-header-freelancer-box img.img-fluid.user-image {
  border-radius: 50%;
  min-width: 430px;
  min-height: 430px;
  width: 100%;
  height: 100%;
  aspect-ratio: 1 /1;
  object-fit: cover;
}

.user-detail-box .btn-box .btn {
  border-radius: 8px !important;
}
.mirror-x {
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}
.mirror-y {
  -webkit-transform: scaleY(-1);
  transform: scaleY(-1);
}

/* freelance master theme styles end */

.theme-header-banner-box .loaderWide {
  height: auto !important;
}

