.gender-drpdown-toggle::after {
      position: absolute;
      right: 13px;
      top: 18px;
}

.gender-drp-down .drp-down-gender {
  min-width: 100%;
}
.text-araea_signup textarea {
  height: 150px;
}
.gender-input-sec {
  width: 100%;
}
.signup-info-contentBox input{
  height: 60px;
}

.gender-drp-down.dropdown {
  width: 100%;
  display: flex;
  flex-direction: column;
  max-width: 100%;
  text-align: left;
}

.signup-info-contentBox .form-control,
.signup-info-contentBox .gender-drpdown-toggle,
.date-picker-form .DatePicker__input {
  padding: 9px 11px;
  border-radius: 6px !important;
  -webkit-border-radius: 6px !important;
  -moz-border-radius: 6px !important;
  -ms-border-radius: 6px !important;
  -o-border-radius: 6px !important;
}

.signup-info-contentBox .DatePicker__input{
  font-size: 14px;
  color: #6c757d;
}

.signup-info-contentBox button.gender-drpdown-toggle{
  color: #6c757d !important;
  text-align: left;
}
.date-picker-form .DatePicker__input{
  text-align: left;
}

@media (max-width: 380px) {

  .gender-drp-down .gender-drpdown-toggle::after {
    margin-left: 6.8em !important;
  }
}

@media (max-width: 575.98px) {
  .gender-drp-down .gender-drpdown-toggle {
    text-align: left;
  }

  .gender-drp-down .gender-drpdown-toggle::after {
    /* margin-left: 10.8em; */
    vertical-align: 0.175em;
    position: absolute;
    right: 20px;
    top: 16px;
  }
  .mobile-lbl-form{
    margin-bottom: 0em !important;
  }
}

@media (min-width: 768px) and (max-width: 921.98px) {
  .gender-drpdown-toggle::after {
    margin-left: 8.225em;
    position: absolute;
    right: 10px;
  }
}

@media (min-width: 1024px) and (max-width: 1199.99px) {
  .role-drpdown-toggle::after {
    margin-left: 7.225em;
  }
}