.color_sidebar .color-selection-wrapper .type-title{
  font-size: 22px;
  margin-bottom: 18px;
}

.theme_clr {display: flex;flex-wrap: wrap;}

.theme_clr .color_t {
  margin-right: 22px;
  text-align: center;
  font-size: 1em;
  border-radius: 110px;
  width: 40px;
  height: 40px;
  margin-bottom: 22px;
  line-height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.color_sidebar{
  width: 360px !important;
}
/* .theme_clr .color_t:last-child {
  margin: 0;
} */
.theme_clr .color_t svg path {fill: #fff;}

.theme_clr .color_t svg {font-size: 1em;}
.theme_clr .color_t:first-child {margin-left: 0;}

/* Set Theme Color Toolbar */
.sidebar-tab-body .theme_clr .theme1,
.bottom-tab-body .theme_clr .theme1{
    background: var(--theme1Color);
    color: var(--theme1ColorBtn);
  }
  .sidebar-tab-body .theme_clr .theme2,
  .bottom-tab-body .theme_clr .theme2{
    background: var(--theme2Color);
    color: var(--theme1ColorBtn);
  }
  .sidebar-tab-body .theme_clr .theme3,
  .bottom-tab-body .theme_clr .theme3{
    background: var(--theme3Color);
    color: var(--theme3ColorBtn);
  }
  .sidebar-tab-body .theme_clr .theme4,
  .bottom-tab-body .theme_clr .theme4{
    background: var(--theme4Color);
    color: var(--theme4ColorBtn);
  }
  .sidebar-tab-body .theme_clr .theme5,
  .bottom-tab-body .theme_clr .theme5{
    background: var(--theme5Color);
    color: var(--theme5ColorBtn);
  }
  .sidebar-tab-body .theme_clr .theme6,
  .bottom-tab-body .theme_clr .theme6{
    background: var(--theme6Color);
    color: var(--theme6ColorBtn);
  }
  .sidebar-tab-body .theme_clr .theme7,
  .bottom-tab-body .theme_clr .theme7{
    background: var(--theme7Color);
    color: var(--theme7ColorBtn);
  }
  .sidebar-tab-body .theme_clr .theme8,
  .bottom-tab-body .theme_clr .theme8{
    background: var(--theme8Color);
    color: var(--theme8ColorBtn);
  }
/* Set Theme Color Toolbar End */

@media(max-width: 575.97px){
  .theme_clr{
    margin-top: 16px;
  }
}