@keyframes zoom-in-zoom-out {
  0% {
    transform: scale(1);
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    font-weight: 800;
}
  50% {
    transform: scale(1.02);
    -webkit-transform: scale(1.02);
    -moz-transform: scale(1.02);
    -ms-transform: scale(1.02);
    -o-transform: scale(1.02);
    font-weight: 800;
}
  100% {
    transform: scale(1);
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    font-weight: 800;
}
}

.price-card {
  position: relative;
  /* border-radius: 16px;
  box-shadow: 0px 5px 15px #00000040; */
  /* width: 398px; */
}

.price-card.active {
  border-color: rgb(140, 56, 175);
}

.price-card.active .price-type-box {
  border-color: rgb(140, 56, 175) !important;

}

.price-card .price-badge {
  font-family: var(--primaryFont);
  font-size: 1em;
  font-weight: 400;
  color: #000000;
  text-align: left;
}

.price-card .price-tag {
  font-size: 0.5em;
  font-family: var(--primaryFontBold);
  text-transform: uppercase;
}

.price-detail-box .price-detail-text {
  font-size: 0.875em;
}
.pricing_row{
  display: flex;
  flex-wrap: nowrap;
  align-items: stretch;
}
.pricing_row .col-md-4:nth-child(2){
  order: 2 !important;
 }
