.theme-registration-section{
    background-color: #f8f8f8;
    min-height: 100vh;
    overflow: hidden;
}

.theme-registration-section .action-btn-box .btn{
    max-width: 15em;
    font-size: 22px;
    width: 100%;
}
.theme-registration-section .card.active {
    border: 2px solid var(--primaryColor);
}

/* .theme-registration-section .theme-select-card{
    border:1px solid #eea10c
} */

.theme-registration-section .section-header .img-box img{
    width: 166px;
    height: 100%;
}

.theme-registration-section .main-title {
    color: var(--black);
    font-family: var(--secondaryFontMedium);
    font-size: 20px;
    line-height: 32px;
    text-align: center;
    margin-bottom: 1em;
}

.theme-registration-section .section-header .progress-box{
    display: flex;
    justify-content: center;
    padding-top: 1.5em;
    padding-bottom: 2em;
}

.theme-registration-section .progress-box .icon-box{
    color: #E2E2E2;
    padding-left: 2em;
    padding-right: 2em;
    font-size: 1.25em;
}
.theme-registration-section .background{
    /* position: relative; */
    z-index: -2;
}
.theme-registration-section .front{
    position: relative;
    z-index: 20!important;
}

.theme-registration-section .progress-box .icon-box.active{
    color: #F8AA54;
}
.theme-registration-section .rocket{
    animation: rocket_animation 4s ease-in-out 1s;
    animation-fill-mode:forwards;
}
.theme-registration-section .sun{
    animation: sun_animation 4s ease-in-out 1s ;
    animation-fill-mode:forwards;
}
.theme-registration-section .bottom{
    animation: bottom_animation 4s ease-in-out 1s ;
    animation-fill-mode:forwards;
}

@keyframes rocket_animation {
    0% {
        right: -40px;
        top: -20px;
    }
    100%{
        top:-500px;
        right:-500px;
    }
  }
@keyframes bottom_animation {
    0% {
        bottom: -60px;
        left: 0px;
    }
    100%{
        bottom: -200px;
        left: 0px;
    }
  }
@keyframes sun_animation {
    0% {
        right: 260px;
        top: 190px;
    }
    100%{
        top:100px;
        right:260px;
    }
  }


  @media (max-width: 575.95px) {

    .theme-registration-section .main-title {
        font-size: 1.1em;
        line-height: 26px;
        margin: 20px 0 30px;
      }
  }

  /* Extra large devices (large desktops, 1200px and up) */

  @media (min-width: 768px) and (max-width: 921.98px) {
    .theme-registration-section .main-title{
        font-size: 2em;
      }
  }

