/* statsCard */
.statsCard {
    background-color: var(--themePrimaryColor);
    color: var(--themeTextColor);
    flex: 1 0 auto;
    height: 100%;
    border: none;
    justify-content: center;
    align-items: center;
    padding-top: 2em;
    padding-bottom: 2em;
    border-radius: 17px;
    text-align: center;
    -webkit-border-radius: 17px;
    -moz-border-radius: 17px;
    -ms-border-radius: 17px;
    -o-border-radius: 17px;
  }
.style_3.statsCard  {
  display: flex;
  justify-content: stretch;
  padding-left:1.875em;
  padding-right: 1.875em;
  padding-top: 2.625em;
  padding-bottom: 2em;
}
.style_3.statsCard .cardBody {
  display: flex;
  flex-direction: column-reverse;
  text-align: left;
  width: 100%;
  justify-content: flex-end;
}
.style_3.statsCard .cardBody .cardText{
  border-bottom: 1.85px solid var(--themeTextColor);
}
.guideMsg {
  display: none;
  position: absolute;
  background-color: var(--primaryColor);
  font-family: var(--primaryFont);
  color: #ffffff;
  font-size: 12px;
  text-align: left;
  top: 0px;
  right: 0px;
  padding: 2px 5px;
  border-top-right-radius: inherit;
  border-bottom-left-radius: inherit;
}
.statsCard:hover .guideMsg {
  display: block;
  cursor: pointer;
}
.guideMsg:hover {
  cursor: pointer;
  display: inline;
}
  /* line after states card */

  .statsCard::after {
    content: "";
    width: 1px;
    top: 30px;
    right: 1px;
    height: 130px;
    background-color: #ffffff6b;
    position: absolute;
  }

  .statsCard:last-child:after {
    display: none;
  }

  .statsCard .cardTitle {
    margin-bottom: 7px;
    font-family: var(--themePrimaryFontMedium);
    font-size:4.500em;
    letter-spacing: 1px;
    line-height: var(--bs-body-line-height);
  }

  .statsCard .cardText {
    font-family: var(--themePrimaryFontMedium);
    color: var(--themeTextColor);
    font-size: 1.5em;
    text-transform: capitalize;
    margin-bottom: 7px;
    padding-bottom: 8px;
    line-height: var(--bs-body-line-height);
  }
  /*Laptops*/
    @media (min-width: 768.98px) and (max-width: 1024px) {
      .statsCard .cardTitle {
        font-size: 3.75em;
      }
    }
  /* Extra large devices (large desktops, 1200px and up) */

  @media (min-width: 1200px) and (max-width: 1399.98px) {
    .statsCard .cardTitle{
      font-size: 4.5em
    }
  }

  @media (min-width: 1400px) and (max-width: 1599.98px) {
    .statsCard .cardTitle{
      font-size: 4em;
    }
    .statsCard .cardText{
      font-size: 1.25em;
    }
  }

  @media (min-width: 1600px) and (max-width: 1799.98px) {
     .statsCard .cardTitle{
      font-size: 4.625em;
    }
  }

  /*Tablet*/
    @media (max-width: 768px) {
      .statsCard .cardTitle {
        font-size: 2.8em;
        margin-bottom: 0;
      }

      /* .statsCard .cardText {
        font-size: 1em;
      } */

    }
  /*Large Mobile devices  */

  @media (max-width: 575.98px) {
    .style_3.statsCard {
      /* background-color: green !important; */
      padding-left: 1.5em;
      padding-right: 1.5em;
      padding-top: 1.5em;
      padding-bottom: 1em;
    }
    .statsCard .cardTitle {
      font-size: 1.8em;
      margin-bottom: 0;
    }

    .statsCard .cardText {
      font-size: 1em;
    }

  }
