.full-img-card{
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    min-height: 350px;
    text-align: center;
    border-radius: 17px;
    -webkit-border-radius: 17px;
    -moz-border-radius: 17px;
    -ms-border-radius: 17px;
    -o-border-radius: 17px;
}
.full-img-card .card-body{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    color: #ffffff;
    padding: 28px 22px;
    padding-bottom: 0;
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
}
.full-img-card .card-footer{
    padding: 28px 22px;
    padding-top: 0;
}
.full-img-card .card-body .card-title{
    font-family: var(--themePrimaryFontBold);
    font-size: 1.125em;
    line-height: 21px;
    margin-bottom: 6px;
}
.full-img-card .card-body .card-text{
    font-size: 14px;
    font-family: var(--themePrimaryFontLight);
    min-height: 60px;
}
.full-img-card .card-footer .btn-box > .btn-outline-primary{
    background-color: transparent;
    color: #ffffff;
    border-color: #ffffff;
}
.full-img-card .card-footer .link > a{
    color: #ffffff;
}


.collapse-card{
    border: none;
}
.collapse-card .accordion-text
 {
     color:black
 }
.collapse-card .stretched_body
{
   padding: 0px 0px !important;
}
.collapse-card .stretched_body .editable-content-box
{
    text-align: left !important;
}
.collapse-card .accordion-button
{
border-top-left-radius: 25px !important;
border-top-right-radius: 25px !important;
    height: 1em;
}
.collapse-card .accordion-item
{
border-top-left-radius: 25px !important;
border-top-right-radius: 25px !important;
}
.collapse-card .card-footer .link > a
{
    color: black !important;
}
.collapse-card .card-footer
{
   background: none !important;
    border-top: none !important;
}
.collapse-card .card-footer .btn-box, .collapse-card .card-footer .link
{
    text-align: left !important;
}
.collapse-card .card-footer .btn-box > button
{
    width: 100px!important;
    margin-left: -58px;
    color: black;
    font-size: 14px;
    text-decoration: none;
}
.collapse-card .accordion-button.collapsed{
    color: black !important;
    background-color: aliceblue !important;
}

.collapse-card .accordion-button{
    color: black !important;
    background-color: aliceblue !important;
}
.collapse-card .accordion-item{
    color: black !important;
    background-color: aliceblue !important;
}
.collapse-card  .accordion-button:focus
{
border-color: white !important;
    box-shadow: none !important;
}
