.subscriptionSwitchModal .modalContent {
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
}

.subscriptionSwitchModal .modalHeader {
    border-bottom: none;
    justify-content: center;
    padding-top: 40px;
    padding-bottom: 8px;
}

.subscriptionSwitchModal .modalHeader .closeIcon {
    border: none;
    margin-left: auto;
    position: absolute;
    top: 6px;
    right: 10px;
}

.subscriptionSwitchModal .modalHeader .closeIcon .icon{
    color: var(--primaryColor);
}

.subscriptionSwitchModal .modalHeader .title {
    font-family: var(--primaryFontBold);
    font-size: 2.625em;
    text-align: center;
}

.subscriptionSwitchModal .contentBox {
    width: 81%;
    margin: 0 auto;
    text-align: center;
}

.subscriptionSwitchConfirmationModal .contentBox{
    width: 77%;
}

.subscriptionSwitchModal .contentBox .heading {
    font-size: 1.375em;
    font-family: var(--primaryFontMedium);
    margin-bottom: 16px;
    color: #5E606A;
}

.contentBox .desc {
    font-family: var(--primaryFont);
    font-size: 1em;
    color: #7A7A7A;
}

.subscriptionSwitchModal .modalFooter {
    justify-content: center;
    border-top: none;
}

.btnOutlinePrimary {
    background-color: #ffffff;
    color: var(--primaryColor);
    border: 1px solid var(--primaryColor);
    text-decoration: none;
    padding: 12px 33px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    min-width: 181px;
    margin-right: 22px;
    text-align: center;
}

.btnOutlinePrimary:hover {
    color: var(--primaryColor);
    cursor: pointer;
}

.btnPro {
    background-color: var(--orange);
    color: #ffffff;
    text-decoration: none;
    padding: 12px 33px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    min-width: 181px;
    text-align: center;
}

.btnPro:hover {
    color: #ffffff;
    cursor: pointer;
}

.subscriptionSwitchModal .btnBox .btn:first-child{
    margin-right: 16px;
}

.subscriptionFailModal .modalFooter .btnBox .btn{
    margin-right: 0px;
}

.btn{
    text-decoration: none;
    padding: 11px 16px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    min-width: 177px;
    text-align: center;
    display: inline-block;
}

.btnPrimary{
    background-color: var(--primaryColor);
    color: #ffffff;
}

.btnPrimary:hover{
    color: #ffffff;
}

.btnProOutline {
    background-color: transparent;
    color: var(--orange);
    border: 1px solid var(--orange);
}

.btnProOutline:hover{
    color: var(--orange);
}

.subscriptionSwitchModal .modalFooter {
    margin-bottom: 55px;
}

.subscriptionFailModal .modalFooter{
    margin-bottom: 46px;
}

@media (max-width: 575.9808px) {
    .subscriptionSwitchModal .modalHeader .title {
        font-size: 1.375em;
    }

    .subscriptionSwitchModal .contentBox {
        width: 100%;
    }

    .subscriptionSwitchModal .contentBox .heading {
        font-size: 1.125em;
    }

    .subscriptionSwitchModal .modalFooter {
        margin-bottom: 31px;
    }

    .subscriptionSwitchModal .modalFooter .btnBox {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .subscriptionSwitchModal .btnBox .btn:first-child{
        margin-right: 0px;
        margin-bottom: 16px;
    }

    .subscriptionSwitchModal .modalFooter .btnOutlinePrimary {
        margin-right: 0px;
        margin-bottom: 16px;
    }
}