

.SubscriptionPackages{
    margin-top: 40px;
}
.heading {
    text-align: left;
}
.heading h2{
    font-weight: 700;
    font-size: 16px;
    color: #282828;
    margin-bottom: 16px;
    font-family: var(--primaryFontBold);
}
.montlyCol h3{
    display: none;
}
.activePkg .packagesBox{
    border: 1px solid #f07c26;
    box-shadow: 0 0 0 1px #f07c26 inset;
}
.labelPkg{
    width: 100%;
    position: relative;
    margin-top: 14px;
}

.packagesBox {
    border: 1px solid #E7E7E7;
    border-radius: 8px;
    padding: 16px;
    position: relative;
    height: 145px;
    cursor: pointer;
    transition: all 0.2s;
    background-color: #fbfcff;
}

.packagesBox:hover {
    /* transform: scale(1.05); */
    box-shadow: 0px 10px 20px -10px #ddd;
    margin-top: -4px;
}

.pkgActive{
    border: 1px solid #f07c26;
    box-shadow: 0 0 0 1px #f07c26 inset;
}
.packagesBox .checkbox{
    visibility: hidden;
    position: absolute;
}

.currentLabel {
    background: #f07c26;
    padding: 6px;
    display: block;
    position: relative;
    top: 10px;
    text-align: center;
    color: #fff;
    border-radius: 10px 10px 0 0;
    position: absolute;
    top: -24px;
    right: 0;
    left: 0;
    font-size: 12px;
}

.packagesBox h2{
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    color: #343434;
    font-family: var(--primaryFontBold);
    margin-bottom: 0;
}
.packagesBox h3{
    font-weight: 700;
    font-size: 18px;
    line-height: 32px;
    color: #F07C26;
    font-family: var(--primaryFontBold);
}
.packagesBox p{
    color: #5E606A;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    width: 168px;
    font-family: var(--primaryFontBold);
    position: absolute;
    bottom: 0;
}
.checkCircle{
    position: absolute;
    width: 26px;
    height: 26px;
    right: 20px;
    border-radius: 40px;
    border: 1px solid #E7E7E7;
}

@keyframes ani_arrow {
    50% {
        transform: scale(1.2);
    }
}

.pkgActive .checkedArrow {
    animation: ani_arrow 0.5s;
}

.checkedArrow {
    position: absolute;
    width: 26px;
    height: 26px;
    background: #F07C26;
    right: 20px;
    border-radius: 40px;
    color: #fff;
    font-size: 14px;
}


.checkedArrow svg{
    position: absolute;
    left: 6px;
    top: 6px;
}

@media (max-width: 1366px) {
    .packagesBox h2{
        font-size: 18px;
    }
    .packagesBox h3{
        font-size: 13px;
    }
    .packagesBox p{
        font-size: 12px;
        width: auto;
    }
}

@media (max-width: 575.95px) {
    .packagesBox{
        padding: 10px;
        height: 165px;
        margin-bottom: 15px;
    }
    .packagesBox h2{
        font-size: 20px;
        width: 96px;
        line-height: 24px;
    }
    .packagesBox h3{
        font-size: 15px;
        bottom: 50px;
        position: absolute;
    }
    .packagesBox p{
        font-size: 12px;
        width: auto;
    }
    .checkCircle{
        width: 23px;
        height: 23px;
        right: 10px;
        top: 10px;
    }
    .checkedArrow{
        width: 23px;
        height: 23px;
        right: 10px;
        top: 10px;
    }
    .checkedArrow svg{
        left: 5px;
        top: 6px;
        height: 12px;
    }
}