.accountConfirmationModal .modalDialog{
    max-width: 538px;
}
.accountConfirmationModal .modalContent {
    border-radius: 13px;
    -webkit-border-radius: 13px;
    -moz-border-radius: 13px;
    -ms-border-radius: 13px;
    -o-border-radius: 13px;
}

.accountConfirmationModal .modalHeader {
    border-bottom: none;
    justify-content: center;
    padding-top: 40px;
}

.accountConfirmationModal .modalHeader .closeIcon {
    border: none;
    margin-left: auto;
    position: absolute;
    top: 6px;
    right: 10px;
}

.accountConfirmationModal .modalHeader .closeIcon .icon{
    color: var(--primaryColor);
}

.accountConfirmationModal .contentBox {
    width: 89%;
    margin: 0 auto;
    text-align: center;
}

.accountConfirmationModal .contentBox .heading {
    font-size: 1.5em;
    font-family: var(--primaryFontMedium);
    margin-bottom: 16px;
    color: #5E606A;
}

.contentBox .desc {
    font-family: var(--primaryFont);
    font-size: 1em;
    color: #7A7A7A;
}

.accountConfirmationModal .modalFooter {
    justify-content: center;
    border-top: none;
    margin-bottom: 17px;
    flex-direction: column;
}

.btn{
    text-decoration: none;
    padding: 11px 16px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    min-width: 154px;
    text-align: center;
}

.btnPrimary{
    background-color: var(--primaryColor);
    color: #ffffff;
}

.btnPrimary:hover{
    color: #ffffff;
}

.btnLink{
    color: var(--orange);
    text-decoration: none;
}

.btnLink:hover{
    color: var(--orange);
}

@media (max-width: 575.9808px) {
    .accountConfirmationModal .modalHeader .title {
        font-size: 1.375em;
    }

    .accountConfirmationModal .contentBox {
        width: 100%;
    }

    .accountConfirmationModal .contentBox .heading {
        font-size: 1.125em;
    }

    .accountConfirmationModal .modalFooter {
        margin-bottom: 31px;
    }

    .accountConfirmationModal .btnBox .btn:first-child{
        margin-right: 0px;
        margin-bottom: 10px;
    }

}