.discoverRegisterResults{
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background-color: #fff;
    padding: 0px;
}

.discoverResultsWrapper{
    position: relative;
    top: 58px;
    text-align: center;
    padding-top: 48px;
    padding-bottom: 37px;
}

.discoverResultsWrapper .contentBox .title{
    font-family: var(--primaryFontMedium);
    font-size: 2em;
    color: var(--primaryColor);
    margin-bottom: 13px;
}

.discoverResultsWrapper .contentBox .desc{
    font-family: var(--primaryFont);
    font-size: 1.5em;
    color: #343434;
}

.discoverResultsWrapper .imgBox{
    margin-bottom: 28px;
}

.discoverRegisterResults .imgBox .msg{
    font-family: var(--primaryFontBold);
    font-size: 1.5em;
    color: #7250B2;
}

.discoverResultsWrapper .btnBox .btnPrimary{
    background-color: var(--primaryColor);
    color: #ffffff;
    font-size: 15px;
    padding: 12px 32px;
    border-radius: 50em;
    text-decoration: none;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}

.discoverResultsWrapper .btnBox .btnOutlinePrimary{
    background-color: #ffffff;
    color: var(--primaryColor);
    border: 1px solid var(--primaryColor);
    font-size: 15px;
    padding: 12px 32px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}
/* submit page */
.discoverRegisterSubmitSection .discoverResultsWrapper .contentBox{
    margin-bottom: 140px;
}

.discoverRegisterSubmitSection .imgBox .img{
    margin-bottom: 19px;
}

/* fail page */

.discoverRegisterFailSection .contentBox{
    margin-bottom: 84px;
}

.discoverRegisterFailSection .contentBox .title{
    margin-bottom: 24px;
}

.discoverRegisterFailSection .imgBox .img{
    margin-bottom: 68px;
}

.discoverRegisterFailSection .imgBox .msg{
    margin-bottom: 24px;
}

.discoverRegisterFailSection .btnBox button:first-child{
    margin-right: 24px;
}

@media (max-width: 575.98px) {
    .discoverResultsWrapper{
        top: 0px;
    }
    .discoverResultsWrapper .contentBox .title{
        font-size: 1.5em;
    }

    .discoverResultsWrapper .contentBox .desc{
        font-size: 1.1em;
    }

    /* fail */

    .discoverRegisterFailSection .contentBox{
        margin-bottom: 42px;
    }

    .discoverRegisterFailSection .imgBox .img{
        margin-bottom: 45px;
    }

    .discoverRegisterFailSection .btnBox{
        display: flex;
        flex-direction: column;
    }

    .discoverRegisterFailSection .btnBox button:first-child{
        margin: 0 auto;
        margin-bottom: 16px;
    }

    .discoverRegisterFailSection .btnBox button{
        margin: 0 auto;
    }

    /* submit */

    .discoverRegisterSubmitSection .discoverResultsWrapper .contentBox{
        margin-bottom: 70px;
    }

    .discoverRegisterResults .imgBox .msg{
        font-size: 1.1em;
    }
}