.pricingContainer{
    margin-bottom: 70px;
    position: relative;
}
.circleSvg{
    position: absolute;
    left: 0;
    top: 24px;
}
.pricingMobile{
    background: linear-gradient(209.25deg, rgba(255, 255, 255, 0.7) 23.36%, rgba(255, 255, 255, 0) 93.45%);
    border: 1px solid #E7E7E7;
    backdrop-filter: blur(20px);
    border-radius: 12px;
    padding: 20px;
    margin-bottom: 25px;
    position: relative;
}
.pricing_topArea h3{
    background: linear-gradient(289.79deg, #FFA847 60.59%, #9461FA 95.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    font-family: var(--primaryFontBold);
    font-size: 24px;
    margin-bottom: 16px;
}
.pricing_topArea hr{
    border-color: #f58634;
}
.pricingMobilePro .pricing_topArea p{
    position: absolute;
    right: 18px;
    font-size: 14px;
    line-height: 18px;
    color: #F07C26;
}
.pricingMobilePro .pricing_topArea p img{ 
    width: 30px;
    position: relative;
    top: -4px;
}

.pricing_topArea h4{
    position: absolute;
    right: 20px;
    color: #169F0A;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    top: 81px;
}
.pricing_topArea h2{
    font-weight: 500;
    font-size: 32px;
    line-height: 40px;
    font-family: var(--primaryFontBold);
    color: #282828;
}
.pricing_topArea h2 span{
    font-size: 24px;
    position: relative;
    left: -8px;
}
.pricing_topArea button{
    height: 44px;
    width: 100%;
    border-radius: 50px;
    background: #fff;
    transition: .3s;
    color: #5b3f91;
}
.pricing_topArea button:hover{
    background-color: #5b3f91;
    color: #fff;
}
.pricingMobilePro .pricing_topArea button{
    background: #F58634;
    border-color: #F58634;
    color: #fff;
}

.contentAreaText p{
    position: relative;
    margin-bottom: 0;
    height: 65px;
    padding-right: 50px;
    font-size: 14px;
    color: #5E606A;
    border-bottom: 1px solid #E7E7E7;
    padding-top: 21px;
}
.contentAreaText p:last-child{
    border-bottom: 0;
}
.contentAreaText p:nth-child(2),
.contentAreaText p:nth-child(3),
.contentAreaText p:nth-child(4){
    padding-top: 12px;
}
.contentAreaText .check{
    position: absolute;
    right: 0;
}
.pricingMobilePro .contentAreaText .check svg path{
    fill: #f58634;   
}