
 .user-history-table {
    background-color: #fff;
    /* border: 1px solid #d7dce1; */
    border-radius: 17px;
    /* box-shadow: 0px 0px 5px #0000000d; */
    color: #1e1e1e;
    /* padding: 10px; */
}

.dashboard-container .user-history-table .user-table-heading {
    font-size: 1.25em;
    font-family: var(--primaryFontMedium);
}


.dashboard-container .user-history-table .user-table-heading>thead {
    border-style: none;
}

/* .dashboard-container .user-history-table>tbody tr:nth-child(odd) {
    background-color: #ffffff;
} */

.dashboard-container .user-history-table .user-table-subheading {
    font-size: 18px;
    font-family: var(--primaryFont);
}

.user-history-table .table-head {
    position: relative;
    padding: 18px 40px;
}

.user-history-table .table-body .row {
    position: relative;
    padding: 15px 40px;
}

.user-history-table .table-body .row:last-child {
    border-bottom-left-radius: 17px;
    border-bottom-right-radius: 17px;
}



 .user-history-table .table-head .cell .title {
    font-family: var(--primaryFontSemiBold);
    font-size: 1.25em;
}

.payment-history-table .table-head {
    position: relative;
    padding: 18px 40px;
}

.payment-history-section .payment-history-table .title {
    font-family: var(--primaryFontSemiBold);
    font-size: 1.25em;
}

.payment-history-table .table-head .cell.one, .payment-history-table .table-body .cell.one {
    text-align: left;
}

.payment-history-table .table-head .cell, .payment-history-table .table-body .cell {
    text-align: left;
}

.user-history-table .table-head,
.user-history-table .table-body {
    width: 100%;
}

.user-history-table .table-body{
    cursor: pointer;
}

.user-history-table .table-head .cell,
.user-history-table .table-body .cell {
    text-align: left;
}

.user-history-table .table-head .cell.one,
.user-history-table .table-body .cell.one {
    text-align: left;
}

.user-history-table .table-body .cell .data-text {
    font-family: var(--primaryFont);
    font-size: 1.125em;
    margin-bottom: 0px;
    color: #1e1e1e;
    border-radius: 20px;
    width: 80%;
    cursor: pointer;
}

.table-body .cell .data-text.badge.badge-success {
background-color: #84DE2D;
}
 .table-body .cell .data-text.badge.badge-danger {
    background-color: #FF7878;
}
 .table-body .cell .data-text.badge.badge-secondary {
    background-color: #D2CECE;
}
/* Mobile devices small screen */

@media (max-width: 380px) {
.table-body .payment-history-heading .payment-heading {

    font-size: 0.95em !important;
}
.payment-user-dets .data-text{
    font-size: 0.75em;
}
.user-email .data-text {
    font-size: 0.8em !important;

}
.table-body .payment-history-status {
    position: relative;
    top: -4px;
}
.table-body .cell .data-text.badge{
    font-size: 0.7em !important;
}
}

/* Mobile devices large screen */

@media (max-width: 575.98px) {
    #SvgjsG1119 #SvgjsText1120{
        font-size: 16px;
    }
    .dashboard-container{
        height: 100% !important;
        width: 100%;
        margin-left: 0;
    }
.table-body .cell .data-text{
    font-size: 0.95em;
    width: 100%;
}
.table-body .payment-history-heading .payment-heading{

    font-size: 1.125em;
}
.table-body .cell .data-text.badge{
    color: #fff;
    font-size: 0.875em;
}

 .user-email .data-text{
    font-size: 1em;

}
.user-history-card.card-body{
    padding:0.5em 0.75em;
}

.user-history-card:first-child>.history-card-sec {
    margin-top: 1.5em;
}
}

/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
.table-body .cell .data-text.badge{
    width: 100%;
    font-size: 0.9em;
}
.table-body .payment-history-heading .payment-heading {
    font-size: 1.2em;
}

.table-body .payment-history-heading .payment-heading{
    font-family: var(--primaryFontMedium);
    color: #52545A;
    font-size: 1.2em;
}
.user-email .data-text {
    font-size: 1em;
    font-family: var(--primaryFont);
    color: #52545A;
}
.payment-user-dets .data-text {
    font-family: var(--primaryFont);
    color: #52545A;
    font-size: 1em;
}

}

/* Extra large devices (large desktops, 1200px and up) */

@media (min-width: 1200px) and (max-width: 1399.98px) {
.user-history-table .table-body .cell .data-text {
    font-size: 1em !important;
    flex-wrap: wrap;
    word-break: break-word;
}
}

@media (min-width: 1400px) and (max-width: 1599.98px) {
.user-history-table .table-head .title{
    font-size: 1.2rem;
}
.user-history-table .table-body .cell .data-text{
    font-size: 1em !important;
    flex-wrap: wrap;
    word-break: break-word;
}
}

