.mainPriceDiv{
    position: relative;
}
.main_row{
    display: flex;
    justify-content: center;
    margin-bottom: 160px;
    position: relative;
}
.inner_col{
    background: linear-gradient(209.25deg, rgb(255 255 255 / 22%) 23.36%, rgba(255, 255, 255, 0) 93.45%);
    filter: drop-shadow(0px -8px 20px rgba(0, 0, 0, 0.05));
    backdrop-filter: blur(20px);
    border-radius: 12px;
    width: 200px;
}
.colOne{
    background: none;
    z-Index: -1;
    width: 385px;
    border-radius: 0;
    box-shadow: none;
    filter: none;
}

.topArea{
    padding: 30px 0;
    height: 205px;
    position: relative;
}
.topArea h2{
    text-align: center;
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    font-family: var(--primaryFontBold);
    font-size: 24px;
}
.colOne .topArea h2{
    position: absolute;
    bottom: 18px;
    left: 25px;
    -webkit-text-fill-color: #000;
}
.topArea h3{
    text-align: center;
    font-size: 22px;
    font-family: var(--primaryFontBold);
    color: #282828;
    margin-top: 10px;
    margin-bottom: 18px;
}
.topArea button{
    display: block;
    margin: auto;
    font-size: 14px;
    border-radius: 50px;
    padding: 7px 18px;
    background: #F58634;
    border: #F586;
    width: 123px;
    height: 39px;
    line-height: 26px;
}
.colTwo .topArea button{
    border: 1px solid #5B3F91;
    background: #fff;
    color: #5B3F91;
}
.colTwo .topArea button:hover{
    border: 1px solid #5B3F91;
    background: #5B3F91;
    color: #fff;
}
.colOne .contentArea p{
    text-align: left;
    justify-content: space-between;
    padding-left: 20px;
}
.contentArea p{
    text-align: center;
    margin-bottom: 0;
    height: 75px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.colTwo .topArea br,
.colThree .topArea br,
.colSix .topArea br{
    display: none;
}

.inner_col:nth-child(1) button,
.inner_col:nth-child(2) button,
.inner_col:nth-child(5) button{
    position: relative;
}

/* .inner_col:nth-child(2) h3:after, */
.pkg5 h3:after{
    content: "Limited Time Offer*";
    position: absolute;
    font-size: 14px;
    left: 37px;
    top: 113px;
}
.inner_col:nth-child(1) h3{
    position: relative;
    left: -27px;
}
.inner_col:nth-child(1) h3:after{
    content: "/month";
    position: absolute;
    font-size: 16px;
    transform: translate(4px, 7px);
}


.colThree .contentArea p,
.colFour .contentArea p,
.colFive .contentArea p,
.pkg1 .contentArea p{
    background: #ffb96c3b !important;
}
.pkg2 .contentArea p{
    background: transparent !important;
}
.contentArea p:nth-child(odd){
    background: #FBFCFF;
}
.contentArea p:nth-child(even){
    background: #fff;
}

.colThree .check svg path,
.colFour .check svg path,
.colFive .check svg path,
.colSix .check svg path{
    fill: orange;
}
.pricingCols{
    display: flex;
}
.monthlyPrice{
    font-size: 16px;
    text-align: center;
    display: block;
    position: relative;
    top: -14px;
    background: linear-gradient(289.79deg, #14870d 23.59%, #14870d 66.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.monthlyStyle{
    font-size: 16px;
}
.starSvg{
    position: absolute;
    transform: translate(-245px, 55px);
}

.starIcon{
    position: absolute;
    top: -75px;
    right: 107px;
    z-index: -1;
}
.starCircle{
    position: absolute;
    top: -40px;
    right: 80px;
    z-index: -10;
    transform: rotate(-90deg);
}
.starIcon{
    animation: starIcon 6s linear infinite; 
}
@keyframes starIcon{
    to{ transform: rotate(360deg); }
}

.FreePriceIcon{
    position: absolute;
    z-index: -1;
    right: 85px;
    transform: rotate(180deg);
    top: -60px;
}
.FreePriceIcon .iconOne{
    position: absolute;
    right: 65px;
    bottom: -11px;
}
.FreePriceIcon .iconTwo{
    position: relative;
    right: 15px;
}
.star{
    animation: star 6s linear infinite; 
  }
  @keyframes star{
    to{ transform: rotate(360deg); }
  }

.ProPriceIcon{
    position: absolute;
    top: 170px;
    left: -60px;
    z-index: -1;
}
.ProPriceIcon .iconOne{
    width: 157px;
    height: 150px;
    border: 6px dashed #AC82FF;
    border-radius: 118.231px;
    position: absolute;
    right: -80px;
    bottom: -45px;
}
.ProPriceIcon .iconTwo{
    width: 157px;
    height: 150px;
    background: #5B3F91;
    border-radius: 118.231px;
}
.circleBar{
    animation: circleBar 7s linear infinite; 
  }
  @keyframes circleBar{
    to{ transform: rotate(-180deg); }
  }

  .loaderDiv{
    background: #0000004d;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    display: none;
  }

  .loader {
    margin: auto;
    flex-direction: row;
    align-content: center;
    height: 100%;
    justify-content: center;
    align-items: center;
  }
    
  .loader__item {
    width: 16px;
    height: 16px;
    margin: 4px;
    background: pink;
    border-radius: 50%; 
    list-style: none;
    animation: animation-loading 1.2s infinite;
  }
    
  .loader__item:nth-child(1) {
      animation-delay: -0.3s;
    }
    .loader__item:nth-child(2) {
      animation-delay: -0.15s;
    }
  
  
  @keyframes animation-loading {
    0% {
      transform: scale(0);
    }
    40% {
      transform: scale(1);
    }
    80% {
      transform: scale(0);
    }
    100% {
      transform: scale(0);
    }
      
  }

  .pro_pricing{
    position: absolute;
    left: 295px;
    top: -78px;
    height: 285px;
    width: 800px;
    text-align: center;
    border-top-left-radius: 11px;
    border-top-right-radius: 11px;
    background: linear-gradient(209.25deg, rgb(255 255 255 / 22%) 23.36%, rgba(255, 255, 255, 0) 93.45%);
    filter: drop-shadow(0px -8px 20px rgba(0, 0, 0, 0.05));
    -webkit-backdrop-filter: blur(20px);
    backdrop-filter: blur(20px);
    box-shadow: -5px -4px 19px #00000003;
    border: 1px solid #e5d5f17a;
    background: linear-gradient(358deg, #ffffff00 23.59%, #9461fa0d 63.85%);
    border-bottom: none;
}
.pro_pricing h2{
    text-align: center;
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    font-family: var(--primaryFontBold);
    font-size: 30px;
    padding-top: 30px;
}
.pkg2, .pkg3, .pkg4, .pkg5{
    background: transparent;
    filter: none;
    backdrop-filter: none;
}
.pkg1 .proTxt,
.pkg2 .proTxt{
    margin-top: 8px;
}
.pkg1 .savetext,
.pkg2 .savetext,
.pkg5 .savetext{
    visibility: hidden;
}

.savetext{
    display: block;
}
.proMob{
    display: none;
}
.className{
    
}

/* Responsive Styles */

@media (max-width: 1660px) {
    .pricingCols{
        width: 100%;
        display: -webkit-box;
        overflow-y: scroll;
    }
    .pro_pricing{
        width: 100%;
        left: 0;
    }
    .starSvg{
        top: 25px;
    transform: rotate(-180deg) !important;
    left: -89px;
    }
}
@media (max-width: 550px) {
    .inner_col{
        min-width: 190px;
    }
    /* .pkg1{
        order: 3;
    }
    .pkg2, .pkg3, .pkg4{
        order: 2;
    }
    .pkg5{
        order: 1;
    } */

    .proMob{
        display: block;
        position: absolute;
        top: 0;
        width: 0;
        height: 0;
        border-top: 60px solid #f8aa54;
        border-right: 60px solid transparent;
    }
    .proMob span{
        position: absolute;
        transform: rotate(-44deg);
    font-size: 18px;
    font-family: var(--primaryFontBold);
    color: #ffffff;
    top: -53px;
    left: 6px;
    }

    .pkg2, .pkg3, .pkg4, .pkg5{
        background: linear-gradient(209.25deg, rgb(255 255 255 / 22%) 23.36%, rgba(255, 255, 255, 0) 93.45%);
        filter: drop-shadow(0px -8px 20px rgba(0, 0, 0, 0.05));
        -webkit-backdrop-filter: blur(20px);
        backdrop-filter: blur(20px);
    }

    .pro_pricing{
        display: none;
    }

    .pricingCols{
        width: 100%;
        display: flex;
        overflow-y: scroll;
    }
    .contentArea p{
        height: 90px;
        padding-right: 10px;
    }
}