.basic-long-card {
    position: relative;
    border-radius: 17px;
    -webkit-border-radius: 17px;
    -moz-border-radius: 17px;
    -ms-border-radius: 17px;
    -o-border-radius: 17px;
    text-align: center;
    background-color: var(--themePrimaryColor);
    color: var(--themeTextColor);
    height: 100%;
}

.img_wrapper_box {
    position: relative;
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
}

.img_wrapper_box .img_wrapper_box_loader img {
    width: 150px !important;
    height: 150px !important;
}

.basic-long-card .card-header {
    width: 100%;
    height: 305px;
    background-repeat: no-repeat;
    background-size: cover !important;
    background-position: center;
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
    border-bottom: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.basic-long-card .card-header .card-title {
    font-family: var(--themePrimaryFontBold);
    font-size: 1.125em;
    line-height: 16px;
    word-break: break-word;
    color: var(--themeTextColor);
}

.basic-long-card .card-header .card-text {
    font-family: var(--themePrimaryFontLight);
    font-size: .875em;
    line-height: 16px;
    word-break: break-word;
    color: var(--themeTextColor);
}

.basic-long-card .card-body.portfolio-card-body {
    position: relative;
    text-align: left;
    padding-top: 7px;
    border-bottom-right-radius: inherit;
    border-bottom-left-radius: inherit;
}

.basic-long-card .card-body.portfolio-card-body .user-info-box {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    flex-wrap: wrap;
    position: relative;
    top: -50px;
}

.basic-long-card .card-body.portfolio-card-body>.editable-content-box {
    top: -40px;
}

.basic-long-card .card-body.portfolio-card-body .user-img-box {
    position: relative;
}

.basic-long-card .card-body.portfolio-card-body .user-info-box .card-edit-block {
    right: -17px;
}

.basic-long-card .card-body.portfolio-card-body .user-img-box .user-img {
    width: 85px;
    height: 85px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}

.basic-long-card .card-body.portfolio-card-body .user-img-box.portfolio-user-edit>.card-edit-block {
    position: relative;
    z-index: 1;
}

.basic-long-card .card-body.portfolio-card-body .user-img-box.portfolio-user-edit {
    position: relative;
}

.basic-long-card .card-body.portfolio-card-body .user-img-box.portfolio-user-edit>.user-img {
    position: absolute;
    top: 10px;
}

.basic-long-card .card-body.portfolio-card-body .user-img-box.portfolio-user-edit>.card-edit-block .edit-box {
    position: absolute;
    left: 67px;
    font-size: 12px;
    width: 27px;
    height: 27px;
}

.basic-long-card .card-body {
    background-color: var(--themePrimaryColor);
    color: var(--themeTextColorSecondary);
    border-radius: 17px;
    -webkit-border-radius: 17px;
    -moz-border-radius: 17px;
    -ms-border-radius: 17px;
    -o-border-radius: 17px;
}

.basic-long-card .card-body.btn-edit-generic{
    background-color: #fff;
}

.basic-long-card .card-body .card-title {
    font-family: var(--themePrimaryFontBold);
    font-size: 1em;
    margin-bottom: 7px;
    line-height: 16px;
    color: var(--themeTextColor);
}

.basic-long-card .card-body .card-text {
    font-family: var(--themePrimaryFontLight);
    font-size: .875em;
    line-height: 16px;
    color: var(--themeTextColor);
}

.basic-long-card .card-footer {
    background-color: transparent;
    border-top: none;
}

.basic-long-card .card-footer .link>a {
    color: var(--themeTextColor);
    text-decoration: none;
    cursor: pointer;
}

.basic-long-card .card-footer .btn-box .btn-outline-primary {
    border-color: var(--themeTertiaryColor);
    color: var(--themeTertiaryColor);
}

.basic-long-card .card-footer .btn-box .btn-outline-primary:hover {
    color: #fff;
    background-color: var(--themeTertiaryColor);
}


/* Style 3 */

#collab.style_3 .basic-long-card {
    padding: 15px;
}

#collab.style_3 .basic-long-card .portfolio-card-body {
    order: 1;
    max-height: 140px;
    padding-top: 64px;
    padding-inline: 0;
}

#collab.style_3 .basic-long-card .portfolio-card-body .user-info-box {
    align-items: center;
}

#collab.style_3 .basic-long-card .card-header {
    order: 2;
    border-radius: 20px;
}

#collab.style_3 .basic-long-card .card-body.portfolio-card-body .user-img-box .user-img {
    margin-right: 10px;
}


#collab.style_3 .basic-long-card .card-footer {
    order: 5;
    padding-inline: 0;
}



@media (max-width: 800px) {

    #collab.style_3 .basic-long-card .card-header {
        height: 184px;
    }

    #collab.style_3 .basic-long-card .card-body.portfolio-card-body .user-img-box .user-img {
        width: 64px;
        height: 64px;
    }

    /* collab section */
    #collab .basic-long-card .imageLoaderCard img {
        width: 150px;
        height: 150px;
    }
}

@media(max-width: 575.98px){
    .basic-long-card .card-body.portfolio-card-body .user-info-box .card-edit-block{
        right: -17px;
    }
}