.shortCard {
    width: 100%;
    height: 100%;
    background-color: var(--themePrimaryColor);
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
    border: none;
}

.cardBody {
    display: flex;
}

.contentBox {
    flex: 1;
    padding-left: 1em;
}

.shortCardHeader {
    background-color: transparent;
}

.cardTitle {
    font-size: 1.15rem;
    font-family: var(--primaryFontMedium);

}

.cardDesc {
    font-family: var(--primaryFont);
    font-size: 1.125rem;
}

.shortCardFooter {
    background-color: transparent;
    border-top: 0px;
}

.btnFooter {
    background-color: transparent !important;
    color: var(--purple) !important;
}

.btnClassName {
    background-color: var(--themeTextColor) !important;
    border-color: var(--themeTextColor) !important;
    color: var(--themePrimaryColor) !important;
    height: auto;
    width: 60% !important;
}

.btnClassName:hover {
    background-color: transparent !important;
    color: var(--themeTextColor) !important;
}

.imgBox {
    width: 125px;
    /* height: 100%; */
    height: 118px;
    /*align-items: center;
    */display: flex;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
    position: relative;
}

.classNameImg {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
}

/* Mobile devices small screen */
@media (max-width: 380px) {}

@media (max-width: 575.98px) {

    .shortCard.style_3.book .imgBox .classNameImg {}

    .shortCard .cardTitle {
        font-size: 1rem;
        margin-bottom: 0px;
    }

    .shortCard .cardDesc {
        font-size: .875rem;
    }

    .shortCard .imgBox {
        height: 96px;
        width: 90px;
    }

    .btnClassName {
        width: 100% !important;
    }

    .customeEditBlock {
        top: 13px !important;
    }
}

/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {}

/* Extra large devices (large desktops, 1200px and up) */

@media (min-width: 1200px) and (max-width: 1399.98px) {}

@media (min-width: 1400px) and (max-width: 1599.98px) {}