:global(.dashboard-container-wrapper) {
    padding-top: 10px;
}

.topHeading h1{
    font-weight: 700;
    font-size: 24px;
    color: #282828;
}

.topHeading p{
    font-weight: 600;
    font-size: 14px;
    color: #343434;
    margin-bottom: 24px;
}

.Container{
    background: #FBFCFF;
    border-radius: 8px;
    padding: 24px;
    border: 0;
}

.wrapperPlan h4{
    font-weight: 700;
    font-size: 16px;
    color: #282828;
    margin-bottom: 16px;
    font-family: var(--primaryFontBold);
}
.CurrentPlan{
    background: #FFFFFF;
    border-radius: 8px;
    padding: 16px;
}
.titlePlan{
    display: flex;
    align-items: center;
    justify-content: space-between;
    align-content: center;
}
.titlePlan h3{
    margin-bottom: 0;
}
.planName{
    font-weight: 700;
    font-size: 24px;
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    font-family: var(--primaryFontBold);
    top: 3px;
    position: relative;
}
.planName svg{
    margin-right: 15px;
    position: relative;
    top: -3px;
}
.planName span{

}
.planPrice{
    font-family: var(--primaryFontBold);
    font-size: 24px;
    line-height: 32px;
    color: #5B3F91;
}
.CurrentPlan p{
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    color: #5E606A;
    margin-bottom: 0;
    margin-top: 38px;
}


.wrapperSubs h4{
    font-weight: 700;
    font-size: 16px;
    color: #282828;
    margin-bottom: 16px;
    font-family: var(--primaryFontBold);
}
.CurrentSubs{
    background: #F8F9FA;
    border-radius: 8px;
    padding: 16px;
    min-height: 125px;
}
.CurrentSubs p{
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    color: #5E606A;
    margin-bottom: 0;
    margin-top: 23px;
}
.subsCard{
    position: relative;
}
.subsCard input{
    height: 48px;
    background: #E7E7E7;
    border-radius: 4px !important;
    padding-left: 50px;
    border: none;
}
.subsCard input:focus{
    background: #E7E7E7;
    border: none;
}
.subsCard input::placeholder{
    font-weight: 600;
    font-size: 14px;
    color: #5E606A;
}
.subsCard svg{
    position: absolute;
    top: 12px;
    left: 12px;
}

.btnCardEdit {
    position: absolute;
    width: 30px;
    height: 30px;
    right: 10px;
    top: 11px;
    background: transparent;
    border: none;
}

.btnCardEdit:hover{
    background-color: var(--primaryColor);
    color: #FFFFFF;
}

.btnCardEdit:active,
.btnCardEdit:focus{
    background: transparent;
    border: none;
}

.btnCardEdit:not(:disabled):not(.disabled):active svg ,
.btnCardEdit:hover svg ,
.btnCardEdit:active svg {
    filter: invert(1) brightness(2);
}


.btnCardEdit svg{
    position: absolute;
    top: 7px;
    left: 7px;
}
.btnCancel{
    color: #DC3646;
    /* position: absolute; */
    right: 0;
    background: transparent;
    border: 1px solid #DC3646;
    border-radius: 60px;
    padding: 9px 20px;
    /* bottom: -50px; */
    /* bottom: 0; */
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 14px;
    transition: .3s;
}
.btnCancel:hover{
    background: #DC3646;
    color: #fff;
    transition: .3s;
    border: 1px solid #DC3646;
}

@media (max-width: 1366px) {
    .wrapperPlan{
        margin-bottom: 30px;
    }
    .btnCancel{
        position: inherit;
        width: 100%;
    }
    .wrapperSubs p{
        position: relative;
        /* bottom: 50px; */
    }
    .CurrentPlan{
        height: 165px;
        position: relative;
    }
    .CurrentPlan p{
        position: absolute;
        /* bottom: 20px; */
    }
}

@media (max-width: 575.95px) {
    .CurrentPlan{
        height: 120px;
    }
    .Container{
        padding: 0;
    }
    .wrapperPlan{
        margin-bottom: 30px;
    }
    .btnCancel{
        position: inherit;
        width: 100%;
    }
    .wrapperSubs p{
        position: relative;
        bottom: 0px;
    }
}