.semi-full-img-card{
    text-align: center;
    border-radius: 17px;
    -webkit-border-radius: 17px;
    -moz-border-radius: 17px;
    -ms-border-radius: 17px;
    -o-border-radius: 17px;
    min-height: 350px;
}

.semi-full-img-card .card-body{
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    color: #ffffff;
}

.semi-full-img-card .card-body .card-title{
    font-family: var(--themePrimaryFontBold);
    font-size: 1.125rem;
}

.semi-full-img-card .card-body .card-text{
    font-family: var(--themePrimaryFontLight);
    font-size: 0.875rem;
}

.semi-full-img-card .card-footer{
    background-color: var(--themePrimaryColor);
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
    border-top: none;
}
.semiCard_Accoridion .card-body {
    padding: 0;
    margin-top: -20px;
  }
  .semiCard_Accoridion.card{
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
  }

.semi-full-img-card .card-footer a{
    color: var(--themeTextColor);
    cursor: pointer;
}

.semi-full-img-card .card-footer .btn-outline-primary{
    color: var(--themeTextColor);
    border-color: var(--themeTextColor);
}
.semi-full-img-card .card-footer .btn-outline-primary:hover{
    color: var(--themeTextColor);
    border-color: var(--themeTextColor);
    background-color: transparent;
}

.semi-full-img-card .card-footer .btn-check:active+.btn-outline-primary, 
.semi-full-img-card .card-footer .btn-check:checked+.btn-outline-primary, 
.semi-full-img-card .card-footer .btn-outline-primary.active, 
.semi-full-img-card .card-footer .btn-outline-primary.dropdown-toggle.show, 
.semi-full-img-card .card-footer .btn-outline-primary:active {
    color: var(--themeTextColor);
    border-color: var(--themeTextColor);
    background-color: transparent;
}