.modalDialog {
    max-width: 550px;
}

.upgradeSuccessModal .modalContent {
    background-color: #3D0D8D;
    color: #ffffff;
    border-radius: 12px;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    -ms-border-radius: 12px;
    -o-border-radius: 12px;
}

.upgradeSuccessModal .modalHeader {
    position: relative;
    text-align: center;
    background-image: url("../../../../assets/images/Modal/confetti.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    border-bottom: none;
    height: 181px;
    justify-content: center;
}

.upgradeSuccessModal .modalHeader button {
    color: white;
    background-color: #ffffff;
    position: absolute;
    top: 24px;
    right: 22px;
    font-size: 7px;
}

.upgradeSuccessModal .modalBody {
    border-bottom: none;
}

.upgradeSuccessModal .modalHeader .title {
    font-family: var(--primaryFontBold);
    font-size: 2.625em;
    text-align: center;
}

.upgradeSuccessModal .contentBox {
    width: 67%;
    margin: 0 auto;
}

.upgradeSuccessModal .contentBox .heading {
    font-size: 1.5em;
    font-family: var(--primaryFontMedium);
    margin-bottom: 22px;
}

.upgradeSuccessModal .modalFooter {
    justify-content: center;
    border-top: none;
}

.upgradeSuccessModal .modalFooter .btnBox {
    margin-bottom: 44px;
}

.btnSolidWhite {
    background-color: #ffffff;
    color: var(--primaryColor);
    text-decoration: none;
    padding: 12px 33px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    min-width: 181px;
    margin-right: 22px;
    text-align: center;
}

.btnSolidWhite:hover {
    color: var(--primaryColor);
    cursor: pointer;
}

.btnoutlineWhite {
    background-color: transparent;
    color: #ffffff;
    border: 1px solid #ffffff;
    text-decoration: none;
    padding: 12px 33px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    min-width: 181px;
    text-align: center;
}

.btnoutlineWhite:hover {
    color: #ffffff;
    cursor: pointer;
}

.videoInfoContent {
    color: #ffffff;
}

@media (max-width: 575.98px) {
    .upgradeSuccessModal .modalHeader {
        position: relative;
        background-size: cover;
        height: 130px;
        justify-content: center;
        align-items: flex-end;
    }

    .upgradeSuccessModal .modalHeader .title {
        font-size: 1.375em;
    }

    .upgradeSuccessModal .contentBox{
        width: 76%;
    }

    .upgradeSuccessModal .contentBox .heading{
        font-size: 1.125em;
    }

    .upgradeSuccessModal .modalFooter .btnBox{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .upgradeSuccessModal .modalFooter .btnSolidWhite{
        margin-right: 0px;
        margin-bottom: 16px;
    }
}