.dashboard_sideabr_menu {
  position: fixed;
  width: inherit;
  height: 100vh;
  /* overflow: hidden; */
  /* makes the body non-scrollable (we will add scrolling to the sidebar and main content containers) */
  margin: 0px;
  /* removes default style */
  box-sizing: border-box;
  padding: 25px 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 5;
}

.lockIcon {
  position: absolute;
  right: 15px;
  width: 23px;
}

.currentPlan:global(.FreePlan) {
  background-color: var(--orange);
}

.currentPlan:global(.StudentPlan) {
  background: linear-gradient(289.79deg, #C833E1 23.59%, #9461FA 63.85%);
}

.currentPlan:global(.ProPlan) {
  background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
}

.currentPlan {
  color: #fff;
  margin-bottom: 0;
  top: 65px;
  padding: 5px 15px 5px 15px;
  border-radius: 20px;
  font-family: var(--primaryFontMedium);
  font-size: 16px;
  position: absolute;
  left: 12px;
  display: inline-table;
}

.dashboard_sideabr_menu .sidemenu_logo_box {
  padding-left: 6px;
  padding-right: 6px;
  margin-bottom: 48px;
}

.sidemenu_logo_box .logo {
  width: 114px;
  height: 100%;
  margin-bottom: 8px;
}

.dashboard_sideabr_menu .sidemenu_logo_box .planText {
  color: var(--orange);
  font-family: var(--primaryFont);
  font-size: 1em;
}

.sideMenuList {
  position: relative;
}

.sideMenuList .item {
  padding: 14px 8px;
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  font-size: 1em;
  color: var(--purple);
  font-family: var(--secondaryFont);
}

.sideMenuList .item.dropDownItem {
  padding: 15px 15px;
}

.sideMenuList .item a {
  padding: 0px;
  color: var(--purple);
}

.sideMenuList .item:hover a {
  color: #ffffff;
}

.sideMenuList .item a:hover {
  color: #ffffff;
}

.sideMenuList .item:active, .sideMenuList .item:hover, .sideMenuList .item.active {
  background-color: var(--purple) !important;
  color: #ffffff !important;
  border-radius: 8px;
  cursor: pointer;

}

.sideMenuList .item:hover>.iconBox, .sideMenuList .item.active>.iconBox {
  filter: brightness(0) invert(1);
}

.sideMenuList .item.active a {
  color: #ffffff;
}

.item.show a {
  background-color: var(--purple);
}

.sideMenuList .item .iconBox:hover {
  color: #ffffff !important;
  cursor: pointer;
}

/* .sideMenuList .item:hover {
  cursor: pointer;
  color: var(--purple);
} */

.sideMenuList .item .iconBox {
  width: 24px;
  margin-right: 8px;
  text-align: center;
}

.sideMenuList .item .logoutIconBox {
  width: 40px !important;
  margin-left: -10px !important;
}

.logoutLbl {
  position: relative;
  left: -4px;
}


.sidebarMenuBottom {
  padding: 0px 21px;
}

.sidebarMenuBottom .btnBox {
  margin-top: 20px;
}

.sidebarMenuUpgradeBox {
  text-align: center;
}

.sidebarMenuUpgradeBox .text {
  font-family: var(--primaryFontMedium);
  color: var(--purple);
  font-size: 0.875em;
}

.sidebarMenuUpgradeBox .emphasize {
  font-family: var(--primaryFontBold);
}

.sideMenuList .item:hover .countBox {
  background-color: #ffffff;
  color: var(--purple);
}

.countBox {
  background: var(--purple);
  border-radius: 50em;
  color: white;
  margin-left: 5px;
  font-size: 12px;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 2px;
}

.sideMenuList .menuDropDownItem {
  padding-left: 0;
  /* border-top: none;
   border-bottom: none; */
  border: none;

}

.sideMenuList .menuDropDownItem .item.collapsed {
  background-color: var(--primaryColor);
  color: #ffffff;
}

.sideMenuList .menuLabel {
  background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85% );
  color: #ffffff;
  font-family: var(--primaryFontBold);
  font-size: 11px;
  padding: 1px 6px;
  margin-left: 8px;
  border-radius: 50em;
  -webkit-border-radius: 50em;
  -moz-border-radius: 50em;
  -ms-border-radius: 50em;
  -o-border-radius: 50em;
  /* -webkit-animation: morph 5s ease infinite;
  -moz-animation: morph 5s ease infinite;
  animation: morph 5s ease infinite; */
}

@-webkit-keyframes morph {
  0% {
    background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85%,);
  }

  50% {
    background: linear-gradient(289.79deg,#C833E1 23.59%, #9461FA 63.85%);
  }

  100% {
    background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85%,);
  }
}

@-moz-keyframes morph {
  0% {
    background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85%,);
  }

  50% {
    background: linear-gradient(289.79deg,#C833E1 23.59%, #9461FA 63.85%);
  }

  100% {
    background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85%,);
  }
}

@keyframes morph {
  0% {
    background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85%,);
  }

  50% {
    background: linear-gradient(289.79deg,#C833E1 23.59%, #9461FA 63.85%);
  }

  100% {
    background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85%,);
  }
}

@media (max-width: 1366px) {
  .sideMenuList {}
}