

.roundBtn{
    background: #fff;
    color: #DC3646;
    border: 2px solid #DC3646;
}

.roundBtn:hover{
    background: #DC3646;
    color: #fff;
    border: 2px solid #DC3646;
}
.modalFooter{
    text-align: right;
}
.modalFooter button{
    margin-left: 20px;
}

.modalBody h3{
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #282828;
    font-family: var(--primaryFontBold);
    margin-bottom: 8px;
}
.modalBody p{
    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    color: #5E606A;
    font-family: var(--primaryFontMedium);
    margin-bottom: 30px;
}

.dFlexRow{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.dFlexRow p {
    margin-bottom: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #5E606A;
    font-family: var(--primaryFontBold);
}
.dFlexRow p input{
    margin-right: 10px;
}
.btn{
    background: #F58634;
    border-radius: 40px;
    border: none;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #FFFFFF;
    padding: 8px 16px;
}
.btn:hover{
    background: #f07e2d;
}
.infoIcon{
    position: absolute;
    border: none;
    padding: 0;
    width: 24px;
    height: 24px;
    background: #EAD0FF;
    border-radius: 4px;
    right: 24px;
    top: 24px;
}
.infoIcon:hover{
    background: #ddbdf7;
}
.infoIcon:focus{
    background: #EAD0FF !important;
    box-shadow: none !important;
}

.withdrawClass p:first-child{
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #343434;
    margin-bottom: 4px;
}
.withdrawClass p:last-child{
    margin-bottom: 0px;
}
.withdrawClass{
    margin-bottom: 30px;
}
.h3_heading{
    margin-bottom: 25px !important;
}

.cardWithdarw{
    width: 240px;
    height: 120px;
    background: #6772E5;
    border: 2px solid #F07C26;
    border-radius: 8px;
    padding: 16px;

}
.cardWithdarw svg{
    height: 33px;
    width: 65px;
}

.cardWithdarw h4{
    color: #fff;
    font-size: 14px;
    margin-bottom: 5px;
    margin-top: 12px;
}

.cardWithdarw p{
    color: #fff;
    margin-bottom: 0;
}
.bykeaCard{
    background: #00AA2F !important;
}
.hr_class{
    background: #BCBCBC;
    height: 2px;
    margin: 30px 0;
}
.amountDiv{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.amountDiv h3{
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: #5E606A;
}
.amountDiv p{
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    text-align: right;
    color: #343434;
    margin-bottom: 0;
}
.p_total_amount{
    font-size: 24px !important;
}