.themeCard {
    border-radius: 20px;
}

.themeCard:hover {
    transform: scale(1.02);
    transition: all 0.5s;
    cursor: pointer;
    box-shadow: 0px 5px 6px #ddd;
}

.themeCard .themeCardHeader {
    border-radius: 8px;
    overflow: hidden;
    border: 1px solid #000 !important;
    padding: 0 !important;
    height: 332px;
}

@keyframes ani_screen {
    50% {
        margin-top: -200%;
    }
}

@media ( min-width: 800px ) {
    .themeCard:hover .themeCardHeader .themeCardImage { 
        animation: ani_screen 10s infinite;
    }
}


:global(.info-section-box) .themeCard .themeCardHeader {
    height: 110px;
}

:global(.info-section-box) .themeCard .themeCardHeader .themeCardImage {
    height: auto;
}

:global(.themeCol) .themeCard .themeCardImage {
    height: auto !important;
    object-fit: cover !important;
}


.themeCard .btnTitle {
    min-width: 174px;
}

:global(.info-section-box) .themeCard .btnTitle {
    min-width: 100%;
    margin-top: 10px;
    font-size: 14px;
}

.themeCard .btnIcon:hover {
    background: transparent;
}


/* Signature Theme */
.themeCard:global(.Signature) {
    background-color: #F4EFFF;
    border-color: #F4EFFF;
}

.themeCard:global(.Signature) .themeCardHeader {
    border-color: #DDCCFF !important;
}

.themeCard:global(.Signature) .btnTitle {
    border-color: #8351E4;
    background-color: #8351E4;
    color: #ffffff;
}

.themeCard:global(.Signature) .btnIcon {
    border-color: #BF6DFF;
}

.themeCard:global(.Freelancer) .btnIcon img {
    filter: brightness(2) hue-rotate(10deg)
}

/* Freelancer Theme */
.themeCard:global(.Freelancer) {
    background-color: #D6EFFF;
    border-color: #D6EFFF;
}

.themeCard:global(.Freelancer) .themeCardHeader {
