.disconnect {
    background-color: #DC3646;
}
.badgeComingSoon{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    font-family: var(--primaryFontBold);
    font-size: 12px;
    color: #FFFFFF;
    padding: 7px 9px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}

.formGroup label{
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #343434;
    font-family: var(--primaryFontBold);
}
.formGroup input{
    background: #FFFFFF;
    border: 1px solid #E7E7E7;
    border-radius: 4px !important;
    height: 48px;
    font-size: 14px;
    line-height: 16px;
    color: #5E606A;
    font-weight: 600;
}
.formGroup input:focus{
    border: 1px solid #E7E7E7;
}
.formGroup input::placeholder{
    font-weight: 600;
    font-size: 14px;
    line-height: 16px;
    color: #5E606A;
}
.formGroup .Group{
    margin-bottom: 25px;
    position: relative;
}
.formGroup .Group .notify{
    color: #169F0A;
    font-size: 14px;
    font-weight: 600;
    margin-top: 10px;
    display: block;
    display: none;
}
/* .GroupActive input{
    border: 1px solid #169F0A;
} */
.GroupActive .notify{
    display: block !important;
}
.formGroupRight .Group{
    position: relative;
}
.SearchDomain{
    margin-bottom: 0 !important;
    position: relative;
}
.SearchDomain button{
    position: absolute;
    top: 30px;
    right: 0;
    border-radius: 0;
    height: 48px;
    width: 48px;
    background: transparent;
    border: none;
}
.SearchDomain button:hover{
    background: transparent;
}
.SearchDomain button:focus{
    background: transparent !important;
    box-shadow: none !important;
}
.SearchDomain button svg{
    width: 20px;
    height: 22px;
}
.formBtn{
    position: absolute;
    top: 30px;
    right: 0;
    height: 48px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border: none;
    width: 50px;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
}
.formGroupRight .Group input{
    padding-left: 40px;
}
.formGroup .icon_placeholder{
    position: absolute;
    bottom: 15px;
    left: 12px;
}
.searchTable{
    background: #FFFFFF;
    border: 1px solid #E7E7E7;
}
.searchTable tr td{

}
.btnBuy{
    background: transparent;
    border: none;
    padding: 0;
    color: #5B3F91;
    font-weight: 700 !important;
    position: relative;
    font-size: 16px;
}
.btnBuy:hover{
    background: transparent;
    color: #5B3F91;
}
.btnBuy:focus{
    background: transparent !important;
    box-shadow: none !important;
    color: #5B3F91;
}
.btnBuy svg{
    margin-left: 4px;
}
.badgeStatus{
    background: #D4F9D1 !important;
    border-radius: 4px;
    color: #169F0A;
    padding: 5px 12px;
    font-size: 14px;   
}
.priceDomain{
    color: #5E606A !important;
    font-size: 16px;
    font-weight: 600;
}
.nameDomain{
    color: #000;
    font-size: 16px;
    font-weight: 600;
}

.buttonDomain{
    text-align: right;
    margin-top: 50px;
    border-top: 1px solid #E7E7E7;
    padding-top: 40px;
}
.buttonDomain button{
    margin-left: 15px;
    background: #5B3F91;
    border: 1px solid #5B3F91;
    border-radius: 40px;
    padding: 8px 24px;
    font-size: 16px;
}
.buttonDomain .cancelBtn{
    background: transparent;
    color: #5B3F91;
    transition: 0.3s;
}
.buttonDomain .cancelBtn:hover{
    background: #5B3F91;
    color: #fff;
    transition: 0.3s;
}
.hintIcon{
    position: absolute;
    right: 13px;
    bottom: 14px;
    cursor: auto;
}
.hintIcon svg{
    width: 18px;
    height: 18px;
}
.tooltip .tooltip-arrow{
    display: none !important;
}
.infotext h1{
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #343434;
    font-family: var(--primaryFontBold);
}
.infotext h5{
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #5E606A;
    font-family: var(--primaryFontBold);
}
.infotext ul{

}
.infotext ul li{
    font-weight: 500;
    font-size: 14px;
    color: #343434;
    font-family: var(--primaryFontMedium);
    line-height: 25px;
}


.GroupActive .btnHover {
    display: none;
    position: absolute;
    top: 31px;
    height: 47px;
    border: 0;
    right: 0;
    width: 49px;
    border-radius: 0;
    background: #d00 !important;
}

.GroupActive:hover .btnHover,
.formBtn:hover + .btnHover {
    display: block;
}

.hover {   
  cursor: pointer;
}

@media (max-width: 575.95px) {
    .searchTable tr td{
        max-width: 80px;
        font-size: 14px;
        
    }
    .badgeStatus{
        width: 25px;
        height: 25px;
    }
    .badgeStatus svg{
        left: -7px;
        position: relative;
    }
    .btnBuy{
        font-size: 14px;
        padding-top: 3px;
    }
    .nameDomain{
        padding-top: 10px !important; 
    }
    .btnCol{
        text-align: right;
        padding-right: 20px !important;
    }
}