.RegisterPricingPlan{
    width: 465px;
    display: flex;
    margin-left: 60px;
}
.pricing_row{
    display: flex;
    justify-content: center;
    padding-bottom: 60px;
}

@media (max-width: 1024px) {
    .RegisterPricingPlan{
        width: 420px;
    }
    .RegisterPricingPlan:last-child{
        width: auto;
    }
}

@media (max-width: 767px) {
    .pricing_row{
        display: flex;
        flex-direction: column-reverse;
    }
    .RegisterPricingPlan{
        margin: auto;
        width: 100%;
        display: flex;
        justify-content: center;
    }
    .RegisterPricingPlan:last-child{
        width: 100%;
        margin-bottom: 120px;
    }
}

@media (max-width: 575.99px) {
    .hide_responsive_row{
        
    }
    .pricing_row{
        padding: 0 !important;
    }
    .RegisterPricingPlan{
        width: 330px !important;
    }
}

@media (max-width: 400px) {
    .pricing_row{
        display: flex;
        align-items: center;
    }
    .RegisterPricingPlan{
        width: 80% !important;
    }
}