.GamificationMenu {
    position: fixed;
    right: 30px;
    bottom: 110px;
    background-color: var(--bs-white);
    color: #000;
    padding: 20px;
    border-radius: 10px;
    z-index: 150;
    min-width: 380px;
    box-shadow: 0 0 4px 0px var(--midGray);
    transition: all 0.2s;
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    font-weight: 600;
    cursor: pointer;
}

.subTitle {
    display: flex;
    justify-content: unset;
}

.subTitleText {
    max-width: 280px;
    line-height: 18px;
    margin-bottom: 8px;
}

.stepDoneImage {
    max-width: 340px;
    margin-bottom: 15px;
    max-height: 200px;
    display: flex;
    align-items: center;
    overflow: hidden;
}

.stepDoneImage img {
    max-width: 100%;
    max-height: 100%;
}

.pullRight {
    margin-left: auto;
}

.pullLeft {
    margin-right: auto;
}

.ratings {
    display: flex;
    margin: 10px 0;
}

.isCompleted {
    opacity: 1;
    cursor: not-allowed;
}

.isCompleted .filler {
 width: 100% !important;
}
.isNotCompleted .iconCheck {
    opacity: 0.5;
}

.ratingBar {
    background-color: var(--purpleDisabled);
    height: 10px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    width: 100%;
    text-align: right;
    position: relative;
    border-radius: 10px;
}

@keyframes ani_filler {
    100% {
        width: 100%;
    }
}

.filler {
    content: "";
    position: absolute;
    background: var(--purple);
    height: 100%;
    width: 0px;
    left: 0px;
    transition: all 1s;
    border-radius: 10px;
    /* animation: ani_filler 1s forwards; */
}

.ratingBar ~ .ratingBar {
    margin-left: 10px;
}

.iconStar {
    position: relative;
    top: -6px;
    left: 6px;
    color: #F8AA54;
}

.stepsNavbar {
    margin-top: 24px;
}

.navItem {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.iconCheck {
    margin: 4px 10px 4px 0px;
    width: 22px;
    text-align: center;
    border-radius: 30px;
    height: 22px;
    line-height: 22px;
    background-color: #F58634;
    font-size: 10px;
    color: #fff;
    opacity: 1;
}

.iconCheck svg { margin-top: 6px; }

.iconRightArrow {
    margin-left: auto;
}

.iconArrowDown {
    padding-left: 8px;
    font-size: 20px;
    min-width: 20px;
}

.isNotActive {
    cursor: not-allowed;
}

.isNotActive .iconCheck {
    opacity: 0.8;
}

.viewAllSteps {
    width: 100%;
    margin-top: 4px;
    color: #5b3f91;
    font-weight: 800;
}

.viewAllSteps span {
    margin-right: 8px;
}

@media (max-width: 800px ) {
    .GamificationMenu {
        bottom: 150px;
        right: 15px;
        left: 15px;
    }

    .ratingBar {
        height: 5px;
    }

    .iconStar {
        top: -10px;
    }


    .GamificationMenu {
        min-width: calc( 100% - 30px );
    }
    
}