.switchCardConfirmModal .modalTitle{
    font-family: var(--primaryFontMedium);
    font-size: 24px;
    line-height: 26px;
    color: var(--primaryColor);
    margin-bottom: 22px;
    margin-top: 32px;
}

.switchCardConfirmModal .modalText{
    font-family: var(--primaryFont);
    font-size: 1em;
    line-height: 20px;
    color: #575757;
    margin-bottom: 32px;
    max-width: 83%;
    margin: 0 auto 32px;
}

.switchCardConfirmModal .modalContent{
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
    box-shadow: 0px 2px 6px rgba(31, 61, 84, 0.2);
    border: none;
}

.switchCardConfirmModal .modalHeader{
    border-bottom: none;
}

.switchCardConfirmModal .modalHeader .close{
    color: var(--primaryColor);
}

.switchCardConfirmModal .modalBody{
    text-align: center;
    padding: 6px 32px 44px 32px;
}

.switchCardConfirmModal .modalBody .btnBox{
    display: flex;
    justify-content: space-evenly;
}

.switchCardConfirmModal .modalBody .btnBox .btnAction{
    padding: 11px 16px;
    min-width: 177px;
    font-family: var(--primaryFontMedium);
}

@media (max-width: 575.98px) {
    .switchCardConfirmModal .modalBody{
        padding: 0px 32px 44px 32px;
    }
    .switchCardConfirmModal .modalText{
        max-width: 100%;
    }

    .switchCardConfirmModal .modalTitle{
        margin-top: 43px;
    }
    
    .switchCardConfirmModal .modalBody .btnBox{
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
    }

    .switchCardConfirmModal .modalBody .btnBox .btnAction{
        min-width: 191px;
        margin-bottom: 22px !important;
        width: fit-content;
    }
    .switchCardConfirmModal .modalBody .btnBox .btnAction:last-child{
        margin-bottom: 0px;
    }
}