/* influncer banner style */

.influencer.theme-header-section {
  background: linear-gradient(180deg, #13131300 0%, #131313 100%),
    url("../../../assets/images/section_variations/Banner/Influencer-banner.jpg");
  /* background-image: url("../../../assets/images/section_variations/Banner/Influencer-banner.jpg"); */
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  width: 100%;
  height: 672px;
}

.theme-header-section .card-edit-block {
  display: block;
  visibility: hidden;
}

.theme-header-section:hover .card-edit-block {
  display: block;
  visibility: visible;
}

.influencer .theme-header-banner-section {
  height: calc(100% - 56px);
}

.influencer .theme-header-banner-section {
  padding-bottom: 45px !important;
}

.influencer .theme-header-banner-box {
  height: calc(100% - 50px);
}

.influencer .theme-header-banner-box .row {
  height: 100%;
}

.influencer .user-detail-box {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  color: #ffffff;
  text-align: center;
}

.influencer .user-detail-box .social-link-box{
  justify-content: center;
}

.influencer
  .theme-header-banner-section
  .theme-header-banner-box
  .social-link-box
  .social-links
  .social-links-list {
  border-radius: 50%;
  background-color: #ffffff;
  width: 40px;
  height: 40px;
  text-align: center;
}
.influencer
  .theme-header-banner-section
  .theme-header-banner-box
  .social-link-box
  .social-links
  .social-links-list
  .social-icon {
  position: relative;
  bottom: -10px;
  margin-right: 0rem !important;
}
/* .influencer .theme-header-banner-section .theme-header-banner-box .social-link-box .social-links{
    display: flex;
    flex-direction: row;
} */

.influencer
  .theme-header-banner-section
  .theme-header-banner-box
  .social-link-box
  .social-links
  .social-links-list
  .link {
  display: flex;
  justify-content: center;
  align-items: center;
}

.influencer
  .theme-header-banner-section
  .theme-header-banner-box
  .social-link-box
  .social-links
  .social-links-list
  .social-icon {
  display: flex;
  justify-content: center;
  align-items: center;
}

