@media (max-width: 575.98px) {
  .influencer.theme-header-section {
    height: 260px;
  }

  .theme-header-section .card-edit-block {
    right: 43px !important;
    top: 16px;
  }
  .influencer .theme-header-banner-section {
    width: 100%;
    margin-top: -40px;
  }

  .influencer .theme-header-banner-box {
    height: calc(100% - -36px);
  }

  .influencer .theme-header-banner-section {
    padding-bottom: 0px !important;
  }
  .influencer .user-detail-box {
    background-color: white;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    color: black;
  }
}
