.bg-trans {
    background-color: transparent;
}

.register-user-info-interest{
    position: relative;
    background-color: #FBFCFF;
    width: 100%;
    height: 100%;
    padding-top: 2em;
    padding-bottom: 10em;
    min-height: 100vh;
    overflow-y: clip;
}

.register-user-info-interest .section-header .img-box {
    height: 80px;
}

.register-user-info-interest .section-header .img-box img{
    width: 166px;
    height: auto;
}

.register-user-info-interest .section-header .progress-box{
    display: flex;
    justify-content: center;
    padding-top: 1.5em;
    padding-bottom: 2em;
}

.register-user-info-interest .progress-box .icon-box{
    color: #E2E2E2;
    padding-left: 2em;
    padding-right: 2em;
    font-size: 1.25em;
}

.register-user-info-interest .progress-box .icon-box.active{
    color: #F8AA54;
}

.register-user-info-interest .user-interest-card{
    border: none;
    max-width: 780px;
    margin: 0 auto;
}

.register-user-info-interest .user-interest-card .card-body{
    padding: 25px;
}

.register-user-info-interest .user-interest-card .selectable-btn-wrapper > .btn{
    padding: 12px, 64px, 12px, 64px;
    font-size: 21px;
    line-height: 26px;
}


.register-user-info-interest .user-interest-card .selectable-section .selectable-btn-wrapper > .btn {
    color: #777;
    border-color: #777;
    font-size: 16px;
}

.register-user-info-interest .user-interest-card .selectable-section .selectable-btn-wrapper > .btn.active {
    background: #F8AA54;
    border-color: #F8AA54;
    color: #fff;
}

.register-user-info-interest .user-interest-card .select-role-section .selectable-btn-wrapper > .btn {
    border-radius: 0;
    border: 0;
    border-bottom: 2px solid transparent;
    padding: 0;
    color: var(--gray);
    background-color: transparent;
    font-size: 18px;
    font-weight: 600;
}

.register-user-info-interest .user-interest-card .selectable-btn-wrapper > .btn {
    color: #777;
    border-color: #777;
    font-size: 16px;
}

.register-user-info-interest .user-interest-card .select-role-section .selectable-btn-wrapper > .btn[class*="active"] {
    border-bottom-color: var(--primaryColor);
    color: var(--primaryColor);
}

.register-user-info-interest .main-title{
    font-family: var(--secondaryFontMedium);
    color: var(--primaryColor);
    font-size: 2.625em;
    line-height: 53px;
}

.register-user-info-interest .sub-title{
    font-family: var(--secondaryFont);
    color: var(--black);
    font-size: 20px;
    line-height: 20px;
}

.select-up-to-section {
    padding-inline: 13px;
}

.user-info-interest-section{
    padding-bottom: 0em;
}

.user-info-interest-section .action-btn-box{
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: right;
    padding-inline: 20px;
}

.user-info-interest-section .action-btn-box .btn{
    /* min-width: 317px;
    font-size: 16px; */
    font-family: var(--secondaryFont);
    padding-block: 10px;
}

.register-data-category .title-box{
    font-family: var(--secondaryFont);
    color: var(--primaryColor);
}

.register-data-category .title-box .title{
    color: var(--primaryColor);
    font-size: 2em;

}

.register-data-category .title-box .counter-text{
    font-size: 1.5625em;
    padding-top: 6px;
}


.signupStep2Bg1 {
    padding: 0;
    background-image: url("../../assets/images/signup/signupStep2BG1.png");
    background-position: top right;
    background-repeat: no-repeat;
}

.signupStep2Bg2 {
    padding: 0;
    background-image: url("../../assets/images/signup/signupStep2BG2.png");
    background-position: bottom left;
    background-repeat: no-repeat;
}

.register_form_section {
    max-width: 100%;
}

.user-interest-card .card-img-loader {
    text-align: center;
}

.user-interest-card .card-img-loader .loader-img {
    height: 100px;
    width: 100px;
}

.selectable-section .selectable-btn-wrapper {
    margin: 0.8em 0 0 0.8em  !important;
}

.selected-tags-section {
    margin-top: 10px;
    padding-left: 12px;
    margin-bottom: 10px;
}

.selected-tags-section .btn {
    margin-right: 12px;
    font-size: 15px;
    min-width: 86px;
    padding: 2px 22px;
    margin-bottom: 15px;
    position: relative;
}
.closeicon_tag svg {
    width: 8px;
    height: 8px;
    position: relative;
    top: -3px;
}

.closeicon_tag {
    position: absolute;
    background: #fff;
    width: 20px;
    height: 20px;
    right: -6px;
    top: -10px;
    border-radius: 30px;
    box-shadow: 0px 0px 13px #00000033;
    border: 1px solid #8c38af;
    transition: .3s;
}

.closeicon_tag:hover {
    background: #f8aa54;
}


/* Mobile devices small screen */
@media (max-width: 380px) {
    .register-data-category .title-box .counter-text{
        padding-top: 0px;
    }

}

@media (max-width: 575.98px) {

    .register-user-info-interest .main-title{
        font-size: 2em;
    }
    .register-user-info-interest .progress-box .icon-box{
        padding-left: 1em;
        padding-right: 1em;
    }

    .register-data-category .title-box {
        text-align: left !important;
    }


    .register-data-category .title-box .counter-text{
        font-size: 0.9em;
        padding-top: 3px;
    }

    .register-user-info-interest .user-interest-card .selectable-btn-wrapper > .btn {
        font-size: 1em;
        line-height: 18px;
    }

    .register-user-info-interest .user-interest-card .selectable-btn-wrapper{
        margin: 4px !important;
    }

.user-info-interest-section .action-btn-box .btn{
    max-width: 200px;
    min-width: fit-content;
    width: 100%;
    font-size: 18px;
}
.user-info-interest-section .action-btn-box{
    padding-bottom: 0;
    padding-top: 35px;
}

}

/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
    .register-data-category .title-box .counter-text{
        font-size: 1.15em;
        padding-top: 3px;
    }
}

/* Extra large devices (large desktops, 1200px and up) */

@media (min-width: 1200px) and (max-width: 1399.98px) {

}

@media (min-width: 1400px) and (max-width: 1599.98px) {
   .register-data-category .title-box .title {
        font-size: 1.5em;
    }
.register-data-category .title-box .counter-text{
        font-size: 1.2625em;
}
/* .register-user-info-interest .user-interest-card .selectable-btn-wrapper{
    margin: 0.8em !important;
} */

}

@media (min-width: 1600px) and (max-width: 1799.98px) {

}



@media (max-width: 896px ) {

    .selected-tags-section {
        padding-left: 2px;
    }

    .selected-tags-section .btn {
        font-size: 14px;
    }

    .register_form_section,
    .signupStep2Bg2 {
        background-size: 180px;
    }

    .signupStep2Bg1 {
        background-size: 84px;
    }


    .register-user-info-interest .user-interest-card .select-role-section .selectable-btn-wrapper > .btn {
        margin-top: 0px;
        margin-bottom: 0px;
        margin-block: 4px;
    }

    .register-user-info-interest .user-interest-card .selectable-btn-wrapper > .btn {
        margin-bottom: 10px;
        line-height: 20px;
    }

    .user-info-interest-section .register-data-category {
        margin-bottom: 0px !important;
    }


    .user-info-interest-section .action-btn-box {
        padding-top: 30px;
    }

    .user-info-interest-section .action-btn-box .btn {
        padding: 10px;
    }

    .register-data-category .title-box .title {
        font-size: 1.3rem !important;
        padding-left: 0px;
    }

    .register-user-info-interest .user-interest-card {
        min-height: 55vh;
    }

    .register-user-info-interest .sub-title {
        margin: 24px 0 10px;
        font-weight: 600;
        font-size: 18px;
    }

}


@media (max-width: 480px ) {

    .register_form_section, .signupStep2Bg2 {
        background-size: 110px;
    }

    .select-up-to-section {
        padding-inline: 4px !important;
    }

    .user-info-interest-section {
        padding-bottom: 60px;
    }

    .register-data-category .title-box {
        padding-left: 4px !important;
    }
}


@media (max-width: 400px ) {
    .user-info-interest-section .action-btn-box {
        margin-bottom: 30px;
        padding-inline: 30px;
    }
}