

.card{
    padding: 2px;
    border-radius: 8px;
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
}
.card h3{
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #282828;
    font-family: var(--primaryFontBold);
    margin-bottom: 8px;
}
.innerCard{
    background: #FBFCFF;
    border-radius: 6px;
    padding: 24px;
    min-height: 156px;
}
.dFlexRow{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 40px;
}
.dFlexRow p {
    margin-bottom: 0;
    font-family: var(--primaryFontBold);
    color: #5B3F91;
    font-weight: 700;
    font-size: 40px;
    line-height: 48px;
}
.dFlexRow p span {
    font-size: 18px;
    line-height: 24px;
    color: #858585;
}
.btn{
    background: #E7E7E7;
    border-radius: 40px;
    border: none;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #fff;
    padding: 8px 16px;
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
}
.btn:hover{
    background: linear-gradient(289.79deg, #f39931 23.59%, #8552ec 63.85%);
    color: #fff;
}

@media (max-width: 575.98px) {
    .card{
        margin-bottom: 20px;
    }
    .dFlexRow{
        display: block;
        margin-top: 20px;
    }
    .btn{
        margin-top: 15px;
        width: 100%;
    }
}