.linkBtn {
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    text-decoration: none;
    font-family: var(--primaryFontBold);
    font-size: 16px;
}
.linkBtn .icon{
    margin-right: 9px;
}

.linkSuccessBtn{
    font-family: var(--primaryFontBold);
    font-size: 16px;
    color: #169F0A;
    text-decoration: none;
}
.checkPromo{
    background: #fff;
    border: none;
    color: #169F0A;
    font-size: 14px;
    font-weight: 600;
}
.checkPromo svg{ 
    width: 19px;
    height: 20px;
}
.checkPromo:hover{
    background: #fff;
    border: none;
    color: #169F0A;
}
.checkPromo:focus{
    background: #fff !important;
    box-shadow: none !important;
    border: none !important;
    color: #169F0A !important;
}