}
.btn-google {
  max-height: 34px;
  font-size: 0.9em;
  overflow: hidden;
}
.btn-google span {
  width: 100%;
  font-weight: 800;
}
.btn-success {
  background-color: white!important;
  color: black !important;
  border: 1px solid #ced4da;
  border-color: #ced4da!important;
}
 .show>.btn-success.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba(108, 60, 153, 0.5) !important
}

.register_form_section .auth-card {
  z-index: 9;
  position: relative;
}
.register_form_section .auth_titles,
.register_form_section .main-title {
  z-index: 9;
