}
.register-card-form .register-input-gp{
  max-height: 55px;
}

.register-card-form .register-submit-btn {
  height: 50px;
  font-size: 1em;
}

.register_animate_section {
  position: absolute;
  overflow-y: hidden;
  overflow-x: hidden;
  top: 56px;
  height: -webkit-fill-available;
  width: 100%;
  min-width: 100vw;
}
.register_animate_section .stars {
  position: absolute;
  z-index: 2;
  top: 64px;
  width: 80%;
  object-fit: contain;
}
.register_animate_section .sun {
  position: absolute;
  z-index: 4;
  right: 260px;
  top: 190px;
  width: 20%;
  -webkit-transition: 3s ease-in-out;
  -moz-transition: 3s ease-in-out;
  -o-transition: 3s ease-in-out;
  transition: 3s ease-in-out;
  transition: 3s ease-in-out;
}
.ready .register_animate_section .sun {
  -webkit-transform: translate(0, -8em);
  -moz-transform: translate(0, -8em);
  -o-transform: translate(0, -8em);
  -ms-transform: translate(0, -8em);
  transform: translate(0, -8em);
}
.register_animate_section .rocket {
  position: absolute;
  z-index: 2;
  right: 96px;
  top: -20px;
  width: 70%;
  -webkit-transition: 5s ease-in-out;
  -moz-transition: 5s ease-in-out;
  -o-transition: 5s ease-in-out;
  transition: 5s ease-in-out;
  transition: 5s ease-in-out;
}
.ready .register_animate_section .rocket {
  -webkit-transform: translate(30em, -30em);
  -moz-transform: translate(30em, -30em);
  -o-transform: translate(30em, -30em);
  -ms-transform: translate(30em, -30em);
  transform: translate(30em, -30em);
}
.register_animate_section .bottom {
  position: absolute;
  z-index: 2;
  left: 0px;
  bottom: -190px;
  width: 100%;
  -webkit-transition: 2s ease-in-out;
  -moz-transition: 2s ease-in-out;
  -o-transition: 2s ease-in-out;
  transition: 2s ease-in-out;
  transition: 2s ease-in-out;
}

.register_bottom_animation{
  position: relative;
}
.register_bottom_animation .bottom{
  position: absolute;
  bottom: -60px;
  height: 491px;

  width: 1500px;
  z-index: 2;
  -webkit-transition: 2s ease-in-out;
  -moz-transition: 2s ease-in-out;
  -o-transition: 2s ease-in-out;
  transition: 2s ease-in-out;
  transition: 2s ease-in-out;
}
.ready .register_bottom_animation .bottom {
  -webkit-transform: translate(0, 5em);
  -moz-transform: translate(0, 5em);
  -o-transform: translate(0, 5em);
  -ms-transform: translate(0, 5em);
  transform: translate(0, 5em);
}

.register-submit-btn{
