

.Categories{
   
}
.Categories .categoryBtn {
    font-family: var(--primaryFontMedium);
    box-shadow: 0px 4px 40px rgb(149 97 250 / 10%);
    border-radius: 40px;
    font-size: 16px;
    padding: 8px 12px;
    margin-right: 15px;
    background: transparent;
    color: #71737C;
    border: 2px solid #71737C;
    transition: .3s;
}
.Categories .categoryBtn:hover {
    background: #9772EE;
    color: #fff;
    border: 2px solid #9772EE;
}

.Categories .categoryBtn:focus {
    background: linear-gradient(289.79deg, #9772EE 23.59%, #9461FA 63.85%);
    border: 2px solid #9772EE ;
    box-shadow: none !important;
}
.btnActive {
    background: linear-gradient(289.79deg, #9772EE 23.59%, #9461FA 63.85%) !important;
    color: #fff !important;
    border: 2px solid #9772EE !important;
}
.btnDeActive {
    background: transparent !important;
    color: #71737C !important;
    border: 2px solid #71737C !important;
}
.categoryList {
    margin-bottom: 25px;
    border-bottom: 1px solid #E7E7E7;
    padding-bottom: 25px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.btnApplied {
    background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    border: 2px solid #FF7439;
    border-radius: 50px;
    padding: 6px 23px 4px;
    font-family: var(--primaryFontBold);
    font-size: 19px;
    transform: .3s;
}

.applyClick {
    background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85%);
    color: #fff;
    -webkit-text-fill-color: inherit; 
}
.applyNotClick {
    background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    border: 2px solid #FF7439;
}
.btnApplied:hover {
    border: 2px solid #FF7439;
}
.btnApplied:focus {
    border: 2px solid #FF7439;
}

@media (max-width: 575.98px) { 
    .categoryBtn {
        padding: 6px 10px !important;
        font-size: 12px !important;
        margin-right: 8px !important;
        margin-bottom: 8px;
    }
    .btnApplied {
        background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85%) !important;
        -webkit-background-clip: text !important;
        -webkit-text-fill-color: transparent !important;
        background-clip: text !important;
        text-fill-color: transparent !important;
        border: 2px solid #FF7439 !important;
        border-radius: 50px;
        padding: 6px 10px !important;
        font-size: 12px !important;
        margin-top: -8px !important;
        transform: .3s;
    }
    .applyClick {
        background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85%) !important;
        color: #fff !important;
        -webkit-text-fill-color: inherit !important; 
        border: 2px solid #FF7439 !important;
    }
    
}