.loginGoogle{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    border-radius: 60px;
    margin-bottom: 15px;
    padding: 1px;
}
.submitBtnGoogle{
    font-family: var(--primaryFontBold) !important;
    background: #fff;
    font-size: 16px !important;
    max-width: 100%;
    width: 100%;
    height: 44px;
    color: #5E606A !important;
    padding-top: 7px !important;
    border: 0 !important;
    transition: 0s;
    border-radius: 60px !important;
    justify-content: center;
}
.submitBtnGoogle svg{
    margin-right: 8px;
    position: relative;
    top: -2px;
}
.submitBtnGoogle div{
    padding: 0 !important;
}
.submitBtnGoogle:hover{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    color: #fff !important;
    transition: 0s;
}
.submitBtnGoogle:focus{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    color: #fff !important;
    transition: 0s;
}
.submitBtnGoogle:focus svg path{
    fill: #fff;
}
.submitBtnGoogle:hover svg path{
    fill: #fff;
}