.editable-content-box {
  position: relative;
  word-break: break-word;
}

.editable-content-box *[contenteditable="true"]:focus+.character-limit {
  display: inline-block;
}

.character-limit {
  display: none;
  background-color: var(--primaryColor);
  color: #ffffff;
  position: absolute;
  top: -20px;
  left: 0px;
  min-width: 170px;
  z-index:10;
}

.character-limit .limit-text {
  font-family: var(--primaryFont);
  margin-bottom: 0;
  min-width: 150px;
  font-size: 12px;
  padding-right: 10px;
  padding-left: 10px;
}