    --bs-bg-opacity: 1;
    background-color: rgba(var(--bs-light-rgb), var(--bs-bg-opacity)) !important;
    position: relative;
    display: block;
    padding: 50px;
    overflow: hidden;
    height: 100%;
    min-height: 100vh;
}

