.style_1.theme-videos-section .content-container {
    box-shadow: 0px 5px 20px #00000029;
    border-radius: 30px;
}

.style_2.theme-videos-section .content-container {
    border: none;
    box-shadow: none;
}

.video-player-box {
    background-color: var(--themePrimaryColor);
    color: var(--themeTextColor);
    /* border-radius: 0px 17px 17px 0px; */
}

.video-player-box .theme-video-player {
    height: 100%;
    padding: 1px 0px 1px 0px;
}

.theme-video-player .user-video {
    width: 100% !important;
    height: 100% !important;
}

.influencer-thumbnail-section {
    background-color: var(--themePrimaryColor) -80%;
    border-radius: 0px 17px 17px 0px;
    filter: brightness(85%);

}

.influencer-thumbnail-section .influencer-video-thumbnail {
    padding-right: 0px;
    /* padding: 12px 10px 20px 12px; */
    height: calc( 100% - 1rem );
    /* padding-left: 5px;
    padding-top: 3px;
    padding-bottom: 3px; */
}

.video-thumbnail-sec {
    border-width: 0px;
    border-color: transparent;
}

.video-thumbnail-sec .video-thumbnail-img {
    width: 168px;
    height: 94px;
    object-fit: cover;
}

.video-thumbnail-sec .video-thumbail-desc {
    justify-content: center;
    align-items: center;
    text-align: left;
}

.video-thumbnail-sec .video-thumbail-desc .video-thumbnail-title {
    font-size: 1em;
    font-family: var(--primaryFontBold);
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    /* number of lines to show */
    line-clamp: 2;
    -webkit-box-orient: vertical;
    align-self: flex-start;
}

.video-thumbnail-sec .video-thumbail-desc .video-thumbnail-info {
    font-size: 12px !important;
    font-family: var(--primaryFont);
    word-break: break-word;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    /* number of lines to show */
    line-clamp: 3;
    -webkit-box-orient: vertical;
}

.video-add-modal .modal-header {
    border-bottom: none;
    padding-bottom: 0;
}

.video-add-modal .modal-header .btn-close {
    font-size: 0.8em;
}

.video-add-modal .modal-body .modal-title {
    font-family: var(--primaryFontMedium);
    font-size: 1em;
    text-align: center;
    margin-bottom: 1em;
    font-weight: 600;
}

.video-add-modal .modal-body .video-icon-sec {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-around;
    margin-bottom: 1em;
}

.video-add-modal .modal-body .video-icon-sec .icon {
    width: 50px;
    height: 50px;
}

.theme-video-box .slick-slider-thumbnail-parent:hover .card-edit-block{
    display: block;
    right: 45px;
    font-size: 12px;
    top: 25px;
}

.theme-video-box .slick-slider-thumbnail-parent:hover .card-edit-block .edit-option{
   width: 20px;
   height: 20px;
}

#videos .video-thumbnail-sec .card-edit-block{
    top: 9px;
    right: 4px;
}

#videos .video-thumbnail-sec:hover .card-edit-block {
    visibility: visible;
}
/* video variant two style_3 */

.theme-video-player.style_3.videos .user-video{
    height: 481px !important;
}

.theme-video-player.style_3.videos .user-video iframe{
    border-radius: 18px;
    -webkit-border-radius: 18px;
    -moz-border-radius: 18px;
    -ms-border-radius: 18px;
    -o-border-radius: 18px;
}
#videos.style_3 .slick-slide{
    display: flex;
    max-width: 230px;
}
#videos.style_3 .slick-list{
    width: 96%;
}
#videos.style_3 .theme-video-thumbnail .slider-arrow{
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(-10px, -165%);
    cursor: pointer;
}


@media (max-width: 575.95px) {
    #videos.style_1 .card-edit-block{
        right: inherit !important;
    }
    #videos.style_1 .edit-option {
        left: 45px;
    }
    #videos.style_3 .card-edit-block{
        right: inherit !important;
    }
    #videos.style_3 .edit-option {
        position: absolute;
        left: 40px;
        top: -10px;
    }
}

@media (max-width: 380px) {
    .mobile-responsive-video {
        width: 320px !important;
        height: 200px !important;
    }

    .mobile-responsive-thumbnail {
        width: 68px !important;
        height: 45px !important;
    }
}

.slick-prev {
    display: none !important;
}

.video-slider-player-box {
    /* background-color: var(--themePrimaryColor); */
    color: var(--themeTextColor);
}

/* .slider-arrow{
    position: absolute;
    right: 0;
    top: 57px;
} */
.video-slider {
    width: 185px !important;
    height: 94px !important;
    object-fit: cover !important;
}

/* .influencer-video-thumbnail .slick-slider{
display: flex;
flex-direction: row;
} */


.video-thumbnail-sec .video-thumbail-desc .video-thumbnail-slider-title {
    font-size: 18px;
    font-weight: 400px;
    font-family: var(--themePrimaryFont);
    color: var(--themeTextColor);
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    /* number of lines to show */
    line-clamp: 2;
    -webkit-box-orient: vertical;
    text-align: center;
    /* align-self: flex-start; */
}

/* .slick-next{
    right: 10px !important;
} */

#videos.style_3 .slick-slider .video-slider {
    width: 200px !important;
    height: 117px !important;
}

#videos.style_3 .video-thumbnail-sec .video-thumbail-desc {
    width: 200px !important;
}

#videos.style_3 .influencer-video-thumbnail .slider-arrow {
    position: absolute;
    top: 50px;
    top: 50px;
    right: -5%;
    cursor: pointer;
}

#videos.style_3 .influencer-video-thumbnail .slider-arrow svg {
    width: 19.25px;
    height: 23px;
}
#videos.style_3 .video-thumbnail-sec:hover .card-edit-block{
    display: block;
}
