.pro_user{
    width: 56px;
    height: 32px;
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    border-radius: 20px;
    margin: 0;
    font-size: 16px;
    line-height: 32px;
    font-family: var(--primaryFontBold);
    text-align: center;
    color: #fff;
}

@media (max-width: 575.98px) {
    .socialLinkBox {
        justify-content: center;
    }
    .badgeDiv{
        position: absolute;
        top: -15px;
        width: 100%;
    }
    .badgeDiv h2{
        margin: auto;
        color: #fff !important;
    }
    .isInfluencerBadge{
        position: inherit;
    }
    .isInfluencerBadge h2{
        font-size: inherit !important;
        color: #fff;
    }
}