.wrapper{
margin-top: 190px;
margin-bottom: 120px;
}
.main_div{
    background: #FBFCFF;
    border-radius: 35.2188px;
    padding: 50px;
}
.imgBanner{
    text-align: center;
}
.imgBanner img{
    height: 350px;
}
.bannerAds_btn{
    width: 211px;
    height: 48px;
    background: #5B3F91;
    border: 1px solid #5B3F91;
    border-radius: 25px;
    color: #fff;
    margin-top: 70px;
    font-weight: bold;
    line-height: 34px;
    transition: .3s;
}
.bannerAds_btn:hover{
    background: #6c50a4;
    transition: .3s;
}
.FlexDiv{
    align-items: center;
}
.bannerDecs p{
    width: 68%;
    font-size: 1.75rem;
    line-height: 36px;
}
.bannerDecs h4{
    color: #5b3f91;
    font-weight: bold;
    font-size: 21px;
}

@media (max-width: 1024px) {
    .wrapper{
        margin-top: 100px;
        margin-bottom: 120px;
    }
    .imgBanner img{
        width: 100%;
        height: auto;
    }
    .bannerDecs p{
        width: 100%;
    }
    .bannerAds_btn{
        margin-top: 35px;
    }
}

@media (max-width: 991px) {
    .bannerDecs p{
        font-size: 20px;
        line-height: 30px;
    }
    .bannerAds_btn{
        margin-top: 35px;
    }
    .bannerDecs h4{
        font-size: 16px;
    }
    .bannerAds_btn{
        height: 40px;
        width: 190px;
        font-size: 14px;
        margin-top: 15px;
    }
}

@media (max-width: 767px) {
    .imgBanner img{
        height: 290px;
        width: 275px;
    }
    .bannerDecs{
        text-align: center;
        margin-top: 30px;
    }
}
@media (max-width: 400px) {
    .imgBanner img{
        width: 100%;
        height: inherit;
    }
    .main_div{
        padding: 30px;
    }
}