.freelancer-stats-box{
    border-radius:  50px;
}
.freelancer-stats-box .section-tag {
    font-family: var(--themePrimaryFont);
    font-size: 2em;
    margin-bottom: 2.25rem;
    color: var(--themePrimaryColor);
    font-weight: 600;
}

.card-heading {
    font-family: var(--themePrimaryFont);
}

/* Theme stats highlight  */

.theme-highlights-section .stats-card{
    background-color: var(--themePrimaryColor);
    color: var(--themeTextColor);
    flex: 1 0 auto;
    height: 100%;
    border: none;
    justify-content: center;
    align-items: center;
    padding-top: 2em;
    padding-bottom: 2em;
    border-radius: 17px;
    -webkit-border-radius: 17px;
    -moz-border-radius: 17px;
    -ms-border-radius: 17px;
    -o-border-radius: 17px;
}
.theme-highlights-section .stats-card .guide-msg{
    display: none;
    position: absolute;
    background-color: var(--primaryColor);
    font-family: var(--primaryFont);
    color: #ffffff;
    font-size: 12px;
    text-align: left;
    top: 0px;
    right: 0px;
    padding: 2px 5px;
    border-top-right-radius: inherit;
    border-bottom-left-radius: inherit;
}

.theme-highlights-section .stats-card:hover .guide-msg{
    display: block;
    cursor: pointer;
}

.stats-box .editable-content-box{
    z-index: 1;
}
#highlights.style_3 .stats-wrapper .character-limit {
    position: absolute;
    top: -20px;
}

