.btnActionBox .btnAction {
    padding-left: 1.5em;
    padding-right: 1.5em;
}

.controlBar {
    background-color: #5B3F91;
    width: auto;
    border-radius: 8px;
    height: 48px;
    display: flex;
    align-items: center;
    position: relative;
    top: -3px;
}

.svgIcons {
    width: 30px;
    height: 30px;
    display: inline-block;
    text-align: center;
    transition: .3s;
    margin-left: 15px;
    margin-right: 15px;
}

.svgIcons:hover {
    cursor: pointer;
    transform: translate(-1px, -3px);
    transition: .3s;
}

.seperator {
    margin-left: 8px;
}

@media (min-width: 991.98px) and (max-width: 1199.98px) {
    .controlBar {
        height: 38px;

    }

    .sectionHead .btnActionBox .btnAction {
        padding-left: 11px;
        padding-right: 11px;
        font-size: 14px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
    }

    .svgIcons {
        width: 20px;
        height: 20px;
        margin-left: 10px;
        margin-right: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .svgIcons svg {
        width: 16px;
        height: 16px;
    }
}

@media (min-width: 1200px) and (max-width: 1399.98px) {
    .controlBar {
        height: 38px;

    }

    .sectionHead .btnActionBox .btnAction {
        padding-left: 11px;
        padding-right: 11px;
        font-size: 14px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
    }

    .svgIcons {
        width: 20px;
        height: 20px;
        margin-left: 10px;
        margin-right: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .svgIcons svg {
        width: 16px;
        height: 16px;
    }
}

@media (min-width: 1400px) and (max-width: 1599.98px) {
    .controlBar {
        height: 45px;

    }

    .svgIcons {
        width: 26px;
        height: 26px;
    }

    .svgIcons svg {
        width: 22px;
        height: 22px;
    }
}

@media (max-width: 575.98px) {
    .controlBar {
        height: 30px;
        position: absolute;
        top: -47px;
        right: 0;
        width: max-content;
        display: flex;
        align-items: center;
    }

    .sectionHead .btnActionBox .btnAction {
        padding-left: 11px;
        padding-right: 11px;
        font-size: 14px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
    }

    .sectionHead .btnActionBox {
        display: flex;
    }

    .addBtn_style {
        background: transparent !important;
        border-color: var(--themeTextColorSecondary);
        color: var(--themeTextColorSecondary) !important;
    }

    .svgIcons {
        width: 20px;
        height: 20px;
        margin-left: 7px;
        margin-right: 7px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .svgIcons svg {
        width: 14px;
        height: 14px;
    }
}