.faqPricing .accordion-header button {
    color: #000 !important;
    font-size: 21px;
    background: #fff !important;
    border-bottom: 1px solid #5E606A;
}
.faqPricing .accordion-header button:focus{
    box-shadow: none;
}
.faqPricing .accordion-body {
    font-size: 18px;
}

@media (max-width: 991px) {
    .faqPricing .accordion-header button{
        font-size: 17px;
    }
    .faqPricing .accordion-body {
        font-size: 15px;
    }
}