
.heading{
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    font-family: var(--primaryFontBold);
    color: #282828;
    margin-top: 40px;
    margin-bottom: -18px;
    text-transform: capitalize;
}
.container{
    padding: 24px !important; 
    background: #FBFCFF;
    border-radius: 8px;
    margin-bottom: 20px;
    border: none;
}
.payment_method_invoice{
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    color: #5E606A;
}
.cardBody{
    padding: 0;
}
.tr_heading{
    background: #F8F9FA;
    border-radius: 4px;
}
.tr_heading th{
    padding: 15px 8px;
    color: #5E606A;
    font-size: 14px;
}
.tr_heading th:first-child{
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
}
.tr_heading th:last-child{
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
}
.tr_inner td{
    border-bottom: 1px solid #E7E7E7;
}
.td_name{
    color: #343434;
    font-weight: 500;
    font-family: var(--primaryFontMedium);
}
.td_date{
    color: #858585;
    font-weight: 500;
    font-family: var(--primaryFontMedium);
}
.tr_amount{
    font-weight: 500;
    font-family: var(--primaryFontMedium);
}
.amountPaid{
    color: #169F0A;
}
.ammountUnpaid{
    color: #DC3646;
}
.tr_statusArrow{
    padding-left: 30px !important;
}
.blankInoviceData{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 80px 0;
}
.blankInoviceData p{
    font-weight: 700;
font-size: 24px;
line-height: 32px;
text-align: center;
color: #5B3F91;
margin-bottom: 0;
margin-top: 20px;
}

@media (max-width: 575.98px) {
    .cardBody{
        padding: 0 !important;
        overflow-y: scroll;
    }
    .cardBody::-webkit-scrollbar {
        height: 6px;
      }
      /* Track */
      .cardBody::-webkit-scrollbar-thumb {
        background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
      }
      .tr_statusArrow{
        padding-left: 15px !important;
    }
      .tr_heading th:nth-child(1){
        min-width: 40px;
      }
      .tr_heading th:nth-child(2){
        min-width: 140px;
      }
      .tr_heading th:nth-child(3){
        min-width: 120px;
      }
      .tr_heading th:nth-child(4){
        min-width: 120px;
      }
      .tr_heading th:nth-child(5){
        min-width: 120px;
      }
      .tr_heading th:nth-child(6){
        min-width: 80px;
      }
}