.wrapper{
    padding-top: 100px;
    padding-bottom: 150px;
}
.heading{
    text-align: center;
}
.heading h1{
    font-style: normal;
    font-weight: 700;
    font-size: 56px;
    font-family: var(--primaryFontBold);
}
.heading h1 span{
    position: relative;
}
.heading h1 span:before{
    content: "";
    position: absolute;
    width: 263px;
    height: 62px;
    right: -3px;
    top: -2px;
    background: #E5D5F1;
    border-radius: 42px;
    z-index: -1;
}

@media (max-width: 1024px) {
    .wrapper{
        padding-top: 100px;
        padding-bottom: 100px;
    }
}

@media (max-width: 767px) {
    .heading h1{
        font-size: 48px;
    }
    .heading h1 span:before{
        width: 207px;
    }
}

@media (max-width: 400px) {
    .heading h1{
        font-size: 35px;
    }
    .heading h1 span:before{
        width: 152px;
        height: 42px;
        right: -9px;
    }
    .wrapper{
        padding-bottom: 35px;
    }
}