.main_div{
    text-align: center;
    padding-bottom: 0px;
}

.headingBtmCta h2{
    font-size: 60px;
    font-weight: 600;
    font-family: var(--primaryFontBold);
}
.headingBtmCta h2 span{
    position: relative;
}
.headingBtmCta h2 span:before{
    content: "";
    background: #FBD4A9;
    width: 100%;
    height: 65px;
    position: absolute;
    z-index: -1;
    border-radius: 50px;
    right: 10px;
}
.headingBtmCta p{
    color: #5E606A;
    font-size: 26px;
    padding-top: 25px;
}
.headingBtmCta button{
    width: 211px;
    height: 48px;
    left: 551px;
    top: 2684.63px;
    padding: 12px 16px 16px;
    background: #5B3F91;
    border: 1px solid #5B3F91;
    border-radius: 25px;
    color: #fff;
    margin-top: 25px;
}

@media (max-width: 1024px) {
    .headingBtmCta h2{
        font-size: 50px;
    }
    .main_div{
        padding-bottom: 105px;
    }
}

@media (max-width: 767px) {
    .headingBtmCta h2{
        font-size: 45px;
    }
    .headingBtmCta h2 span:before{
        width: 185px;
        height: 58px;
        right: 150px;
        bottom: -4px;
    }
}

@media (max-width: 400px) {
    .headingBtmCta p{
        line-height: 40px;
    }
    .headingBtmCta h2{
        font-size: 30px;
    }
    .headingBtmCta p{
        font-size: 21px;
    }
    .headingBtmCta h2 span:before{
        width: 130px;
        height: 34px;
        right: 97px;
        bottom: -2px;
    }
}