.emailVerifyAlert {
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    margin: 1em 0.5em 0 0.5em;
    border: 0;
}

.emailVerifyAlert .alertHeader {
    font-size: 1.2rem;
    margin-bottom: 0px;
}

.emailVerifyAlert :global(.btn-close) {
    font-size: 0.8em;
}

/* mobile device */
@media (max-width: 575.98px) {
    .emailVerifyAlert .alertHeader {
        font-size: 1em;
    }

    .emailVerifyAlert {
        margin-top: 100px !important;
    }

    .emailVerifyAlert + .alert {
        margin-top: 15px;
    }

    .emailVerifyAlert+.dashboard-container-wrapper {
        top: 0px;
    }

    :global(.container_wrapper) .emailVerifyAlert {
        top: 90px;
        padding: 60px 20px;
    }
}

@media (max-width: 380px) {
    .dashboard-container .emailVerifyAlert .alertHeader {
        font-size: 0.8em;
    }

    .dashboard-container .emailVerifyAlert :global(.btn) {
        font-size: 0.8em;
    }
}