
.topHeading h1{
    font-weight: 700;
    font-size: 24px;
    color: #282828;
}
.topHeading {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.topHeading p{
    font-weight: 600;
    font-size: 14px;
    color: #343434;
    margin-bottom: 24px;
}
.Container{
    background: #FBFCFF;
    border-radius: 8px;
    padding: 24px;
}.tutorialWrapper{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.imgBox{
    margin-right: 5px;
    cursor: pointer;
}
.imgBox .icon{
    width: 33px;
    height: 33px;
}

.infoBox{
    color: #52545a;
}

.tutorialWrapper .text{
    font-size: 0.75rem;
    color: inherit;
    line-height: 18px;
    margin-bottom: 1px;
}

.stepTutorialModal iframe{
    display: block;
}
.modalTitle {
    font-size: 20px;
    font-weight: 700;
    color: #000;
    font-family: var(--primaryFontBold);
}

.btnNew {
   background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85% );
    color: #ffffff;
    font-family: var(--primaryFontBold);
    font-size: 14px;
    margin-left: 8px;
    border: none;
    z-index: 1;
    border-radius: 40px;
    box-shadow: rgb(100 100 111 / 20%) 0px 7px 29px 0px;
    transition: .3s;
    height: 45px;
    padding: 0 20px;
    text-transform: capitalize;
}
.btnNew:hover {
    background: linear-gradient(289.79deg, #FFA10D 23.59%, #FF7439 63.85% );
    transition: .3s;
    transform: scale(1.1);
    border-radius: 5px;
}

@media (max-width: 575.95px) {
    .Container{
        padding: 0;
    }
    .topHeading { 
        display: flex;
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
    }
    .modalTitle {
        font-size: 16px;
        line-height: 19px;
    }
    
}