.invoice-box {
    padding: 30px;
    font-size: 16px;
    line-height: 24px;
    color: #555;
}
.inoviceTable tr td {
    font-family: 'MarkPro';
}
.topBackBtn span {
    font-weight: bold;
    font-family: 'MarkPro';
}
.td_left span{
    color: #52545A;
    font-weight: bold;
}
.clr_grey{
    color: #52545A;
    font-weight: bold;
}
.clr_grey-b-0 td{
    color: #52545A;
}
.clr_purple td{
    color: var(--purple);
    font-weight: bold;
}
.status_color{
    color: var(--orange);
    font-weight: bold;
}
.invoice-box table {
    width: 100%;
    line-height: inherit;
    text-align: left;
}
.float-right{
    float: right;
}
.float-left{
    float: left;
}

.invoice-box table td {
    padding: 5px;
    vertical-align: top;
}

.invoice-box table tr td:nth-child(2) {
    text-align: right;
}

.invoice-box table tr.top table td {
    padding-bottom: 20px;
}

.invoice-box table tr.top table td.title {
    font-size: 45px;
    line-height: 45px;
    color: #333;
}

.invoice-box table tr.information table td {
    padding-bottom: 40px;
}

.invoice-box table tr.heading td {
    background: #eee;
    border-bottom: 1px solid #ddd;
    font-weight: bold;
}

.invoice-box table tr.details td {
    padding-bottom: 20px;
}

.invoice-box table tr.item td {
    border-bottom: 1px solid #eee;
}

.invoice-box table tr.item.last td {
    border-bottom: none;
}

.invoice-box table tr.total td:nth-child(2) {
    border-top: 2px solid #eee;
    font-weight: bold;
}

.back_btnInovice {
    background: #F5F6F8;
    border: none;
    width: 25px;
    height: 25px;
    transition: .3s;
    color: var(--purple);
    padding: 1px;
}

.back_btnInovice:hover {
    background: var(--purple);
    color: #fff;
}
.inoviceTable{
    margin-top: 40px;
}
.tr_ td {
    width: 150px;
    text-align: left !important;
}

.tr_topRow td {
    width: 25%;
}
.td_left{
    text-align: left !important;
}
.td_right{
    text-align: right;
}

.tr_row td {
    width: 150px !important;
    text-align: left !important;
}

.tr_topRow {
    height: 45px !important;
    line-height: 45px;
}
.btn_invoiceDownload {
    float: right;
    width: 133px;
    height: 36px;
    background: var(--purple);
    border-radius: 24px;
    color: #fff;
    border: none;
    line-height: 22px;
    top: -65px;
    position: relative;
}
.heading_style_table{
    font-size: 16px;
    color: #52545A;
    font-weight: bold;
    margin-bottom: 10px;
}
.heading_style_table_col{
    font-size: 16px;
    padding-top: 10px;
}
.btm_tableArea {
    border: 1px solid #EAEAEA;
    padding: 8px;
}
.btm_tableArea .heading_row{
    border-bottom: 1px solid #EAEAEA;
    margin: 0;
}
.btm_tableArea .heading_row:last-child {
    border: none;
}
.border_col_row{
  margin: 0;
}
.border_col{
    border-top: 1px solid #EAEAEA;
}
.m-top-6{
    margin-top: 150px;
}
.f-bold{
    font-weight: bold;
}
.disclaimer_text {
    color: var(--purple);
    font-weight: bold;
    margin-bottom: 0;
    margin-top: 30px;
}
.last_pra {
    margin-bottom: 0;
    margin-top: 40px;
    font-weight: bold;
    color: var(--black);
    padding-left: 4px;
    font-size: 13px;
}

.last_pra svg {
    position: relative;
    left: -5px;
    top: -2px;
    width: 13px;
    height: 13px;
}

.last_pra button {
    background: transparent !important;
    border: none;
    color: #F58634;
    font-weight: bold;
}
.invoice-box-mobile .topBackBtn span{
    color: #5E606A;
    padding-left: 4px;
}
.invoice-box-mobile .back_btnInovice{
    width: 21px;
    height: 21px;
    position: relative;
    top: 2px;
}
.invoice-box-mobile .back_btnInovice svg {
    width: 12px;
    height: 12px;
    position: relative;
    top: -3px;
}
.status_mobile {
    position: absolute;
    right: 6px;
    top: 28px;
    font-size: 13px;
}
.back_btnInovice svg {
    width: 12px;
    height: 12px;
    position: relative;
    top: -3px;
}

.status_mobile {
    position: absolute;
    right: 10px;
    top: 28px;
    font-size: 13px;
}

.data_invoice {
    padding: 0;
    margin-bottom: 0;
    margin-top: 20px;
}

.data_invoice li {
    color: #52545A;
    list-style: none;
    display: inline-block;
    font-size: 14px;
}

.data_invoice li:last-child {
    float: right;
}

.b_bold_m {
    font-weight: bold;
}

.data_invoice_bill {
    padding: 0;
    margin-bottom: 0;
    margin-top: 20px;
}

.data_invoice_bill li {
    font-size: 14px;
    list-style: none;
    display: inline-block;
    color: #52545A;
    font-weight: bold;
}

.bill_details {
    display: grid;
    padding-left: 25px;
}

.data_invoice_bill .clr_purple {
    color: var(--purple);
    font-weight: bold;
}

.mobile_down {
    float: inherit;
    top: auto;
    position: inherit;
    margin-top: 25px;
}
.payment_mobile_invoice{
    font-size: 14px;
    color: #52545A;
    font-weight: bold;
    margin-top: 25px;
    margin-bottom: 20px;
}
.details_account {
    display: flex;
}

.details_account .flex_row {
    display: grid;
    font-size: 13px;
    font-weight: bold;
    color: #52545A;
}

.details_account .flex_row:nth-child(1) {
    width: 124px;
}

.details_account .flex_row:nth-child(2) {width: 121px;}

.details_account .flex_row .clr_purple {
    color: var(--purple);
    margin-top: 3px;
}
.list_orders {
    border: 1px solid #EAEAEA;
    padding: 2px;
    margin-top: 23px;
}

.list_orders .flex_row {
    font-size: 12px;
}
.innerDetails {
    display: flex;
    border-bottom: 1px solid #EAEAEA;
}
.total_div .flex_row {
    display: inline-flex;
    width: 100% !important;
}

.total_div .flex_row .clr_grey {
    padding-left: 65px;
}

.total_div {
    justify-content: flex-end;
    padding-right: 10px;
    margin-top: 100px;
    margin-bottom: 50px;
}

.total_div .innerDetails {
    padding-top: 10px;
    border-top: 1px solid #EAEAEA;
    border-bottom: 0;
}
.des_mobile_inovice {
    color: var(--purple);
    font-size: 13px;
    font-weight: bold;
    padding: 0 6px;
    margin-bottom: 10px;
}


@media only screen and (max-width: 600px) {
    .invoice-box table tr.top table td {
        width: 100%;
        display: block;
        text-align: center;
    }

    .invoice-box table tr.information table td {
        width: 100%;
        display: block;
        text-align: center;
    }
}


@media only screen and (max-width: 535px) {

    /* width */
.mobile_scroll_invoice::-webkit-scrollbar:horizontal {
    height: 6px;
  }
  
  /* Track */
  .mobile_scroll_invoice::-webkit-scrollbar-track:horizontal {
    background: #f1f1f1; 
  }
   
  
  /* Handle on hover */
  .mobile_scroll_invoice::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }
    .list_orders .innerDetails{
        display: block;
        border-bottom: 1px solid #EAEAEA;
        flex-direction: column;
        width: 100%;
        overflow-y: scroll;
    }
    .list_orders .innerDetails .heading_row{
        display: flex;
        flex-wrap: nowrap;
        width: 100px;
        padding: 4px;
    }
}

/** RTL **/
.invoice-box.rtl {
    direction: rtl;
    font-family: Tahoma, 'Helvetica Neue', 'Helvetica', Helvetica, Arial, sans-serif;
}

.invoice-box.rtl table {
    text-align: right;
}

.invoice-box.rtl table tr td:nth-child(2) {
    text-align: left;
}


@media (max-width: 412px) {
    .status_mobile {
        max-width: 70px;
        text-align: right;
        top: 20px;
        line-height: 14px;
    }
    
    .status_heading_mobile {
        font-size: 12px;
    }

}