.horizontal-card .img-box img{
    width: 180px;
    height: 180px;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    /* object-fit: cover; */
}

.horizontal-card .card-body .card-title{
    font-size: 28px;
    font-family: var(--themePrimaryFontMedium);
    line-height: 29px;
    margin-bottom: 5px;
}

.horizontal-card .card-body .card-text{
    font-size: 18px;
    font-family: var(--themePrimaryFontMedium);
    line-height: 29px;
}

.horizontal-card .card-footer{
    background-color: var(--themeTextColorSecondary);
    color: #fff;
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
}

.horizontal-card .card-footer .btn-link{
    color: #fff;
    text-decoration: none;
    font-size: 28px;
}

.horizontal-card .card-edit-block{
    display: none;
}

.horizontal-card:hover .card-edit-block{
    display: flex;
}

.horizontal-card.experience .img-box{
    width: 125px;
}

.horizontal-card.experience .img-box .img{
    width: 125px;
    height: 125px;
}

.horizontal-card.experience .card-body .card-title{
    font-size: 18px;
    line-height: 19px;
}

.horizontal-card.experience .card-body .card-text{
    font-size: 16px;
}
.horizontal-card.experience .card-footer .btn-box button{
    width: 100%;
}

/* service style 4  */
.horizontal-card.services.style_4 .card-body{
    padding-top: 33px;
    padding-bottom: 33px;
}

/* coupon section */
.horizontal-card.coupon .card-footer .btn-box button{
    width: 100%;
}

@media(max-width: 575.98px){
    .horizontal-card.services.style_4 .card-body{
        padding-top: 20px;
        padding-bottom: 20px;
    }

}