.progressBox {
    display: flex;
    justify-content: center;
    padding-top: 0em;
    padding-bottom: 2em;
}

.numberBadge {
    background-color:#858585;
    color: #fff;
    height: 32px;
    width: 32px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
    padding-top: 4px;
    font-weight: 600;
}


.active .numberBadge {
    background: #5b3f91;
}

.active  ~ .iconBox .numberBadge {
    background: #BCBCBC ;
}

.progressBox .iconBox {
    color: #52545a;
    padding-left: 2em;
    padding-right: 2em;
    font-size: 1.25em;
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
}

.progressBox .iconBox:not(:last-child):after {
    content: " ";
    height: 2px;
    background: #858585;
    width: 38%;
    position: absolute;
    right: auto;
    left: 80%;
    top: 24px;
}


.progressBox .iconBox.active:after {
    background: #5b3f91;
}

.progressBox .iconBox.active  ~ .iconBox:after  {
    background: #BCBCBC ;
}

.progressBox .iconBox {
    color: #858585;
}

.progressBox .iconBox.active {
    color: #5b3f91;
}

.progressBox .iconBox.active  ~ .iconBox  {
    color: #BCBCBC ;
}

.text {
    margin-bottom: 0;
    text-transform: uppercase;
    font-family: var(--primaryFontMedium);
    font-size: 16px;
    text-transform: inherit;
    margin-top: 10px;
}

.blur {
    opacity: 0.6;
}

.iconCheckmark {
    font-size: 10px;
    margin-top: -1px;
}

@media (max-width: 1450px) {
    .progressBox {
        padding-bottom: 0;
    }
}


@media ( max-width: 800px ) {
    
    .numberBadge {
        padding-top: 3px;
    }

    .progressBox {
        margin-top: 30px;
        padding-bottom: 10px;
    }

    .progressBox .iconBox {
        padding: 0 15px;
    }

    .text { 
        font-size: 12px;
        line-height: 16px;
    }

}


@media ( max-width: 450px ) {
    .progressBox .iconBox:not(:last-child):after {
        top: 18px;
    }
}
