.db-pricing-plan-section{
    position: relative;
    margin-bottom: 20px;
}

.pricing-plan-info-box{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}
.pricing-plan-line-break{
    border-bottom: 1px solid #C4C4C4;
}

.pricing-plan-info-box .plan-info,
.pricing-plan-info-box .plan-action{
    display: flex;
}
.Pro{
    flex-direction: row-reverse;
}

.plan-detail-box .plan-detail-list{
    list-style: none;
}

.chk-plan{
    font-size: 11px;
    color: #F8AA54;
    margin-right: 10px;
}
.plan-detail-box .pricing-plan-info-box{
    font-family: var(--secondaryFont);
    font-size: 1.125em;
}
.orderOne_price {
    order: 1;
}

.plan-detail-item{
    color: #52545A;
    font-size: 0.875em;
    font-family: var(--secondaryFontLight);
    margin-bottom: 10px;
    text-decoration: none;
    list-style: none;
    display: flex;
}

.dashboard-plan-title{
    font-size: 1.5rem;
    font-family: var(--secondaryFont);
    color: #52545A;
}

.plan-card-outline{
    border: 2px solid var(--orange);
    border-radius: 17px;
    -webkit-border-radius: 17px;
    -moz-border-radius: 17px;
    -ms-border-radius: 17px;
    -o-border-radius: 17px;
    width: auto;
    height: auto;
}

.plan-card-shaded{
    background-color: #FDFBFF;
    border: 0px; border-radius: 17px;
    -webkit-border-radius: 17px;
    -moz-border-radius: 17px;
    -ms-border-radius: 17px;
    -o-border-radius: 17px;
    width: auto;
    height: auto;
}
.plan-info .plan-info-heading{
    font-size: 1em;
    color: #52545A;
    font-family: var(--secondaryFont);
}

.plan-info .plan-info-heading .sub-body-text{
    color: var(--grey);
    font-family: var(--secondaryFontMedium);
}

.plan-dets .plan-info-pricing{
    font-family: var(--secondaryFontMedium);
    color: var(--purple);
    font-size: 1.5em;
}

.dashboard-plan-card-footer{
    border-top: 0px;
    background-color: transparent;
}

.upgrade-plan-btn{
    background-color: var(--orange) !important;
    border-radius: 20px;
    border: 0px;
    width: 100%;
    font-family: var(--secondaryFontBold);

    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
}
.upgrade-plan-btn .arrow-upgrade{
    font-size: 14px;
    color: #F8AA54;
    position: relative;
    top: 0px;
}

.user-history-table .primary-bold:hover {
    background: #F3F3F3;
    cursor: pointer;
}

.dashboarPlanWrapper {
    position: relative;
    padding-top: 40px;
    padding-bottom: 40px;
    max-width: 900px;
    margin-bottom: 30px;
}
.pg_profile .desktop_pricing {
    padding-top: 8%;
}
.pg_profile #priceCols{
    z-index: 1;
}
.pg_profile .firstStar_icon{
    z-index: 10;
    transform: translate(-262px, 55px);
}
.pg_profile .desktop_pricing_row {
    margin-bottom: 50px;
}
.pg_profile .savePrice {
    top: 74px !important;
    font-size: 14px !important;
}
.pg_profile .h3Price {
    font-size: 22px !important;
}
.pg_profile .h2amount {
    font-size: 22px !important;
}
.pg_profile .h2amount span {
    font-size: 18px !important;
}
.pg_profile .p0pricingContainer{
    padding: 0;
}
/* .activeCardPrice .tagPrice{
    display: none;
} */

/* Mobile devices small screen */

@media (max-width: 380px) {
}
/* Mobile devices large screen */

@media (max-width: 575.98px) {
    .dashboarPlanWrapper{
        flex-direction: column-reverse;
    }
    .orderOne_price {
        order: 1;
        margin: 25px 0 25px 0;
    }
    .dashboard-plan-title{
        font-size: 1.3em;
    }
    .plan-info .plan-info-heading{
    font-size: 0.75em;
    }
    .plan-dets .plan-info-pricing{
        font-size: 1.2em;
    }
    .plan-card-outline{
        height: auto;
        margin-bottom: 25px;
    }
    .plan-card-shaded.card {
    height: auto;
}
/* .plan-card-shaded .card-footer {
    display: none;
} */
.plan-detail-item:last-child{
    margin-bottom: 0;
}
}


