@keyframes ani_colors {
    0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
    }
}

.Notify{
    border: 0px;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #9461FA, #ee7752);
    background-size: 400% 400%;
    position: fixed;
    width: 100%;
    z-index: 100;
    height: 65px;
    text-align: center;
    top: 0;
    border-radius: 0;
    margin-bottom: 0;
    animation: ani_colors 15s 10s infinite;
    border: 0px;
}

.Notify + div { margin-top: 64px; }

.Notify:global(.plain) {
    position: relative;
}

.Notify :global(.btn-close) {
    position: absolute;
    top: 4px;
    opacity: 1;
    filter: invert(1);
    transition: all 0.5s;
}

.Notify :global(.btn-close):hover {
    transform: rotate(90deg) scale(1.2);
}

.text img{
    width: 35px;
    position: relative;
    top: -3px;
}

.title {
    color: #fff;
    font-size: 18px;
    font-weight: 500;
    padding-left: 10px;
}

@keyframes ani_Upgrade {
    50% {
        transform: scale(1.1);
    }
}

.btnUpgrade{
    padding: 0;
    background: transparent;
    border: none;
    position: absolute;
    right: 61px;
    top: 18px;
    font-size: 17px;
    font-weight: bold;
    color: #fff;
    text-decoration: none;
}

.btnUpgrade:hover{
    background: transparent;
    border: none;
    color: #fff;
    text-decoration: none;
    animation: ani_Upgrade 1s infinite;
}


.btnUpgrade:focus{
    background: transparent;
    border: none;
}


@media (max-width: 550px) {
    .Notify {
        text-align: left;
        padding: 2px;
        display: flex;
    }

    .Notify .icon {
        padding-left: 15px;
    }

    .text img{
        width: 27px;
        top: 1px;
        left: -5px;
    }

    .text {
        padding: 0;
        display: flex;
        max-width: 90%;
        align-items: center;
    }
    
    
    .btnUpgrade {
        position: relative;
        right: 0;
        top: 0;
        padding: 10px;
        white-space: nowrap;
        border-bottom: 1px solid #fff;
        border-radius: 0;
        padding: 0;
        font-size: 12px;
        margin: 0 4px;
    }
    
    .title {
        padding: 8px;
        font-size: 14px;
    }

  }