.dashboard_wrapper{
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.dashboard_divider_wrapper{
    display: flex;
    position: relative;
}
.renew{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    right: 12px;
    margin-bottom: 10px;
    margin-top: 10px;
}
.mt_top_wrapper{
    margin-top: 55px;
}
.renew p{
    margin-bottom: 0;
    font-family: var(--primaryFontMedium);
    color: #343434;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    margin-right: 20px;
}
.renew button{
    background: #F58634;
    border-radius: 40px;
    border: none;
    color: #fff;
    align-items: center;
    padding: 8px 24px;
    gap: 10px;
    transition: .3s;
}
.renew button:hover{
    background: #f78129;
    transition: .3s;
    box-shadow: 2px 4px 10px #0000003d;
}
.dashboard_divider_wrapper .column_left{
    width: 240px;
    flex-shrink: 0; /* makes sure that content is not cut off in a smaller browser window */
}
.dashboard_divider_wrapper .column_right{
    flex: 1;
    --bs-bg-opacity: 1;
    background-color: rgba(var(--bs-light-rgb), var(--bs-bg-opacity)) !important;
    width: calc(100% - 240px);
}

:global(html), :global(body), :global(#root), :global(.App), :global(.dashboard-wrapper) {
    min-height: 100vh;
}

:global(.introjs-tooltip-header) {
    padding: 0px 5px;
}

:global(.introjs-tooltiptext) {
    padding: 1px 10px;
}

:global(.introjs-tooltipbuttons) {
    padding: 4px;
    border: 0;
}
