.tooltip {
    position: absolute;
    color: #000;
    min-width: 200px;
    background: #fff8f1;
    text-align: center;
    border-radius: 5px;
    padding: 8px;
    z-index: 100;
    box-shadow: 0 0px 6px 0px #ccc;
    font-weight: 600;
}

.tooltip:before {
    content: " ";
    position: absolute;
    height: 10px;
    width: 10px;
}

:global(.pos-top).tooltip {
    max-width: 200px;
}

:global(.pos-top).tooltip:before {
    bottom: -10px;
    top: auto;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid #fff8f1;
    filter: drop-shadow( 0px 3px 1px #ddd);
    left: 50%;
    margin-left: -10px;
}

:global(.pos-left).tooltip {
    -webkit-transform-origin-x: translateX(-80%);
}

:global(.pos-right).tooltip:before {
    left: -9px;
    top: 10px;
    border-right: 10px solid #fff8f1;
    border-bottom: 10px solid transparent;
    border-top: 10px solid transparent;
    filter: drop-shadow(-4px 0px 2px #ddd );
}


:global(.pos-bottom).tooltip {
    -webkit-transform-origin-y: translateY(40px);
}

:global(.pos-bottom):before {
    top: -8px;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #fff8f1;
    left: 50%;
    margin-left: 4px;
    filter: drop-shadow(0px -4px 3px #ddd );
}

