.heading{
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    font-family: var(--primaryFontBold);
    color: #282828;
    margin-bottom: 16px;
}
.DashboardContentBox h2{
    color: #282828;
    margin-top: 20px;
}
.DashboardContentBox span{
    background: linear-gradient(289.79deg, #9772EE 23.59%, #9461FA 63.85%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
background-clip: text;
text-fill-color: transparent;
font-style: italic;
font-weight: 700;
}
.DashboardContentBox p{
    color: #343434;
    font-weight: 500;
    font-family: var(--primaryFontMedium);
}
.container{
    padding: 24px !important; 
    background: #FBFCFF;
    border-radius: 8px;
    margin-bottom: 40px;
    border: none;
}
.mainWrapper h2{
    margin-top: 10px;
}
.mainWrapper p {
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 26px;
    display: flex;
    align-items: center;
    color: #343434;
    font-family: var(--primaryFontMedium);
    margin-top: -3px;
}
.cardBody{
    padding: 0;
}
.paymentCard{
    background: #BCBCBC;
    border-radius: 8px;
    height: 200px;
    padding: 16px;
    position: relative;
     /* background: linear-gradient(100deg, rgba(188,188,188,1) 65%, rgba(255,255,255,1) 65%, rgba(255,255,255,1) 65%, rgba(188,188,188,1) 100%); */
     box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
     cursor: pointer;
     /* transform: perspective(3000px) rotateY(22deg); */
     transition: .3s;
}
.paymentCard:hover{
    /* background: linear-gradient(100deg, rgba(188,188,188,1) 65%, rgba(255,255,255,1) 65%, rgba(255,255,255,1) 65%, rgba(188,188,188,1) 100%); */
    /* transform: perspective(3000px) rotateY(0deg); */
     transition: .3s;
     box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
}

.paymentCard p{
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    color: #fff;
    position: absolute;
    bottom: 16px;
    right: 16px;
    left: 16px;
    font-family: var(--primaryFontMedium);
    margin-bottom: 0;
}
.cardLogo{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.cardLogo span{
    background: white;
    width: 25px;
    height: 25px;
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 20px;
    top: 20px;
}
.stripeCard .logo svg {
    width: 143px;
    height: 57px;
    position: relative;
    top: -5px;
    left: -4px;
}
.stripeCard:hover{
    background: #6772E5;
}
.bykeaCard:hover{
    background: #00AA2F;
}
.stripeCard.activeCard{
    background: #6772E5;
}
.bykeaCard.activeCard{
    background: #00AA2F;
}
.spanText{
    color: #fff;
    margin-top: 10px;
    display: block;
    font-size: 16px;
    font-weight: 500;
}


@media (max-width: 575.98px) {
    .cardBody{
        padding: 0 !important;
    }
    .paymentCard{
        transform: perspective(3000px) rotateY(0deg);
        margin-bottom: 20px;
        height: 180px;
    }
    .spanText{
        font-size: 14px;
    }
    .stripeCard .logo svg{
        width: 100px !important;
        height: 40px !important;
    }
    .logo svg{
        width: 100px !important;
        height: 24px !important;
    }
}