

.card{
    padding: 24px;
    background: #FBFCFF;
    border-radius: 8px;
    min-height: 156px;
}
.card h3{
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #282828;
    font-family: var(--primaryFontBold);
    margin-bottom: 8px;
}
.card p{
    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    color: #5E606A;
    font-family: var(--primaryFontMedium);
    margin-bottom: 30px;
}
.dFlexRow{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.dFlexRow p {
    margin-bottom: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #5E606A;
    font-family: var(--primaryFontBold);
}
.dFlexRow p input{
    margin-right: 10px;
}
.btn{
    background: #F58634;
    border-radius: 40px;
    border: none;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #FFFFFF;
    padding: 8px 16px;
}
.btn:hover{
    background: #f07e2d;
}