/* Theme 2 card styling */

.card-section-body{
  display: flex;
  height: 100%;
  margin-top: auto;
  flex-direction: column;
  justify-content: flex-end;
}
.card-section-body .card-two-header{
color: #fff;
font-family: var(--primaryFontMedium);
}
#experience.style_1 .imageLoaderCard {
  transform: translate(-67px, -233px);
}

.card-section-body .card-two-description{
font-family: var(--themePrimaryFontLight);
color: #fff;
}
.influencer-pricing-footer .dropdown .dropdown-item .fa-lock{
  color: var(--purple)!important;
}
.card-two-btn{
  background-color: transparent !important;
  border: 1px solid #fff !important;
  color: #fff !important;
}

.card-bg-overlay {
  width: 100%;
  height: 100%;
  background-size: cover !important;
  background-repeat: no-repeat !important;
  border-radius: 17px;
}


.logo-box{
   border-radius: 18px;

}

.card-two-logo .img-box .user-img{
    width: 100%;
    height: 100%;
    max-width: 83px;
    max-height: 83px;
    object-fit: cover;
}
.disabledEdit > label {pointer-events: none !important;opacity: .8;filter: contrast(0.5);}

.disabledEdit {cursor: not-allowed !important;}

/* END OF Card with image header */
.fa-times{
  color:#323232;
}
/* END of THEME 2 CARD  */
/* Action button  */

.btn-edit-generic {
  position: absolute;
  background: white;
  height: fit-content;
  min-width: 220px;
  top: 50%;
  left: 50%;
  border: 1px solid #0000003d;
  border-radius: 14px;
  box-shadow: 3px 3px 6px #00000029;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  z-index: 1;
  padding-top: 24px;
  padding-bottom: 12px;
}

.theme-general-card .upload-generic-card-image {
  position: absolute;
  margin: 0;
  padding: 0;
  top: 15px;
  right: 50px;
  display: none;
}
.theme-general-card:hover .upload-generic-card-image{
  display: flex;
}
.upload-generic-card-image-input {
  width: 0px;
  height: 0px;
  overflow: hidden;
}
.theme-general-card .upload-icon-area {
  background-color: #00000454;
  border-radius: 50px;
  width: 24px;
  height: 24px;
  cursor: pointer;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  -o-border-radius: 50px;
  font-size: 13px;
}

.btn-edit-generic .additional-data-box .text-end {
  cursor: pointer;
  position: absolute;
  right: -8px;
  top: -8px;
  width: 25px;
  height: 25px;
  background: var(--purple);
  border-radius: 30px;
  box-shadow: -1px 1px 13px #0000005c;
  transition: .3s;
}
.btn-edit-generic .additional-data-box .text-end:hover {
  box-shadow: -2px 3px 10px #00000087;
  transition: .3s;
}
.btn-edit-generic .additional-data-box .text-end svg{
  color: #fff !important;
  width: 13px;
  height: 14px;
  position: relative;
  left: -5px;
  top: 0px;
}
.btn-edit-generic .additional-data-box .mb-3 {
  margin-bottom:8px !important;
}
.btn-edit-generic .additional-data-box input {
  height: 35px;
  font-size: 14px;
}
.btn-edit-generic .btn-box button {
  margin-top: 6px !important;
  margin-bottom: 6px !important;
  padding: 6px 0;
}


.enable-btn-card:disabled{
color: var(--primaryColor);
opacity: 25%;
}
/* theme generic section code */

.theme-general-card {
  position: relative;
  background-color: var(--themePrimaryColor);
  text-align: center;
  border-radius: 17px;
  border: none;
  min-height: 471px;
  height: 100%;
}
.theme-general-card .card-body {
  padding: 26px;
}
.theme-general-card .card-footer {
  padding: 26px;
  padding-top: 0;
}
.theme-general-card .img-Card {
  border-radius: 17px;
  width: 100%;
  height: 190px;
  object-fit: cover;
  margin-bottom: 24px;
}
.theme-general-card .theme-img-header {
  border-radius: 17px 17px 0px 0px;
  width: 100%;
  height: 272px !important;
  text-align: center;
}
.theme-general-card .theme-img-header .top-img-Card {
  border-radius: 0px;
  border-top-left-radius: 17px !important;
  border-top-right-radius: 17px !important;
  width: 100%;
  height: 272px !important;
  object-fit: cover;
}

.theme-general-card .card-title {
  font-family: var(--themePrimaryFontBold);
  font-size: 26px;
  margin-bottom: 6px;
  color: var(--themeTextColor);
  line-height: 25px;
}
.theme-general-card .card-text {
  font-family: var(--primaryFontMedium);
  font-size: 22px;
  line-height: 26px;
  color: var(--themeTextColor);
}
.theme-general-card .card-footer {
  background-color: transparent;
  border: none;
  opacity: 1;
}
.theme-general-card .btn-theme-outline-primary {
  background-color: transparent;
  border: 1px solid var(--themeTertiaryColor);
  width: 100%;
  border-radius: 21px;
  color: var(--themeTertiaryColor);
  font-family: var(--themePrimaryFontBold);
  font-size: 18px;
}

.theme-general-card .guide-msg {
  display: none;
  background-color: var(--themeTextColor);
  color: var(--themePrimaryColor);
  position: absolute;
  top: 0px;
  right: 0px;
  padding: 2px 10px;
  border: 2px;
  font-size: 12px;
}
.theme-general-card:hover .guide-msg {
  display: inline-block;
  border-top-right-radius: inherit;
  border-bottom-left-radius: inherit;
}

.theme-general-card .link a {
  color: var(--themeTextColor);
  cursor: pointer;
}

/* Theme Package Card */
.theme-package-card {
  background-color: var(--themePrimaryColor);
  border-radius: 17px;
  border: none;
  min-height: 491px;
  height: 100%;
}
.theme-package-card .card-body {
  padding: 24px 26px;
  padding-bottom: 0;
}
.theme-package-card .package-price .currency+.character-limit{
  z-index: 7;
}
.theme-package-card .package-price {
  display: flex;
  justify-content: center;
  font-size: 1em;
  color: var(--themeTextColor);
  text-align: center;
  font-family: var(--themePrimaryFontExtraBold);
  margin-bottom: 24px;
}

.theme-package-card .card-body .card-text{
  color: var(--themeTextColor);
}

.theme-package-card .package-price .price {
  font-size: 2.5em;
  top: -0.26em;
}
.theme-package-card .package-price .price+.character-limit{
  top: -48px;
}
.theme-package-card .package-price .duration {
  font-family: var(--themePrimaryFontLight);
}
.theme-package-card .description {
  font-family: var(--themePrimaryFontMedium);
  font-size: 1.0625em;
  text-align: center;
  color: var(--themeTextColor);
  margin: 0 auto;
  margin-bottom: 23px;
  width: 90%;
}

.theme-package-card .list-icon {
  margin-right: 12px;
}

.theme-package-card .list-icon .icon {
  color: var(--themeTertiaryColor);
}

.theme-package-card .title-box {
  background-color: var(--themeTextColor);
  padding: 5px 10px;
  color: var(--themePrimaryColor);
  display: inline-block;
  width: fit-content;
  margin-top: 31px;
  margin-bottom: 7px;
  border-top-right-radius: 18px;
  border-bottom-right-radius: 5px;
}

.title-box h1 {
  font-size: 1.25rem;
  font-family: var(--themePrimaryFontBold);
  margin-bottom: 0;
}

.theme-package-card .guide-msg {
  display: none;
  background-color: var(--themeTextColor);
  color: var(--themePrimaryColor);
  position: absolute;
  top: 0px;
  right: 0;
  padding: 2px 10px;
  border: 2px;
  font-size: 12px;
}
.theme-package-card:hover .guide-msg {
  display: inline-block;
  border-top-right-radius: inherit;
  border-bottom-left-radius: inherit;
}

.theme-package-card .btn-primary-theme {
  min-width: 181px;
}
.theme-package-card .card-footer {
  background-color: transparent;
  border-top: none;
  text-align: center;
  padding-bottom: 26px;
  padding-top: 0;
}
.theme-package-card .link a{
  color: var(--themeTextColor);
  cursor: pointer;
}

.theme-package-card .package-service-list ul li .list-text {
  /* margin-left: 10px; */
  font-size: 0.875em;
  color: var(--themeTextColor);
  word-break: break-word;
}
.card .payment-activate-dropdown{
  width: fit-content;
  margin: 0 auto;
  cursor: pointer;
  color: var(--themeTextColor);
}

/* Mobile devices small screen */
@media (max-width: 380px) {
  .theme-general-card {
    min-height: 344px;
  }
  .theme-general-card .card-body {
    padding: 1em;
  }
  .theme-general-card .card-title {
    font-size: 1.25em;
  }

  .theme-general-card .card-text {
    font-size: 1em;
    line-height: 1.13em;
  }

  .theme-general-card .card-text {
    font-size: 1em;
  }

  .mobile-cropper-title{
    font-size: 12px;
  }
  .modal-dialog-centered{
        min-height: calc(100% - -16rem);
  }
  .modal-dialog-centered.cropper-modal-img{
    min-height: calc(100% - -28rem);
  }
  .mobile-cropper-modal{
    justify-content: center !important;
    padding: 0.3rem !important;
  }
  .theme-package-card .package-price .currency{
    font-size: 0.875em;
  }
  .theme-package-card .package-price .duration {
     font-size: 0.675em;
   }
   .theme-package-card .package-price .price{
     font-size: 1.2375em !important;
   }
}

@media (max-width: 575.98px) {
  .theme-general-card {
    min-height: 387px;
    height: 100%;
  }
  .theme-package-card {
    min-height: 387px;
  }
  .theme-general-card .card-title {
    font-size: 1.25em;
    line-height: 20px;
  }

  .theme-general-card .card-text {
    font-size: 1em;
    line-height: 1.13em;
  }
  .theme-general-card .card-body {
    padding: 1em;
  }
  .theme-general-card .img-Card {
    margin-bottom: 16px;
  }
  .theme-general-card .card-footer {
    padding: 16px;
    padding-top: 0;
  }
  .theme-package-card .package-price .currency{
    font-size: 1em;

  }
   .theme-package-card .package-price .duration {
     font-size: 0.875em;
   }
   .theme-package-card .package-price .price{
     font-size: 1.4375em;
   }
   .btn-edit-generic{
    min-width: 85%;
   }
}


/* Tablet Devices */
@media (min-width: 768px) and (max-width: 991.98px) {
  .theme-general-card .card-title{
    font-size: 1.25em;
    line-height: 22px;
  }
  .theme-general-card .card-text{
    font-size: 1em;
    line-height: 20px;
  }

  .theme-package-card .package-price .price{
    font-size: 1.8em;
  }

}


@media (min-width: 992px) and (max-width: 1199.98px) {
 .theme-general-card .card-title{
    font-size: 1.15em !important;
  }
    .theme-general-card .card-text{
    font-size: 1em;
  }

  .theme-package-card .package-price .price{
    font-size: 1.8em;
  }
}

/* Extra large devices (large desktops, 1200px and up) */

@media (min-width: 1200px) and (max-width: 1399.98px) {
  .theme-general-card .card-title{
    font-size: 1.15rem;
  }
  .theme-general-card .card-text{
    font-size: 1.025rem;
  }

  .theme-general-card .img-Card{
    height: 154px;
  }
}


@media (min-width: 1400px) and (max-width: 1599.98px) {
  .theme-general-card .card-title{
    font-size: 1.25rem;
  }
  .theme-general-card .card-text{
    font-size: 1.125rem;
  }
}
@media (min-width: 1600px) and (max-width: 1799.98px) {
  .theme-general-card .card-title{
    font-size: 1.35rem;
  }
  .theme-general-card .card-text{
    font-size: 1.225rem;
  }
}
