.section-head {
  vertical-align: text-top;
}

/* general theme contact styles */

.style_1.theme-contact-section .content-container {
  box-shadow: 0px 5px 20px #00000029;
  border-radius: 30px;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  -ms-border-radius: 30px;
  -o-border-radius: 30px;
}

.contact-card.style_4 .card-body .card-display-contact {
  display: flex;
  align-items: center;
}

.contact-card.style_4 .card-body .card-display-contact svg {
  width: 23px;
  height: 23px;
  position: relative;
  left: -9px;
  top: -3px;
}

.style_2.theme-contact-section .content-container {
  box-shadow: none;
  border-radius: none;
  -webkit-border-radius: none;
  -moz-border-radius: none;
  -ms-border-radius: none;
  -o-border-radius: none;
}

.theme-contact-section .section-body {
  border-radius: 30px;
  overflow: hidden;
}

.header-card-title {
  font-family: var(--themePrimaryFontBold);
  color: var(--themeTextColor);
  font-size: 1.63em;
}

.contact-det-card {
  padding: 2.375em;
}

.section-body .contact-card {
  border-radius: 30px 0px 0px 30px;
  height: 100%;
  background-color: var(--themePrimaryColor);
  border-width: 0;
}

.section-body .form-detail-section {
  padding: 2.375em;
}

.contact-card .card-header-contact {
  background: transparent;
  position: relative;
}

.contact-card .card-header-contact .contact-img {
  max-width: 240px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 30px 0px 0px 30px !important;
}

.card-header-contact .btn-box {
  position: absolute;
  bottom: 10px;
  display: flex;
  justify-content: center;
  width: 100%;
}

.card-header-contact .btn-box .btn-secondary-theme {
  background-color: var(--themeTertiaryColor);
  /* color: var(--themeTextColorSecondary); */
  color: #fff;
  min-width: 200px;
  font-family: var(--themePrimaryFontBold);
  font-size: 18px;
}

.card-header-contact .card .card-sub-header {
  font-size: 1.375em;
  font-family: var(--themePrimaryFontMedium);
}

.card .card-text-input {
  font-size: 1.38em;
  font-family: var(--themePrimaryFontMedium);
}

.contact-field-box .form-label {
  font-family: var(--themePrimaryFontMedium);
  font-size: 1.375em;
  color: var(--themeTextColor);
  align-self: flex-start;
}

.contact-field-box .text-input {
  border-radius: 11px;
  height: 42px;
}

.contact-field-box .text-area-container {
  position: relative;
  text-align: right;
}

.contact-field-box .text-area-container .text-input-area {
  border-radius: 11px;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

.text-area-container .submit-btn {
  border-radius: 11px 0px 11px 0px;
  font-size: 16px;
  font-family: var(--themePrimaryFontMedium);
  -webkit-border-radius: 11px 0px 11px 0px;
  -moz-border-radius: 11px 0px 11px 0px;
  -ms-border-radius: 11px 0px 11px 0px;
  -o-border-radius: 11px 0px 11px 0px;
  margin-top: 11px;
}

.contact-det-card .contact-info-section .detail-section {
  border-right: 1px solid #00000029;
  color: var(--themeTextColor);
}


/* Variant Four */
.contact-card.style_4 {
  background-color: white;
}

.contact-card.style_4 .card-body {
  padding: 0;
}

.contact-card.style_4 .card-body .detail-section {
  background-color: var(--themePrimaryColor);
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
  padding: 50px;
  border-right: none;
}

.contact-card.style_4 .card-body .form-input-section {
  padding: 30px 50px;
}

.contact-card.style_4 .form-submit-section .contact-submit-btn {
  float: left;
}


@media (max-width: 380px) {
  .section-head .section-name {
    font-size: 1.56em;
  }

  .section-head .section-text {
    font-size: 1em;
  }

  .contact-det-card .contact-info-section .detail-section .card-display-contact {
    margin-top: 10px !important;
  }

  .contact-card .card-header-contact .contact-img {
    border-radius: 17px 17px 0px 0px !important;
    max-width: fit-content;
  }

  .card-header-contact .btn-box {
    bottom: -20px;
  }

  .contact-det-card .contact-info-section {
    flex-direction: column-reverse;
  }

  .contact-det-card .contact-info-section .detail-section {
    border-right: none;
    border-top: 1px solid #00000029;
    margin-top: 1.2em;
  }

  .header-card-title {
    font-size: 1.25em;
    margin-top: 1.2em;
  }

  .contact-field-box .form-label {
    font-size: 1em;
    color: var(--themeTextColor);
  }

  .card.card-title {
    font-size: 1em;
  }

  .card .card-text-input {
    font-size: 1em;
  }

  .form-input-section {
    padding-top: 0 !important;
    padding-bottom: 0px !important;
    padding-left: 13px !important;
    padding-right: 13px !important;
  }

  .contact-field-box .text-input {
    height: 27px;
  }

  .contact-det-card {
    padding: 1.3em;
  }

  .contact-field-box .contact-field-group {
    margin-top: 0 !important;
    margin-bottom: 0 !important;
    align-items: center !important;
    justify-content: center !important;
  }

  .card-display-contact .card-sub-header {
    font-size: 1em !important;
    font-family: var(--themePrimaryFontMedium);
  }
}

