.title {
    font-family: var(--primaryFontBold);
    font-size: 32px;
    line-height: 40px;
}

.orderSummaryCard {
    background: #FBFCFF;
    padding: 24px;
    border: none;
    border-radius: 8px;
    /* height: 100%; */
    min-height: 540px;
}

.orderSummaryCard :global(.card-header) {
    background-color: transparent;
    display: flex;
    justify-content: space-between;
    padding: 0px;
    padding-bottom: 14px;
    border-color: #E7E7E7;
}

.orderSummaryCard :global(.card-body)  {
    padding: 0px;
    padding-top: 16px;
}

.orderDataWrapper {
    display: flex;
    align-items: center;
}

.orderDataWrapper .iconBox {
    background-color: #F8F9FA;
    padding: 20px;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
    margin-right: 16px;
}

.dataBox .title {
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    font-family: var(--primaryFontBold);
    font-size: 24px;
    line-height: 32px;
    display: inline-block;
}

.dataBox .desc {
    font-family: var(--primaryFontBold);
    font-size: 18px;
    color: #5E606A;
}

.amountBox .amount {
    font-family: var(--primaryFontBold);
    font-size: 24px;
    line-height: 32px;
    color: #343434;
}

.subTotalBox{
    display: flex;
    justify-content: space-between;
}

.subTotalBox .text,
.subTotalBox .subAmount{
    font-family: var(--primaryFontBold);
    font-size: 18px;
    color: #343434;
}
