.imgBox {
    position: relative;
    width: 180px;
    height: 180px;
}

.imgBox :global(.card-edit-block) {
    right: 0px;
}

.imgBox :global(.card-edit-block .edit-box) {
    width: 30px;
    height: 30px;
}

.imgBox :global(.card-edit-block .edit-box svg) {
    position: relative;
    top: 1px;
    left: 0px;
}

.userImg {
    width: 180px;
    height: 180px;
    object-fit: cover;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}

.ci_label {
    cursor: pointer;
    position: relative;
}

/* Mobile devices small screen */

.iconBox {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--purple);
    color: var(--bluepastel);
    width: 50px;
    height: 50px;
    border-radius: 50%;
    position: absolute;
    top: 0;
    right: 0;
}

@media (max-width: 380px) {}

/* Mobile devices large screen */

@media (max-width: 575.98px) {}

/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
    .imgBox {
        width: 120px;
        height: 120px;
    }

    .userImg {
        width: 120px;
        height: 120px;
        object-fit: cover;
    }
}

/* Extra large devices (large desktops, 1200px and up) */

@media (min-width: 1200px) and (max-width: 1399.98px) {}

@media (min-width: 1400px) and (max-width: 1599.98px) {}

@media (min-width: 1600px) and (max-width: 1799.98px) {}