.function-tools-wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    position: relative;
    height: 5em;
}

.function-tools-sec {
    position: fixed;
    height: 36px;
    z-index: 8;
    background-color: #ffffff;
    box-shadow: 0px 0px 12px #00000052;
    border-radius: 6px;
    margin: 0 auto;
    width: 70%;
}

.function-tools-box {
    display: flex;
    justify-content: space-between;
}

.function-tools-box .icon-box {
    padding: 10px;
}

.zoom-in {
    transform: scale(1);
}

.zoom-out {
    transform: scale(0.8);
    transform-origin: top;
}

.function-tools-wrapper .left-icons {
    padding: 4px 6px;
}

.function-tools-wrapper .right-icons {
    padding: 4px 6px;
}

.tool-icon {
    width: 21px;
    height: 21px;
    vertical-align: text-top;
    cursor: pointer;
}

@media (max-width: 575.98px) {
    .function-tools-wrapper {
        display: flex;
        justify-content: flex-end;
        flex-wrap: wrap;
        position: relative;
        align-items: flex-start;
        height: auto;
        /* background-color: transparent; */
        padding: 0 !important;
    }
    .function-tools-wrapper .function-tools-sec {
        position: fixed;
        z-index: 1000000;
        background-color: #ffffff;
        box-shadow: 0px 0px 12px #00000052;
        border-radius: 6px;
        margin: 0 auto;
        width: auto;
        top: 50%;
        right: 20px;
        transform: translateY(-50%);
        height: 60%;

    }
    .function-tools-wrapper .function-tools-box {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
    }
    .function-tools-wrapper .function-tools-option-box, .function-tools-option {
        display: flex;
        flex-direction: column;
    }
    .function-tools-wrapper .function-tool-toggler-bar {
        position: fixed;
        top: 50%;
        right: 0px;
        transform: translateY(-50%);
        width: 10px;
        z-index: 10;
        height: 130px;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
    }
}