    90deg,
    var(--themePrimaryColor) 0%,
    var(--themeSecondaryColor) 100%
  );
   box-shadow: 0px 3px 10px #00000029; 
  min-height: 52px;
} 
*/

.theme-navbar .navbar-nav{
  flex-wrap: wrap;
}

.theme-navbar .navbar-brand {
  color: var(--themeTextColor);
}

.theme-navbar .navbar-brand.navbar-identity h2 {
  max-width: 180px;
}

.theme-navbar .navbar-nav .nav-link {
  color: var(--themeTextColor);
  font-size: 1em;
  padding-left: 1.0625em;
  padding-right: 1.0625em;
}
.theme-header-section .theme-navbar .container .navbar-collapse .nav-item .btn{
  padding-left: 1.0625em;
}
.theme-navbar .navbar-nav .nav-link{
  padding-left: 0em;
}
.theme-navbar .navbar-action-sec .nav-link.btn-primary {
  font-size: 1.125em;
  color: #ffffff;
}
#dropdown-basic .dropdown-btn-text{
  font-size: 80%;
}
/* theme 4 black theme */
.theme4 .theme-navbar.navbar-light .navbar-nav .nav-link.active,
.theme4 .theme-navbar.navbar-light .navbar-nav .show > .nav-link {
  color: var(--themeTextColor);
}
.theme4 .theme-navbar a:focus,
.theme4 .theme-navbar a::after {
  color: var(--themeTextColor) !important;
  outline: none;
}
/* theme 4 black theme end*/

@media (max-width: 575.98px) {
  .theme-navbar {
    box-shadow: none;
  }

  .navbar-action-sec {
    display: none;
  }

  .theme-header-section.influencer .card-edit-block{
    display: flex;
  }
}

@media (min-width: 1200px) and (max-width: 1399.99px) {
  .theme-navbar .navbar-nav .nav-link {
    font-size: 1em;
  }
}

@media (max-width: 1024px) {
  .theme-navbar .navbar-nav .nav-link {
    font-size: 14px;
  }
}
