

.card {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 0 25px;
    border-bottom: 1px solid #E7E7E7;
    padding-bottom: 25px;
    transition: .3s;
}
/* .card:hover {
    background: #f4f4f5;
    transition: .3s;
} */

.cardHeader {
    display: flex;
    align-items: center;
}
.cardimg {
    margin-right: 15px;
}
.cardimg .image {
    width: 85px;
    height: 85px;
    object-fit: cover;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}
.cardContent {

}
.cardContent h2 {
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    color: #343434;
    font-family: var(--primaryFontBold);
    margin-bottom: 0px;
}
.cardContent p {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 32px;
    color: #5E606A;
    font-family: var(--primaryFontMedium);
    margin-bottom: 8px;
}
.cardTags {
    display: flex;
    margin-top: 18px;
}
.cardTags p {
    margin: 0 20px 0 0;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    color: #5E606A;
    border: 3px solid #BCBCBC;
    padding: 9px 15px 5px;
    text-transform: capitalize;
    border-radius: 30px;
}
.cardTags p svg {
    width: 20px;
    height: 20px;
    margin-right: 6px;
    position: relative;
    top: -2px;
}

.cardBtn h3 {
    background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    font-family: var(--primaryFontBold);
    font-size: 21px;
    padding-right: 19px;
}
.cardBtn button {
    padding: 0;
    background: none;
    border: none;
    color: #9461FA;
    font-family: var(--primaryFontBold);
    font-size: 22px;
    transition: .3s;
}
.cardBtn button:hover {
    background: none;
    color: #9461FA;
}
.cardBtn button:focus {
    background: none !important;
    color: #9461FA !important;
    box-shadow: none !important;
    outline: none!important;
}
/* .cardBtn button:hover svg{
    fill: #FF7B32;
} */
.cardBtn button svg {
    width: 19px;
    margin-left: 5px;
}

@media (max-width: 575.98px) {
    .card {
        flex-direction: column;
    }
    .cardimg img {
        width: 48px;
        height: 48px;
    }
    .cardContent h2 {
        font-size: 18px;
    }
    .cardContent p {
        font-size: 14px;
    }
    .cardTags p {
        margin: 0 8px 0 0;
        font-size: 11px;
        line-height: 13px;
        padding: 7px 12px 5px;
    }
    .cardTags p svg {
        width: 14px;
        height: 14px;
        margin-right: 0;
    }
    .cardBtn {
        width: 100%;
        margin-top: 12px;
    }
    .cardBtn button {
        border: 2px solid #9461FA;
        width: 100%;
        border-radius: 50px;
        font-size: 17px;
        padding: 6px 0 5px;
    }
    .cardBtn button:hover {
        color: #fff !important;
        background: #9461FA !important;
        border: 2px solid #9461FA;
    }
    .cardBtn button:focus {
        color: #fff !important;
        background: #9461FA !important;
        border: 2px solid #9461FA !important;
    }
    .cardBtn button svg {
        display: none;
    }
    .cardBtn h3 {
        background: linear-gradient(289.79deg, #FF7439 23.59%, #FFA10D 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    border: 2px solid #FF7439;
        width: 100%;
        color: #fff !important;
        text-align: center;
        border-radius: 50px;
        font-size: 17px;
        padding: 8px 0 7px;
    }
}