


.formInput label{
    font-family: var(--primaryFontBold);
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #343434;
}
.formInput input {
    background: #FBFCFF;
    border: 1px solid #E7E7E7;
    border-radius: 4px !important;
    height: 48px;
    font-size: 15px;
    font-weight: 500;
    font-family: var(--primaryFontMedium);
    color: #2e2929;
    margin-bottom: 20px;
}
.formInput button {
    background: #FBFCFF !important;
    border: 1px solid #E7E7E7 !important;
    border-radius: 4px !important;
    height: 48px;
    font-size: 15px !important;
    font-weight: 500 !important;
    font-family: var(--primaryFontMedium);
    margin-top: 7px;
    color: #2e2929;
    
}

.formInput button:after {
    top: 30px !important;
}
.formInput :global(button):focus{
    box-shadow: none !important;
}
.formInput button:focus {
    border: 1px solid #E7E7E7 !important;
    box-shadow: none !important;
    outline: none !important;
}
.formInput input:focus {
    background: #FBFCFF !important;
    border: 1px solid #E7E7E7 !important;
}

.formInput input::placeholder{
    font-size: 15px;
    font-weight: 400;
    color: #7f818b;
}
.formInput textarea{
    background: #FBFCFF;
    border: 1px solid #E7E7E7;
    border-radius: 4px !important;
    height: 120px;
    font-size: 15px;
    font-weight: 500;
    font-family: var(--primaryFontMedium);
    color: #2e2929
}

.formTextarea {
    background: #FBFCFF;
    border: 1px solid #E7E7E7;
    border-radius: 4px !important;
    height: 120px;
    font-size: 15px;
    font-weight: 500;
    font-family: var(--primaryFontMedium);
    color: #2e2929;
    margin-top: 9px;
}

.formTextarea:focus {
    background: #FBFCFF !important;
    border: 1px solid #E7E7E7 !important;
}

.applyBtn {
    text-align: right;
    border-top: 1px solid #e3e3e3;
    margin-top: 30px;
    padding-top: 26px;
}

.applyBtn button {
    font-size: 18px;
    font-family: var(--primaryFontMedium);
    font-weight: 400;
    border-radius: 50px;
    padding: 8px 50px;
}

@media (max-width: 575.98px) { 
    .formSelect {
        margin-bottom: 15px;
    }
}