.deviceHistoryContainer .title{
    font-family: var(--primaryFontBold);
    font-size: 16px;
    margin-bottom: 0px;
}

.mediaObject{
    display: flex;
    justify-content: flex-start;
    border-bottom: 1px solid #E7E7E7;
    padding-top: 12px;
    padding-bottom: 12px;
}

.deviceHistoryContainer .mediaObject:last-child{
    border-bottom: none;
}

.mediaObject .imgBox{
    background-color: #ffffff;
    width: 48px;
    height: 48px;
    padding: 13px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    margin-right: 16px;
}

.mediaObjectBody .heading,
.mediaObjectBody .text{
    font-family: var(--primaryFontMedium);
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 4px;
}

.mediaObjectBody .text{
    color: #858585;
    margin-bottom: 0px;
}

@media (max-width: 575.98px){
    .deviceHistoryContainer{
        margin-bottom: 16px;
    }
}