.footer-section {
  background-color: #182818;
  color: #ffffff;
}

.footer-top {
  padding-top: 100px;
  padding-bottom: 70px;
}

.footer-section .footer-top .title {
  font-family: var(--themePrimaryFont);
  letter-spacing: 1px;
  margin-bottom: 1.25em;
  font-weight: 600;
}

.footer-bottom-sec {
  padding: 40px 0px 20px 0px;
}

.footer-bottom-sec .social-link a.social-text {
  color: #ffffff;
}

.navigate-list ul li {
  cursor: pointer;
}
.footer-section li a {
  color: #fff;
  text-decoration: none;
}
.footer-section li a:hover {
  color: inherit;
}
.social-link .social-text {
  text-decoration: none;
}

.theme-footer-section {
  padding-top: 70px;
  padding-bottom: 163px;
}

.theme-footer-section .footer-logo {
  width: 266px;
}

/* Extra small devices (potrait phones, less than 380px  */
@media (max-width: 380px) {
  .theme-footer-section{
    padding-top: 50px !important;
    padding-bottom: 50px !important;
  }
}

/* Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .rights-text {
    margin-bottom: 40px;
  }

  .theme-footer-section {
    padding-top: 90px;
    padding-bottom: 90px;
  }
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .theme-footer-section {
    padding-bottom: 100px;
    padding-top: 35px;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px)  and (max-width: 1399.98px)  {
  .theme-footer-section {
  padding-top: 0px;
  padding-bottom: 80px;
}
}

@media (min-width: 1400px) and (max-width: 1599.98px) {
 .theme-footer-section {
  padding-top: 0px;
  padding-bottom: 90px;
}
}
@media (min-width: 1600px) and (max-width: 1799.98px) {

