.main-navbar{
    background-color: #ffffff;
    padding: 0;
    z-index: 1000 !important;
    position: fixed;
    width: 100%;
    top: 0;
}

.main-navbar .landing-logo{
    max-width: 100px;
}
.discover_page .discovery_tabs {
    margin: 0 18px;
}

.landing-login-btn{
    border-color: var(--primaryColor);
    padding-left: 10px;
    padding-right: 10px;
    background-color: transparent !important;
    color: var(--primaryColor) !important;
    font-family: var(--primaryFont) !important;
}

.landing-login-btn, .landing-register-btn{
    width: max-content;
    padding-bottom: 4px;
    padding-top: 4px;
}

.discoveryHeader .container {
    max-width: 100%;
}
.discoveryHeader .landing-register-btn {
    background: linear-gradient(289.79deg, #ffa847 23.59%, #9461fa 63.85%);
    color: #ffffff;
    border: none;
}

.discoveryHeader .landing-register-btn:hover {
    background: linear-gradient(289.79deg, #ffa847 23.59%, #9461fa 63.85%) !important;
    color: #ffffff;
    border: none;
}




@media (max-width: 575.98px){

    .discover_page .discovery_tabs {
        margin: 0 6px;
    }
    .main-navbar .navbar-nav .nav-link{
        padding-left: 1em;
    }

    .main-navbar .navbar-nav .nav-link .btn-primary{
        background-color: transparent;
        color: inherit;
        border: transparent;
        padding-left: 0px;
        padding-top: 0;
        padding-bottom: 0;
    }
}