

.proModal {
    text-align: center;
    padding: 30px 0;
}

.proModal h3 {
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    color: #343434;
    font-family: var(--primaryFontBold);
    width: 68%;
    margin: 12px auto;
}

.proModal h3 span{
    background: linear-gradient(289.79deg, #ffa847c9 23.59%, #9461fab8 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}
.proModal p{
    font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 24px;
text-align: center;
color: #858585;
}
.btns_modal {
    display: flex;
    align-items: center;
    justify-content: center;
}
.btns_modal a{
    text-decoration: none;
    background: #fff;
    margin-left: 12px;
    width: 197px;
    height: 46px;
    color: #343434;
    line-height: 44px;
    font-size: 16px;
    border-radius: 50px;
    border: 2px solid #5E606A;
    font-family: var(--primaryFontMedium);
}
.btns_modal a:last-child{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    border: none;
    color: #fff;
    line-height: 46px;
}
.crossBtn {
    background: none;
    border: none;
    padding: 0;
    position: absolute;
    right: 17px;
    top: 16px;
}
.crossBtn:hover {
    background: none;
    border: none;
}
.crossBtn:focus {
    background: none !important;
    border: none !important;
}


@media (max-width: 575.98px) { 
    .proModal h3 {
        width: 100%;
    }
    .proModal p {
        font-size: 16px;
    }
    .btns_modal {
        flex-direction: column;
    }
    .btns_modal a{ 
        margin-bottom: 10px;
        width: 100%;
        margin-left: 0;
    }
}