.backToHomeModal {
    font-size: 1em;
    height: 45px;
    background-color: var(--primaryColor);
    border-color: var(--primaryColor);
    color: #fff;
    font-family: var(--primaryFont);
    width: 200px;
    display: block;
    margin: auto;
    text-decoration: none;
    line-height: 44px;
    border-radius: 50px;
    transition: .3s;
    margin-top: 30px;
}
.backToHomeModal:hover {
    background-color: #553199;
    color: #fff;
    transition: .3s;
}
.bykea-thankyou{
    border: 1px solid #b7b7b7;
    position: fixed;
    width: 50%;
    height: 50%;
    border-radius: 5px;
    box-shadow: 0px 0px 20px #2e2e2e0f;
    padding-top: 40px !important;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}