.infoDetails{
    text-align: center;
}
.infoDetails h3{
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    color: #DC3646;
    font-family: var(--primaryFontBold);
    margin-bottom: 0;
    margin-top: 30px;
}
.infoDetails h4{
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    color: #343434;
    font-family: var(--primaryFontBold);
}
.infoDetails p{
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #858585;
}
.infoDetails p a{
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #5B3F91;
    font-family: var(--primaryFontBold);
    text-decoration: none;
}
.modalFooter{
    text-align: center;
    margin-top: 24px;
}
.modalFooter button{
    width: 232px;
    background: #fff;
    border-radius: 40px;
    border: 1px solid #343434;
    color: #343434;
}
.modalFooter button:hover{
    width: 232px;
    background: #343434;
    border-radius: 40px;
    border: 1px solid #343434;
    color: #fff;
}
.modalFooter button:focus{
    background: #fff;
    border-radius: 40px;
    border: 1px solid #343434;
    color: #343434;
}
.modal{
    padding: 25px 80px;
}