/* overlay styling for when bottom tab is active */
.overlay-tab-active {
  width: 100vw;
  height: 100vh;
  background: black;
  position: fixed;
  top: 0;
  z-index: 10;
  opacity: 0.3;
}

/* hotfix */
.overlay-tab-active {
  display: none;
}

/* overlay styling ends */
.introjs-tooltip .bottom {
  display: none !important;
}

/* mobile bottom tab section card styling */
.tab-option.active img {
  filter: brightness(0) invert();
}

.mobile-section-card {
  border-radius: 17px;
  border-width: 0.5px;
  border-color: #D7DCE1;
  margin-bottom: 1em;
}

.mobile-section-card.active {
  border: 1px solid var(--primaryColor);
  background-color: #5B3F910F;
}

.mobile-section-card .mobile-section-footer {
  background-color: transparent;
  border-top: 0px;
}

.mobile-section-body .card-section-body .section-body-header {
  font-size: 1em;
  font-family: var(--themePrimaryFontBold);
  margin-top: 15px;
  line-height: 17px;
}

.mobile-section-body .card-section-body .section-body-text {
  font-size: 0.875rem;
  font-family: var(--themePrimaryFontMedium);
}

.mobile-section-card .mobile-section-footer .card-next-arrow {
  width: 20px;
  float: right;
  padding-bottom: 10px;
}

.mobileTabAddtoSite {
  position: absolute;
  top: 7px;
  height: 40px;
  width: 40px;
  right: 10px;
}

.mobileTabAddtoSite img {
  width: 40px;
  height: 40px;
}

.mobileTabAddtoSite div {
  display: block !important;
}

/* card styling ends */

.bottom-tab {
  width: 100%;
  padding: 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  background-color: #fff;
  bottom: 0;
  height: 64px;
  border-top: 4px solid var(--primaryColor);
  z-index: 10;
}

.icon-section {
  position: block;
  overflow: hidden;
  /* min-width: 33%; */
}

.icon-box .icon-img {
  position: block;
  overflow: hidden;
  min-width: 33% !important;
  width: 33% !important;
}

.bottom-tab-content-box .bottom-tab-options {
  flex-wrap: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  border-bottom: 1px solid var(--borderColor);
}

.bottom-tab-content-box .bottom-tab-options::-webkit-scrollbar {
  display: none;
}

.bottom-tab-content-box .nav-link {
  padding: 0px;
  margin: 0.5rem 1rem;
  margin-right: 0;
}

.bottom-tab-content-box .nav-link:last-child {
  margin-right: 1em;
}

.bottom-tab-content-box .nav-link.active {
  border-bottom: 2px solid;
  font-weight: bold;
}

.bottom-tab-content-box .caption-box {
  padding: 16px 10px;
  border-bottom: 1px solid var(--borderColor);
}

.bottom-tab .tab-option {
  background-color: #ffffff;
  width: 100%;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.bottom-tab .tab-option.active {
  background-color: var(--primaryColor);
  width: 100%;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.bottom-tab .tab-option .icon-box {
  color: var(--primaryColor);
}

.bottom-tab .tab-option.active .icon-box {
  color: #ffffff;
  filter: brightness(0) invert(1);
}

.bottom-tab .tab-option .option-title {
  margin-bottom: 0px;
  color: inherit;
  font-size: 13px;
}

.bottom-tab .caption-box {
  padding-top: 13px;
  padding-bottom: 13px;
  border-bottom: 1px solid #d7dce1;
}

.caption-box .caption-box-header {
  font-family: var(--primaryFont);
  color: #1E1E1E;
  line-height: 17px;
  margin-bottom: 0;
}

.caption-box .caption-box-header>.caption-box-text {
  font-family: var(--primaryFontBold);
}

.variation_selection {
  overflow-y: scroll;
  height: calc(100% - 65px);
  /* padding-right: 1em; */
}

/* bottom tab content section */
.bottom-tab-content-box {
  height: calc(100vh - 160px);
  box-shadow: 0px -3px 6px #00000029;
  border: 1px solid #D7DCE1;
}

.bottom-tab-body .bottom-tab-options .nav-link {
  padding: 0;
  margin: 0.5rem 1rem;
  margin-left: 0;
}

.bottom-tab-content-box .section-toggle-container {
  position: relative;
  padding-top: 7px;
  height: calc(100% - 100px);
}

.bottom-tab-content-box .section-toggle-container.mobile_addSection{
  height: calc(100% - 0px);
}

.bottom-tab-content-box .section-toggle-box .section-title {
  font-size: 14px;
  font-family: var(--themePrimaryFontBold);
}

.bottom-tab-content-box .section-toggle-container.scroller {
  float: left;
  overflow-y: auto;
  overflow-x: hidden;
  width: -webkit-fill-available;
  width: 100%;
  background-color: var(--bs-white);
  padding: 10px 4px 5px 10px;
}



.variation_selection .single_variation {
  position: relative;
  border: 1px solid #d8dde1;
  border-radius: 9px;
  margin-bottom: 1em;
  overflow: hidden;
}

.variation_selection .single_variation.active {
  border: 2px solid var(--primaryColor);
}

.single_variation .guide-msg {
  display: inline-block;
  background-color: var(--primaryColor);
  color: #ffffff;
  position: absolute;
  top: 0px;
  right: 0px;
  padding: 1px 10px;
  border: 2px;
  font-size: 11px;
  /* border-top-right-radius: inherit; */
  border-bottom-left-radius: inherit;
}

.bottom-tab .icon-box .bottom-icon {
  font-size: 24px;
}

.bottom-tab .tab-option {
  display: flex;
}

.sortableHelper {
  z-index: 999999999;
}

.bottom-tab-content-box .bottom-tab-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: nowrap;
  padding: 15px;
  border-bottom: 1px solid var(--borderColor);
}

.bottom-tab-content-box .bottom-tab-body {
  padding: 15px;
  height: calc(100% - 121px);
}

.bottom-tab-content-box .bottom-tab-header .bottom-tab-title {
  font-family: var(--primaryFontMedium);
  font-size: 1.1875em;
  margin-bottom: 0;
}

/* temporary disabled until info page not design  */
/* .bottom-tab-content-box .bottom-tab-header .icon-section .icon-box:first-child {
  margin-right: 18px;
} */

.bottom-tab-content-box .bottom-tab-header .icon-section .icon-box .icon {
  font-size: 14px;
}

.bottom-tab-content-box .bottom-tab-body .bottom-tab-sub-title-box {
  padding: 5px 5px;
  border-bottom: 1px solid var(--borderColor);
  width: 100%;
}

.bottom-tab-content-box .bottom-tab-body .bottom-tab-sub-title-box .bottom-tab-sub-title {
  font-family: var(--primaryFontMedium);
  font-size: 16px;
  color: var(--black);
}

.bottom-tab-body {
  height: calc(100% - 55px);
}

.section-tab,
.tab-pane {
  height: 100%;
}

.bottom-tab-content-box .bottom-tab-body .social-icons-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  border-bottom: 1px solid var(--borderColor);
}

.bottom-tab-content-box .bottom-tab-body .social-icons-wrapper .icon-box {
  padding: 18px;
  width: 20%;
}

.bottom-tab-content-box .bottom-tab-body .social-icons-wrapper .icon-box .icon-img {
  width: 43px !important;
  height: 43px;
}

.bottom-tab-content-box .bottom-tab-body .social-icons-wrapper .icon-box .icon-text {
  font-size: 12px;
}

.section_toggler {
  border-bottom: 0.5px solid #c9c9c9;
}

.section-tab .right-section {
  /* width: 50%; */
  border-left: 0.5px solid #c9c9c9;
  overflow-y: auto;
  height: calc(100% - 25px);
}

.bottom-tab-content-box .tab-content {
  border-top: 0.5px solid #dee2e6 !important;
  height: calc(100% - 31px);
}

.bottom-tab-body .theme-selection-card .sub-title {
  font-size: 16px;
}

/* to be deleted soon this line applies underline globally in active tag */
/* .nav-link.active {
    border-bottom: 2px solid;
    font-weight: bold;
} */
.bottom-tab-body .nav-link.active {
  border-bottom: 2px solid;
  font-weight: bold;
}

.scroller-y {
  float: left;
  overflow-y: scroll;
}

/* Manage Section */
.manage-section-button {
  color: #8e8e8e;
  font-family: var(--primaryFontMedium);
  opacity: 1;
  visibility: visible;
  display: flex;
  justify-content: left;
  background-color: #fff;
  border: 1px solid #d7dce1 !important;
}

.manage-section-button .msg-icon {
  margin-right: 5px;
}

.manage-section-button .icon-box {
  padding-left: 0.6em;
  border-left: 1px solid var(--primaryColor);
}

.manage-section-button.lock {
  color: #8e8e8e;
}

.manage-section-button .icon {
  width: 18px;
  height: 18px;
}

.manage-caption-section {
  border-bottom: 1px solid #d7dce1;
}

.manage-caption-section .caption-header {
  font-family: var(--primaryFontMedium);
}

.manage-caption-section .caption-header .caption-header-text {
  font-family: var(--primaryFontBold);
}

.manage-section-button.selected-section-button:active,
.manage-section-button.selected-section-button:focus,
.manage-section-button.selected-section-button:hover {
  border: 1px solid #5b3f91 !important;
  color: #5b3f91 !important;
  background-color: #f5f3f9 !important;
}

@media (max-width: 380px) {
  .bottom-tab-body .bottom-tab-options .nav-link {
    padding: 0.5em 0.7em;
    font-size: 0.9em;
  }

  .manage-section-button {
    justify-content: space-between !important;
  }

  .selected-section-button:active .selected-section-button:focus {
    border: 1px solid #5b3f91 !important;
    color: #f5f3f9 !important;
  }

  .caption-box .banner-btn,
  .right-section .banner-btn {
    font-size: 12px !important;

  }

  .caption-box .caption-box-header {
    font-size: 12px !important;
  }

  .mobile-section-body .card-section-body .section-body-header {
    margin-top: 8px;
  }
}

@media (max-width: 991px) {
  .manage-section-button {
    justify-content: space-between;
  }

  /* .bottom-tab-content-box {
    overflow-y: scroll;
  } */
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .bottom-tab-content-box .bottom-tab-body .social-icons-wrapper .icon-box{
    width: 11%
  }
}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .bottom-tab-content-box .bottom-tab-body .social-icons-wrapper .icon-box {
    width: 10%;
  }
}

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {

}

/* large mobile view */
@media (max-width: 575.98px) {

  .d_flex_property{
    display: block;
  }
  .d_flex_property .right-section.text-end {
    margin-top: -8px;
    margin-bottom: 5px;
}

  .managed_list {
    height: calc(100vh - 322px) !important;
    overflow-y: scroll;
  }

  .manage-section-button {
    justify-content: space-between !important;
  }

  .selected-section-button:active .selected-section-button:focus {
    border: 1px solid #5b3f91 !important;
    color: #f5f3f9 !important;
  }

  .caption-box .banner-btn,
  .right-section .banner-btn {
    font-size: 14px;
    line-height: 16px;
  }

  .card-section-body .section-body-header>h4 {
    font-size: 1em;
  }

  .card-section-body .section-body-text>p {
    font-size: 14px;
  }

  .mobile-section-body.card-body {
    padding: 12px !important;
  }
}