.horizontalCard{
    position: relative;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    background-color: var(--themePrimaryColor);
    border: none;
    height: 100%;
}

.horizontalCard .cardBody{
    display: flex;
}

.horizontalCard .cardBody .imgBox{
    margin-right: 10px;
}

.horizontalCard .cardBody .contentBox{
    flex: 1;
}