.sidebar-navbar.nav-pills .show>.nav-link{
    background-color: var(--purple) !important;
  }
  
  .sidebar-navbar .sidebarDropdown {
    position: relative;
  }
  .sidebarDropdown .dropdownMobile{
    right: 40px!important;
    top: 55px!important;
  }
  .sidebarDropdown .dropdown-content {
    display: none;
    position: absolute;
    right: -190px;
    top: 0px;
    background-color: white;
    width: 210px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}
  .sidebarDropdown .dropdown-content li{
    margin-bottom: 0px;
  }
  .sidebarDropdown .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  
  .dropdown-content a:hover {background-color: #f1f1f1;}
  .sidebarDropdown:hover .dropdown-content {display: block;}