.informationBox {
    text-align: center;
    margin-bottom: 50px;
}

:global(.discoverTabs_area .formStep),
:global(.discoverTabs_area .formStep.active ~ .formStep) {
    /* opacity: 0.7; */
    transform: scale(1);
    transition: top 0.5s ease 0s;
    /* top: 0px; */
}
:global(.discoverTabs_area .formStep.active ~ .formStep) {
    /* opacity: 0.7; */
}

:global(.discoverTabs_area .formStep:hover),
:global(.discoverTabs_area .formStep.active ~ .formStep:hover) {
    z-index: 100;
    position: relative;
    /* opacity: 1; */
    /* top: 0px; */
}


@keyframes ani_input {
    100% {
        /* transform: scale(1); */
        top: 20px;
    }
}

:global(.discoverTabs_area .formStep.active) {
    /* opacity: 1; */
    /* animation: ani_input 0.5s forwards; */
    position: relative;
    z-index: 1;
    transition: top 1s ease 0s;
}


.informationBox .title {
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
    background: linear-gradient(289.79deg, #ffa847 23.59%, #9461fa 63.85%);
    background-clip: border-box;
    background-clip: initial;
    -webkit-background-clip: text;
    background-clip: text;
    font-family: var(--primaryFontBold);
    font-size: 40px;
    margin-bottom: 20px;
}

.informationBox .text {
    font-family: var(--primaryFontBold);
    font-size: 16px;
    color: #343434;
    margin-bottom: 20px;
}

.informationBox .btnPrimary {
    background: linear-gradient(289.79deg, #ffa847 23.59%, #9461fa 63.85%);
    color: #ffffff;
    font-family: var(--primaryFontBold);
    font-size: 16px;
    padding: 12px 16px;
    border: none;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    box-shadow: 0px 4px 40px rgba(148, 97, 250, 0.2);
}


.informationBox .btnPrimary .iconBox {
    margin-right: 5px;
}

.step1Img {
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: fixed;
    right: 0;
    overflow: hidden;
}

.colTwoLogo {
    display: none;
}

.firstCol {
    position: relative;
    height: 100%;
}

.tabsArea {
    width: 100%;
    padding: 10px 20% 40px 20%;
}

.btmHeading {
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
    width: 100%;
    bottom: 30px;
    opacity: .4;
}

.btmHeading h2 {
    color: #5E606A;
    font-size: 32px;
    font-family: var(--primaryFontBold);
}

.tabsBtn {
    margin-top: 28px;
    position: relative;
    z-index: 10;
}

.backTabs {
    width: 83px;
    height: 36px;
    border-radius: 40px;
    font-size: 15px;
    line-height: 21px;
}

.btnNext {
    position: absolute;
    right: 0;
    background-color: #442f6e !important;
    border-color: #CBABE3;
}

.backBtn {
    color: #7250B2 !important;
    border-color: #7250B2 !important;
    background: #fff !important;
}

.selectedTagsSection {
    margin-top: 10px;
    padding-left: 12px;
    margin-bottom: 10px;
    text-align: center;
}

.selectedTagsSection :global(.btn) {
    margin-right: 12px;
    font-size: 15px;
    min-width: 86px;
    padding: 2px 22px;
    margin-bottom: 15px;
    position: relative;
}


@media (max-width: 1450px) {
    .tabsArea {
        padding: 75px 30px 25px;
        height: 100vh;
    }
}

@media (max-width: 1366px) {
    .tabsBtn {
        padding-bottom: 35px !important;
    }
}


@media (max-width: 1100px) {

    .firstCol {
        width: 100%;
        top: 400px;
    }

    .rowFlexSlide {
        display: flex;
        flex-direction: column-reverse;
    }

    .step1Img {
        height: 355px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        overflow: hidden;
        top: 0;
    }

    .btmHeading {
        position: relative;
        bottom: -120px !important;
        text-align: center;
        width: 100%;
        bottom: 30px;
        opacity: .4;
    }

    .tabsArea {
        position: relative;
        top: 0 !important;
        transform: translate(0px, 0px);
        width: 100%;
        left: 0%;
        padding-bottom: 50px;
    }
}

@media (max-width: 1098px) {
    .colTwoLogo {
        display: block !important;
        background: transparent;
    }

    .logo {
        display: none;
    }
}

@media (max-width: 800px) {
    .step1Img {
        position: relative;
    }

    .tabsRightImg :global(.animate1) {
        top: 55px;
    }

    .firstCol {
        top: auto;
    }

    .tabsArea {
        padding: 25px 30px 25px;
    }

    .selectedTagsSection {
        padding-left: 2px;
    }

    .selectedTagsSection .btn {
        font-size: 14px;
    }
}

@media (max-width: 575.98px) {

    .logo {
        margin: 0;
    }

    .logo img {
        width: 100px;
    }

    .informationBox .title{
        font-size: 24px;
        margin-bottom: 12px;
    }

    .informationBox .btnPrimary{
        padding: 8px 16px;
    }

    .btmHeading {
        position: relative;
        bottom: -120px !important;
        text-align: center;
        width: 100%;
        bottom: 30px;
        opacity: .4;
    }

    .tabsBtn {
        margin-top: 14px;
    }

    .step1Img {
        height: 130px;
        display: none;
    }

    .tabsArea {
        padding: 25px 0px;
    }
}