.separator {
    width: 100%;
    height: 1px;
    border-bottom: 1px solid var(--themeTextColor);
    margin-top: 2em;
    margin-bottom: 1em;
}

.theme-general-card.experience-default-card .card-title {
    text-align: left;
    font-family: var(--themePrimaryFont);
    font-size: 1.5em;
}

.theme-general-card.experience-default-card .main-title {
    text-align: left;
    font-family: var(--themePrimaryFontMedium);
    font-size: 1.75em;
    margin-bottom: 13px;
}

.theme-general-card.experience-default-card .sub-text {
    text-align: left;
    font-family: var(--themePrimaryFont) !important;
    font-size: 1.5em;
}

.theme-general-card.experience-default-card .card-text {
    font-family: var(--themePrimaryFont) !important;
    font-size: 1rem;
    line-height: 37px;
    text-align: left !important;
}

.theme-general-card.experience-default-card {
    min-height: 428px;
}

@media (max-width: 575.98px) {

    /* style 5 experience card */
    #experience.style_5 .theme-general-card{
        min-height: 385px;
    }
    #experience.style_5 .editable-content-box .h4 {
        margin-bottom: 20px;
        font-size: 21px;
    }

    .theme-general-card.experience-default-card .sub-text{
        font-size: 1em;
    }

    .theme-general-card.experience-default-card .separator{
        margin-top: 1em;
    }
}