.inputFileGroup {
    position: relative;
    border: 1px solid;
    padding: 30px;
    border-radius: 8px;
    margin: 10px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.iconUpload {
    height: 44px;
    width: 44px;
    background: #e6dffa;
    display: inline-block;
    line-height: 44px;
    text-align: center;
    border-radius: 50px;
    margin-bottom: 14px;
}

.inputFile {
    position: absolute;
    opacity: 0;
    height: 100%;
}

@media (max-width: 575.98px){
    .inputFileGroup{
        text-align: center;
        padding: 24px 14px;
    }
}