.PricingCardPlanCard{
    width: 400px;
    box-shadow: 0px 2px 6px rgba(31, 61, 84, 0.2);
    border-radius: 18px;
}
.priceBody{
    padding: 30px 35px;
}
.svgVector_Free_one{
    position: absolute;
    top: -38px;
    left: -46px;
    z-index: -1;
}
.svgVector_Lifetime{
    bottom: -38px;
    right: -46px;
    position: absolute;
    z-index: -1;
}
.starSvg_half{
    position: absolute;
    right: 2px;
    transform: rotate(90deg);
    top: 369px;
    z-index: -1;
}
.price_heading_tag{
    height: 120px;
    position: relative;
}
.price_heading_tag h3{
    font-size: 32px;
    font-weight: bold;
}
.price_heading_tag h3 span{
    font-weight: bold;
    font-size: 22px;
    color: #5E606A;
}
.price_heading_tag small{
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    display: block;
    width: 240px;
    position: absolute;
    bottom: 18px;
}
.freepkg h2{
    font-weight: bold;
    font-size: 42px;
    color: var(--orange);
    font-family: var(--primaryFontBold);
}
.propkg h2{
    font-weight: bold;
    font-size: 42px;
    color: var(--purple);
    font-family: var(--primaryFontBold);
}
.freepkg small{
    color: #5E606A;
}
.propkg small{
    color: var(--orange);
}
.getPricePlan{
    padding: 11px 16px 16px;
    gap: 10px;
    width: 177.98px;
    height: 44.65px;
    border-radius: 25px;
}
.getPriceDetails {
    padding: 40px 0px 30px;
    color: #5E606A;
    font-size: 21px;
}
.freepkg .getPricePlan{
    
}
.propkg .getPricePlan{
    background: var(--orange);
    border: none;
}
.listDecs{
    padding-left: 0;
}
.listDecs li p{
    padding-left: 10px;
    color: #5E606A;
    font-size: 18px;
}
.listDecs li svg{
    border: 2px solid #F8AA54;
    width: 20px !important;
    height: 20px;
    padding: 3px;
    border-radius: 10px;
    color: #F8AA54;
}
.starIcon{
    position: absolute;
    top: -75px;
    right: 107px;
    z-index: -1;
}
.starCircle{
    position: absolute;
    top: -40px;
    right: 80px;
    z-index: -10;
}
.tagNameLabel{
    position: absolute;
    right: -20px;
}
.tagNameLabel span{
    font-size: 21px;
    color: #fff;
    position: absolute;
    right: 53px;
    top: 9px;
}
.startTag{
    position: absolute;
    right: 9px;
    top: 9px;
}
.FreePriceIcon{
    position: absolute;
    z-index: -1;
    right: 85px;
}
.FreePriceIcon .iconOne{
    position: absolute;
    right: 65px;
    bottom: -11px;
}
.FreePriceIcon .iconTwo{
    position: relative;
    right: 15px;
}
.ProPriceIcon{
    position: absolute;
    bottom: -53px;
    right: -35px;
    z-index: -1;
}
.ProPriceIcon .iconOne{
    width: 157px;
    height: 150px;
    border: 6px dashed #AC82FF;
    border-radius: 118.231px;
    position: absolute;
    right: -19px;
    bottom: 15px;
}
.ProPriceIcon .iconTwo{
    width: 157px;
    height: 150px;
    background: #5B3F91;
    border-radius: 118.231px;
}

.startTag{
    animation: startTag 7s linear infinite; 
  }
  @keyframes startTag{
    to{ transform: rotate(-180deg); }
  }


  .starIcon{
    animation: starIcon 6s linear infinite; 
  }
  @keyframes starIcon{
    to{ transform: rotate(360deg); }
  }


  .star{
    animation: star 6s linear infinite; 
  }
  @keyframes star{
    to{ transform: rotate(360deg); }
  }

  .circleBar{
    animation: circleBar 7s linear infinite; 
  }
  @keyframes circleBar{
    to{ transform: rotate(-180deg); }
  }
  


@media (max-width: 575.99px) {
    .ProPriceIcon{
        right: 60px;
    }
    .PricingCardPlanCard{
        width: 340px;
    }
    .propkg small{
        font-size: 19px;
        line-height: 18px;
    }
    .getPriceDetails{
        font-size: 17px;
    }
    .listDecs li p{
        font-size: 15px;
    }
    .listDecs li svg{
        width: 16px !important;
        height: 16px;
        position: relative;
        top: 2px;
    }
}