
.cardWrapper {
    border-radius: 10px;
    box-shadow: rgb(100 100 111 / 20%) 0px 7px 29px 0px;
    margin-bottom: 40px;
    transition: .3s;
    overflow: hidden;
    filter: drop-shadow(0px 3.56388px 35.6388px rgba(148, 97, 250, 0.2));
}
.cardWrapper:hover {
    box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 15px 50px -20px, rgba(0, 0, 0, 0.3) 0px 20px 60px -30px;
    cursor: pointer;
    transition: .3s;
}
.card {
    border-radius: 7px;
    overflow: hidden;
    border-image: linear-gradient(45deg, #ffa84759, #9461fa69) 1;
    border-style: solid;
    border-width: 4px;
}
.sub_heading {
    font-family: var(--primaryFontBold);
    color: #282828;
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    text-transform: capitalize;
}
.pkg_badge {
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    border-radius: 17.8194px;
    width: fit-content;
    padding: 5px 16px 5px;
    font-style: inherit !important;
    text-transform: capitalize;
    color: #fff !important;
    font-size: 14px !important;
    font-weight: 600 !important;
    margin-bottom: 10px !important;
    font-family: var(--primaryFont) !important;
}
.btnLink {
    text-decoration: none;
}
.pkg_badge_student {
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    width: fit-content;
    padding: 2px;
    border-radius: 50px;
    font-style: inherit !important;
    margin-bottom: 7px !important;
}
.pkg_badge_student span{
    background: #fff;
    border-radius: 50px;
    display: block;
    color: #9863F4;
    padding: 5px 16px 5px;
    text-transform: capitalize;
    font-size: 14px !important;
    font-weight: 600 !important;
    font-family: var(--primaryFont) !important;
}

.innerCard {
    padding: 20px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}
.header {
    display: flex;
}
.imgDiv {
    margin-right: 12px;
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.imgDiv img {
    width: 85px;
    height: 85px;
    border-radius: 100px;
}
.imgDiv span {
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    border-radius: 17.8194px;
    color: #fff;
    padding: 3px 14px;
    font-size: 13px;
    font-weight: 700;
    margin: -16px auto;
}
.headings {

}
.headings h3 {
    color: #343434;
    font-style: normal;
    font-weight: 700;
    font-size: 17.8194px;
    font-family: var(--primaryFontBold);
    margin-bottom: 5px;
}

.headings .desc {
    font-family: 'Lato';
    font-style: italic;
    font-weight: 500;
    font-size: 16.4736px;
    line-height: 18px;
    margin-bottom: 0;
    color: #343434;
    min-height: 55px;
    max-height: 55px;
    overflow: hidden;
}

.tags {
    display: flex;
    margin-top: 10px;
}

.tags p {
    color: #5E606A;
    font-weight: 700;
    background: #fff;
    margin-right: 15px;
    padding: 4px 13px;
    border-radius: 50px;
    text-align: center;
    font-size: 14px;
    margin-bottom: 0;
}

.tags p:last-child {
    margin-right: 0;
 }
.imageWrapper .imageBox {
    display: inline-block;
    border-bottom-left-radius: 7px;
    border-bottom-right-radius: 7px;
    width: 33.33%;
    height: 131px;
}
.imageWrapper .imageBox > img{
    width: 100%;
    height: 100%;
}
.imageWrapper .imageBox > img.image{
    border-right: 1px solid #5E606A;
    object-fit: cover;
}
.imageWrapper .imageBox:first-child > img.image{
    border-bottom-left-radius: inherit;
}
.imageWrapper .imageBox:last-child > img.image{
    border-bottom-right-radius: inherit;
    border: none;
}


@media (max-width: 575.98px) {
    .images img{
        width: 120px;
    }
    .pkg_badge {
        padding: 4px 13px 3px;
        font-size: 12px !important;
    }
}