:global(.Toastify__toast) {
    background-color: rgba(255, 255, 255, 0.5);
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
    margin-bottom: 0px;

    border: 1px solid #E7E7E7;
    border-radius: 8px 0px 0 0;
    margin-bottom: 10px;
}

:global(.Toastify__toast:last-child) {
    margin-bottom: 0px;
}

:global(.Toastify__toast-container) {
    min-width: 600px;
    top: auto;
    bottom: -4px;
    right: -4px;
}

@media (max-width: 600px ) {
    :global(.Toastify__toast-container) {
        min-width: 100%;
    }
}


:global(.Toastify__progress-bar) {
    opacity: 0 !important;
}

.title {
    display: flex;
    font-weight: 400;
    font-size: 18px;
}

:global(.success) .title {
    color: #169F0A;
}

:global(.warn) .title {
    color: #F07C26;
}

:global(.error) .title {
    color: #DC3646;
}


.icon {
    margin-right: 10px;
}

.desc {
    padding-left: 30px;
}

.text {
    margin-bottom: 0px;
}