/* Navbar Global */

.no-toggle-icon::after {
  display: none;
}

.navbar-light .navbar-toggler {
  border-color: transparent;
}

.navbar-toggler:focus {
  text-decoration: none;
  outline: 0;
  box-shadow: none;
}

.text-primary{
  color:#5b3f91!important;
}
/* .fixed-top{
  position: fixed;
  width: 100%;
} */
.theme-navbar.navbar-light .navbar-nav .nav-link:focus,
.theme-navbar.navbar-light .navbar-nav .nav-link:hover {
  color: var(--themeTertiaryColor);
}

.navbar-identity.navbar-brand:hover {
  color: #ffffff;
}

/* Navbar Global end */

/* Navbar toggle button */
.hamburger {
  display: none;
}

.hamburger {
  position: absolute;
  cursor: pointer;
  right: 7px;
  top: 27px;
  transform: translate(-5%, -50%);
  z-index: 2;
}

.hamburger .line {
  width: 24px;
  height: 3px;
  background: var(--themeTextColor);
  margin: 4.95px;
  transition: all 0.5s ease;
}

.navbar-toggler.collapsed .hamburger .line {
  height: 3px;
}

.navbar-toggler .hamburger .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.navbar-toggler .hamburger .line2 {
  opacity: 0;
}

.navbar-toggler .hamburger .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

.navbar-toggler.show .hamburger .line1 {
  transform: rotate(0) translate(0px, 0px);
}

.navbar-toggler.show .hamburger .line2 {
  opacity: 1;
  transform: rotate(0) translate(0px, 0px);
}

.navbar-toggler.show .hamburger .line3 {
  transform: rotate(0) translate(0px, 0px);
}

.offcanvas.show,.navbar-toggler .hamburger{
  z-index: 1000001;
}
.modal-open #root nav~div{
  opacity: 0.3;
  pointer-events:none;
}
/* Navbar toggle button end */

/* control panel navbar */

.cp-navbar {
  background-color: #fdfdfd;
  box-shadow: 3px 3px 6px #00000029;
  height: 86px;
  border: 1px solid #d7dce1;
  z-index: 10000000000;
  border-left: none;
  border-right: none;
  z-index: 10 !important;
}
.cp-navbar > .container-fluid {
  background-color: #fdfdfd;
}

.cp-navbar .nav-link .btn {
  min-width: 143px;
  font-size: 1.125em;
  padding-top: 4px;
  padding-bottom: 3px;
}

.cp-navbar .nav-item {
  position: relative;
  color: var(--black);
}

.cp-navbar .nav-item .nav-link {
  font-size: 1.125em;
  color: inherit;
}

.cp-navbar .nav-item.active .nav-link {
  color: var(--black);
}

.cp-navbar .nav-item.active .underline {
  background-color: var(--black);
  width: 100%;
}

.cp-navbar .nav-item .underline {
  position: absolute;
  height: 2px;
  background-color: transparent;
  width: 0%;
  bottom: -22px;
  transition: width 0.2s, background-color 0.5s;
}

.cp-navbar .nav-actions-box .nav-item .nav-link.user-link {
  font-family: var(--primaryFontLight);
  font-size: 1.125em;
  cursor: pointer;
  color: #263238;
}
.cp-navbar .nav-actions-box .nav-item .nav-link.user-link .user-link-title {
  font-family: var(--primaryFontMedium);
  color: var(--black);
}
.notification-icon-box{
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  height: 35px;
  width: 35px;
  background-color: var(--purple);
  display: flex;
  justify-content: center;
  align-items: center;
}
 .section-btn-toggle::after{
display: none;
}
.icon-bell{
  position: relative;
  left: 1.5px;
  top: 6px;
}
/* user link modal */

.user-link-modal .modal-content {
  width: 90%;
  margin: 0 auto;
  border-radius: 21px;
  -webkit-border-radius: 21px;
  -moz-border-radius: 21px;
  -ms-border-radius: 21px;
  -o-border-radius: 21px;
  padding: 26px 13px;
  padding-top: 18px;
}

.user-link-modal .data-box {
  position: relative;
  text-align: center;
  margin-bottom: 15px;
}

.user-link-modal .modal-header .btn-close {
  font-size: 0.8em;
}

.user-link-modal .modal-header .back-icon {
  padding-top: 0.3em;
}

.user-link-modal .data-box .title {
  font-family: var(--primaryFontBold);
  font-size: 1em;
  margin-bottom: 0px;
}

.user-link-modal .data-box .user-link {
  font-family: var(--primaryFontSemiBold);
  font-size: 0.9375em;
  margin-bottom: 15px;
  color: #1e1e1e;
  text-decoration: none;
}

.user-link-modal .user-action-box .action-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}

@keyframes ani_navIconRipple {
  50% {
   transform: scale(1.2);
  }
  100% {
   transform: scale(1);
  }
}

.user-link-modal .user-action-box .icon-box {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--primaryColor);
  color: #ffffff;
  width: 40px;
  height: 40px;
  border-radius: 50em;
  -webkit-border-radius: 50em;
  -moz-border-radius: 50em;
  -ms-border-radius: 50em;
  -o-border-radius: 50em;
}

.user-link-modal .user-action-box .action-box:hover .icon-box {
  animation: ani_navIconRipple 0.5s;
}

.user-link-modal .user-action-box .text {
  font-family: var(--primaryFontMedium);
  font-size: 0.875em;
  color: var(--primaryColor);
  margin-bottom: 0;
}

/* user link modal end */

/* control panel navbar end */

.freelancer-navbar {
  background: var(--themePrimaryColor);
  background: linear-gradient(
    90deg,
    var(--themePrimaryColor) 0%,
    var(--themeSecondaryColor) 100%
  );
}

.freelancer-navbar .navbar-identity {
  font-size: 1.6em;
}
.freelancer-navbar .navbar-identity h2 {
  min-width: 160px;
}

.freelancer-navbar .navbar-nav .nav-link,
.freelancer-navbar .navbar-brand {
  color: var(--themeTextColor);
}

.freelancer-navbar .section-add-dropdown .dropdown-menu .dropdown-item {
  font-family: var(--primaryFont);
}

.freelancer-navbar .dropdown-icon .icon-img {
  width: 14px;
  height: 14px;
  margin-top: -3px;
}

/* mobile publish bar */
.mobile-publish-nav {
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  align-items: center;
  position: fixed;
  top: 60px;
  z-index: 9;
  background-color: #fff;
  box-shadow: 3px 3px 6px #00000029;
}

.link-box .user-link {
  font-size: 12px;
  color: var(--primaryColor);
}

.mobile-publish-nav .mb-public-btn .btn {
  font-size: 12px;
  margin: 4px 0;
  min-height: 26px;
  min-width: 74px;
}
.mobile-publish-nav .share-btn-box {
  border-color: var(--primaryColor) !important;
}

.mobile-publish-nav .share-btn-box .icon {
  font-size: 12px;
}

.mobile-publish-nav .link-box .user-link .user-link-title {
  font-family: var(--primaryFontMedium);
  color: var(--black);
}
