.freeAccountConfirmationModal .modalDialog{
    max-width: 538px;
}
.freeAccountConfirmationModal .modalContent {
    border-radius: 13px;
    -webkit-border-radius: 13px;
    -moz-border-radius: 13px;
    -ms-border-radius: 13px;
    -o-border-radius: 13px;
}

.freeAccountConfirmationModal .modalHeader {
    border-bottom: none;
    justify-content: center;
    padding-top: 40px;
}

.freeAccountConfirmationModal .modalHeader .closeIcon {
    border: none;
    margin-left: auto;
    position: absolute;
    top: 6px;
    right: 10px;
}

.freeAccountConfirmationModal .modalHeader .closeIcon .icon{
    color: var(--primaryColor);
}

.freeAccountConfirmationModal .contentBox {
    width: 89%;
    margin: 0 auto;
    text-align: center;
}

.freeAccountConfirmationModal .contentBox .heading {
    font-size: 1.5em;
    font-family: var(--primaryFontMedium);
    margin-bottom: 16px;
    color: var(--primaryColor);
}

.contentBox .desc {
    font-family: var(--primaryFont);
    font-size: 1em;
    color: #7A7A7A;
}

.contentBox .proListBox{
    text-align: left;
    width: 87%;
    margin: 0 auto;
}

.proListBox .proList .proListItem{
    display: flex;
    justify-content: flex-start;
}

.proListBox .proList .proListItem .iconBox{
    margin-right: 12px;
    display: flex;
}

.proList .proListItem .iconBox .icon{
    font-size: 24px;
    color: #EE3131;
}

.proListBox .proListItem .listText{
    color: #858585;
    font-size: 1em;
}

.freeAccountConfirmationModal .modalFooter {
    justify-content: space-between;
    border-top: none;
    margin-bottom: 17px;
}

.btn{
    text-decoration: none;
    padding: 8px 24px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    min-width: 154px;
    text-align: center;
}

.btnPrimary{
    background-color: var(--primaryColor);
    color: #ffffff;
}

.btnPrimary:hover{
    color: #ffffff;
}

.btnPro{
    background-color: var(--orange);
    color: #ffffff;
    text-decoration: none;
}

.btnPro:hover{
    color: #ffffff;
}

@media (max-width: 575.98px) {
    .freeAccountConfirmationModal .modalHeader .title {
        font-size: 1.375em;
    }

    .freeAccountConfirmationModal .contentBox {
        width: 100%;
    }

    .freeAccountConfirmationModal .contentBox .heading {
        font-size: 1.125em;
    }

    .freeAccountConfirmationModal .modalFooter {
        margin-bottom: 31px;
    }

    .freeAccountConfirmationModal .modalFooter .btn{
        padding: 8px 16px;
        margin: 0 auto;
        margin-bottom: 10px;
    }

}