.rightSection {
    background-color: #EAD0FF;
    border-radius: 17px;
    -webkit-border-radius: 17px;
    -moz-border-radius: 17px;
    -ms-border-radius: 17px;
    -o-border-radius: 17px;
    height: 100%;
    position: relative;
    overflow: hidden;
}

.contentBox {
    align-items: center;
    justify-content: center;
    position: relative;
    text-align: left;
    padding-top: 30px;
    padding-left: 30px;
    padding-right: 15px;
}

.contentBox .heading {
    font-size: 2rem;
    font-family: var(--secondaryFontMedium);
    color: var(--purple);
    margin-bottom: 4px;
}

.rightSection .contentBox {
    text-align: left;
    padding-left: 10px;
    padding-right: 7px;
}

.rightSection .contentBox .contentSubHeading {
    font-size: 1.3125rem;
    font-family: var(--secondaryFont);
}

.rightSection .contentNavHeading {
    font-size: 1.125rem;
    margin-top: 0.4em;
    text-align: center;
    font-family: var(--primaryFontBold);
    position: absolute;
    bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 0 auto;
    text-align: center;
    transform: translateX(-50%);
    left: 50%;
    width: 100%;
}

.rightSection .link {
    color: var(--purple);
    display: block;
    font-family: var(--primaryFont);
    text-decoration: none;
}

/* Input Fields */

.registerInputGroup {
    position: relative;
    padding: 15px 0 0;
    margin-top: 10px;
}
 
.inputGroup {
    padding: 5px 0 0;
    margin-top: 0px;
}

/* Img styling */
.imgBox {
    overflow: hidden;
    position: relative;
    top: 16px;
    margin-top: -20px; 
}

@keyframes ani_imgBox {
    0% {
        margin-left: 400px  
    }

    100% {
       margin-left: 0px    
    }
}


.imgBox img{
    width: 344px;
    right: -21px;
    animation: ani_imgBox 1s;
}

.imgBoxContent {
    position: relative;
    right: -30px;
    flex-wrap: wrap;
}

@media (max-width: 1400px ) {
    .contentBox .heading {
        font-size: 1.8rem;
        width: 90%;
    }
    
    .imgBox img {
        width: 308px;
    }

    :global(.text-araea_signup textarea) {
        height: 100px;
    }
}


@media (max-width: 380px) {

    .genderDrpDown .genderDropdownToggle::after {
        margin-left: 6.8em !important;
    }
}


@media (max-width: 575.98px) {

    .contentBox {
        padding: 30px 15px;
    }

    .registerFieldSec {
        flex-direction: column !important;
    }

    .contentBox .heading {
        font-size: 1.5rem;
    }

    .rightSectionWrapper {
        display: none;
    }

    .formGroup{
        margin-bottom: 11px;
    }
}


@media (min-width: 576px) and (max-width: 767.98px) {
    .rightSectionWrapper {
        display: none;
    }

    .contentBox {
        padding: 30px 15px;
    }
}

@media (min-width: 768px) and (max-width: 991.98px) {

    .genderDropdownToggle::after {
        margin-left: 8.225em;
        position: relative;
        right: 10px;
    }

    .rightSectionWrapper {
        display: none;
    }

    .contentBox {
        padding: 30px 15px;
    }
}

@media (min-width: 1024px) and (max-width: 1199.99px) {

    .genderDropdownToggle::after {
        margin-left: 7.225em;
    }
}