/* Freelance service section code  */

.freelancer-service-box {
    /* background-image: url("../../../assets/images/layers/overlay-dots.png");
      background-repeat: no-repeat;
      background-position: top right;
      background-size: 27%; */
    position: relative;
    border-radius: 50px;
  }
  
  .freelancer-service-box .section-tag {
    font-family: var(--themePrimaryFont);
    font-size: 2em;
    margin-bottom: 1.25rem;
    color: var(--themePrimaryColor);
    font-weight: 600;
  }
  
  .freelancer-service-box .message-text {
    font-size: 1.3em;
    font-family: var(--themePrimaryFont);
    line-height: 40px;
    letter-spacing: 1px;
    margin-bottom: 1.4em;
  }
  
  .btn-box.btn-more .btn {
    min-width: 174px;
  }
  
  .circle-overlay-container {
    position: relative;
    z-index: 0;
  }
  
  .circle-overlay-solid {
    width: 310px;
    height: 310px;
    background-color: var(--themePrimaryColor);
    border-radius: 50%;
    position: absolute;
    top: 0px;
    right: 0px;
    /* background-blend-mode: overlay;
      mix-blend-mode: soft-light; */
    opacity: 0.5;
    z-index: 0;
  }
  /* Freelance service section code ends */
  
  /* theme generic section code */
  .theme-service-box .section-tag {
    color: var(--themeTextColor) !important;
  }
  .theme-service-box .message-text {
    color: var(--themeTextColor) !important;
  }
  
  .theme-service-box {
    /* background-image: url("../../../assets/images/layers/overlay-dots.png");
      background-repeat: no-repeat;
      background-position: top right;
      background-size: 27%; */
    position: relative;
    border-radius: 50px;
  }
  
  .theme-service-box .section-tag {
    font-family: var(--themePrimaryFont);
    font-size: 2em;
    margin-bottom: 1.25rem;
    color: var(--themePrimaryColor);
    font-weight: 600;
  }
  
  .theme-service-box .message-text {
    font-size: 1.3em;
    font-family: var(--themePrimaryFont);
    line-height: 40px;
    letter-spacing: 1px;
    margin-bottom: 1.4em;
  }

  #music.style_3 .card-text{
    margin-bottom: 0;
  }
  #music.style_3  .btn-edit-generic{
    padding-left: 20px;
    padding-right: 20px;
  }
  #music.style_3 .card-footer .link{
    text-align: left !important;
  }
  #music.style_3 .card-footer button{
    background: var(--bs-gray-700);
      color: var(--bs-body-bg);
      border-color: var(--themeTextColor);
  }
  
  /* theme generic section code */
  
  @media (max-width: 575.98px) {
    .circle-overlay-solid {
      width: 100px;
      height: 100px;
      background-color: var(--themePrimaryColor);
      border-radius: 50%;
      position: absolute;
      top: 0px;
      right: 0px;
      /* background-blend-mode: overlay;
          mix-blend-mode: soft-light; */
      opacity: 0.5;
    }
    .freelancer-service-box .section-tag {
      font-size: 1.6em;
      margin-bottom: 18px;
      font-weight: 800;
    }
    .freelancer-service-box .message-text {
      font-size: 1em;
      font-weight: 800;
      margin-bottom: 24px;
      line-height: 24px;
    }
  }
  