

.inputForm{
    margin-bottom: 24px;
}
.inputForm label{
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #343434;
    font-family: var(----primaryFontBold);
}
.inputForm input{
    background: #FBFCFF;
    border: 1px solid #E7E7E7;
    border-radius: 4px !important;
    height: 48px;
    color: #5E606A;
    font-family: var(--primaryFontBold);
    font-size: 14px;
}
.inputForm input:focus{
    background: #FBFCFF;
    border: 1px solid #E7E7E7;
    border-radius: 4px !important;
    height: 48px;
    color: #5E606A;
    font-family: var(--primaryFontBold);
    font-size: 14px;
}
.inputForm input::placeholder{
    color: #bfbfcc;
    font-family: var(--primaryFontBold);
    font-size: 14px;
}