.style_5 .link a {
    color: #000;
    text-decoration: none;
    font-size: 16px;
}
.style_5 .bg_new {
    margin-bottom: 8px;
}
.style_5 .card-footer {
    flex: 1;
    background: none;
    border-top: 0;
}
.style_5 .flip-card .card-footer .btn-box button.btn-theme-outline-primary{
    background-color: var(--themePrimaryColor);
    height: 70px;
    font-size: 21px !important;
    border: none;
    color: var(--themeTextColor);
}

.style_5 .flip-card .card-footer .link > a{
    color: var(--themeTextColorSecondary);
}

@media (max-width: 575.95px){
    .style_5 .flip-card .card-footer .btn-box button.btn-theme-outline-primary{
       height: 38px;
       font-size: 14px !important;
    }
}