.mobileRow tr {
    display: grid;
}
.mobileRow .inovice_hover:hover {
    background: #fff;
}
.mobileRow tr td{
    font-size: 13px;
}

.mobileRow {
    border: 1px solid #F5F6F8;
    margin-bottom: 15px;
    border-radius: 5px;
    position: relative;
    padding: 10px;
}
.mobileRow .color_invoiceRef {
    font-size: 13px;
    margin-bottom: 5px;
}
.mobileRow .badgeLabel {
    width: 53px;
    height: 18px;
    font-size: 11px;
    margin-left: 15px;
}
.mobileRow .name_email_history {
    display: grid;
}
.mobileRow .btn_download_invoice {
    position: absolute;
    right: 10px;
    top: 8px;
}
.mobileRow .toolTip_badge {
    position: absolute;
    left: -22px;
    width: 100px;
    height: 24px;
    line-height: 22px;
    font-size: 9px;
}
.mobileRow .toolTip_badge:before{
    left: 35px;
}
.rightArrow {
    color: #52545A;
    position: absolute;
    right: 16px;
    font-size: 16px;
    bottom: 9px;
}