

.ComparePlans{
    margin-top: 40px;
}
.AccordionBtn{
    
}
.AccordionItem{
    padding: 15px;
    background: transparent;
}
.comingSoonBtn span{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    border-radius: 50px;
    color: #fff;
    padding: 10px 18px;
    font-size: 14px;
    font-weight: 600;
}
.mobProIcon svg{
    width: 21px !important;
    height: 32px !important;
}
.AccordionBtn button{
    background: transparent !important;
font-weight: 700;
font-size: 18px;
color: #343434 !important;
font-family: var(--primaryFontBold);
background-color: transparent !important;
box-shadow: none !important;
border: none !important;
border-radius: 0 !important;
}


.AccordionBtn button:focus{
    box-shadow: none;
}
.AccordionBody{
    padding: 10px 15px;
}

.headingArea{
    display: flex;
}
.headingArea .cols{
    width: 100%;
}
.headingArea .colsFeatures{
    width: 200%;
}
.headingArea .cols h2{
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #5E606A;
    font-family: var(--primaryFontBold);
}
.contentArea{
    display: flex;
}
.contentArea .cols{
    width: 100%;
}
.contentArea .cols h2{
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: #5E606A;
}
.contentArea .colsFeatures{
    width: 200%;
}

.contentArea .cols p{
    margin-bottom: 0;
    padding: 11px 15px;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #5E606A;
}

.contentArea .cols p:nth-child(even){
    background: #F8F9FA;
}
.border_left p{
    border-radius: 20px 0 0 20px;
}
.border_right p{
    border-radius: 0px 20px 20px 0px;
}
.cols_center h2{
    text-align: center;
}
.cols_center p{
    text-align: center;
}
.purple_col h2{
    color: #5b3f91 !important;
}
.purple_col svg path{
    fill: #5b3f91;
}
.gredient_col h2{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}
.gredient_col p svg{
    width: 21px;
    height: 21px;
}
.contentArea .cols p svg{ 
    width: 22px;
    height: 17px;
}

@media (max-width: 1400px) {
    .contentArea .cols p{
        font-size: 12px;
    }
}


@media (max-width: 575.95px) {
    .AccordionBody{
        padding: 10px 0;
    }
    .colsFeatures h2{
        position: relative;
        left: -17px;
        top: 10px;
    }
    .headingArea .cols h2{
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 16px;
    }
    .headingArea .cols h2 img{
        width: 20px;
    }
    .contentArea .cols p{
        font-size: 14px;
        height: 95px;
        display: flex;
        align-items: center;
    }
    .contentArea .cols p svg{ 
        width: 16px;
        height: 16px;
    }
    .cols_center p{
        display: flex !important;
        justify-content: center;
    }
    .contentArea{
        height: 350px;
        overflow-y: scroll;
    }
}