.content-container {
  position: relative;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  /* remove this when system changes */
  box-shadow: 5px 5px 12px 1px #00000029;
  border-radius: 30px;
  /* remove this when system changes */
  overflow: auto;
  /* white-space: nowrap; */
  padding: 40px 60px;
}


/* influencer */
.style_2 .content-container{
  border: none;
  box-shadow: none;
}

.influencer .content-container{
  border: none !important;
  box-shadow: none !important;
}

/* basic theme */
.style_1 .content-container{
  box-shadow: 0px 5px 20px #00000029;
  border-radius: 30px;
}

.scroller-x {
  overflow-x: auto;
  padding-bottom: 16px;
}

.scroller-x::-webkit-scrollbar-thumb {
  background-color: var(--themePrimaryColor) !important;
}

.section-head .btn-box .btn-action{
  padding-left: 1.5em;
  padding-right: 1.5em;
}

@media (max-width: 575.98px) {
  .content-container {
    padding: 16px 16px;
    overflow: visible;
    border-radius: 15px !important;
  }

  .section-head .btn-box .btn-action{
    padding-left: 10px;
    padding-right: 10px;
  }
}
