
.dashboard-theme-section .manage-theme-table {
    background-color: #fbfbfb;
    border: 1px solid #d7dce1;
    border-radius: 17px;
    box-shadow: 0px 0px 5px #0000000d;
    color: #1e1e1e;
}
.dashboard-container .manage-theme-table .manage-table-heading {
  font-size: 1.25em;
  font-family: var(--primaryFontMedium);
}
.Msgdiv svg path {
  fill: #5b3f91;
}
.Msgdiv svg {
  width: 22px;
  height: 22px;
}
.unreadMsg {
  background: #5b3f91;
  width: 16px;
  height: 16px;
  display: block;
  border-radius: 30px;
  position: relative;
  top: 5px;
}
.deleteBtnMsg{
  background: #5b3f91;
  color: #fff;
  display: block;
  padding: 0 6px;
  width: 70px;
  text-align: center;
  border-radius: 60px;
  font-size: 14px;
  height: 24px;
  line-height: 22px;
  transition: .3s;
  border: 2px solid #5b3f91;
}
.deleteBtnMsg:hover {
  cursor: pointer;
  border: 2px solid #5b3f91;
  color: #5b3f91;
  background: transparent;
}
.dashboard-container .manage-theme-table .manage-table-heading > thead {
  border-style: none;
}
.dashboard-container .manage-theme-table > tbody tr:nth-child(odd) {
  background-color: #ffffff;
}

.dashboard-container .manage-theme-table .manage-table-subheading {
  font-size: 18px;
  font-family: var(--primaryFont);
}

.manage-theme-table .table-head {
  position: relative;
  padding: 18px 40px;
}
.manage-theme-table .table-body .row {
  position: relative;
  padding: 15px 40px;
}
.manage-theme-table .table-body .row:last-child {
  border-bottom-left-radius: 17px;
  border-bottom-right-radius: 17px;
}
.manage-theme-table .table-body .row:nth-child(odd) {
  background-color: white;
}
.manage-message-section .manage-theme-table .title {
  font-family: var(--primaryFontSemiBold);
  font-size: 1.25em;
}



.manage-theme-table .table-head,
.manage-theme-table .table-body {
  width: 100%;
}
.manage-theme-table .table-head .cell,
.manage-theme-table .table-body .cell {
  text-align: center;
}
.manage-theme-table .table-head .cell.one,
.manage-theme-table .table-body .cell.one {
  text-align: left;
}
.manage-theme-table .table-body .cell .data-text {
  font-family: var(--primaryFontMedium);
  font-size: 1.125em;
  margin-bottom: 0px;
}

/* Message Section TITLE and Sub-title */
.manage-message-section .manage-theme-header {
  font-size: 1.375em;
  font-family: var(--primaryFontSemiBold);
  margin-bottom: 22px;
}

.manage-message-section .manage-theme-subheading {
  font-size: 1.25em;
  font-family: var(--primaryFontMedium);
  color: #1e1e1e;
}


/* Message Box card */
/* ========== Message Box Card ========= */
.message-section-heading {
  margin: 0em 0px !important;
  font-family: var(--primaryFontSemiBold);
  font-size: 1.375em;
  margin-bottom: 31px !important;
}
.message-box-section {
  background-color: #fbfbfb;
  /* border: 1px solid #d7dce1; */
  border-radius: 17px;
  box-shadow: 0px 0px 5px #0000000d;
  color: #1e1e1e;
  /* height: 100%; */
  background: white;
  height: 100%;
}

.message-box-section .message-box-table {
  background-color: #fbfbfb;
  /* border: 1px solid #d7dce1; */
  border-radius: 17px;
  box-shadow: 0px 0px 5px #0000000d;
  color: #1e1e1e;
}

.message-box-section .message-box-table thead th {
  font-weight: bold;
  font-size: 1.1em;
  padding: 1.1em;
}

.message-box-section .message-box-table tbody > tr > td {
  /* border-bottom: 1px solid rgba(0, 0, 0, 0.09); */
  padding: 0.7em 1em;
}

.live-btn .align {
  align-items: center;
  margin-left: 5px;
}
.message-card-footer {
  background-color: transparent;
  border-top: 0px;
}
.dashboard-msg-card .card-header {
  padding: 18px 40px;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;

}

.dashboard-msg-card .card-header .header-title {
  font-family: var(--primaryFontSemiBold);
  font-size: 1.25em;
  margin-bottom: 0;
}
.dashboard-msg-card .card-body .data-box {
  padding: 13px 42px;
  border-bottom: 1px solid #d7dce1;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  /* align-items: center; */
}


.dashboard-msg-card .data-box .title-box {
  width: 30%;
}
.dashboard-msg-card .data-box .para-box {
  width: 70%;
  max-height: 200px;
}
.user-history-table .primary-bold:hover {
  background: #F3F3F3;
  cursor: pointer;
}

.dashboard-msg-card .card-body .data-box:last-child {
  border-bottom: none;
}
.dashboard-msg-card .card-body .subject {
  font-family: var(--primaryFontSemiBold);
  font-size: 1em;
  margin-bottom: 0;
  color: #333333;
}
.dashboard-msg-card .card-body .text {
  font-family: var(--primaryFont);
  font-size: 1em;
  margin-bottom: 0;
  color: #333333;
  word-wrap: anywhere;
}
.dashboard-container-screen .dashboard-msg-card .card-footer {
  background-color: transparent;
  border-top: none;
}

/* END of Message box card */

/* Mobile Devices small src */
@media (max-width: 380px) {
  .dashboard-msg-card .card-body .subject {
    font-size: 0.75em;
  }
  .dashboard-msg-card .card-body .text {
    font-size: 0.875em;
  }

  .manage-message-section .manage-theme-subheading {
    font-size: 0.9375em !important;
  }
}

@media (max-width: 575.95px) {
  .message-section-heading {
    font-size: 1.1875em;
  }
  .manage-message-section .manage-theme-header,
  .manage-theme-mobile-section .heading {
    font-size: 1.1875em;
  }
  .manage-message-section .manage-theme-subheading {
    font-size: 1.0625em;
    margin-bottom: 24px;
  }
    .dashboard-msg-card .card-header .header-title,
  .manage-theme-mobile-section .sub-heading {
    font-size: 1em;
  }
   .dashboard-msg-card .card-body .data-box {
    padding: 13px 13px;
    justify-content: center;
    align-items: center;
  }

   .dashboard-msg-card .card-body .subject {
    text-align: left;
  }
  .dashboard-msg-card .card-body .data-box {
    padding: 13px 13px;
    justify-content: center;
    align-items: center;
  }
  .dashboard-msg-card .card-body .text {
    text-align: left;
    padding-right: 7px;
  }
}


/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
  .manage-theme-table .table-head,
  .manage-theme-table .table-body .row {
    padding-left: 20px;
  }
  .manage-message-section .manage-theme-header {
    font-size: 1.25em;
  }
}

/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
  .manage-theme-table .table-head,
  .manage-theme-table .table-body .row {
    padding-left: 20px;
  }
  .manage-message-section .manage-theme-header {
    font-size: 1.25em;
  }
}

@media (min-width: 1200px) and (max-width: 1399.98px) {
  .db-edit-profile-card .edit-profile-box .img-box {
    width: 25%;
  }
  .dashboard-screen .db-edit-profile-card .user-img {
    max-width: 90px;
    max-height: 90px;
  }
  .manage-message-section .manage-theme-header {
    font-size: 1.175em !important;
  }
    .manage-message-section .manage-theme-subheading {
    font-size: 1em !important;
  }
    .message-section-heading {
    font-size: 1.175em !important;
  }
}

/* xxl screen */
@media (min-width: 1400px) and (max-width: 1599.98px) {
  .db-edit-profile-card .edit-profile-box .img-box {
    width: 22%;
  }
  .dashboard-screen .db-edit-profile-card .user-img {
    max-width: 100px;
    max-height: 100px;
  }
  .theme-select-card .card-body .card-title {
    font-size: 1rem;
  }
  .manage-sort-section .manage-sort-heading {
    font-size: 1em;
  }
  .manage-message-section .manage-theme-header {
    font-size: 1.275em;
  }
  .manage-message-section .manage-theme-subheading {
    font-size: 1.05em;
  }
   .message-section-heading {
    font-size: 1.275em !important;
  }
   .dashboard-msg-card .card-body .subject {
    font-size: 0.85em;
  }
  .dashboard-msg-card .card-body .text {
    font-size: 0.85em;
  }
}