/* theme header */

.freelancer-wrapper {
  font-family: var(--themePrimaryFont);
  background-color: #ffffff;
}

/* .theme-header-section {
  height: 100%;
  background: var(--themePrimaryColor);
  background: linear-gradient(
    90deg,
    var(--themePrimaryColor) 0%,
    var(--themeSecondaryColor) 100%
  );
} */
.theme-header-section {
  height: 100%;
  background: var(--themePrimaryColor);
}

.bg-primary {
  background-color: #65bc8a;
}

.btn-theme-outline-primary {
  border-color: var(--themeTertiaryColor);
  color: var(--themeTertiaryColor);
}

.btn-theme-outline-primary:hover {
  color: #fff;
  background-color: var(--themeTertiaryColor);
}

.btn-theme-primary {
  background-color: var(--themeTertiaryColor);
  color: var(--themeTextColor);
}

.btn-theme-primary:hover {
  background-color: var(--themeTertiaryColor);
  color: var(--themeTextColor);
}

.btn-primary-theme {
  background-color: var(--themeTextColorSecondary);
  color: #ffffff;
}

.btn-primary-theme:hover {
  background-color: var(--themeTextColorSecondary);
  color: #ffffff;
}

.btn-theme-outline-primary

.btn-purple:hover,
.btn-purple:active,
.btn-purple:focus {
  background-color: #000000;
  color: #ffffff;
  outline: none;
  box-shadow: none;
}

*[contenteditable="true"]:hover {
  background-color: rgba(217, 245, 255, 0.5);
  border-width: 1px;
  border-style: solid;
  border-color: var(--primaryColor);
  z-index: 6;
}
*[contenteditable="true"]:focus {
  outline: none;
  border: 1px solid var(--primaryColor);
}
*[contenteditable="true"] {
  transition: 0.5s ease-out;
  border-width: 1px;
  border-color: transparent;
  z-index: 6;
}
.initial_render *[contenteditable="true"] {
  background-color: rgba(217, 245, 255, 0.5);
  border-width: 1px;
  border-style: solid;
  border-color: var(--primaryColor);
}

.show_boundary [contenteditable="true"] {
  margin: -3px;
  padding: 3px;
}

.freelancer-stats-section {
  padding-bottom: 73.5px;
  padding-top: 40px;
}

.freelancer-pricing-section,
.freelancer-services-section,
.freelancer-gallery-section,
.freelancer-contact-section {
  padding-top: 73.5px;
  padding-bottom: 73.5px;
}

/* Pagination */

.pagination-sec {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.pagination-sec li {
  width: 27px;
  height: 27px;
  text-align: center;
  border: 1px solid var(--themePrimaryColor);
  margin: 5px;
  padding: 3px;
}

.pagination-sec li.next .icon,
.pagination-sec li.previous .icon {
  color: var(--themePrimaryColor);
}

.paginate-list a.paginate-tag {
  text-decoration: none;
  color: inherit;
}

.paginate-list.selected {
  background-color: var(--themePrimaryColor);
  color: #ffffff;
}

.border-dashed {
  border: 1px dashed var(--primaryColor);
}

.w-90 {
  width: 90%;
}

.w-95 {
  width: 95%;
}

.theme-section-wrapper {
  padding-top: 93px;
  padding-bottom: 93px;
}

.influencer .theme-section-wrapper{
  padding-top: 40px;
  padding-bottom: 40px;
}

/* global section head style */

/* .section-head{
  flex-wrap: wrap;
} */

.section-head .section-name {
  font-family: var(--themePrimaryFontBold);
  font-size: 2.5em;
  margin-bottom: 12px;
  color: var(--themeTextColorSecondary);
  word-break: break-word;
}
.section-head .section-text {
  font-family: var(--themePrimaryFontMedium);
  font-size: 1.5em;
  margin-bottom: 40px;
  color: var(--themeTextColorSecondary);
  word-break: break-word;
}

/* global section head style end */
@media (max-width: 380px) {
  .manage-access-btn .manage-btn-float {
    font-size: 12px !important;
  }
  .section-head .section-name {
    font-size: 1.3625em !important;
  }
}


@media (max-width: 575.98px) {
  .theme-section-wrapper {
    padding-top: 30px;
    padding-bottom: 30px;
  }
  /* global section head style */

  .section-head .section-name {
    font-family: var(--themePrimaryFontBold);
    font-size: 1.5625em;
    margin-bottom: 10px;
    color: var(--themeTextColorSecondary);
  }
  .section-head .section-text {
    font-family: var(--themePrimaryFontMedium);
    font-size: 1em;
    margin-bottom: 20px;
    color: var(--themeTextColorSecondary);
    line-height: 20px;
  }

  .section-body .multi-child-card .card-wrapper {
    width: 90%;
    /* min-width: 90%; */
    padding-right: 0 !important;
  }

  .manage-access-btn .manage-btn-float {
    font-size: 16px;
    margin-bottom: 22px;
  }

  /* global section head style end */
}


.manage-access-btn {
  width: 100%;
  padding: 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  background-color: #fff;
  background-color: transparent;
  bottom: 60px;
  z-index: 5;
}


/* Tablet Devices */
@media (min-width: 768px) and (max-width: 991.98px) {
  .section-head .section-name{
    font-size: 2em;
  }
  .section-head .section-text{
    font-size: 1.2em;
  }

  .stats-card .card-title{
    font-size: 3em;
  }
  .stats-card .card-text{
    font-size: 1.2em;
  }
  .card-wrapper.col-md-6 {
    width: 48% !important;
  }

  .theme-general-card .card-title{
    font-size: 20px;
  }

}
@media (min-width: 992px) and (max-width: 1199.98px) {
  .section-head .section-name{
    font-size: 2em;
  }
  .section-head .section-text{
    font-size: 1.2em;
  }

  .stats-card .card-title{
    font-size: 3.8em;
  }
  .stats-card .card-text{
    font-size: 1.3em;
  }
  .card-wrapper.col-md-6 {
    width: 48% !important;
  }

  .theme-general-card .card-title{
    font-size: 20px;
  }
}

/* Extra large devices (large desktops, 1200px and up) */

@media (min-width: 1200px) and (max-width: 1399.98px) {
  .section-head .section-name {
    font-size: 1.75em;
  }
  .section-head .section-text{
    font-size: 1.15em;
  }
  .theme-section-wrapper{
    padding-bottom: 60px;
    padding-top: 60px;
  }
}

@media (min-width: 1400px) and (max-width: 1599.98px) {
  .theme-section-wrapper {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .section-head .section-name {
    font-size: 2.25em;
  }
  .section-head .section-text{
    font-size: 1.25em;
  }
}

@media (min-width: 1600px) and (max-width: 1799.98px) {
  .section-head .section-name {
    font-size: 2.35em;
  }
  .section-head .section-text{
    font-size: 1.35em;
  }
}