
.dFlexRow{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.dFlexRow p {
    margin-bottom: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #5E606A;
    font-family: var(--primaryFontBold);
}
.dFlexRow p input{
    margin-right: 10px;
}
.btn{
    background: #F58634;
    border-radius: 40px;
    border: none;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #FFFFFF;
    padding: 8px 16px;
}
.btn:hover{
    background: #f57e29;
}
.btn:focus{
    background: #F58634 !important;
    box-shadow: none !important;
}
.editDetails p{
    color: #000;
}
.editDetails{
    display: flex;
    align-items: center;
}
.editDetails svg {
    margin-right: 15px;
}
.accountDetails{
    color: #858585 !important;
    margin-top: 5px;
    font-size: 14px !important;
    line-height: 18px !important;
}

@media (max-width: 575.98px) {
    .dFlexRow p {
        font-size: 14px;
    }
    .accountDetails{
        font-size: 12px !important;
        margin-top: 0;
    }
}