.ribbonLabel{
    color: var(--bs-white);
    background: #f5821e;
    width: 60px;
    position: absolute;
    right: 0;
    top: 14px;
    height: 30px;
  }

  .ribbonLabel:after {
    content: '';
    width: 0;
    height: 0;
    top: 0px;
    position: absolute;
    left: -13px;
    border-left: 14px solid transparent;
    border-top: 15px solid #f5821e;
    border-bottom: 16px solid #f5821e;
  }

  .ribbonLabel span {
    font-size: 16px;
    position: relative;
    left: 13px;
    top: 0px;
    line-height: 28px;
  }

  .newCardIndicate{
    position: absolute;
    top: -10px;
    right: 25px;
    width: 25px;
    height: 25px;
  }