.cardGrid{
    display: grid;
    align-items: center;
    grid-template-columns: repeat(auto-fill, minmax(475px, 1fr));
    gap: 1rem;
    position: relative;
}
.flipCard{
    position: relative;
    margin-bottom: 25px;
    border-radius: 30px;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    -ms-border-radius: 30px;
    -o-border-radius: 30px;
    background-color: var(--themePrimaryColor);
    transform-style: preserve-3d;
    cursor: pointer;
    height: 100%;
    min-height: 280px;
    width: 100%;
    transition: 150ms;
    transform: perspective(1000px) rotateY(var( — rotate-y, 0)) translateY(var(--translate-y, 0)) ;
    -webkit-transform: perspective(1000px) rotateY(var( — rotate-y, 0)) translateY(var(--translate-y, 0)) ;
    -moz-transform: perspective(1000px) rotateY(var( — rotate-y, 0)) translateY(var(--translate-y, 0)) ;
    -ms-transform: perspective(1000px) rotateY(var( — rotate-y, 0)) translateY(var(--translate-y, 0)) ;
    -o-transform: perspective(1000px) rotateY(var( — rotate-y, 0)) translateY(var(--translate-y, 0)) ;
    -webkit-transition: 150ms;
    -moz-transition: 150ms;
    -ms-transition: 150ms;
    -o-transition: 150ms;
    border: none;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.flipCard.flip{
    transform: rotateY(180deg);
    transform: rotateY(180deg);
    -webkit-transform: rotateY(180deg);
    -moz-transform: rotateY(180deg);
    -ms-transform: rotateY(180deg);
    -o-transform: rotateY(180deg);
}
.frontCard {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
  }

.frontCard, .backCard{
  position: absolute;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  padding: 1em 1em 0 1em;
  width: 100%;
  height: 100%;
  border-radius: 30px;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  -ms-border-radius: 30px;
  -o-border-radius: 30px;
}
.backCard{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: white;
  transform: rotateY(180deg);
  -webkit-transform: rotateY(180deg);
  -moz-transform: rotateY(180deg);
  -ms-transform: rotateY(180deg);
  -o-transform: rotateY(180deg);

}
.headingSection{
    border-bottom: 1px solid white;
}
.cardHeaderSection{
    background-color: transparent;
    text-align: left;
    font-family: var(--primaryFont) !important;
    color: var(--themeTextPrimary);
    font-size: 2.5rem;
}
.btnSubscribe{
    background-color: var(--orange) !important;
    color: white !important;
    border-radius: 20px !important;
    -webkit-border-radius: 20px !important;
    -moz-border-radius: 20px !important;
    -ms-border-radius: 20px !important;
    -o-border-radius: 20px !important;
    border: none !important;
}

.iconBox{
    text-align: right;
    border: 1px solid;
    width: 60px;
    height: 60px;
    text-align: center;
    justify-content: center;
    border-radius: 60px;
    -webkit-border-radius: 60px;
    -moz-border-radius: 60px;
    -ms-border-radius: 60px;
    -o-border-radius: 60px;
}
.iconBox{
    align-self: flex-end;
    border-color: var(--themeTextColor);
    color: var(--themeTextColor);
}

.iconBoxLeft{
    align-self: flex-start;
    border-color: var(--themeTextColor);
}
.iconLeft{
    font-size: 22px;
    position: relative;
    bottom: -7px;
    text-align: right;
    color: var(--themeTextColorSecondary);
}
.iconRight{
    font-size: 22px;
    position: relative;
    bottom: -18px;
    text-align: right;
    color: inherit;

}

.flipCard:hover .guide {
    display: inline-block !important;
    border-top-right-radius: 30px;
    border-bottom-left-radius: 30px;
  }


/* Mobile devices small screen */
@media (max-width: 380px) {

}


@media (max-width: 575.95px){
    .flipCard{
        min-height: 208px;
    }
    .cardHeaderSection{
        font-size: 24px;
    }
}