.emptyDonutShape {
    height: 190px;
    width: 190px;
    margin: 0 auto;
    border-radius: 180px;
    box-shadow: 0 0 0 28px inset #eeeeee;
}

.emptyDonutContent {
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.emptyDonutContent h4 {
    font-size: 16px;
    color: var(--purple);
    font-weight: 600;
}

.emptyDonutContent p {
    font-size: 14px;
    color: #686868;
    margin-bottom: 8px;
}

.emptyDonutContent a {
    color: #004EE5;
    font-size: 14px;
}

.sourceOfTrafficChart :global(.apexcharts-legend) {
    justify-content: center !important;
    top: 0 !important;
    height: 100% !important;
    font-size: 12px;
}

.sourceOfTrafficChart {
    min-height: 420px !important;
    display: flex;
    align-items: center;
    flex: 0 0 100%;
}
