.classNameHeading {
    font-family: var(--secondaryFontMedium);
    font-size: 1.125em;
    color: var(--grey);
    text-align: center;
}

.classNameSubHeading {
    font-family: var(--secondaryFontBold);
    font-size: 2.25em;
    color: var(--purple);
    text-align: center;
}



/* Media Queries */
/* Mobile devices small screen */

@media (max-width: 380px) {
    .classNameHeading {
        font-size: 0.875em !important;
    }
    
    .classNameSubHeading {
        font-size: 1em !important;
    }
}

/* Mobile devices large screen */

@media (max-width: 575.98px) {
    .classNameHeading {
        font-size: 1em;
    }
    .classNameSubHeading {
        font-size: 1.3em;
    }
}

/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
    .classNameHeading {
        font-size: 1.025em;
    }
    .classNameSubHeading {
        font-size: 1.95em;
    }
}

/* Extra large devices (large desktops, 1200px and up) */

@media (min-width: 1200px) and (max-width: 1399.98px) {}

@media (min-width: 1400px) and (max-width: 1599.98px) {}