.freelancer-pricing-box{
    /* background-image: url("../../../assets/images/layers/overlay-dots.png");
    background-repeat: no-repeat;
    background-position: top right;
    background-size: 27%; */
    position: relative;
    border-radius: 50px;
    /* max-height: 453px; */
}

.freelancer-pricing-box .section-tag{
    font-family: var(--themePrimaryFont);
    font-size: 2em;
    margin-bottom: 1.25rem;
    color: var(--themePrimaryColor);
    font-weight: 600;
}

.freelancer-pricing-box .message-text{
    font-size: 1.3em;
    font-family: var(--themePrimaryFont);
    line-height: 40px;
    letter-spacing: 1px;
    margin-bottom: 1.4em;
}
/* Influencer PRICING BOX */
.theme-pricing-box{
  /* box-shadow: none !important; */
}

/* Influencer CARD STYLING */
.influencer-basic-package{
      background-color: var(--themePrimaryColor);
  border-radius: 17px;
  border: none;
  min-height: 491px;
  height: 100%;

}

.influencer-card-footer{
   margin-bottom: 0;
    background-color: transparent !important;
    border-top: 0px !important;
}

.influencer-basic-package .pricing-header{
    margin-bottom: 0;
    background-color: transparent !important;
    border-bottom: 0px !important;
}
.influencer-basic-package .card-body {
  padding: 24px 26px;
  padding-bottom: 0;
}
.influencer-basic-package .card-body.btn-edit-generic{
  padding: 10px 26px;
}
.influencer-basic-package .card-body.bykea-modal {
  z-index: 6;
}


.influencer-basic-package .package-price .currency+.character-limit{
  z-index: 7;
}
.influencer-basic-package .package-price .currency{
  left: -2px;
  font-family: var(--themePrimaryFontLight);

}
.influencer-basic-package .package-price {
    color: var(--themeTextColor);
    display: flex;
    font-family: var(--themePrimaryFontExtraBold);
    font-size: 1em;
    justify-content: left;
    margin-bottom: 24px;
    text-align: center;
}

.influencer-basic-package .card-body .card-text{
  color: var(--themeTextColor);
}

.influencer-basic-package .package-price .price {
  font-size: 2.5em;

}
 .influencer-basic-package .package-price .price+.character-limit{
  top: -48px;
}
.influencer-basic-package .package-price .duration {
  font-family: var(--themePrimaryFontLight);
  position: relative;
  bottom: -21px;
}
.influencer-basic-package .package-price .description {
  font-family: var(--themePrimaryFontMedium);
  font-size: 1.0625em;
  text-align: center;
  color: var(--themeTextColor);
  margin: 0 auto;
  margin-bottom: 23px;
  width: 90%;
}
.influencer-pricing-footer{
  background-color: transparent;
  border-top: 0px;
}
.influencer-pricing-footer .influencer-pricing-btn > button, .influencer-pricing-footer .influencer-pricing-btn > button:not(:disabled):not(.disabled):active,.influencer-pricing-footer .influencer-pricing-btn > button:active:focus {
  background-color: transparent;
  color: var(--themeTextColor);
  border-color: transparent;
  box-shadow: none;
}
.influencer-pricing-footer .influencer-pricing-btn.show>.btn-primary.dropdown-toggle{
   border-radius: 50em;
}
.influencer-pricing-footer.card-footer .influencer-pricing-btn .show > a{
  color: var(--themeTextColorSecondary);
}
.influencer-pricing-footer.card-footer .influencer-pricing-btn .show > a:active{
  background-color: var(--themePrimaryColor);
}
.custom-link-modal {
  z-index: 6;
}
.pricing-header .title-header{
  font-family: var(--themePrimaryFontExtraBold);
  color: var(--themeTextColor);
}
.pricing-header .influencer-subheading{
  font-family: var(--themePrimaryFontLight);
  color: var(--themeTextColor);
}

.influencer-basic-package .list-text{
  font-size: 1em;
  font-family: var(--themePrimaryFontLight);
  color: var(--themeTextColor);
}
/* global slick Dots */
.slick-dots li button:before{
    font-size: 13px;
    color: #C7D8CE;
    top: 10px;
}
.slick-dots li.slick-active button:before {
    opacity: .75;
    color: var(--themePrimaryColor);
}


.influencer-pricing-footer.card-footer .link a{
  cursor: pointer;
    color: var(--themeTextColorSecondary);

}

@media (max-width: 575.98px) {

.freelancer-pricing-box .section-tag{
    font-size: 1.6em;
    margin-bottom: 18px;
    font-weight: 600;
}

.freelancer-pricing-box .message-text{
    font-size: 1em;
    font-weight: 800;
    margin-bottom: 24px;
    line-height: 24px
}
.influencer-basic-package .package-price .currency{
    font-size: 0.625em;
}
.influencer-basic-package .package-price .price {
     font-size: 1.4375em;
}
.influencer-basic-package .package-price .duration {
     font-size: 0.6875em;
}
.pricing-header .title-header{
 font-size:  1.0625em;
}
.pricing-header .influencer-subheading{
  font-size: 15px;
}
.influencer-basic-package .list-text{
font-size: 15px;
}
}
