div.packageStyleThree {
    margin-top: 10px;
    border-radius: 40px;
}

.packageTitle h1 {
    font-size: 28px;
    margin-top: 10px;
    color: var(--themeTextColor);
}

.cardTitle {
    line-height: 18px;
}
.influencer-pricing-footer .dropdown .dropdown-item .fa-lock{
    color: var(--purple);
}

@media (max-width: 992px ) {
    .packageTitle h1 {
        font-size: 18px;
    }
    .cardTitle {
        line-height: 10px;
    }
}

.packagePriceInnerWrapper {
    padding-top: 50px;
    color: var(--themePrimaryColor);
    background-color: var(--themeTextColorSecondary);
    text-align: center;
    position: relative;
    height: 134px;
    top: -34px;
    min-width: 150px;
}

@media ( max-width: 1400px ) {
    .packagePriceInnerWrapper {
        min-width: 110px;
        height: 90px;
        padding-top: 15px;
    }
}

@media ( max-width: 992px ) {
    .packagePriceInnerWrapper {
        min-width: 110px;
    }
}

@media ( max-width: 800px ) {
    .packagePriceInnerWrapper {
        min-width: 90px;
        left: 0px;
    }
}

.packagePriceInnerWrapper:after {
    content: " ";
    position: absolute;
    height: 10px;
    bottom: -30px;
    width: 100%;
    left: 0;
    border-left: 75px solid transparent;
    right: 0;
    border-right: 75px solid transparent;
    border-top: 30px solid var(--themeTextColorSecondary);
}

@media (max-width: 992px) {
    .packagePriceInnerWrapper:after {
        bottom: -20px;
        border-left: 55px solid transparent;
        border-right: 55px solid transparent;
        border-top: 20px solid var(--themeTextColorSecondary);
    }
}

@media ( max-width: 800px ) {
    .packagePriceInnerWrapper:after {
        border-left: 45px solid transparent;
        border-right: 45px solid transparent;
    }
}


.packagePriceInnerWrapper:before {
    content: " ";
    position: absolute;
    left: -10px;
    top: 0px;
    border-left: 10px solid transparent;
    width: 8px;
    display: block;
    height: 10px;
    border-bottom: 10px solid var(--themeTextColorSecondary);
}


.price {
    color: var(--bs-white);
    font-size: 28px;
}


@media (max-width: 992px) {
    .price {
        font-size: 14px;
    }
}


.packagePriceHolder {
    display: flex;
    flex: 0 0 100%;
    justify-content: center;
    position: relative;
}

.currency {
    color: var(--bs-white);
    font-size: 22px;
}

@media (max-width: 992px) {
    .currency {
        font-size: 12px;
    }
}

.duration {
    font-size: 16px;
    margin-top: 10px;
    display: inline-block;
    color: var(--bs-white);
}

@media (max-width: 992px) {
    .duration {
        font-size: 10px;
    }
}


.cardText {
    color: var(--bs-white);
    font-size: 12px;
}

@media (max-width: 992px) {
    .cardText {
        font-size: 10px;
        margin-bottom: 0;
        line-height: 12px;
    }
}

.btn,
.btn.btn-primary {
    background: var(--themeTextColorSecondary) !important;
    border-color: var(--themeTextColorSecondary) !important;
    height: 50px;
}


@media (max-width: 1400px) {
    .price {
        font-size: 22px;
    }

    .currency {
        font-size: 17px;
    }

    .packagePriceInnerWrapper:after {
        border-left: 60px solid transparent;
        border-right: 60px solid transparent;
    }
}


@media (max-width: 1200px ) {
    .packagePriceInnerWrapper {
        width: 120px;
        left: 40px;
    }
}

@media (max-width: 767px ) {
    .packagePriceInnerWrapper {
        min-width: 90px;
        left: 0px;
    }
}