/* Global Card */

.card .card-footer .link > a{
  cursor: pointer;
  color: var(--themeTextColor);
}

.dark-bg-color {
  background-color: #292f48 !important;
}

.dark-gradient-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 0) 61%,
    rgba(0, 2, 31, 1) 100%
  );
  border-radius: 14px;
}

.package-card .link > a,
.service-card .link > a {
  color: var(--primaryColor) !important;
  font-size: 0.875em;
  cursor: pointer;
}

.package-card .btn-box .btn-action,
.service-card .btn-box .btn-action {
  font-family: var(--primaryFont);
  text-transform: capitalize;
}

.package-card:hover .guide-msg,
.service-card:hover .guide-msg,
.stats-card:hover .guide-msg {
  display: inline;
}

.package-card .guide-msg,
.service-card .guide-msg{
  display: none;
  text-align: left;
  position: absolute;
  top: -18px;
  left: 0px;
  border-radius: 2px;
  background-color: var(--primaryColor);
  color: #ffffff;
  padding: 2px 5px;
  font-size: 12px;
  font-family: var(--primaryFont);
}

