.create-package-card,
.create-service-card,
.create-stats-card {
  border: 1px solid #5b3f91;
  color: #5b3f91;
}

.create-package-card .card-body,
.create-service-card .card-body,
.create-stats-card .card-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.package-card:hover,
.service-card:hover {
  border: 1px solid var(--primaryColor);
}

.package-card.active,
.service-card.active {
  border: 1px solid var(--primaryColor);
}

.package-card.active .guide-msg,
.service-card.active .guide-msg {
  display: inline;
}

.package-card .card-title,
.service-card .card-title {
  font-family: var(--primaryFontBold);
  font-size: 1.125em;
  letter-spacing: 1px;
}

.package-card .card-text,
.service-card .card-text {
  color: #525252;
}

.guide-msg:hover {
  cursor: pointer;
}

.additional-data-box .text-end {
  cursor: pointer;
}

.additional-data-box .content-title{
  font-family: var(--primaryFontBold);
  font-size: 1em;
  text-align: center;
}

.additional-data-box .form-label{
  font-family: var(--primaryFont);
  font-weight: 400;
  font-size: 14px;
  color: black;
  display: none;
}
.additional-data-box .form-control{
  border-radius: 7px;
  -webkit-border-radius: 7px;
  -moz-border-radius: 7px;
  -ms-border-radius: 7px;
  -o-border-radius: 7px;
}

/* package card */

.package-card,
.service-card {
  background: #f4f8fd;
  transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
  border-radius: 17px;
  border: 1px solid transparent;
  box-shadow: 0px 5px 15px #00000029;
}

.package-card {
  min-height: 297px;
  height: 100%;
}

.package-card .package-service-list {
  color: #525252;
}

.package-card .package-info {
  font-family: var(--themePrimaryFont);
  color: #122828;
}
.package-card .package-info p {
  margin-bottom: 35px;
}

.create-package-card {
  min-height: 297px;
}

.package-card .card-title {
  margin-bottom: 20px;
}

.package-card .package-service-list ul li .list-text {
  margin-left: 10px;
  font-size: 0.875em;
  color: #525252;
}

.package-card .card-body .card-text {
  margin-bottom: 34px;
  font-size: 1em;
}

.package-card .service-price-box .price-text {
  font-family: var(--themePrimaryFont);
  color: var(--themePrimaryColor);
  font-size: 2rem;
  font-weight: 700;
  line-height: 40px;
  letter-spacing: 1px;
  margin-bottom: 20px;
}

/* Service Card */

.service-card {
  min-height: 290px;
  text-align: center;
  box-shadow: 0px 5px 15px #00000029;
  border-radius: 17px;
}

.service-card .additional-data-box {
  background-color: #ffffff;
  padding: 10px 15px 10px 15px;
  box-shadow: 0px 0px 12px #00000052;
  border-radius: 6px;
}

.service-card .additional-data-box .form-control {
  height: 33px;
  border-radius: 7px !important;
  border-color: var(--primaryColor);
}

.create-service-card {
  min-height: 290px;
}

/* stats-card */

.stats-card {
  position: relative;
  text-align: center;
}

/* line after states card */

.stats-card::after {
  content: "";
  width: 1px;
  top: 30px;
  right: 1px;
  height: 130px;
  background-color: #ffffff6b;
  position: absolute;
}

.stats-card:last-child:after {
  display: none;
}

.stats-card .card-title {
  margin-bottom: 7px;
  font-family: var(--themePrimaryFontBold);
  font-size: 5.625em;
  letter-spacing: 1px;
}

.stats-card .card-text {
  font-family: var(--themePrimaryFontBold);
  color: var(--themeTextColor);
  font-size: 1.5em;
  text-transform: capitalize;
  margin-bottom: 7px;
}

.create-stats-card .card-body .icon-box .icon-img {
  font-size: 2em;
}

/* Extra large devices (large desktops, 1200px and up) */

@media (min-width: 1200px) and (max-width: 1399.98px) {
  .stats-card .card-title{
    font-size: 3.75em;
  }
}

@media (min-width: 1400px) and (max-width: 1599.98px) {
  .stats-card .card-title{
    font-size: 4em;
  }
  .stats-card .card-text{
    font-size: 1.25em;
  }
}

@media (min-width: 1600px) and (max-width: 1799.98px) {
   .stats-card .card-title{
    font-size: 4.625em;
  }
}


/*Large Mobile devices  */

@media (max-width: 575.98px) {
  .stats-card .card-title {
    font-size: 1.8em;
    margin-bottom: 0;
  }

  .stats-card .card-text {
    font-size: 1em;
  }

  .package-card,
  .service-card {
    max-width: 350px;
    min-width: 350px;
  }

  .package-card {
    min-height: 324px;
    height: 100%;
    margin-top: 1em;
    margin-bottom: 1em;
    max-height: 360px;
    height: 360px;
  }

  .service-card {
    margin-top: 1em;
    margin-bottom: 1em;
  }

  /* guided message for mobile view */

  .package-card:hover .guide-msg,
  .service-card:hover .guide-msg,
  .stats-card:hover .guide-msg {
    display: inline;
    top: -1px;
  }
}

/* Small mobile devices */
@media (max-width: 380px) {
  .package-card,
  .service-card {
    max-width: 285px;
    min-width: 285px;
  }
}
