.dashboard-content-box{
    border-radius: 18px;
    -webkit-border-radius: 18px;
    -moz-border-radius: 18px;
    -ms-border-radius: 18px;
    -o-border-radius: 18px;
}

.analytics_page .dashboard-content-box{
    max-height: 630px;
}

.db-content-heading-box .content-title{
    font-family: var(--primaryFontMedium);
    color: #52545A;
    font-size: 1.5em;
    
    margin-bottom: 18px;
}


/* Media Queries */
/* Mobile devices small screen */

@media (max-width: 380px) {
    
}

/* Mobile devices large screen */

@media (max-width: 575.98px) {
    .db-content-heading-box .content-title{
        font-size: 1.125em;
        margin-bottom: 12px;
    }

    .db-content-heading-box .content-subtitle{
        font-size: 0.875em;
    }
    .db-content-heading-box{
        margin-top: 28px;
    }
}


/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {}

/* Extra large devices (large desktops, 1200px and up) */

@media (min-width: 1200px) and (max-width: 1399.98px) {}

