.SubscriptionPackages{
    margin-top: 40px;
}

.heading {
    text-align: left;
}

.heading h2{
    font-weight: 700;
    font-size: 16px;
    color: #282828;
    margin-bottom: 16px;
    font-family: var(--primaryFontBold);
}

.PremiumPlans{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    border-radius: 8px;
    padding: 2px;
    transition: all 0.2s;
}


.PremiumPlans:hover {
    box-shadow: 0px 10px 20px -10px #ddd;
    margin-top: 0px;
}

.PremiumPlansInner .cols{
    width: 100%;
    height: 100%;
}

.priceCols{
    text-align: right;
}

.priceCols h2 {
    font-weight: 700;
    font-size: 24px;
    color: #343434;
    margin-bottom: 10px;
    font-family: var(--primaryFontBold);
}

@keyframes ani_colors {
    0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
    }
}

.priceCols button {
    font-weight: 500;
    /* background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%); */
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #9461FA, #ee7752);
    background-size: 400% 400%;
    border-radius: 40px;
    border: none;
    width: 130px;
    height: 45px;
    animation: ani_colors 4s infinite;
}

.PremiumPlans:global(.NOT_FOUND) {
    cursor: not-allowed;
    opacity: 0.4;
}

.PremiumPlansInner{
    border-radius: 6px;
    background: #fff;
    padding: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 135px;
    min-height: 156px;
}

.PremiumPlansInner .title{
    display: flex;
    align-items: center;
}
.PremiumPlansInner .title h2{
    font-weight: 700;
    font-size: 28px;
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    margin-bottom: 0;
    margin-left: 10px;
    margin-top: 5px;
    font-family: var(--primaryFontBold);
}
.PremiumPlansInner .title img{
    margin-left: 20px;
}
.PremiumPlansInner p{
    width: 200px;
    top: 710px;
    font-weight: 700;
    font-size: 15px;
    line-height: 20px;
    color: #5E606A;
    margin-bottom: 0;
    margin-top: 15px;
}


.PremiumPlansSecond{
    background: var(--purple);
}
.PremiumPlansSecond img{
    margin-left: 0 !important;
}
.PremiumPlansSecond .title h2{
    color: var(--purple);
    -webkit-text-fill-color: inherit;
}
.PremiumPlansSecond .priceCols button{
    background: var(--purple);
}

@media (max-width: 1566px) {
    /* .PremiumPlansInner .cols:first-child{
        width: 100%;
    } */
    .PremiumPlansInner .cols{
        width: 50%;
    }
    .PremiumPlansInner p{
        width: 200px;
        font-size: 13px;
        margin-top: 24px;
    }
    .priceCols h2{
        font-size: 22px;
    }
    .PremiumPlansInner .title h2{
        font-size: 22px;
    }
    .PremiumPlansInner{
        height: auto    ;
    }
    .priceCols button {
        margin-top: 24px;

    }
}

@media (max-width: 575.95px) {
    .PremiumPlansInner .cols{
        width: 100%;
    }
    .PremiumPlansInner p{
        width: 100%;
    }
    .PremiumPlans{
        margin-bottom: 20px;
    }
    .PremiumPlansInner{
        display: block;
        padding: 12px;
        position: relative;
        height: auto;
    }
    .PremiumPlansInner .title svg{
        width: 28px;
        height: 28px
    }
    .PremiumPlansInner .title h2{
        font-size: 25px;
    }
    .PremiumPlansInner .title img{
        margin-left: 20px;
        width: 30px;
    }
    .PremiumPlansInner p{
        width: auto;
    }
    .priceCols h2{
        position: absolute;
        top: 16px;
        right: 13px;
        font-size: 20px;
    }
    .priceCols button{
        width: 100%;
        height: 40px;
        margin-top: 10px;
    }
}