.checkoutWrapper {
    width: 100%;
    min-height: 100vh;
    padding: 24px 15px;
    background: url("../../../../assets/images/layers/checkout-layer-g.svg");
    background-size: cover;

}

.stepperBox {
    display: flex;
    margin-bottom: 24px;
}

.stepper {
    display: flex;
    margin-right: 16px;
    color: #BCBCBC;
    font-family: var(--primaryFontBold);
}

.stepper .stepperCircle {
    background-color: #BCBCBC;
    color: #ffffff;
    font-family: var(--primaryFontBold);
    font-size: 14px;
    width: 24px;
    height: 24px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 8px;
}

.stepper.active {
    color: var(--primaryColor);
}

.stepper.active .stepperCircle {
    background-color: var(--primaryColor);
}


