/* theme generic section code */
.theme-service-box .section-tag {
  color: var(--themeTextColor) !important;
}

.theme-service-box .message-text {
  color: var(--themeTextColor) !important;
}

#experience.style_2 .basic-long-card .img_wrapper_box{
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
}
#experience.style_2 .basic-long-card .imageLoaderCard img {
  width: 150px;
  height: 150px;
}

#experience.style_2 .basic-long-card .imageLoaderCard {
  top: 50%;
  transform: translate(-50%, -50%);
}
.theme-service-box {
  /* background-image: url("../../../assets/images/layers/overlay-dots.png");
      background-repeat: no-repeat;
      background-position: top right;
      background-size: 27%; */
  position: relative;
  border-radius: 50px;
}

.theme-service-box .section-tag {
  font-family: var(--themePrimaryFont);
  font-size: 2em;
  margin-bottom: 1.25rem;
  color: var(--themePrimaryColor);
  font-weight: 600;
}

.theme-service-box .message-text {
  font-size: 1.3em;
  font-family: var(--themePrimaryFont);
  line-height: 40px;
  letter-spacing: 1px;
  margin-bottom: 1.4em;
}

.style_3 .horizontal-card .card-footer {
  background: transparent;
  border: none;
}

#experience.style_3 .horizontal-card .card-footer .link {
  text-align: center;
}

#experience.style_3 .horizontal-card .card-footer .link a {
  color: var(--themeTextColor);
  font-size: 14px;
  text-decoration: none;
  font-weight: 500;
}

#experience.style_3 .card-footer button {
  border-color: var(--themeTextColor);
  color: var(--themeTextColor);
  background: var(--themePrimaryColor);
}

#experience.style_3 .card-title .character-limit {
  position: absolute;
  z-index: 10;
  top: -18px;
}

#experience.style_4 .card-footer button {
  background: var(--themeTextColor);
  color: var(--themePrimaryColor);
  border-color: var(--themeTextColor);
  min-width: auto;
  font-size: 16px !important;
  font-weight: 400;
  width: 128px !important;
  font-style: normal;
  height: 35px !important;
  padding: 5px 12px !important;
}

#experience.style_4 .link {
  text-align: left !important;
}

#experience.style_4 .link a {
  text-decoration: none;
  color: var(--themeTextColor);
  padding-left: 2px;
}

#experience.style_3 .img-box img {
  width: 130px;
  height: 130px;
}

#experience.style_3 .img-box {
  width: 130px !important;
}

.style_3 .horizontal-card {
  padding-bottom: 8px;
}

#experience.style_3 .card-body {
  padding-bottom: 5px;
}

.style_3 .horizontal-card .card-title {
  font-weight: 400;
  color: var(--themeTextColor);
  line-height: 21.82px;
  margin-bottom: 10px;
}

.style_3 .horizontal-card .card-text {
  font-weight: 400;
  color: var(--themeTextColor);
  line-height: 19.2px;
}

#experience.style_4 .card-title {
  font-size: 20px;
  line-height: 24.26px;
}

#experience.style_4 .card-text {
  line-height: 21.6px;
}

#experience.style_4 .card-title,
#experience.style_4 .card-text {
  color: var(--themeTextColor);
}

#experience.style_3 .btnSection button svg {
  display: none;
}

#experience.style_3 .btnSection button {
  min-width: 100%;
  text-align: center !important;
  background: var(--themeTextColorSecondary);
  height: 35px;
  line-height: 33px;
  border: 1px solid var(--themeTextColor);
  color: #fff !important;
}

#experience.style_5 .editable-content-box .h4 {
  margin-bottom: 30px;
}

#experience.style_5 .theme-general-card {
  min-height: 410px;
}
#experience.style_5 .card-footer{
  text-align: left;
  padding: 0;
}


#experience.style_5 .card-footer button {
  padding: 0;
  height: auto;
  border: none;
  text-decoration: none;
}

#experience.style_5 .card-footer button:hover {
  background: transparent;
}
#experience.style_6 .circle-image-card .service-title-left {
  margin-bottom: 35px;
  margin-top: 10px !important;
  color: var(--themeTextColor);
  font-size: 36px;
  line-height: 44px;
}

#experience.style_6 .circle-image-card {
  min-height: 370px;
  padding: 15px 10px 25px;
}

#experience.style_6 .circle-image-card .img-box {
  text-align: left;
  width: fit-content;
}

#experience.style_6 .circle-image-card .img-box img {
  border-radius: 200px;
}

#experience.style_6 .card-footer {
  padding-bottom: 0;
}

#experience.style_6 .card-footer button {
  background: var(--themeTextColor);
  color: var(--themePrimaryColor);
  border-color: var(--themeTextColor);
  padding: 8px 0 !important;
  font-size: 16px !important;
}

#experience.style_6 .card-footer .link a {
  text-decoration: none;
}

#experience.style_6 .circle-image-card .service-body-left {
  font-size: 24px;
  line-height: 28px;
  color: var(--themeTextColor);
  margin-bottom: 0;
}


/* theme generic section code */

@media (max-width: 575.98px) {
  #experience.style_4 .card-title {
    font-size: 16px;
  }

  #experience.style_4 .card-text {
    font-size: 12px;
  }

  #experience.style_6 .circle-image-card{
    min-height: 315px;
  }

  #experience.style_6 .circle-image-card .service-title-left {
    font-size: 24px;
    line-height: 21px;
    margin-bottom: 10px;
  }

  #experience.style_6 .circle-image-card .service-body-left{
    font-size: 16px;
    line-height: 21px;
  }
}

/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
  .theme-section-wrapper {
    padding-top: 0;
  }
}