.video-thumbnail-sec{
    position: relative;
    justify-content: flex-start !important;
}

.video-thumbnail-sec .guide-msg {
    display: none;
    background-color: var(--themeTextColor);
    color: var(--themePrimaryColor);
    position: absolute;
    top: 0px;
    right: 0px;
    padding: 2px 10px;
    border: 2px;
    font-size: 12px;
  }
  .video-thumbnail-sec:hover .guide-msg {
    display: inline-block;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px;
  }

  #videos.style_1 .video-thumbnail-sec:hover .card-edit-block{
    display: block;
  }