.wrapper,
.wrapper[data-type="plain"] {
  position: relative;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 9;
  width: 100%;
  background-color: #FFFFFF55;
}

.wrapper[data-display="fullscreen"] {
  position: fixed;
  height: 100vh;
  align-items: center;
  display: flex;
}

.wrapper[data-display="section"] {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.wrapper[data-display="section"] .loader:before {
  display: none;
}

.wrapper[data-type="spinner"] .loader {
  width: 60px;
  height: 60px;
  margin: 10px auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
}

.wrapper[data-display="plain"] .loader {
  width: 60px;
  height: 60px;
  margin: 0 auto;
  position: relative;
  top: 0;
  left: 0;
  transform: none;
  -webkit-transform: none;
  -moz-transform: none;
  -ms-transform: none;
  -o-transform: none;
}

.wrapper[data-display="fullscreen"] .loader:before {
  content: '';
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.1);
}

/* :not(:required) hides these rules from IE9 and below */

.wrapper[data-type="spinner"] .loader:not(:required) {
  /* hide "loader:global(.spinner)..." text */
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}

.wrapper[data-type="spinner"] .loader:not(:required):after {
  content: '';
  display: block;
  font-size: 10px;
  width: 60px;
  height: 60px;
  -webkit-animation: spinner 1500ms infinite linear;
  -moz-animation: spinner 1500ms infinite linear;
  -ms-animation: spinner 1500ms infinite linear;
  -o-animation: spinner 1500ms infinite linear;
  animation: spinner 1500ms infinite linear;
  border-left: 4px solid rgba(255, 153, 51, 0.15);
  border-right: 4px solid rgba(255, 153, 51, 0.15);
  border-bottom: 4px solid rgba(255, 153, 51, 0.15);
  border-top: 4px solid var(--purple);
  border-radius: 100%;
}

/* Animation */

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-moz-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-o-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

:global(.fullscreen) {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #ffffff99;
}

:global(.plain) {
  position: relative;
  height: 100% !important;
}

.wrapper[data-size="sm"] .loader,
.wrapper[data-size="sm"] .loader:not(:required):after {
  width: 30px;
  height: 30px;
  margin-top: -15px;
}
