.interestContentBox{
    background-color: #FBFCFF;
}

.interestContentBoxBody{
    padding: 0px;
}

.interestWrapper{
    padding: 24px;
    padding-bottom: 0px;
}

.selectedCategoryBox .titleBox .title{
    font-family: var(--primaryFontBold);
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 16px;
}

.themeTypeBox{
    margin-top: 20px;
}

.themeTypeBox .list{
    display: flex;
    padding-left: 0px;
    position: relative;
    list-style: none;
}

.themeTypeBox .list .listItem{
    font-size: 16px;
    color: #858585;
    font-family: var(--primaryFontBold);
    margin-left: 16px;
    margin-right: 16px;
    cursor: pointer;
}

.themeTypeBox .list .listItem:first-child{
    margin-left: 0px;
}

.themeTypeBox .list .listItem.active{
    color: var(--primaryColor);
    text-decoration: underline;
    text-underline-position: under;
}

.selectedCategory{
    margin-top: 20px;
    margin-bottom: 20px;
}

.selectedCategoryPills{
    position: relative;
    background-color: var(--primaryColor);
    color: #ffffff;
    border: 1px solid var(--primaryColor);
    font-family: var(--primaryFontMedium);
    font-size: 16px;
    padding: 6px 20px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}

.categoryPills{
    position: relative;
    background-color: #ffffff;
    color: #858585 !important;
    border: 1px solid #858585 !important;
    font-family: var(--primaryFontMedium);
    font-size: 16px;
    padding: 6px 20px;
    border-radius: 50em !important;
    -webkit-border-radius: 50em !important;
    -moz-border-radius: 50em !important;
    -ms-border-radius: 50em !important;
    -o-border-radius: 50em !important;
}

.categoryPills.active{
    background-color: var(--orange) !important;
    color: #ffffff !important;
    border: 1px solid var(--orange) !important;
}

.categoryPills:global(.active){
    background-color: var(--orange) !important;
    color: #ffffff !important;
    border: 1px solid var(--orange) !important;
    padding: 6px 20px;
}

.closeIcon svg {
    width: 8px;
    height: 8px;
    position: relative;
    top: -3px;
}

.closeIcon:hover {
    transform: scale(0.90);
    border-color: #d00;
    transition: all 0.5s;
}

.closeIcon:hover svg {
    filter: invert(1);
}

.closeIcon {
    position: absolute;
    background: #fff;
    width: 22px;
    height: 22px;
    right: -6px;
    top: -10px;
    border-radius: 30px;
    box-shadow: 0px 0px 13px #00000033;
    border: 1px solid #8c38af;
    transition: .3s;
}

.closeIcon:hover {
    background: #ff0000;
}

.closeIcon:hover svg{
    color: #ffffff;
}


.lineBar{
    margin-top: 24px;
    margin-bottom: 24px;
}

.btnBox{
    padding: 24px;
    padding-top: 0px;
    text-align: right;
}

.btnBox .btnPrimary{
    background-color: var(--primaryColor);
    color: #ffffff;
    padding: 8px 24px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}

.btnBox .btnPrimary:hover{
    background-color: var(--primaryColorHover);
}

@media(max-width: 575.98px){
    .interestContentBoxBody{
        padding: 0px !important;
    }
    .interestWrapper{
        padding: 16px;
        padding-bottom: 0px;
    }
    .themeTypeBox .list{
        flex-wrap: wrap;
    }

    .themeTypeBox .list .listItem{
        margin-left: 0px;
        margin-right: 16px;
        margin-bottom: 10px;
    }

    .categoryPills,
    .selectedCategoryPills{
        font-size: 14px;
        padding: 4px 16px;
    }

    .btnBox{
        padding: 16px;
        padding-top: 0px;
    }
}