.card-main-edit-block{
    display: flex;
    justify-content: flex-end;
    position: absolute;
    top: 0px;
    right: 0px;
}
.disabledEdit .edit-box {pointer-events: none !important;opacity: .8;filter: contrast(0.5);}

.disabledEdit {cursor: not-allowed !important;}

.card-main-edit-block .edit-option{
    display: flex;
    margin-right: 1em;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}
.card-main-edit-block .action-box{
    background-color: #ffffff;
    color: var(--primaryColor);
}
.card:hover .card-edit-block{
    visibility: visible;
}

.card-edit-block{
     display: flex;
    visibility: hidden;
    justify-content: flex-end;
    position: absolute;
    top: 15px;
    right: 5px;
    cursor: pointer;
    z-index: 1;
}
.card-edit-block .edit-option{
    display: flex;
    margin-right: 10px;
}
.card-edit-block .edit-box{
    width: 24px;
    height: 24px;
    background-color: var(--primaryColor);
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    font-size: 13px;
}

.card-edit-block .option-box{
    width: 24px;
    height: 24px;
    background-color: #ffffff;
    color: var(--primaryColor);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    font-size: 13px;
}
.card-edit-block .edit-box .influencer-edit-banner{
    cursor: pointer;
}
@media (max-width: 320px){
/* .card-edit-block{
    top: 12px !important;
    right: -6px !important;

} */
.card-edit-block .edit-box{
    margin-left: 10px !important;
}
.influencer-edit-banner{
    font-size: 12px;
    position: relative;
    top: -2px;
}
}

@media (max-width: 575.99px) {
.card-edit-block{
    top: 5px;
}
.influencer-edit-banner{
    font-size: 14px;
    position: relative;
    top: 0px;
}
