:global(.App) {
    overflow-x: hidden;
}

.lineBarDiv{
    position: relative;
}
.lineBar{
    margin: 24px 0;
}
.lineBarDiv p{
    position: absolute;
    top: -9px;
    background: white;
    width: 35px;
    text-align: center;
    margin-bottom: 0;
    left: 47%;
    color: #5E606A;
    font-size: 14px;
    font-family: var(--primaryFontMedium);
    font-weight: 700;
}
.loginGoogle{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    border-radius: 60px;
    margin-bottom: 15px;
    padding: 1px;
}
.submitBtnGoogle{
    font-family: var(--primaryFontBold);
    background: #fff;
    font-size: 16px;
    max-width: 100%;
    width: 100%;
    height: 44px;
    color: #5E606A;
    padding-top: 7px;
    border: 0;
    transition: 0s;
}
.submitBtnGoogle svg{
    margin-right: 8px;
    position: relative;
    top: -2px;
}
.submitBtnGoogle:hover{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%);
    color: #fff;
    transition: 0s;
}
.submitBtnGoogle:hover svg path{
    fill: #fff;
}

.loginSection{
    top: 58px;
}

.loginFormSection{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    z-index: 20;
    position: relative;
    flex-direction: column;
}

.loginFormSection .trialEndAlert{
    background-color: #FFEFFE;
    color: #8B39B2;
    font-size: 1.125em;
    max-width: 870px;
    width: 100%;
    border-radius: 12px;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    -ms-border-radius: 12px;
    -o-border-radius: 12px;
}

.signupLogo.posAbs {
    left: 20px;
    top: 10px;
    position: absolute;
    padding: 10px 24px;
    z-index: 10;
    border-radius: 50px;
}

.signupLogo {
    max-width: 140px;
    background-color: #fff;
    padding: 10px 0px;
}

.signupLogo img {
    max-width: 100%;
}

.alertLink{
    font-family: var(--primaryFontBold);
    color: var(--primaryColor);
    text-decoration: none;
}

.loginFormSection .authLogincard{
    max-width: 870px;
    border-radius: 18px;
    -webkit-border-radius: 18px;
    -moz-border-radius: 18px;
    -ms-border-radius: 18px;
    -o-border-radius: 18px;
    padding: 43px 107px;
    width: 100%;
}

.cardContent .title{
    font-family: var(--primaryFont);
    font-size: 28px;
    text-align: left;
    margin-bottom: 22px;
    color: var(--primaryColor);
    font-weight: 600;
}

.forgetPassBox{
    text-align: center;
    border-bottom: 1px solid #E9E9E9;
    padding-top: 15px;
    padding-bottom: 30px;
    width: 90%;
    margin: auto;
}
.signUpBox{

}

.signUPLink{
    display: flex;
    justify-content: center;
    padding-top: 32px;
}

.signUPLink p {
    color: #C4C4C4;
    font-family: var(--primaryFontBold);
    font-size: 16px;
    text-decoration: none;
    margin-bottom: 0px;
}

.signUPLink .forgetPass{
    color: var(--primaryColor);
    font-family: var(--primaryFontBold);
    font-size: 16px;
    margin-left: 10px;
    text-decoration: none;
    line-height: initial;
}

.forgetPassBox .forgetPass{
    color: var(--primaryColor);
    font-family: var(--primaryFontBold);
    font-size: 1em;
    text-decoration: none;
}

.cardLabel {
    font-family: var(--primaryFont);
    font-weight: 700;
    font-size: 16px;
    margin-bottom: 11px;
}

.inputField {
    height: 50px;
    border-radius: 7px;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    -ms-border-radius: 7px;
    -o-border-radius: 7px;
}

.submitBtn {
    font-size: 16px;
    width: 100%;
    padding: 15px 22px;
    margin-top: 15px;
}

.loginLeftScreen{
    background: #F5E8FF;
    height: 100vh;
}

.mainLogin{
    position: relative;
}

.loginForm{
    position: absolute;
    left: 50%;
    width: 50%;
    top: 50%;
    transform: translate(-50%, 0);
    height: 100vh;
    padding-top: 14%;
}
.loginLeftScreen{
    position: relative;
    overflow: hidden;
}
.card1Img{
    position: absolute;
    right: 250px;
    top: 200px;
    animation: 3s linear 2s forwards card1Img;
    z-Index: 1;
    visibility: hidden;

}
.card1Img .card1img{
    width: 230px;
    transform: rotate(9deg) !important;
}

@keyframes card1Img {
    0% {
        right: -150px;
      visibility: hidden;
      transform: rotate(-360deg) scale(.1);
    }
    100% {
        right: 250px;
        visibility: visible;
        transform: rotate(0deg) scale(1.1);
    }
  }


  .card2Img{
    position: absolute;
    left: 25%;
    top: 400px;
    visibility: hidden;
    animation: 2s linear 4s forwards card2Img;
}
@keyframes card2Img {
    0% {
      left: -200px;
      visibility: hidden;
      transform: rotate(300deg) scale(.3);
    }
    100% {
        left: 25%;
        visibility: visible;
        transform: rotate(0deg) scale(1.1);
    }
  }

  .card3Img{
    position: absolute;
    right: 150px;
    top: 560px;
    animation: 2s linear 4s forwards card3Img ;
    visibility: hidden;
}
@keyframes card3Img {
    0%{
        right: -300px;
        visibility: hidden;
        transform: rotate(300deg) scale(.1);
    }

    100% {
        right: 150px;
        visibility: visible;
        transform: rotate(0deg) scale(1.1);
    }
  }
  .icon01{
    position: absolute;
    left: -75px;
    top: 15px;
    animation: 1s linear 6s forwards icon01 ;
    visibility: hidden;
  }
  @keyframes icon01 {
    0%{
        visibility: hidden;
        opacity: 0;
        transform: scale(3.5);
    }

    100% {
        opacity: 1;
        visibility: visible;
        transform: scale(1.1);
    }
  }
  .icon02{
    position: absolute;
    right: -140px;
    top: 65px;
    animation: 1s linear 7s forwards icon02 ;
    visibility: hidden;
}
@keyframes icon02 {
    0%{
        visibility: hidden;
        opacity: 0;
        transform: scale(3.5);
    }

    100% {
        opacity: 1;
        visibility: visible;
        transform: scale(1.1);
    }
  }
.icon03{
    position: absolute;
    left: -70px;
    top: 200px;
    animation: 1s linear 8s forwards icon03 ;
    visibility: hidden;
}
@keyframes icon03 {
    0%{
        visibility: hidden;
        opacity: 0;
        transform: scale(3.5);
    }

    100% {
        opacity: 1;
        visibility: visible;
        transform: scale(1.1);
    }
  }

  @media (max-width: 1366px){
    .loginForm{
        padding-top: 10%;
    }
  }

@media (max-width: 1024px){
    .resRow{
        display: flex;
        flex-direction: column;
    }
    .resColumn1, .resColumn2{
        width: 100%;
    }
    .loginLeftScreen{
        height: 400px;
    }
    .card1img{
        width: 160px !important;
    }
    .card2img{
        width: 225px;
    }
    .card3img{
    }
    .card1Img{
        top: 26px;
    }
    .card2Img{
        top: 80px;
    }
    .card3Img{
        top: 190px;
    }
    .icon01{
        top: 23px;
    }
    .icon02{
        right: -325px;
    }
    .icon03{
        top: 130px;
    }
    .loginForm{
        padding-top: 150px;
        padding-bottom: 60px;
        height: auto;
    }
}

@media (max-width: 575.98px){
    .loginForm{
        padding-top: 40px;
    }
    .card1Img{
        top: 26px;
        right: 100px;
    }
    @keyframes card1Img {
        0% {
            right: -150px;
          visibility: hidden;
          transform: rotate(-360deg) scale(.1);
        }
        100% {
            right: 100px;
            visibility: visible;
            transform: rotate(0deg) scale(1.1);
        }
      }

      .card2Img{
        top: 80px;
        left: 30px;
    }
      @keyframes card2Img {
        0% {
          left: -200px;
          visibility: hidden;
          transform: rotate(300deg) scale(.3);
        }
        100% {
            left: 30px;
            visibility: visible;
            transform: rotate(0deg) scale(1.1);
        }
      }
      .card3Img{
        top: 190px;
        right: 0px;
    }
    @keyframes card3Img {
        0%{
            right: -300px;
            visibility: hidden;
            transform: rotate(300deg) scale(.1);
        }

        100% {
            right: 0px;
            visibility: visible;
            transform: rotate(0deg) scale(1.1);
        }
      }
      .loginForm{
       width: 90%;
    }
    .loginSection{
        top: 0px;
    }
    .loginFormSection .trialEndAlert{
        font-size: 1em;
    }
    .loginFormSection .authLogincard{
        padding: 10px 0px;
    }

    .authLogincard .cardLabel{
        font-size: 1em;
    }

    .authLogincard .submitBtn{
        font-size: 1em;
    }
}