.upgradePlanFeaturesModal .modalContent {
    border-radius: 12px;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    -ms-border-radius: 12px;
    -o-border-radius: 12px;
}

.upgradePlanFeaturesModal .modalBody{
    padding: 0px;
    position: relative;
}

.upgradePlanFeaturesModal .modalBody .closeIcon {
    border: none;
    margin-left: auto;
    position: absolute;
    top: 6px;
    right: 10px;
}

.upgradePlanFeaturesModal .modalBody .closeIcon .icon {
    color: #ffffff;
}

.upgradePlanFeaturesModal .contentBox {
    display: flex;
    align-items: center;
    height: 552px;
}

.contentBox .leftBox {
    flex: 1;
    height: 100%;
    display: flex;
    align-items: center;
    padding-left: 77px;
    padding-right: 77px;
}

.contentBox .leftBox .contentWrapper{
    width: 77%;
}

.upgradePlanFeaturesModal .contentBox .heading {
    font-size: 1.5em;
    font-family: var(--primaryFontBold);
    margin-bottom: 22px;
    color: #52545A;
}

.contentBox .desc {
    font-family: var(--primaryFont);
    font-size: 1em;
    color: #858585;
}

.contentBox .featureList{
    padding-left: 22px;
    margin-bottom: 22px;
}

.contentBox .featureListItem{
    color: #858585;
    list-style-image: url("../../../assets/images/icons/ic_success.svg");
}

.contentBox .rightBox {
    background-color: #5B3F91;
    width: 239px;
    height: 100%;
    display: flex;
    align-items: center;
    border-top-right-radius: 12px;
    border-bottom-right-radius: 12px;
}

.contentBox .rightBox .img{
    position: relative;
    left: -31px;
}

.btnPro {
    background-color: var(--orange);
    color: #ffffff;
    text-decoration: none;
    padding: 11px 16px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
    min-width: 163px;
    text-align: center;
}

.btnPro:hover {
    color: #ffffff;
    cursor: pointer;
}

@media (max-width: 575.98px) {
    .upgradePlanFeaturesModal .modalBody .closeIcon .icon {
        color: var(--primaryColor);
    }

    .upgradePlanFeaturesModal .modalHeader .title {
        font-size: 1.375em;
    }

    .upgradePlanFeaturesModal .contentBox {
        width: 100%;
        height: 100%;
    }

    .contentBox .leftBox .contentWrapper{
        width: 100%;
    }

    .upgradePlanFeaturesModal .contentBox .heading {
        font-size: 1.125em;
    }

    .contentBox .leftBox{
        padding: 40px 12px 18px 12px;
    }

    .contentBox .rightBox {
        display: none;
    }

    .contentBox .btnBox{
        text-align: center;
    }

}