.layerWrapper {
    /* height: 100%; */
    width: 33.3%;
}

.layerWrapper .layer {
    position: absolute;
}

.layerWrapper .freeLayerOne {
    top: 14px;
    left: -20px;
    width: 128px;
    height: 128px;
}

.layerWrapper .freeLayerTwo {
    bottom: -57px;
    right: 5px;
    width: 132px;
    height: 132px;
}

.layerWrapper .proLayerOne {
    bottom: 0px;
    right: -30px;
    width: 118px;
    height: 118px;
}

.layerWrapper .proLayerTwo {
    top: -88px;
    right: 90px;
    width: 140px;
    height: 140px;
}

.layerWrapper.free .proLayerOne,
.layerWrapper.free .proLayerTwo {
    display: none;
}

.layerWrapper.pro .freeLayerOne,
.layerWrapper.pro .freeLayerTwo {
    display: none;
}

.priceCard {
    /* height: 575px !important; */
    height: 100%;
    padding: 32px;
    background: linear-gradient(134.93deg, rgba(255, 255, 255, 0.7) 6.99%, rgba(242, 241, 239, 0) 190.55%);
backdrop-filter: blur(8px);
/* Note: backdrop-filter has minimal browser support */
    border: none;
    border-radius: 18px;
    margin-right: 24px !important;
    margin-left: 24px !important;
    /* min-height: 730px; */
}

.priceCard .cardBody {
    padding: 0px;
}

.priceCard .cardTitle {
    font-family: var(--primaryFontBold);
    font-size: 32px;
    margin-bottom: 8px;
    line-height: 44px;
    text-align: left;
    text-transform: capitalize !important;
    color: #5B3F91;
}



.priceCard .titleContent {
    display: flex;
    justify-content: space-between;
}

.priceCard .titleContent .fireLabel {
    color: var(--orange);
    font-family: var(--primaryFontMedium);
    font-size: 1em;
    position: relative;
}

.priceCard .packagePrice {
    font-family: var(--primaryFontBold);
    font-size: 20px;
    line-height: 24px;
    color:  #5E606A;
    margin-bottom: 16px;
}

.priceCard .packagePrice>.label {
    color: #5E606A;
}

.tagLine {
    font-size: 1em;
    font-family: var(--primaryFont);
    color: #858585;
    margin-bottom: 20px;
}

.tagLine.pro {
    background: #9461fa;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #9461fa, #ffa847);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #9461fa, #ffa847);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
}

.detailList {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 16px;
}

.priceDetailCheckmarks .iconList {
    font-size: 24px;
    margin-right: 12px;
    color: var(--primaryColor);
}

.priceCard.active .priceDetailCheckmarks .iconList {
    color: #5B3F91;
}

.detailBoxTitle {
    font-family: var(--primaryFontBold);
    font-size: 1.25em;
    color: #5B3F91;
    line-height: 26px;
    margin-bottom: 16px;
}

.priceDetailText {
    margin-bottom: 0px;
    color: #5E606A;
    padding-top: 3px;
    font-size: 16px !important;
}

.priceCard .packageBtn {
    padding: 12px 32px;
}

.priceCard.active .packageBtn {
    background-color: var(--primaryColor);
    color: #ffffff;
}

.priceCard .cardFooter {
    background-color: transparent;
    border-top: none;
    padding: 0px;
}

/* ====================== */

@media screen and (max-width: 575.98px) {
    .layerWrapper {
        height: 100%;
        width: 100%;
        margin-bottom: 20px;
    }
    .priceCard {
        margin-right: 6px !important;
    margin-left: 6px !important;
    }
    .priceCard .cardTitle{
        font-size: 28px;
    }

    .priceCard .packagePrice {
        font-size: 1em;
        margin-bottom: 12px;
    }

    .tagLine {
        font-size: 14px;
    }

    .priceCard .packageBtn {
        padding: 8px 16px;
        font-size: 14px;
    }

    .layerWrapper .proLayerTwo {
        top: -73px;
    }
    .layerWrapper .proLayerOne {
        right: 6px;
    }
}