/* Freelance service section code  */

.freelancer-service-box {
  /* background-image: url("../../../assets/images/layers/overlay-dots.png");
    background-repeat: no-repeat;
    background-position: top right;
    background-size: 27%; */
  position: relative;
  border-radius: 50px;
}

.freelancer-service-box .section-tag {
  font-family: var(--themePrimaryFont);
  font-size: 2em;
  margin-bottom: 1.25rem;
  color: var(--themePrimaryColor);
  font-weight: 600;
}

.freelancer-service-box .message-text {
  font-size: 1.3em;
  font-family: var(--themePrimaryFont);
  line-height: 40px;
  letter-spacing: 1px;
  margin-bottom: 1.4em;
}

.btn-box.btn-more .btn {
  min-width: 174px;
}

.circle-overlay-container {
  position: relative;
  z-index: 0;
}

.circle-overlay-solid {
  width: 310px;
  height: 310px;
  background-color: var(--themePrimaryColor);
  border-radius: 50%;
  position: absolute;
  top: 0px;
  right: 0px;
  /* background-blend-mode: overlay;
    mix-blend-mode: soft-light; */
  opacity: 0.5;
  z-index: 0;
}

/* Freelance service section code ends */

/* theme generic section code */
.theme-service-box .section-tag {
  color: var(--themeTextColor) !important;
}

.theme-service-box .message-text {
  color: var(--themeTextColor) !important;
}

.theme-service-box {
  /* background-image: url("../../../assets/images/layers/overlay-dots.png");
    background-repeat: no-repeat;
    background-position: top right;
    background-size: 27%; */
  position: relative;
  border-radius: 50px;
}

.theme-service-box .section-tag {
  font-family: var(--themePrimaryFont);
  font-size: 2em;
  margin-bottom: 1.25rem;
  color: var(--themePrimaryColor);
  font-weight: 600;
}

.theme-service-box .message-text {
  font-size: 1.3em;
  font-family: var(--themePrimaryFont);
  line-height: 40px;
  letter-spacing: 1px;
  margin-bottom: 1.4em;
}
#services.style_4 .btnSection{
  margin-bottom: 0 !important;
}

#services.style_4 .btnSection button {
  border: none;
  width: 100%;
  display: flex;
  justify-content: center;
  font-size: 18px !important;
  color: var(--themePrimaryColor) !important;
}

#services.style_4 .btnSection button svg {
  top: 5px;
  position: relative;
}

#services.style_4 .card .card-footer .link>a {
  display: block;
  text-align: center !important;
  text-decoration: none;
  color: var(--themePrimaryColor);
}

#services.style_4 .card-wrapper{
  margin-bottom: 30px;
}
#services.style_4 .horizontal-card .card-edit-block .edit-option{
  margin-right: 0px;
}

#services.style_4 .horizontal-card .card-body .card-title{
  font-family: var(--themePrimaryFontMedium);
  color: var(--themeTextColor);
  font-size: 1.75em;
  margin-bottom: 5px;
}
#services.style_4 .horizontal-card .card-body .card-text{
  font-family: var(--themePrimaryFont);
  color: var(--themeTextColor);
  font-size: 1.125em;
}

#services.style_4 .horizontal-card .card-body .img-box img{
  width: 180px;
  height: 190px;
}
#services.style_3 .btnSection button {
  background: transparent;
  display: flex;
  justify-content: left;
  text-decoration: none;
  font-family: var(--themePrimaryFontMedium);
  font-size: 18px !important;
  color: var(--themeTextColor) !important;
}

#services.style_3 .btnSection button svg {
  top: 5px;
  position: relative;
  display: block;
}

#services.style_3 .card .card-footer .link>a {
  display: block;
  text-align: left !important;
  text-decoration: none;
  font-size: 16px;
  color: var(--themeTextColor);
}

#services.style_3 .circle-image-card{
  min-height: 450px;
}

#services.style_3 .circle-image-card .img-box{
  width: 196px ;
  margin-bottom: 10px;
}
#services.style_3 .circle-image-card .img-box img{
  height: 196px ;
  width: 196px ;
  border-radius: 50em;
  /* object-fit: cover; */
}

#services.style_3 .circle-image-card .imageLoaderCard img{
  width: 150px;
  height: 150px;
}

#services.style_3 .card-edit-block .edit-box {
  position: relative;
  /* right: 18px;
  top: 18px; */
}

#services.style_3 .horizontal-line {
  border-color: var(--themeTextColor);
  margin-top: 0;
}
#services.style_3 .service-title-left{
  font-size: 24px;
  line-height: 34px;
  font-weight: 400;
}
#services.style_3 .service-body-left {
  line-height: 34px;
}
#services.style_3 .service-title-left,
#services.style_3 .service-body-left {
  color: var(--themeTextColor);
}

#services.style_4 .card-footer{
  background-color: var(--themeTextColor) !important;
}

#services.style_4 .horizontal-card .card-footer button{
  padding: 15px 10px;
  font-size: 26px !important;
}

#services.style_5 .card-footer .btn-box {
  position: relative;
  top: 4px;
}

#services.style_5 .card-wrapper:hover .card-edit-block{
  display: block;
}

#services.style_5 .flip-card .card-title,
#services.style_5 .flip-card .count-title{
  color: var(--themeTextColor);
}

#services.style_5 .flip-card .card-text{
  color: var(--themeTextColorSecondary);
}

/* theme generic section code */

@media (max-width: 575.98px) {

  #services.style_4 .card-wrapper {
    width: 100%;
  }

  #services.style_4 .horizontal-card .card-body .img-box img{
    width: 120px;
    height: 120px;
  }

  #services.style_4 .horizontal-card .card-body .card-title {
    font-size: 1em;
    line-height: 21px;
  }
  #services.style_4 .horizontal-card .card-body .card-text {
    font-size: 0.875em;
    line-height: 18px;
    margin-bottom: 5px;
  }

  .circle-overlay-solid {
    width: 100px;
    height: 100px;
    background-color: var(--themePrimaryColor);
    border-radius: 50%;
    position: absolute;
    top: 0px;
    right: 0px;
    /* background-blend-mode: overlay;
        mix-blend-mode: soft-light; */
    opacity: 0.5;
  }

  .freelancer-service-box .section-tag {
    font-size: 1.6em;
    margin-bottom: 18px;
    font-weight: 800;
  }

  .freelancer-service-box .message-text {
    font-size: 1em;
    font-weight: 800;
    margin-bottom: 24px;
    line-height: 24px;
  }

  /* style 3 service */
  #services.style_3 .circle-image-card{
    min-height: 315px;
  }
  #services.style_3 .circle-image-card .service-title-left{
    font-size: 21px;
  }

  #services.style_3 .circle-image-card .service-body-left{
    font-size: 1em;
    line-height: 21px;
  }

  #services.style_3 .circle-image-card .img-box{
    width: 90px;
  }

  #services.style_3 .circle-image-card .img-box img{
    width: 90px;
    height: 90px;
  }

  #services.style_4 .horizontal-card .card-footer button{
    padding: 15px 10px;
    font-size: 18px !important;
  }
}