
.wrapper{
    margin-bottom: 120px;
}
.headingFaq h2{
    font-size: 52px;
    font-weight: bold;
    padding-left: 12px;
    font-family: var(--primaryFontBold);
}
.AccordionItem{
    border: none;
}
.AccordionItem h2 button{
    color: #000;
}

@media (max-width: 1024px) {
    .headingFaq h2{
        font-size: 42px;
    }
}

@media (max-width: 767px) {
    .headingFaq {
        text-align: center;
        margin-bottom: 30px;
    }
}

@media (max-width: 400px) {
    .headingFaq h2{
        font-size: 30px;
    }
}