.landing-footer-section {
  position: relative;
  padding-top: 360px;
  padding-bottom: 45px;
  background-repeat: no-repeat !important;
  background-position: 0 0px !important;
  background-size: cover !important;
  max-height: 1600px;
  background-image: url("../../../assets/images/landinggroup/footerImg.webp");
  overflow: hidden;
}

.landing-footer-section .border-end {
  border-right: 0.3px solid #1e1e1e;
}

.footerLinks h3 {
  font-size: 20px;
  line-height: 24px;
  color: #FBFCFF;
  font-family: var(--secondaryFontBold);
  margin-bottom: 24px;
}

.footerLinks ul {
  padding: 0;
}

.footerLinks ul li {
  list-style: none;
  margin-bottom: 16px;
}

.footerLinks ul li a {
  text-decoration: none;
  color: #EAD0FF;
  font-size: 14px;
  transition: .3s;
}

.footerLinks ul li:last-child {
  margin-bottom: 0;
}

.footerLinks ul li a:hover {
  padding-left: 5px;
  transition: .3s;
  color: #fff;
}

.footerLogo {
  text-align: center;
}

.footerText h3 {
  background: linear-gradient(143.85deg, #F8AA54 11.27%, #AC82FF 97.1%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  font-family: var(--secondaryFontBold);
  text-align: right;
  width: 259px;
  font-size: 24px;
}

.footerLinks h3 {
  font-size: 20px;
  line-height: 24px;
  color: #FBFCFF;
  font-family: var(--secondaryFontBold);
  margin-bottom: 24px;
}

.footerLinks ul {
  padding: 0;
}

.footerLinks ul li {
  list-style: none;
  margin-bottom: 16px;
}

.footerLinks ul li a {
  text-decoration: none;
  color: #EAD0FF;
  font-size: 14px;
  transition: .3s;
}

.footerLinks ul li:last-child {
  margin-bottom: 0;
}

.footerLinks ul li a:hover {
  padding-left: 5px;
  transition: .3s;
  color: #fff;
}

.footerLogo {
  text-align: center;
}

.footerText h3 {
  background: linear-gradient(143.85deg, #F8AA54 11.27%, #AC82FF 97.1%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  font-family: var(--secondaryFontBold);
  text-align: right;
  width: 259px;
  font-size: 24px;
}

.footerText ul {
  padding: 0;
  float: right;
  margin-top: 58px;
  margin-bottom: 0;
}

.footerText ul li {
  list-style: none;
  display: inline;
  margin-right: 20px;
}

.footerText ul li:last-child {
  margin-right: 0;
}

.footerText {
  width: 258px;
}

.footerText ul li a :hover path {
  transition: .3s;
  fill: #F58634;
}

.footerText p {
  display: inline-block;
  color: #fff;
  text-align: right;
  font-size: 11px;
  float: right;
  margin-top: 25px;
}

.content-input-box .email-icon {
  border-top-right-radius: 0px !important;
  border-bottom-right-radius: 0px !important;
  border-right: none;
  padding-right: 0px;
  border-color: #474747;
  color: var(--orange);
}

.content-input-box .email-input {
  border-top-right-radius: 50rem !important;
  border-bottom-right-radius: 50rem !important;
  border-top-left-radius: 0px !important;
  border-bottom-left-radius: 0px !important;
  border-left: none;
  border-color: #474747;
}

.footer-content .page-lists li a {
  color: #1e1e1e;
  text-decoration: none;
}

.footer-content .page-lists li a.cursor-none {
  cursor: not-allowed;
}

.footer-content .social-list {
  list-style: none;
  padding-left: 0;
  display: flex;
  flex: wrap;
  flex-direction: row;
}

.footer-content .social-list-item {
  padding: 10.5px;
  padding-top: 0px;
}

.footer-content .social-list-item:first-child {
  padding-left: 0px;
}

.footer-content .social-list-item .social-icon {
  font-size: 27px;
}

.footer-content,
.footer-content-subscribe {
  /* padding-top: 106px; */
  /* padding-bottom: 106px; */
  padding-left: 48px;
  padding-right: 48px;
}

.footer-content-subscribe {
  padding-left: 0;
}

.footer-content .content-heading .heading,
.footer-content-subscribe .content-header .heading {
  font-family: var(--primaryFontBold);
  font-size: 21px;
  margin-bottom: 23px;
}

.footer-content-subscribe .content-input-box {
  margin-bottom: 38px;
}

/* social icons footer */
.social-footer-links {
  text-decoration: none;
  color: #1e1e1e;
}

.social-footer-links:hover {
  color: var(--primaryColor);
}

.footer-content-subscribe .content-header .heading{
  color: #FBFCFF;
}


/* Mobile devices small screen */

@media (max-width: 380px) {

  .footer-content,
  .footer-content-subscribe {
    padding-bottom: 0px;
    padding-top: 10px;
  }

  .landing-footer-section .border-end {
    border-right: none !important;
  }

  .footer-content,
  .footer-content-subscribe {
    padding-left: 20px;
  }
}

/* large mobile view */
@media (max-width: 575.98px) {

  .landing-footer-section {
    background-repeat: no-repeat;
    background-size: contain !important;
    /* background-color: #5B3F91; */
    background: url("../../../assets/images/landinggroup/footerImg.webp");
    padding-top: 60px;
  }

  .landing-footer-section::before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position:absolute;
    top: 100px;
    left: 0;
    background-color: #5B3F91;
    z-index: -1;
  }

  .landing-footer-section .footerLogo{
    padding-top: 52px;
    padding-bottom: 16px;
  }

  .landing-footer-section .footerLogo .footer-logo{
    width: 80%;
  }

  .landing-footer-section .footer-slogan{
    margin-bottom: 28px;
  }


  .footer-content,
  .footer-content-subscribe {
    padding-bottom: 0px;
    padding-top: 10px;
  }

  .landing-footer-section .border-end {
    border-right: none !important;
  }

  .footer-content,
  .footer-content-subscribe {
    padding-left: 20px;
  }

  .footerText{
    width: 100%;
  }

  .footerText h3{
    width: 100%;
    font-size: 1.125em;
    line-height: 20px;
    text-align: center;
  }

  .footerText ul{
    float: none;
    text-align: center;
    margin-top: 20px;
  }

  .footerText p{
    float: none;
    text-align: center;
    margin-top: 14px;
    display: block;
  }

  .scroll-indicator{
    display: none;
  }

  .footer-content-subscribe {
    padding-left: 0px !important;
    padding-right: 0px !important;
  }
}

/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
  .footer-content, .footer-content-subscribe {
    padding: 0;
    padding-left: 0px !important;
    padding-right: 0px !important;
  }

  .border-end {
    border-right: 0 !important;
  }

}

/* Extra large devices (large desktops, 1200px and up) */

@media (min-width: 1200px) and (max-width: 1399.98px) {
  .footer-content .content-heading .heading, .footer-content-subscribe .content-header .heading {
    font-size: 16px;
  }
}

@media (min-width: 1400px) and (max-width: 1599.98px) {

  .footer-content .content-heading .heading, .footer-content-subscribe .content-header .heading {
    font-size: 18px;
  }
}