.forgot-pass-section {
  width: 100%;
  height: 100%;
  position: relative;
  display: block;
}

.forgot-pass-section .forgot-pass-card {
  background: rgba(245, 245, 245, 0.5);
  backdrop-filter: blur(4.5px);
  -webkit-backdrop-filter: blur(4.5px);
  box-shadow: 0px 5px 15px #00000040;
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}
.forgot-pass-section .forgot-pass-card .card-title{
  font-family: var(--primaryFontBold);
  text-align: center;
}
.forgot-pass-section .forgot-pass-card .card-text{
  font-family: var(--primaryFontLight);
  text-align: center;
}
.forgot-pass-section .forgot-pass-card a{
  text-decoration: none;
}

.sonar-wrapper {
  position: relative;
  z-index: 0;
  overflow: hidden;
}

.sonar-emitter {
  position: relative;
  margin: 32px auto;
  width: 60px;
  height: 60px;
  border-radius: 9999px;
  background-color: #cb9dea;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sonar-emitter .icon{
  width: 40px;
  height: 40px;
}

.sonar-wave {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 9999px;
  background-color: #cb9dea;
  opacity: 0;
  z-index: -1;
  pointer-events: none;
}

.sonar-wave1 {
  animation: sonarWave 2s linear infinite;
}

.sonar-wave2 {
  animation: sonarWave 2s 2s linear infinite;
}

.sonar-wave3 {
  animation: sonarWave 2s 2s linear infinite;
}

.sonar-wave4 {
  animation: sonarWave 2s 1.5s linear infinite;
}

@keyframes sonarWave {
  from {
    opacity: 0.4;
  }
  to {
    transform: scale(2);
    opacity: 0;
  }
}