
.pricingUpgrade .pricingPlanUPgrade {
    flex-wrap: wrap;
}
.pricingUpgrade .pricingPlanUPgrade .pricingCard{
    margin-bottom: 100px;
}
.pricingUpgrade .bannerAds{
    margin-top: 0;
}
.pricingUpgrade .monthText{
    display: none;
}


@media (max-width: 575.98px) {
    .pricingUpgrade .pricingPlanUPgrade {
        height: 835px;
        display: block;
        overflow-y: scroll;
        box-shadow: inset 1px -20px 20px 2px #00000008;
        margin-bottom: 100px;
        padding-top: 100px !important;
        position: relative;
        overflow-x: hidden;
    }
    
    .pricingUpgrade .pricingPlanUPgrade:before {
        content: "";
        position: absolute;
        height: 100px;
        z-index: 10;
        top: 0;
        box-shadow: inset 0px 20px 20px 0px #00000008;
        width: 100%;
    }
}