


.doaminPage .tooltip_domain{
    top: 40px !important;
}
.doaminPage .tooltip_domain .tooltip-arrow{
    display: none;
}
.doaminPage .tooltip_domain .tooltip-inner{
    background: #FFFFFF;
    box-shadow: 0px 2px 6px rgb(0 0 0 / 20%);
    border-radius: 8px 0px 8px 8px;
    color: #858585;
    text-align: left;
    width: 157px;
    font-size: 13px;
    line-height: 18px;
    padding: 10px 13px;
}
.doaminPage .validate button{
    background: #858585;
}
.doaminPage .validate button:hover{
    background: #929090;
}
.doaminPage .validate button:focus{
    background: #929090 !important;
    box-shadow: none !important;
}
