    border: 1px solid #858585 !important;
} */

.doaminPage .active #domainBtn{
    background: #169F0A;
    /* pointer-events: auto !important;
    cursor: pointer;
    opacity: 1 !important; */
}
.doaminPage .active #domainBtn:hover{
    background: #1caf0f;
}
.doaminPage .active #domainBtn:focus{
    background: #169F0A !important;
    box-shadow: none !important;
}
