    border: 1px solid #169F0A !important;
} */

.doaminPage .pending button{
    background: #DC3646;
    /* pointer-events: none; */
    opacity: 1 !important;
}
.doaminPage .pending button:hover{
    background: #e93e4f;
}
.doaminPage .pending button:focus{
    background: #DC3646 !important;
    box-shadow: none !important;
}
/* .pending input{
    border: 1px solid #DC3646 !important;
} */
.doaminPage .pending .actionButton span{
   
}
.doaminPage .pending .actionButton .tickSvg,
.doaminPage .active .actionButton .tickSvg{
    background: #169F0A;
    position: absolute;
    height: 100%;
    top: 0;
    width: 100%;
    left: 0;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.doaminPage .inputDomain.pending .actionButton .closeSvg,
.doaminPage .inputDomain.active .actionButton .closeSvg{
    background: #DC3646;
    position: absolute;
    height: 100%;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 100;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.doaminPage .inputDomain.pending .actionButton .closeSvg,
.doaminPage .inputDomain.active .actionButton .closeSvg{
    opacity: 0;
}
.doaminPage .inputDomain.pending .tickSvg:hover .closeSvg,
.doaminPage .inputDomain.active .tickSvg:hover .closeSvg{
    opacity: 1 !important;
}
.doaminPage .tickSvg:hover .msg_disconnet{
    opacity: 1 !important;
}

.doaminPage .inputDomain.pro button{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%) !important;
    opacity: 1 !important;
}
.doaminPage .pro button:hover{
    background: linear-gradient(289.79deg, #f19b39 23.59%, #7f4be6 63.85%);
}
.doaminPage .pro button:focus{
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%) !important;
    box-shadow: none !important;
}
/* .pro input{
    border: 1px solid #9461FA !important;
} */
.doaminPage .inputDomain input{
    padding-right: 60px;
}
.doaminPage .tooltip_domain_search {
    position: relative;
    top: 5px !important;
}

.doaminPage .tooltip-arrow{
    top: -7px !important;
}
.doaminPage .activeStatus{
    display: none;
}

.doaminPage .pending .activeStatus {
    background: #169F0A;
    position: absolute;
    height: 100%;
    top: 0;
    width: 100%;
    left: 0;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.doaminPage .pending span {
    display: none;
}
.doaminPage .msg_disconnet {
    position: absolute;
    background: #fff;
    top: 9px;
    font-size: 14px;
    padding: 5px 10px;
    margin-bottom: 0;
    border-radius: 3px;
    color: #DC3646;
    box-shadow: -5px 0px 20px #0000000f;
    left: 60px;
    opacity: 0;
    cursor: auto !important;
}

.doaminPage .msg_disconnet:before {
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-right: 10px solid #fff;
    border-bottom: 10px solid transparent;
    position: absolute;
    left: -8px;
    content: "";
}

.doaminPage .failed .tickSvg svg{
    display: none;
}
.doaminPage .failed .tickSvg .closeSvg svg{
    display: block;
}

.doaminPage .failed .tickSvg .closeSvg{
    background: #DC3646;
    position: absolute;
    height: 100%;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 100;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.doaminPage .validate .tickSvg{
    display: none;
}
.doaminPage .validate .intialIcon{
    display: none;
}
.doaminPage .inputDomain .tickSvg{
    display: none;
}
.doaminPage .intialIcon{
    display: block;
}
.doaminPage .inputDomain button{
    background: #929090 !important;
}
.doaminPage .pending #validDomain,
.doaminPage .active #validDomain,
.doaminPage .validate #validDomain,
.doaminPage .failed #validDomain{
    display: none !important;
}
.doaminPage #validDomain{
    display: none;
}

.doaminPage .urlStatus{
    display: none;
    position: absolute;
    background: #e9ecef;
    width: 100%;
    height: 48px;
    border-radius: 3px;
    padding-left: 16px;
    font-size: 14px;
    line-height: 49px;
    color: #5E606A;
    font-weight: 600;
}

.doaminPage .active .urlStatus{
    display: block;
    cursor: pointer;
}

.doaminPage .urlStatus svg {
    width: 26px;
    height: 24px;
    top: 1px;
    position: relative;
    fill: #343434;
}
.doaminPage .activeProicon{
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(289.79deg, #FFA847 23.59%, #9461FA 63.85%) !important;
    z-index: 100000;
    left: 0;
    top: 0;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    line-height: 44px;
}

.doaminPage .proActive .actionButton {
    pointer-events: none !important;
    cursor: auto !important;
}
.doaminPage .proActive .msg_disconnet,
.doaminPage .proActive .urlStatus{
    display: none;
}


.doaminPage .pending.proActive span {
    display: block;
}

.doaminPage .failed .intialIcon {
    display: none;
}
.doaminPage .inputDomain.failed .tickSvg{
    display: block;
}
.doaminPage #invalidDomain{
 display: none;
}
.doaminPage .failed #invalidDomain{
    display: block;
}