.borderless-card {
    position: relative;
    border: none;
    text-align: center;
    height: 100%;
    background-color: transparent;
}

.borderless-card:hover .card-edit-block {
    display: flex;
}

.borderless-card .card-header {
    background: transparent;
    border-bottom: none;
    border-radius: 17px;
    height: 294px;
    width: 100%;
    padding: 0;
}

.borderless-card .card-header .img {
    border-radius: inherit;
    height: 100%;
    object-fit: cover;
    width: 100%;
}

.borderless-card .card-header.book-cover-img {
    height: 374px;
}

.theme-book-section .card .card-body.book-sec-body {
    min-height: 9em;
}

.borderless-card .card-body .card-title {
    font-family: var(--themePrimaryFontBold);
    font-size: 1.125em;
    margin-bottom: 5px;
}

.borderless-card .card-body .card-text {
    font-family: var(--themePrimaryFontLight);
    font-size: 0.875em;
    line-height: 20px;
}

.borderless-card .card-footer {
    background-color: transparent;
    border-top: none;
}

.borderless-card .card-footer .link>a {
    color: var(--themeTextColorSecondary);
    text-decoration: none;
}

.borderless-card .card-footer .btn-outline-primary {
    color: var(--themeTextColorSecondary);
    border-color: var(--themeTextColorSecondary);
}

.borderless-card .card-footer .btn-outline-primary:hover {
    color: #ffffff;
    border-color: var(--themeTextColorSecondary);
    background-color: var(--themeTextColorSecondary);
}

.borderless-card .card-footer .btn-check:active+.btn-outline-primary,
.borderless-card .card-footer .btn-check:checked+.btn-outline-primary,
.borderless-card .card-footer .btn-outline-primary.active,
.borderless-card .card-footer .btn-outline-primary.dropdown-toggle.show,
.borderless-card .card-footer .btn-outline-primary:active {
    color: #fff;
    background-color: var(--themeTextColorSecondary);
    border-color: var(--themeTextColorSecondary);
}

/* borderless card music variation */

.borderless-card.music {
    text-align: left;
}

.borderless-card.music .card-body {
    padding-left: 0;
    padding-right: 0;
}

.borderless-card.music .card-body .card-title {
    font-size: 1.5em;
    font-family: var(--themePrimaryFontMedium);
}

.borderless-card.music .card-footer {
    padding-left: 0;
    padding-right: 0;
}

.borderless-card.music .card-footer .btn-box {
    text-align: left !important;
}

.borderless-card.music .card-footer .btn-box button {
    width: 70% !important;
}

/* Music Audio Card Styling */
.music-audio-player {
    z-index: 2;
    position: relative;
    top: -58px;
}

.rhap_play-pause-button {
    border-width: 0px !important;
    background-color: transparent !important;
    font-size: 33px;
    color: white;
}

/* END MUSIC AUDIO */

@media (max-width: 575.98px) {

    .borderless-card .card-header {
        height: 100%;
        max-height: 275px;
        min-height: 275px;
    }

    .borderless-card.music .card-body .card-title {
        font-size: 1.1em;
    }
}