    display: flex;
} */
.title {
    font-size: 2em;
    font-family: var(--secondaryFontMedium);
    color: var(--grey);
}

.analyticSubheading {
    font-family: var(--secondaryFontMedium);
    color: #858585;
    font-size: 1.125em
}
.modalAnalytics{
    width: 50%;
    z-index: 5;
}

.modalBox{
    font-family: var(--secondaryFont);
    color: var(--purple);
    text-align: center;
}
.modalHeader{
    /* padding: 20px; */
    left: 48%;
    position: relative;


}
.modalIcon{
    text-align: left;
    color: var(--purple);
    font-size: 1.35em;
}
.btnBox{
    text-align: center;
    margin-top: 20px;
    font-family: var(--secondaryFontBold);
}
.parent {
	width: 100%;
	height: 100%;
	position: relative;
	overflow: hidden;
}
.parent .blur {
    display: block;
    -moz-filter: blur(5px);
    -o-filter: blur(5px);
    -ms-filter: blur(5px);
    filter: blur(5px);
    position: absolute;
    top: -10px;
    right: -10px;
    bottom: -10px;
    left: -10px;
    background-color: #ccc;
}
.parent h1 {
	position: relative;
	font-size: 25px;
	color: var(--grey);
	z-index: 5;
	text-align: center;
	line-height: 150px;
}

