/* Analytics Summary Card Styling */

.classNameTitle {
    font-family: var(--secondaryFontMedium);
    font-size: 1.125em;
    color: var(--white);

}

.classNamesubTitle {
    font-family: var(--secondaryFontBold);
    font-size: 2.25em;
    color: var(--white);
}

.classNameDesc {
    font-size: 1.125em;
    color: var(--white);
    font-family: var(--secondaryFontMedium);
}

.imgBox {
    width: 50%;
    right: -8%;
    opacity: 0.5;
    top: 9%;
}

.dollarImg {
    width: 73%;
    right: -23%;
    opacity: 0.5;
    top: 12%;
}
.linkText {
    font-family: var(--primaryFont);
    color: #9B4200;
    font-size: 1em;
    text-decoration: none;
}

.linkText:hover {
    color: #9B4200;
}

/* END */
.sectionPieChart {
    height: 89%;
}
.sectionPieChart :global(.card-body){
   height: 630px;
}
.sectionBox {
    margin-bottom: 1.5em;
}

.chartBody {
    font-size: 1.125em;
    font-family: var(--secondaryFont);
    color: #5E606A;
    letter-spacing: 2px;
}

/* .cardTwo{
    background-color: var(--purple);
} */
.cardThree {
    background-color: #F8AA54;
}

.cardFour {
    background-color: var(--orange);
}

.square {
    display: flex;
    width: 20px;
    height: 20px;
    /* vertical-align:`; */
}

.containerCheckbox {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
}

.containerCheckbox:hover input {
    background-color: var(--purple);
}

.containerCheckbox input:checked {
    background-color: var(--purple);
}

.containerCheckbox .checkbox:checked~.checkbox {
    background-color: var(--purple);
}

.checkbox {
    width: 15px;
    height: 15px;
    border: 1px solid black;
    display: inline-block;
}

.checkboxHighlighted{
    background-color: var(--purple);
}

.checkbox:checked[type=checkbox] :checked {
    background-color: var(--purple);
}

.checkbox:checked {
    background-color: var(--purple) !important;
}

.checkbox:checked[type=checkbox] {
    background-color: var(--purple) !important;
}

.checkbox:active {
    color: var(--purple);
    background-color: var(--purple);
}

.squareBoxColor {
    background-color: var(--gold);
}

/* Progress Bar Styling */
.barHeading {
    width: 10%;
    margin-top: 25px;
    margin-left: 25px;
}

.progressTrack {
    height: 20px;
    width: 80%;
    margin-left: 10px;
    position: relative;
    top: 0px;
    margin-top: 30px;
    background-color: transparent;
    border-radius: 50px;
    margin-bottom: 10px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
}

.fillerProgress {
    height: 100%;
    border-radius: inherit;
    text-align: right;
    background-color: #EAD0FF;

}


.labelStyles {
    padding: 5px;
    color: white;
    font-weight: bold;
    position: relative;
    top: -2px;
}

.emptyBoxContainer{
    /*display:flex;
    */background-color: transparent;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 50%;
    transform: translate(-50%);
    -webkit-transform: translate(-50%);
    -moz-transform: translate(-50%);
    -ms-transform: translate(-50%);
    -o-transform: translate(-50%);
    z-index: 1;
}

.value{
    min-width: 105px;
    display: flex;
}

.keyLabelsText{
    min-width: 136px;
    text-align: left;
}
.keyLabels:hover{
    cursor: pointer;
}
.keyLabels:hover .checkbox{
    background: var(--purpleDisabled);
}

.donutHolder {
    min-height: 456px;
}

.donutHolder :global(.card-body) {
    align-items: center;
    display: flex;
    flex: 0 0 0 100%;
}

/* Media Queries */
/* Mobile devices small screen */

@media (max-width: 380px) {
    .title{
        font-size: 1.35em;
    }

    .keyLabels{
        font-size: 0.85em !important;
    }
    .barChart{
        font-size: 10px !important;
        padding: 0.5em;
    }
    .labelStyles {
    right: 5px;
    }
}

/* Mobile devices large screen */

@media (max-width: 575.98px) {
    .overlayModal{
        top: 45%;
        left: 50%;
        width: 85%;
        position: fixed;
    }
    .emptyBoxContainer{
    width: auto;
    height: auto;
    }
    .progressBarSec{
        flex-direction: column !important;
    }

    .progressBar{
        position: relative;
        top: -5px;
        right: 5px;
    }
    .barHeading {
        width: 100%;
        margin-left: 10px !important;
        margin-top: 10px;
    }

    .barHeading > h5 {
        font-size: 1.15em;
        margin-bottom: 0px !important;
        margin-left: 0px !important;
    }
    .progressTrack {
        width: 100%;
        margin-top: 15px;
    }
    .labelStyles {
        font-size: 0.75em;
        top: -3px;
    }
    .classNamesubTitle {
        font-size: 1.75em;
    }

    .keyLabels{
        font-size: 1em;
    }
    .chartBody{
        font-size: 1em;
        letter-spacing: 1px;
        text-align: left;
    }
    .checkbox {

        width: 12px;
        height: 12px;
    }
}
/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
    .title {
        font-size: 1.75em;

    }
    .analyticSubheading {
        font-size: 1em
    }
    .analyticsCard{
        min-height: 170px;
        margin-top: 15px;
    }
    .keyLabels{
        font-size: 1.15em !important;
    }
    .chartBody{
        font-size: 1.15em;
        letter-spacing: 1px;
        text-align: left;
    }
    .barHeading {
        width: 30%;
        margin-left: 10px !important;
    }


}
@media (min-width: 1024px) and (max-width: 1199.99px) {
    .keyLabels{
        font-size: 1.15em !important;
    }
    .chartBody{
        font-size: 1.15em;
        letter-spacing: 1px;
        text-align: left;
        line-height: 22px;
    }
    .barHeading {
        width: 30%;
        margin-left: 10px !important;
    }


}
/* Extra large devices (large desktops, 1200px and up) */

@media (min-width: 1200px) and (max-width: 1399.98px) {
    .keyLabels{
        font-size: 1.15em !important;
    }
    .barHeading {
        width: 30%;
        margin-left: 10px !important;
    }
    .chartBody {
        letter-spacing: 1px;
    }

    .labelStyles {
        padding: 3px;
    }

}

@media (min-width: 1400px) and (max-width: 1599.98px) {
    .barHeading {
        width: 30%;
        margin-left: 10px !important;
    }

    .classNameDesc{
        font-size: 1em;
    }
}

.sectionAreaBox {
    margin-bottom: 50px;
}
