.subStepsWrapper {
    position: fixed;
    left: 0;
    right: 0;
    background: var(--bs-white);
    width: 400px;
    top: 110px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 5555;
    padding: 10px 18px;
    border-radius: 8px;
    box-shadow: 0 2px 14px 0px #aaa;
    color: var(--black);
    min-height: 60px;
    margin: 15px 0;
}

:global(.modal-open) .subStepsWrapper,
.subStepsWrapper:global(.shareLink) {
    top: 0;
}

.subStepsWrapper[data-step="1"] .icon {
    margin-left: 50%;
}

.subStepsWrapper[data-step="2"] .icon {
    margin-left: 100%;
    left: -24px;
}

.progress {
    color: var(--black);
    margin: 15px 0;
    position: relative;
}

.progressBar {
    height: 8px;
    background: var(--orange);
    border-radius: 10px;
    opacity: 0.5;
    width: 100%;
}

.progressBar.completed {
    opacity: 1;
}

.progressBar ~ .progressBar {
    margin-left: 10px;
}

.counter {
    min-width: 50px;
    text-align: right;
}

.icon {
    display: block;
    width: 32px;
    margin-top: -12px;
    border-radius: 30px;
    text-align: center;
    font-size: 20px;
    background: transparent;
    height: 32px;
    margin-left: -5px;
    position: absolute;
    bottom: 0;
    top: 0;
    transition: all 0.5s;
    left: -5px;
}


@media (max-width: 800px ) {
    .subStepsWrapper {
        top: 0px;
        margin: 5px 0;
        padding: 4px 6px;
    }

    .subStepsWrapper {
        width: calc(100% - 30px );
    }

    #heroBannerUploadBtn.card-edit-block.hightlighter {
        visibility: visible !important;
    }
}
