/* Action button  */

.circle-image-card.btn-edit-generic {
  position: absolute;
  background: white !important;
  width: 80%;
  height: fit-content;
  top: 50%;
  left: 50%;
  border: 1px solid #0000003d;
  border-radius: 14px;
  box-shadow: 3px 3px 6px #00000029;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  max-width: 85%;
}

.circle-image-card.btn-edit-generic{
  padding-top: 10px !important;
  padding-bottom: 10px !important;
}

.circle-image-card .upload-icon-area {
  background-color: #00000454;
  border-radius: 50px;
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.enable-btn-card:disabled{
color: var(--primaryColor);
opacity: 25%;
}
/* theme generic section code */

.circle-image-card {
  position: relative;
  background-color: var(--themePrimaryColor);
  text-align: center;
  border-radius: 17px;
  border: none;
  min-height: 471px;
  height: 100%;
}
.circle-image-card:hover .card-edit-block{
  display: flex;
}
.circle-image-card .card-body {
  padding: 26px;
}
.circle-image-card .card-footer {
  padding: 26px;
  padding-top: 0;
}
.circle-image-card .img-Card {
  /* border-radius: 17px; */
  width: 130px;
  height: 130px;
  object-fit: cover;
  margin-bottom: 24px;
  border-radius: 50em;
}
.circle-image-card .theme-img-header {
  border-radius: 17px 17px 0px 0px;
  width: 100%;
  height: 272px !important;
  text-align: center;
}
.circle-image-card .theme-img-header .top-img-Card {
  border-radius: 0px;
  border-top-left-radius: 17px !important;
  border-top-right-radius: 17px !important;
  width: 100%;
  height: 272px !important;
  object-fit: cover;
}

.circle-image-card .card-title {
  font-family: var(--themePrimaryFontBold);
  font-size: 26px;
  margin-bottom: 6px;
  color: var(--themeTextColor);
  line-height: 25px;
}
.circle-image-card .card-text {
  font-family: var(--primaryFontMedium);
  font-size: 22px;
  line-height: 26px;
  color: var(--themeTextColor);
}
.circle-image-card .card-footer {
  background-color: transparent;
  border: none;
  opacity: 1;
}
.circle-image-card .btn-theme-outline-primary {
  /* background-color: transparent;
