/* Theme Package Card */
.theme-package-card {
  background-color: var(--themePrimaryColor);
  border-radius: 17px;
  border: none;
  min-height: 491px;
  height: 100%;
}
.theme-package-card .card-body {
  padding: 24px 26px;
  padding-bottom: 0;
}
.theme-package-card .package-price .currency+.character-limit{
  z-index: 7;
}
.theme-package-card .package-price {
  display: flex;
  justify-content: center;
  font-size: 1em;
  color: var(--themeTextColor);
  text-align: center;
  font-family: var(--themePrimaryFontExtraBold);
  margin-bottom: 24px;
}

.theme-package-card .card-body .card-text{
  color: var(--themeTextColor);
}

.theme-package-card .package-price .price {
  font-size: 2.5em;
  top: -0.26em;
}
.theme-package-card .package-price .price+.character-limit{
  top: -48px;
}
.theme-package-card .package-price .duration {
  font-family: var(--themePrimaryFontLight);
}
.theme-package-card .description {
  font-family: var(--themePrimaryFontMedium);
  font-size: 1.0625em;
  text-align: center;
  color: var(--themeTextColor);
  margin: 0 auto;
  margin-bottom: 23px;
  width: 90%;
}

.theme-package-card .list-icon {
  margin-right: 12px;
}

.theme-package-card .list-icon .icon {
  color: var(--themeTertiaryColor);
}

.theme-package-card .title-box {
  background-color: var(--themeTextColor);
  padding: 5px 10px;
  color: var(--themePrimaryColor);
  display: inline-block;
  width: fit-content;
  margin-top: 31px;
  margin-bottom: 7px;
  border-top-right-radius: 18px;
  border-bottom-right-radius: 5px;
}

.title-box h1 {
  font-size: 1.25rem;
  font-family: var(--themePrimaryFontBold);
  margin-bottom: 0;
}

.theme-package-card .guide-msg {
  display: none;
  background-color: var(--themeTextColor);
  color: var(--themePrimaryColor);
  position: absolute;
  top: 0px;
  right: 0;
  padding: 2px 10px;
  border: 2px;
  font-size: 12px;
}
.theme-package-card:hover .guide-msg {
  display: inline-block;
  border-top-right-radius: inherit;
  border-bottom-left-radius: inherit;
}

.theme-package-card .btn-primary-theme {
  min-width: 181px;
}
.theme-package-card .card-footer {
  background-color: transparent;
  border-top: none;
  text-align: center;
  padding-bottom: 26px;
  padding-top: 0;
}
.theme-package-card .link a{
  color: var(--themeTextColor);
  cursor: pointer;
}

.theme-package-card .package-service-list ul li .list-text {
  /* margin-left: 10px; */
  font-size: 0.875em;
  color: var(--themeTextColor);
  word-break: break-word;
}



/* Mobile devices small screen */
@media (max-width: 380px) {
  .circle-image-card {
    min-height: 344px;
  }
  .circle-image-card .card-body {
    padding: 1em;
  }
  .circle-image-card .card-title {
    font-size: 1.25em;
  }

  .circle-image-card .card-text {
    font-size: 1em;
    line-height: 1.13em;
  }

  .circle-image-card .card-text {
    font-size: 1em;
  }

  .mobile-cropper-title{
    font-size: 12px;
  }
  .modal-dialog-centered{
        min-height: calc(100% - -16rem);
  }
  .modal-dialog-centered.cropper-modal-img{
    min-height: calc(100% - -28rem);
  }
  .mobile-cropper-modal{
    justify-content: center !important;
    padding: 0.3rem !important;
  }
  .theme-package-card .package-price .currency{
    font-size: 0.875em;
  }
  .theme-package-card .package-price .duration {
     font-size: 0.675em;
   }
   .theme-package-card .package-price .price{
     font-size: 1.2375em !important;
   }
}

@media (max-width: 575.98px) {
  .circle-image-card {
    min-height: 387px;
    height: 100%;
  }
  .theme-package-card {
    min-height: 387px;
  }
  .circle-image-card .card-title {
    font-size: 1.25em;
    line-height: 20px;
  }

  .circle-image-card .card-text {
    font-size: 1em;
    line-height: 1.13em;
  }
  .circle-image-card .card-body {
    padding: 1em;
  }
  .circle-image-card .img-Card {
    margin-bottom: 16px;
  }
  .circle-image-card .card-footer {
    padding: 16px;
    padding-top: 0;
  }
  .theme-package-card .package-price .currency{
    font-size: 1em;

  }
   .theme-package-card .package-price .duration {
     font-size: 0.875em;
   }
   .theme-package-card .package-price .price{
     font-size: 1.4375em;
   }

   /* style 3 service card */
}


