.contentBox{
    background-color: #FBFCFF;
}

.contentBoxBody{
    padding: 0px;
}

.managePassWrapper{
    padding: 24px;
    padding-bottom: 0px;
}

.managePassWrapper .inputGroup{
    margin-bottom: 24px;
}

.managePassWrapper .inputField{
    height: 48px;
    padding: 16px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}

.managePassWrapper .label{
    font-family: var(--primaryFontBold);
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 8px;
}

.lineBar{
    margin-top: 24px;
    margin-bottom: 24px;
}

.btnBox{
    padding: 24px;
    padding-top: 0px;
    text-align: right;
}

.btnBox .btnPrimary{
    background-color: var(--primaryColor);
    color: #ffffff;
    padding: 8px 24px;
    border-radius: 50em;
    -webkit-border-radius: 50em;
    -moz-border-radius: 50em;
    -ms-border-radius: 50em;
    -o-border-radius: 50em;
}

.btnBox .btnPrimary:hover{
    background-color: var(--primaryColorHover);
}

@media (max-width: 575.98px) {
    .contentBoxBody{
        padding: 0px !important;
    }
    .managePassWrapper{
        padding: 16px;
        padding-bottom: 0px;
    }
    .managePassWrapper :global(.row){
        flex-direction: column-reverse;
    }
    .btnBox{
        padding: 16px;
        padding-top: 0px;
    }

}