.horizontalLongCard{
    width: 1180px;
    height: 160px;
    justify-content: center;
    text-align: center;
    background-color: var(--themePrimaryColor);
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
}

.priceVariantFooter{
    background-color: transparent;
    border-top: 0px;
   
}
.longCard{
    align-items: center;
}
.btnClassName{
    background-color: var(--themeTextColor) !important;
    border-color: var(--themeTextColor) !important;
    color: var(--themePrimaryColor) !important;
    height: auto;
    padding: 0.55em 0.55em ;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
}
 .linkBtn:hover{
    background-color: transparent !important;
    color: var(--themeTextColor) !important;
}

.linkBtn{
    color: var(--themePrimaryColor) !important;
    text-decoration: none !important;
}

.subHeadingSection{
    width: fit-content;
    display: flex;
    flex-direction: column;
}
.subHeading{
    margin-bottom: 0px;
}
.rightSection{
    padding: 0px 5.5rem;
    border-left: 1px solid;
    border-color: var(--grey);
}

/* Mobile devices small screen */
@media (max-width: 380px) {
  

}

@media (max-width: 575.98px) {
    .cardHeading, .price{
        font-size: .875rem;
    }
    .description{
        font-size: .75rem;
        color: var(--grey);
    }
    .horizontalLongCard{
        width: 360px;
        height: 100px;
    }
    
    .btnClassName{
        padding: 0em !important;
    }
    .linkBtn{
        font-size: .75rem;
    }
    .rightSection{
        padding: 0px 0.25rem;
        padding-left: 0.55rem;
    }
}

/* Tablet view (768-922) */
@media (min-width: 768px) and (max-width: 921.98px) {
    .horizontalLongCard{
        width: 649px;
        height: 140px;
    }
    .cardHeading, .price{
        font-size: 1.25rem;
    }
    .description{
        font-size: 1rem;
        color: var(--grey);
    }
    .linkBtn{
        font-size: 0.75rem;
    }
    .rightSection{
        padding: 0px 0.25rem;
        padding-left: 0.55rem;
    }
    .btnClassName{
        padding: 0em !important;
    }   
    .subHeadingSection{
        align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: column;
    }
}

