.btn_remove_loader {position: relative;}

.btn_remove_loader .loader-box {position: absolute;right: 0;
    top: -1px;}
.btn_remove_loader .loader-box img {
    width: 40px;
    height: 40px;
}
.activeModalRemove {
    padding-right: 48px !important;
}
.btn_delete {
    position: relative;
}

@media (max-width: 575.98px) {
    .btn_remove_loader .loader-box{
        top: -5px;
        right: -6px;
    }
    .activeModalRemove svg:first-child{
        display: none;
    }
    .btn_remove_loader .activeModalRemove {
        width: 30px !important;
        padding: 0 !important;
    }
}