
/* Dashboard payment integration */

.integration-info-card .card-body .card-text {
    font-family: var(--primaryFontLight);
    font-size: 1.125em;
    line-height: 22px;
    color: #52545A;
}
.contactUs_btn{
    color: var(--theme2ColorBtn);
    cursor: pointer;
}

.integration-info-card .card-body .card-text.info {
    font-family: var(--primaryFontSemiBold);
}
.heading_payment{
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    font-family: var(--primaryFontBold);
    color: #282828;
    margin-bottom: 16px;
    text-transform: capitalize;
}
.integration-update-card .card-body .num-input {
    height: 50px;
    border-color: #000;
    min-width: 320px;
}

.integration-update-card .otp-confirm-box .otp-input-box .form-control {
    width: 3em !important;
    height: 50px;
}

.user_history_tabel th {
    color: #5E606A;
    padding-bottom: 24px;
}
.user_history_tabel td {
    font-size: 14px;
    line-height: 28px;
}
.user_history_tabel .name_email_history {
    display: grid;
}
.user_history_tabel .name_email_history .email_history {
    color: #95969B;
    font-size: 12px;
}
.color_invoiceRef{
    color: #F58634;
}
.Unpaid-badge{
background: #FBE3E5 !important;
color: #DC3646;
}
.paid-badge{
background: #D4F9D1 !important;
color: #169F0A;
}
.pending-badge{
    background: #E7E7E7 !important;
    color: #5B3F91;
    }
    .withdrawn-badge{
        background: #FBE3E5 !important;
        color: #DC3646;
    }
.Unpaid-badge:before{
    border-bottom: 9px solid #FBE3E5;
}
.paid-badge:before{
    border-bottom: 9px solid #D4F9D1;
}
.badgeLabel {
    font-size: 12px;
    width: 88px;
    height: 24px;
    border-radius: 4px !important;
    line-height: 19px;
    position: relative;
}
.toolTip_badge {
    top: 38px;
    position: absolute;
    left: -18px;
    width: 123px;
    height: 28px;
    line-height: 28px;
    font-size: 11px;
    border-radius: 5px;
    bottom: -33px;
    display: none;
    color: #000;
    z-index: 10;
    box-shadow: 0px 0px 12px #0000000d;
}
.Unpaid-badge .toolTip_badge{
    color: #DC3646;
}
.paid-badge .toolTip_badge{
    color: #169F0A;
}
.pending-badge .toolTip_badge{
    background: #E7E7E7 !important;
    color: #5B3F91;
}
.withdrawn-badge .toolTip_badge{
    background: #FBE3E5 !important;
        color: #DC3646;
}
.badgeLabel:hover .toolTip_badge{
    display: block;
}
.toolTip_badge:before {
    content: "";
    width: 0;
    height: 0;
    border-left: 12px solid transparent;
    border-right: 12px solid transparent;
    border-radius: 3px;
    position: absolute;
    top: -8px;
    left: 48px;
}
.btn_download_invoice {
    border: 1px solid #E7E7E7;
    width: 26px;
    height: 26px;
    border-radius: 30px;
    position: relative;
    transition: .3s;
    background: transparent;
    top: 6px;
    margin-left: 15px;
}

.btn_download_invoice svg {
    position: absolute;
    top: 6px;
    left: 5px;
    width: 14px;
    height: 14px;
}

.btn_download_invoice:hover {
    background: #F58634;
}

.btn_download_invoice:hover svg{
    filter: brightness(0) invert(1);
}
.inovice_hover {
    transition: .3s;
}

.inovice_hover:hover {
    background: #F5F6F8;
    cursor: pointer;
}

